import { EcsCallType, EcsConfig, IEcsProvider } from 'skype-calling-interface';

import * as async from '../../common/ts/async';
import * as SlimCoreCommon from './SlimCoreCommon';
import Enums = SlimCore.Enums;

export default class SlimCoreElectronEcsProvider implements IEcsProvider {
  public constructor(private _slimcoreInstance: SlimCoreCommon.Engine) { }

  public setEcsConfig(config: EcsConfig): Promise<void> {
    try {
      this._slimcoreInstance.setEcsConfig(config.ecsBlob, config.userIdentity, config.etag, true);
      return Promise.resolve<void>();
    } catch (e) {
      return Promise.reject(e);
    }
  }

  public shouldTriggerCQF(username: string, callId: string, callType: EcsCallType): Promise<boolean> {
    const block = (callType: EcsCallType) => {
      let slimCoreCallType = Enums.CallType.CallTypeS2s;
      if (callType === EcsCallType.CallTypeGroup) {
        slimCoreCallType = Enums.CallType.CallTypeGroup;
      } else if (callType === EcsCallType.CallTypePstn) {
        slimCoreCallType = Enums.CallType.CallTypePstn;
      } else if (callType === EcsCallType.CallTypeS2s) {
        slimCoreCallType = Enums.CallType.CallTypeS2s;
      }
      return this._slimcoreInstance.shouldTriggerCQF(username, callId, slimCoreCallType);
    };
    return async.asap(() => block(callType));
  }

  public getEcsQueryParameters(): Promise<string> {
    try {
      return Promise.resolve<string>(this._slimcoreInstance.getEcsQueryParameters());
    } catch (e) {
      return Promise.reject(e);
    }
  }
}



