import {
    ContentSharingStatus, IContentSharingSession, SessionTerminatedReason, TerminatedReason
} from 'skype-calling-interface';
import * as async from '../../common/ts/async';
import { CONTENT_SHARING_OPERATIONS as OPERATION } from '../../common/ts/callConstants';
import * as SlimCoreCommon from './SlimCoreCommon';

import { ContentSharingPlatformImpl, ContentSharingSession } from '../../common/ts/contentSharingSession';
import SlimCoreElectronBase from './SlimCoreElectronBase';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { ICallTelemetry } from '../../common/ts/callTelemetry';
import { generateCauseId } from '../../common/ts/causeId';
import { IInternalLogger } from '../../common/ts/internalLogger';
import { callOperation, causeId, OPERATION_TYPE } from '../../common/ts/operationDecorators';
import OperationHandler from '../../common/ts/operationHandler';

export default class SlimCoreElectronContentSharing extends SlimCoreElectronBase<{}> implements IContentSharingSession, ContentSharingPlatformImpl {
    public contentSharingState: string;
    public contentSharingStatus: ContentSharingStatus;
    public contentSharingTerminationReason: SessionTerminatedReason;
    public contentSharingIdentity: string;
    public contentSharingGuid: string;

    public noFailureReason = SlimCore.Enums.ContentSharingFailureReason.NoFailure;
    private _callOperationHandler: OperationHandler;
    private _session: ContentSharingSession;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _slimcoreCallHandler: SlimCoreCommon.CallHandler,
        public slimCoreContentSharingObjectId: SlimCore.ObjectId,
        private _nativeContentSharing: SlimCoreCommon.ContentSharing,
        private callTelemetry: ICallTelemetry
    ) {
        super(logger.createChild(() => `ContentSharing[${this.slimCoreContentSharingObjectId}]`), settings, engine);
        this._logger.info('constructor');

        this._session = new ContentSharingSession(this, this._logger);
        this._callOperationHandler = new OperationHandler(this._logger, this.callTelemetry);
        this._onContentSharingPropertyChanged(SlimCore.Enums.Property.ContentSharingStatus, data => this._onContentSharingStatusChanged(data.value as number));
        this._onContentSharingPropertyChanged(SlimCore.Enums.Property.ContentSharingState, data => this._onContentSharingStateChanged(data.value as string));
        this._onContentSharingPropertyChanged(SlimCore.Enums.Property.ContentSharingFailureReason, data => this._onContentSharingResultCodeChanged(data.value as number));

        this.contentSharingStatus = this.convertContentSharingStatus(this.getIntProperty(SlimCore.Enums.Property.ContentSharingStatus));
        this.contentSharingGuid = this.getStrProperty(SlimCore.Enums.Property.ContentSharingId);
        this.contentSharingState = this.getStrProperty(SlimCore.Enums.Property.ContentSharingState);
        this.contentSharingIdentity = this.getStrProperty(SlimCore.Enums.Property.ContentSharingIdentity);
        this._logger.info(`Session created with status ${this.contentSharingStatus}, guid ${this.contentSharingGuid}`);

        this._registerDisposable(this._nativeContentSharing.handle('update-session-state-result', undefined, data => this._onSessionStateResult(data)));
        this._registerDisposable(this._nativeContentSharing.handle('take-control-result', undefined, data => this._onTakeControlResult(data)));
        this._registerDisposable(this._nativeContentSharing.handle('update-participant-state-result', undefined, data => this._onUpdateParticipantStateResult(data)));
        this._registerDisposable(this._nativeContentSharing.handle('join-result', undefined, data => this._onJoinResult(data)));
    }

    private _onContentSharingPropertyChanged(propKey: Enums.Property, listener: SlimCore.EventHandler<SlimCore.Engine.Events.ObjectPropertyChangedArgs>) {
        this._onObjectPropertyChanged(this.slimCoreContentSharingObjectId, propKey, listener);
    }

    public setContentSharingStatus(status: ContentSharingStatus) {
        this._logger.info(`setContentSharingStatus. currentStatus: ${this.contentSharingStatus} newStatus: ${status}`);
        if (this.contentSharingStatus === status) {
            return;
        }

        // Check if we are already in the final state
        if (this.contentSharingStatus === ContentSharingStatus.Done || this.contentSharingStatus === ContentSharingStatus.Failed) {
            return;
        }

        this._logger.info(`Changing content sharing status to: ${status}`);
        this.contentSharingStatus = status;

        if (status === ContentSharingStatus.Done ||
            status === ContentSharingStatus.Failed) {

            if (this.contentSharingTerminationReason && this.contentSharingTerminationReason.terminatedReason !== TerminatedReason.Undefined) {
                return;
            }

            const failure = this.getIntProperty(Enums.Property.ContentSharingFailureReason);
            const code = this.getIntProperty(Enums.Property.ContentSharingFailureCode);
            const subCode = this.getIntProperty(Enums.Property.ContentSharingFailureSubCode);
            const reason = this.convertFailureReason(failure);

            this._logger.info(`setting failure reason for content sharing session. reason: ${reason}`);

            this.contentSharingTerminationReason = {
                terminatedReason: reason,
                terminatedReasonCode: code,
                terminatedReasonSubCode: subCode,
                errorMessage: 'Session is terminated'
            };
        }

        this._session.statusChanged(status);

        this.raiseChanged();
    }

    private getStrProperty(property: Enums.Property, fallback?: string): string {
        return this._getStrProperty(this._slimcoreCallHandler, this.slimCoreContentSharingObjectId, property, fallback);
    }

    private getIntProperty(property: Enums.Property, fallback?: number): number {
        return this._getIntProperty(this._slimcoreCallHandler, this.slimCoreContentSharingObjectId, property, fallback);
    }

    private _onContentSharingStatusChanged(value: Enums.ContentSharingStatus) {
        this._logger.info(`_onContentSharingStateChanged called`);
        this.setContentSharingStatus(this.convertContentSharingStatus(value));
    }

    private _onContentSharingStateChanged(value: string) {
        this._logger.info(`_onContentSharingStateChanged called`);
        this.contentSharingState = value;
        this.raiseChanged();
    }

    private _onContentSharingResultCodeChanged(value: Enums.ContentSharingFailureReason) {
        this._logger.info(`_onContentSharingResultCodeChanged called`);

        if (this.contentSharingTerminationReason && this.contentSharingTerminationReason.terminatedReason !== TerminatedReason.Undefined) {
            return;
        }

        const code = this.getIntProperty(Enums.Property.ContentSharingFailureCode);
        const subCode = this.getIntProperty(Enums.Property.ContentSharingFailureSubCode);
        const reason = this.convertFailureReason(value);

        this._logger.info(`setting failure reason for content sharing session. reason: ${reason}`);

        this.contentSharingTerminationReason = {
            terminatedReason: reason,
            terminatedReasonCode: code,
            terminatedReasonSubCode: subCode,
            errorMessage: 'Session is terminated'
        };
    }

    public convertContentSharingStatus(slimCoreContentSharingStatus: Enums.ContentSharingStatus): ContentSharingStatus {
        switch (slimCoreContentSharingStatus) {
            case Enums.ContentSharingStatus.Routing:
                return ContentSharingStatus.Connecting;
            case Enums.ContentSharingStatus.Ringing:
                return ContentSharingStatus.Ringing;
            case Enums.ContentSharingStatus.Connected:
                return ContentSharingStatus.Connected;
            case Enums.ContentSharingStatus.Presenting:
                return ContentSharingStatus.Presenting;
            case Enums.ContentSharingStatus.Viewing:
                return ContentSharingStatus.Viewing;
            case Enums.ContentSharingStatus.Finishing:
                return ContentSharingStatus.Finishing;
            case Enums.ContentSharingStatus.Done:
            case Enums.ContentSharingStatus.Failed:
            case Enums.ContentSharingStatus.TimedOut:
                return ContentSharingStatus.Done;
            default:
                return ContentSharingStatus.None;
        }
    }

    // Start a new content sharing session
    @callOperation(OPERATION.START_CONTENT_SHARING)
    public startContentSharing(@causeId causeId = generateCauseId()): Promise<void> {
        return this._session.startContentSharing(causeId);
    }

    // Join an incoming content sharing session
    @callOperation(OPERATION.JOIN_CONTENT_SHARING)
    public joinContentSharing(@causeId causeId = generateCauseId()): Promise<void> {
        return this._session.joinContentSharing(causeId);
    }

    // Update content sharing participant state for an incoming session. This will cause the
    // endpoint to appear as viewer for the session in the roster update to other participants
    @callOperation(OPERATION.UPDATE_PARTICIPANT_STATE)
    public updateContentSharingParticipantStateToViewer(@causeId causeId = generateCauseId()): Promise<void> {
        return this._session.updateContentSharingParticipantStateToViewer(causeId);
    }

    // Update metadata for the session if presenting
    @callOperation(OPERATION.UPDATE_CONTENT_SHARING_SESSION_STATE, { type: OPERATION_TYPE.CHAINED })
    public updateContentSharingSessionState(id: string, sessionState: string, @causeId causeId = generateCauseId()): Promise<void> {
        return this._session.updateContentSharingSessionState(id, sessionState, causeId);
    }

    // Take control of the session to become a presenter if you are currently an attendee
    @callOperation(OPERATION.TAKE_CONTENT_SHARING_CONTROL)
    public takeContentSharingControl(@causeId causeId = generateCauseId()): Promise<void> {
        return this._session.takeContentSharingControl(causeId);
    }
    // Stop the session
    @callOperation(OPERATION.STOP_CONTENT_SHARING, { waitFor: OPERATION.START_CONTENT_SHARING })
    public stopContentSharing(@causeId causeId = generateCauseId()): Promise<void> {
        return this._session.stopContentSharing(causeId);
    }
    private _onJoinResult(eventData: SlimCore.ContentSharing.Events.ContentSharingResultArgs) {
        this._session.onJoinResultEvent(eventData);
    }

    private _onUpdateParticipantStateResult(eventData: SlimCore.ContentSharing.Events.ContentSharingResultArgs) {
        this._session.onUpdateParticipantStateResultEvent(eventData);
    }

    private _onSessionStateResult(eventData: SlimCore.ContentSharing.Events.ContentSharingSessionStateUpdateResultArgs) {
        this._session.onSessionStateResultEvent(eventData);
    }

    private _onTakeControlResult(eventData: SlimCore.ContentSharing.Events.ContentSharingResultArgs) {
        this._session.onTakeControlResultEvent(eventData);
    }

    // calls to native
    public callStartContentSharing(): Promise<void> {
        return async.asap(() => this._nativeContentSharing.startContentSharing());
    }
    public callStopContentSharing(): Promise<void> {
        return async.asap(() => this._nativeContentSharing.stopContentSharing());
    }
    public callUpdateContentSharingSessionState(commandId: string, sessionState: string): Promise<void> {
        return async.asap(() => this._nativeContentSharing.updateContentSharingSessionState(commandId, sessionState));
    }
    public callTakeContentSharingControl(): Promise<void> {
        return async.asap(() => this._nativeContentSharing.takeContentSharingControl());
    }
    public callUpdateContentSharingParticipantState(): Promise<void> {
        return async.asap(() => this._nativeContentSharing.updateContentSharingParticipantState());
    }

    public convertFailureReason(reason: Enums.ContentSharingFailureReason): TerminatedReason {
        switch (reason) {
            case Enums.ContentSharingFailureReason.NoFailure:
                return TerminatedReason.Success;
            case Enums.ContentSharingFailureReason.SessionNotFound:
                return TerminatedReason.SessionNotFound;
            case Enums.ContentSharingFailureReason.SessionTimedOut:
                return TerminatedReason.SessionTimedOut;
            case Enums.ContentSharingFailureReason.NetworkError:
                return TerminatedReason.NetworkError;
            case Enums.ContentSharingFailureReason.NetworkCannotConnectError:
                return TerminatedReason.CannotConnectToNetworkError;
            case Enums.ContentSharingFailureReason.ServiceFailure:
                return TerminatedReason.ServiceUnavailable;
            case Enums.ContentSharingFailureReason.RequestTimedOut:
                return TerminatedReason.NetworkRequestTimeoutError;
            case Enums.ContentSharingFailureReason.AuthFailure:
                return TerminatedReason.SkypeTokenError;
            case Enums.ContentSharingFailureReason.ActionNotAllowed:
                return TerminatedReason.ActionNotAllowed;
            case Enums.ContentSharingFailureReason.Forbidden:
                return TerminatedReason.NoPermission;
            case Enums.ContentSharingFailureReason.Failure:
                return TerminatedReason.UnknownError;
            default:
                return TerminatedReason.UnknownError;
        }
    }

    private _getCallEndOperation = (): Promise<void> => {
        // waiting for stop content sharing
        if (this._callOperationHandler.hasPendingOperation(OPERATION.STOP_CONTENT_SHARING)) {
            return this._callOperationHandler.waitForOperation(OPERATION.STOP_CONTENT_SHARING);
        } else {
            return Promise.resolve<void>();
        }
    }

    public dispose(causeId = generateCauseId()): void {
        const rejectReason: SessionTerminatedReason = this.contentSharingTerminationReason ? this.contentSharingTerminationReason :
        {
            terminatedReason: TerminatedReason.Cancelled,
            terminatedReasonCode: 0,
            terminatedReasonSubCode: 0,
            errorMessage: 'Call ended'
        };
        this.slimCoreContentSharingObjectId = 0;
        this._getCallEndOperation().then(() => {
            this._callOperationHandler.rejectPendingOperations(rejectReason, causeId);
            super.dispose(causeId);
        });
    }
}



