import { CallingLogger, CauseId } from 'skype-calling-interface';
import * as piiUtils from '../../common/ts/piiUtils';
import AsyncOperationHandler from './asyncOperationHandler';
import { ICallTelemetry } from './callTelemetry';
import { generateCauseId } from './causeId';
import ChainedPromise from './chainedPromise';
import * as stopwatch from './stopwatch';

type SyncOperation<T> = () => T;
type AsyncOperation<T= undefined> = () => Promise<T>; export interface IOperationHandler {
    resetOperationChain(causeId?: CauseId): void;
    executeChained<T>(operation: AsyncOperation<T>, operationName: string, operationId?: string, causeId?: CauseId): Promise<T>;
    executeSync<T>(operation: SyncOperation<T>, operationName: string, operationId?: string, causeId?: CauseId): T;
    execute<T>(operation: AsyncOperation<T>, operationName: string, operationId?: string, causeId?: CauseId): Promise<T>;
    logOperation(causeId: CauseId, operationName: string, ...args: any[]): void;
}

export default class OperationHandler extends AsyncOperationHandler implements IOperationHandler {

    private _chainedPromise: ChainedPromise;

    constructor(logger: CallingLogger, private callTelemetry: ICallTelemetry) {
        super(logger);
        this._logger = logger.createChild(`[Operation]`);
        this._chainedPromise = new ChainedPromise(this._logger.createChild('[Chained]'));
    }

    public resetOperationChain(causeId?: CauseId) {
        this._chainedPromise.reset(causeId);
    }

    public executeSync<T= undefined>(operation: SyncOperation<T>, operationName: string, operationId?: string, causeId = generateCauseId()): T {
        const operationInfo = this.getOperationInfoForLogging(causeId, operationName, operationId);
        const operationStopwatch = stopwatch.build();
        this._logger.info(`${operationInfo}API calledSync`);

        const telemetryEvent = this.callTelemetry.recordOperation(operationName, causeId, operationId);

        let operationResult: T;
        try {
            operationResult = operation();
            telemetryEvent.recordSuccess<T>(operationResult, causeId);
            this.logSuccess(operationInfo, operationResult, operationStopwatch);
        } catch (e) {
            telemetryEvent.recordFailure(e, causeId);
            this.logFailure(operationInfo, e, operationStopwatch);
        }

        return operationResult;
    }

    public async executeChained<T= undefined>(operation: AsyncOperation<T>, operationName: string, operationId?: string, causeId = generateCauseId()): Promise<T> {
        return this.checkPendingOperationAndExecute(operation, true, operationName, operationId, causeId);
    }

    public async execute<T= undefined>(operation: AsyncOperation<T>, operationName: string, operationId?: string, causeId = generateCauseId()): Promise<T> {
        return this.checkPendingOperationAndExecute(operation, false, operationName, operationId, causeId);
    }

    public logOperation(causeId: CauseId, operationName: string, ...args: any[]) {
        const argumentsToLog = args.length && args.map(a => this.stringifyForLogging(piiUtils.scrubMriOrOmit(a)));
        this._logger.info(`[${causeId}][${operationName}]API called, ${argumentsToLog ? `with args=${argumentsToLog}` : ``}`);
    }

    private checkPendingOperationAndExecute<T= undefined>(operation: AsyncOperation<T>, chained: boolean, operationName: string, operationId?: string, causeId = generateCauseId()): Promise<T> {
        const operationInfo = this.getOperationInfoForLogging(causeId, operationName, operationId);

        let pendingOperation: Promise<T> = null;
        let asyncOperation: Promise<T> = null;
        if (this.hasPendingOperation(operationName, operationId)) {
            this._logger.info(`${operationInfo}already pending, returning existing operation`);
            pendingOperation = this.waitForOperation<T>(operationName, operationId, causeId);
        } else {
            asyncOperation = this.createPendingOperation(operationName, operationId, causeId);
        }

        if (chained) {
            this._logger.info(`${operationInfo}chained`);
            return this._chainedPromise.chainPromise<T>(() => {
                return pendingOperation || this.executeInternal<T>(asyncOperation, causeId, operation, operationName, operationId);
            }, operationName, causeId);
        } else {
            return pendingOperation || this.executeInternal<T>(asyncOperation, causeId, operation, operationName, operationId);
        }

    }

    private async executeInternal<T= undefined>(asyncOperation: Promise<T>, causeId: CauseId, operation: AsyncOperation<T>, operationName: string, operationId?: string): Promise<T> {
        const operationInfo = this.getOperationInfoForLogging(causeId, operationName, operationId);
        this._logger.info(`${operationInfo} executing operation...`);
        const operationStopwatch = stopwatch.build();
        this.callTelemetry.recordOperation(operationName, causeId, operationId);
        try {
            const operationResult = await operation();
            this.callTelemetry.maybeRecordOperationSuccess(operationName, operationResult, operationId);
            this.logSuccess<T>(operationInfo, operationResult, operationStopwatch);
            this.maybeResolveOperation(operationName, operationResult, operationId, causeId);
        } catch (reason) {
            this.callTelemetry.maybeRecordOperationFailure(operationName, reason, operationId);
            this.logFailure(operationInfo, reason, operationStopwatch);
            this.maybeRejectOperation(operationName, reason, operationId, causeId);
        }

        return asyncOperation;
    }

    private logSuccess = <T= undefined>(operationInfo: string, result: T, operationStopwatch: stopwatch.Stopwatch): void => {
        this._logger.info(`${operationInfo} execution succeeded, result=${result ? this.stringifyForLogging(result) : 'void'}, time=${operationStopwatch.duration()}`);
    }

    private logFailure = (operationInfo: string, e: any, operationStopwatch: stopwatch.Stopwatch): void => {
        this._logger.info(`${operationInfo} execution failed, reason=${this.stringifyForLogging(e)}, time=${operationStopwatch.duration()}`);
    }

    private stringifyForLogging = (data: any) => {
        let output: any;

        if (data instanceof Error) {
            output = data.toString() || 'Unknown Error';

        } else {
            try {
                output = JSON.stringify(data);
            } catch (e) {
                output = 'Unable to stringify data';
            }
        }
        return output;
    }

}



