    @callOperation(CALL_OPERATIONS.START_SCREEN_SHARING, { type: OPERATION_TYPE.CHAINED })
    public async startScreenSharing(source?: ISharingSource, cropRegion?: Rectangle, negotiationTag?: string, @causeId causeId = generateCauseId()): Promise<void> {

        if (this.screenSharingControl && this.screenSharingControl.isScreenSharingControlEnabled()) {
            // If we were viewing (i.e. we are switching from viewing to sharing),
            // cleanup any control state
            this.screenSharingControl.shutdownControlForViewer();
        }

        return Promise.resolve()
            .then(() => {

                if (!this._localScreenShareStream) {
                    this._localScreenShareStream = new SlimCoreElectronLocalScreenShare(
                        this._logger,
                        this._settings,
                        this._slimcoreInstance,
                        this.slimcoreCallId,
                        this._slimcoreCallHandler,
                        this._deviceManager,
                        this._appHooks,
                        this.screenSharingControl,
                        this._callTelemetry
                    );
                    this._localScreenShareStream.changed(() => this._onLocalScreenShareStreamChanged(causeId)); // no need to track this for disposal
                    this._localScreenShareStream.on('sharingSourceLost', () => this._onLocalScreenShareSourceLost());
                    this._localScreenShareStream.on('windowClosed', () => this.stopScreenSharing(false, negotiationTag, causeId));
                    this._localScreenShareStream.on('scraperEvent', args => this._onScraperEvent(args));
                }
            })
            .then(() => this._localScreenShareStream.start(causeId, source, cropRegion, negotiationTag));
    }

    public changeCropRegion(cropRegion: Rectangle): void {
        if (this._localScreenShareStream) {
            this._localScreenShareStream.changeCropRegion(cropRegion);
        }
    }

    private _onLocalScreenShareSourceLost() {

        if (this.screenSharingControl && this.screenSharingControl.isScreenSharingControlEnabled()) {
            this.screenSharingControl.shutdownControlForSharer();
        }
        if (this._localScreenShareStream.isStreaming) {
            this.event('sharingDropped').raise();
        }
    }

    private _onScraperEvent(args: SlimCore.VideoBindingScreenShare.Events.ScraperEventArgs) {
        const eventArgs: ScraperEventArgs = {
            event: (args.event as number),
            data: args.data
        };
        this.event('scraperEvent').raise(eventArgs);
    }

    public raiseRenderedAtViewer(id: string) {
        this.event('sharingRenderedAtViewer').raise(id);
    }

    private _onLocalScreenShareStreamChanged(causeId: CauseId) {
        if (this._localScreenShareStream.isStreaming) {

            if (!this.isScreenSharingOn && this._dataChannel && this.screenSharingControl && this.screenSharingControl.isScreenSharingControlEnabled()) {
                // Initialize screen sharing control for sharer
                this.screenSharingControl.initControlForSharer(this._localScreenShareStream.negotiationTag);

                // Start the data channel used for pointers/remote control
                // if necessary (it may already be started if we were
                // previously viewing and performed a sharer switch)
                this._dataChannel.start();
            }
            this._setScreenSharingOn(true, causeId);

        } else if (!this._localScreenShareStream.isAvailable) {
            if (this.isScreenSharingOn && this.screenSharingControl && this.screenSharingControl.isScreenSharingControlEnabled()) {
                // Cleanup any control state - this is added to handle the call hold scenario
                this.screenSharingControl.shutdownControlForSharer();
            }
            this._setScreenSharingOn(false, causeId);
        }
    }

    @callOperation(CALL_OPERATIONS.STOP_SCREEN_SHARING, { type: OPERATION_TYPE.CHAINED })
    public async stopScreenSharing(isSharerSwitch?: boolean, negotiationTag?: string, @causeId causeId = generateCauseId()): Promise<void> {
        const fnLogger = this._logger.createFnLogger(CALL_OPERATIONS.STOP_SCREEN_SHARING, causeId);
        const always = () => {
            if (this._localScreenShareStream) {
                this._localScreenShareStream.dispose(causeId);
                this._localScreenShareStream = null;
            }
            this._setScreenSharingOn(false, causeId);

            if (this.screenSharingControl && this.screenSharingControl.isScreenSharingControlEnabled()) {
                // Cleanup any control state
                this.screenSharingControl.shutdownControlForSharer();
            }
        };

        const stopScreenSharingPromise = Promise.resolve()
            .then(async () => {
                if (this._localScreenShareStream) {
                    await SlimCoreCommon.forgetAndLog(this._localScreenShareStream.stop(causeId, negotiationTag), fnLogger, 'stopScreenSharing: stopping screen sharing failed');
                }
            });

        return this._callOperationHandler.registerPromise(CALL_OPERATIONS_INTERNAL.ELECTRON_STOP_SCREEN_SHARING, stopScreenSharingPromise)
            .then(always, error => {
                fnLogger.logFailure(error);
                always();
                throw error;
            });
    }

