    @callOperation(CALL_OPERATIONS.BLIND_TRANSFER, { singular: true, type: OPERATION_TYPE.CHAINED })
    public async callBlindTransfer(transferTargetMri: string, @causeId causeId = generateCauseId()): Promise<TerminatedReason> {
        // TransferStatus:
        //    Transferring: transferAcceptance,
        //    Transferred: transferCompletion with Success,
        //    Failed: transferCompletion with Failure
        // For Blind transfer, transferAcceptance is the success criteria
        const transferInfo: TransferInfo = {
            transferTarget: transferTargetMri,
            transferType: TransferType.Transfer,
            transferStatusSuccessList: [Enums.CallStatus.Transferring, Enums.CallStatus.Transferred]
        };
        return this._transferCall(transferInfo, causeId);
    }

