    @callOperation(CALL_OPERATIONS.CONSULTATIVE_TRANSFER, { singular: true, type: OPERATION_TYPE.CHAINED })
    public async callConsultativeTransfer(callWithTarget: ICall, @causeId causeId = generateCauseId()): Promise<TerminatedReason> {
        // TransferStatus:
        //    Transferring: transferAcceptance,
        //    Transferred: transferCompletion with Success,
        //    Failed: transferCompletion with Failure
        // For Consultative transfer, transferCompletion is the success criteria
        const transferInfo: TransferInfo = {
            targetCallId: (callWithTarget as SlimCoreElectronCall).slimcoreCallId,
            transferType: TransferType.Transfer,
            transferStatusSuccessList: [Enums.CallStatus.Transferred]
        };
        return this._transferCall(transferInfo, causeId);
    }

