    @callOperation(CALL_OPERATIONS.START_AUDIO, { type: OPERATION_TYPE.CHAINED })
    public async startAudio(negotiationTag?: string, @causeId causeId = generateCauseId()): Promise<MediaNegotiationStatusCode> {
        const fnLogger = this._logger.createFnLogger(CALL_OPERATIONS.START_AUDIO, causeId);
        if (this.state !== CallState.Connected) {
            fnLogger.logFailure('cannot startAudio when call is not connected');
            return Promise.reject(TerminatedReason.ActionNotAllowed);
        }

        const startAudioPromise = this._callOperationHandler.waitForOperation<MediaNegotiationStatusCode>(CALL_OPERATIONS.START_AUDIO, undefined, causeId);
        this._slimcoreCallHandler.callStartAudio(this.slimcoreCallId, negotiationTag);
        return startAudioPromise;
    }

