    private static convertPushHandlingResult(result: Enums.PushHandlingResult): CallAcknowledgeResult {
        switch (result) {
            case Enums.PushHandlingResult.BadNotificationPayload:
                return { code: CallAcknowledgeResultCode.BadNotificationPayload, fatal: true };
            case Enums.PushHandlingResult.CallSetupSucceeded:
                return { code: CallAcknowledgeResultCode.CallSetupSucceeded, success: true };
            case Enums.PushHandlingResult.BadNotificationEventType:
                return { code: CallAcknowledgeResultCode.BadNotificationEventType, fatal: true };
            case Enums.PushHandlingResult.CallSetupSucceededCallAlreadyExists:
                return { code: CallAcknowledgeResultCode.CallAlreadyExists };
            case Enums.PushHandlingResult.CallSetupFailedCannotConnect:
                return { code: CallAcknowledgeResultCode.CallFailedCannotConnect, fatal: true };
            case Enums.PushHandlingResult.CallSetupFailedNoSignaling:
                return { code: CallAcknowledgeResultCode.CallFailedNoSignaling, fatal: true };
            case Enums.PushHandlingResult.CallSetupFailedConflict:
                return { code: CallAcknowledgeResultCode.CallFailedConflict };
            case Enums.PushHandlingResult.CallSetupFailedPushIgnored:
                return { code: CallAcknowledgeResultCode.CallFailedPushIgnored };
            case Enums.PushHandlingResult.CallSetupFailedAnsweredElsewhere:
                return { code: CallAcknowledgeResultCode.CallFailedAnsweredElsewhere };
            case Enums.PushHandlingResult.CallSetupFailedAlreadyEnded:
                return { code: CallAcknowledgeResultCode.CallFailedCallAlreadyEnded };
            case Enums.PushHandlingResult.CallSetupProgress:
                return { code: CallAcknowledgeResultCode.CallSetupInProgress, success: true };
            case Enums.PushHandlingResult.CallSetupFailed:
            case Enums.PushHandlingResult.CallSetupFailedNoPermission:
            case Enums.PushHandlingResult.CallSetupFailedNoCommonCodec:
            default:
                return { code: CallAcknowledgeResultCode.CallSetupFailed, fatal: true };
        }
    }

