import * as SlimCoreCommon from './SlimCoreCommon';

class SlimCoreElectronScreenScraper {
    private _screenScraper: SlimCore.ScreenScraper;

    constructor(private _slimcoreInstance: SlimCoreCommon.Engine) {
        if (this._slimcoreInstance.getMonitorList && this._slimcoreInstance.getWindowList) {
            return;
        }

        const slimcore = SlimCoreCommon.isSlimCoreRTModuleAvailable() ? (SlimCoreRT as any).SlimCore as typeof SlimCore : SlimCore;
        if (!slimcore.ScreenScraper) {
            throw new Error('ScreenScraper not supported');
        }

        this._screenScraper = new slimcore.ScreenScraper();
    }

    public getMonitorList() {
        if (this._slimcoreInstance.getMonitorList) {
            return this._slimcoreInstance.getMonitorList();
        } else if (this._screenScraper) {
            return this._screenScraper.getMonitorList();
        } else {
            throw new Error('not supported');
        }
    }

    public getMonitorPreview(monitorId: number, width: number, height: number, asImage: boolean, region: SlimCore.Region) {
        if (this._slimcoreInstance.getMonitorSnapshot) {
            return this._slimcoreInstance.getMonitorSnapshot(monitorId, { width, height, asImage }, region).then(SlimCoreCommon.convertImageData);
        } else if (this._screenScraper) {
            return this._screenScraper.getMonitorPreview(monitorId, width, height, asImage) as Promise<ImageData>;
        } else {
            return Promise.reject(new Error('not supported'));
        }
    }

    public getWindowList() {
        if (this._slimcoreInstance.getWindowList) {
            return this._slimcoreInstance.getWindowList();
        } else if (this._screenScraper) {
            return this._screenScraper.getWindowList();
        } else {
            throw new Error('not supported');
        }
    }

    public getWindowPreview(windowId: number, width: number, height: number, asImage: boolean) {
        if (this._slimcoreInstance.getWindowSnapshot) {
            return this._slimcoreInstance.getWindowSnapshot(windowId, { width, height, asImage }).then(SlimCoreCommon.convertImageData);
        } else if (this._screenScraper) {
            return this._screenScraper.getWindowPreview(windowId, width, height, asImage) as Promise<ImageData>;
        } else {
            return Promise.reject(new Error('not supported'));
        }
    }

    public getWindowIcon(windowId: number, width: number, height: number, asImage: boolean) {
        if (this._slimcoreInstance.getWindowIcon) {
            return this._slimcoreInstance.getWindowIcon(windowId, { width, height, asImage }).then(SlimCoreCommon.convertImageData);
        } else if (this._screenScraper) {
            return this._screenScraper.getWindowIcon(windowId, width, height, asImage) as Promise<ImageData>;
        } else {
            return Promise.reject(new Error('not supported'));
        }
    }
}

export default SlimCoreElectronScreenScraper;



