/// <reference path="../typings/slimcore.d.ts" />
/// <reference path="../typings/slimcore-enums.d.ts" />

import {
    CauseId, ISharingSource, IStream, SharingControlDisableReason, SharingSourceType, VideoDeviceDesc
} from 'skype-calling-interface';

import * as _ from 'lodash';
import { ISlimCoreElectronApplicationHooks, ISlimCoreElectronControlInjector, Rectangle } from '../typings/skype-calling-electron';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';

import SlimCoreElectronDeviceManager from './SlimCoreElectronDeviceManager';
import SlimCoreElectronScreenScraper from './SlimCoreElectronScreenScraper';
import SlimCoreElectronScreenSharingControl from './SlimCoreElectronScreenSharingControl';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;

import MonitorInfo = SlimCore.MonitorInfo;
import WindowInfo = SlimCore.WindowInfo;
import VideoBindingScreenShare = SlimCore.VideoBindingScreenShare;
import { CALL_OPERATIONS_INTERNAL } from '../../common/ts/callConstants';
import { ICallTelemetry } from '../../common/ts/callTelemetry';
import { generateCauseId } from '../../common/ts/causeId';
import { IInternalLogger } from '../../common/ts/internalLogger';
import { callOperation } from '../../common/ts/operationDecorators';
import OperationHandler from '../../common/ts/operationHandler';

const VIDEO_OBJECT_WAIT_TIMEOUT_MS = 500;

interface SlimCoreElectronLocalScreenShareEvents {
    sharingSourceLost: () => void; // tslint:disable-line:prefer-method-signature
    windowClosed(): void;
    scraperEvent(args: SlimCore.VideoBindingScreenShare.Events.ScraperEventArgs): void;
}

export default class SlimCoreElectronLocalScreenShare extends SlimCoreElectronBase<SlimCoreElectronLocalScreenShareEvents> implements IStream {
    public isStreaming = false;
    public isAvailable = true;
    public negotiationTag: string;

    private _localVideoObjectId: number = 0;
    private _previewVideoObjectId: number = 0;
    private _videoBindingScreenShare: VideoBindingScreenShare;
    private _screenScraper: SlimCoreElectronScreenScraper;
    private _selectedMonitor: MonitorInfo;
    private _selectedRegion: Rectangle;
    private _selectedWindow: WindowInfo;
    private _selectedCamera: VideoDeviceDesc;
    private _editViewPPTWindowId: number = 0;
    private _videoStatusChangedHandler: SlimCore.Disposable;
    private _callOperationHandler: OperationHandler;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _callObjectId: number,
        private _callHandler: SlimCoreCommon.CallHandler,
        private _deviceManager: SlimCoreElectronDeviceManager,
        private _appHooks: ISlimCoreElectronApplicationHooks,
        private _screenSharingControl: SlimCoreElectronScreenSharingControl,
        private _callTelemetry: ICallTelemetry
    ) {
        super(logger.createChild(() => `LocalScreenShare[${this._localVideoObjectId}]`), settings, engine);
        this._logger.info('constructor');
        this._callOperationHandler = new OperationHandler(this._logger, this._callTelemetry);

        try {
            this._screenScraper = new SlimCoreElectronScreenScraper(this._slimcoreInstance);
        } catch (error) {
            this._logger.warn(`${error}`);
        }

        if (this._screenScraper) {
            this._registerDisposable(this._appHooks.onDisplaysChanged(() => this.handleDisplaysChanged()));
        }
        // TODO: Register a callback for window closed
    }

    public isActive(): boolean {
        return this.isAvailable && this.isStreaming;
    }

    private _showSharingIndicator(region: Rectangle) {

        this._appHooks.showSharingIndicator(region);

        const controlInjector: ISlimCoreElectronControlInjector = this._appHooks.getControlInjector();
        if (controlInjector && controlInjector.setInjectionRect) {
            controlInjector.setInjectionRect(region).catch(error => {
                const errorDetails: string = JSON.stringify({ errorMsg: error, region });
                this._screenSharingControl.enableScreenSharingControl(false, SharingControlDisableReason.SetInjectionFailed, errorDetails);
                this._logger.error(`Set control Injector failure, disable screen sharing control. Error: ${error}`);
            });
        }
    }

    private _videoObjectChanged(value: Enums.VideoStatus) {
        const causeId = generateCauseId();
        this.isAvailable = SlimCoreCommon.isVideoAvailable(value);
        this.isStreaming = false;
        if (SlimCoreCommon.isVideoInState(value, [Enums.VideoStatus.Starting, Enums.VideoStatus.Running])) {
            if (!this._selectedMonitor && !this._selectedWindow && !this._selectedCamera) {
                // we're screen sharing but no monitor selected. stop screensharing
                SlimCoreCommon.forgetAndLog(this.stop(causeId), this._logger, 'stopping screen sharing due to no monitor/camera being selected');
            } else {
                this.isStreaming = true;
                if (!this._selectedCamera) {
                    this._appHooks.showSharingIndicator(this._selectedRegion || (this._selectedMonitor && this._selectedMonitor.region) || (this._selectedWindow && this._selectedWindow.windowId));
                }
            }
        }

        if (SlimCoreCommon.isVideoInState(value, [Enums.VideoStatus.Paused])) {
            this._appHooks.hideSharingIndicator();
        }

        this.raiseChanged();
    }

    private _setCaptureRegionAndWindow(region?: SlimCore.Region, windowId?: number) {
        if (this._slimcoreInstance.setScreenCaptureRectangle) {
            this._slimcoreInstance.setScreenCaptureRectangle(this._localVideoObjectId, region, undefined, windowId);
            return;
        }

        if (this._videoBindingScreenShare) {
            this._videoBindingScreenShare.setCaptureRegionAndWindow(region, windowId);
            return;
        }

        throw new Error('Failed to set capture region / window');
    }

    private _handleScraperEvent(args: SlimCore.VideoBindingScreenShare.Events.ScraperEventArgs) {
        this._logger.info(`Handle Scraper event, event = ${args.event} data = ${args.data}`);
        const eventData = args.data;

        switch (args.event) {
            case Enums.ScraperEvent.PPTPresenterView_Enter:
                this._editViewPPTWindowId = this._selectedWindow.windowId;
                this._selectedWindow.windowId = eventData;
                this._appHooks.showSharingIndicator(eventData);
                this._setCaptureRegionAndWindow(undefined, eventData);
                this._logger.info(`shared PPT entered presenter mode`);
                break;
            case Enums.ScraperEvent.PPTPresenterView_Exit:
                this._selectedWindow.windowId = this._editViewPPTWindowId;
                this._appHooks.showSharingIndicator(this._selectedWindow.windowId);
                this._setCaptureRegionAndWindow(undefined, this._selectedWindow.windowId);
                this._logger.info(`shared PPT exited presenter mode`);
                break;
            case Enums.ScraperEvent.WindowClosed:
                // make sure that the window for the event matches the current window id
                if (this._selectedWindow.windowId !== eventData) { return; }
                this._logger.info(`Application window closed. Stopping the screenshare`);
                this.event('windowClosed').raise();
                break;
            default:
                this.event('scraperEvent').raise(args);
        }
    }

    private _startPreviewVideoObject(): Promise<void> {
        if (!this._shouldUseVideoBindingScreenShare()) {
            return Promise.resolve<void>();
        }
        return this._deviceManager.getPreviewVideo(Enums.VideoType.ScreenShare)
            .then(videoObjectId => {
                return this._deviceManager.getScreenShareBindingManager().acquire(this, videoObjectId)
                    .then(binding => {
                        this._previewVideoObjectId = videoObjectId;
                        this._videoBindingScreenShare = binding; // from now on we must release binding
                        this._videoBindingScreenShare.on('scraper-event', args => this._handleScraperEvent(args));
                    });
            });
    }

    private _stopPreviewVideoObject(): Promise<void> {
        if (!this._shouldUseVideoBindingScreenShare()) {
            return Promise.resolve<void>();
        } else if (this._previewVideoObjectId && this._videoBindingScreenShare) {
            this._logger.info(`calling slimcore video release binding`);
            this._videoBindingScreenShare.removeAllListeners('scraper-event');
            const releasePromise = this._deviceManager.getScreenShareBindingManager().release(this, this._previewVideoObjectId)
                .catch(error => this._logger.error(`Failed on releasing screen sharing binding. Error: ${error}`));
            this._previewVideoObjectId = 0;
            this._videoBindingScreenShare = null;
            return releasePromise;
        } else {
            return Promise.resolve();
        }
    }

    private _shouldUseVideoBindingScreenShare() {
        return !this._slimcoreInstance.setScreenCaptureRectangle && this._deviceManager.hasVideoBindingScreenShare();
    }

    @callOperation(CALL_OPERATIONS_INTERNAL.ELECTRON_START_SCREEN_SHARING, {waitFor: CALL_OPERATIONS_INTERNAL.ELECTRON_STOP_SCREEN_SHARING})
    public start(causeId: CauseId, source?: ISharingSource, cropRegion?: Rectangle, negotiationTag?: string): Promise<void> {
        const fnLogger = this._logger.createFnLogger(CALL_OPERATIONS_INTERNAL.ELECTRON_START_SCREEN_SHARING, causeId);
        fnLogger.info(`local screen share start`);

        const startPromise = Promise.resolve()
            .then(async () => {
                const sourceInfo = this.sourceToInfo(source);
                if (!sourceInfo) {
                    throw new Error('Unable to start sharing, source could not be found');
                }

                fnLogger.info(`calling slimcore create local video`);
                const isCameraSource = !!source && source.getType() === SharingSourceType.Camera;
                if (isCameraSource) {
                    const deviceInfo = sourceInfo as VideoDeviceDesc;
                    this._localVideoObjectId = this._slimcoreInstance.createLocalVideo(Enums.VideoType.ScreenShare, deviceInfo.label, deviceInfo.id);
                } else {
                    this._localVideoObjectId = this._slimcoreInstance.createLocalVideo(Enums.VideoType.ScreenShare, '', '');
                }

                this._videoStatusChangedHandler = this._slimcoreInstance.handle(
                    'object-property-changed',
                    { objectId: this._localVideoObjectId, propKey: Enums.Property.VideoStatus },
                    eventData => this._videoObjectChanged(eventData.value as number));

                const videoStatus = this._getIntProperty(this._callHandler, this._localVideoObjectId, Enums.Property.VideoStatus);
                this._videoObjectChanged(videoStatus);

                this._callHandler.callAttachSendVideo(this._callObjectId, this._localVideoObjectId);
                fnLogger.info(`waiting for video object to be available`);
                await SlimCoreCommon.waitForVideoObjectToBeAvailable(this._slimcoreInstance, this._callHandler, this._localVideoObjectId, this._logger, VIDEO_OBJECT_WAIT_TIMEOUT_MS);
                if (!isCameraSource) {
                    this._logger.info(`start preview video object`);
                    await this._startPreviewVideoObject();
                }
                fnLogger.info(`calling slimcore video start`);
                this.selectSource(source, sourceInfo, cropRegion);

                this.negotiationTag = negotiationTag;
                this._slimcoreInstance.videoStart(this._localVideoObjectId, negotiationTag);
                this.isStreaming = true;
                this.raiseChanged();
            });

        return this._callOperationHandler.registerPromise(CALL_OPERATIONS_INTERNAL.ELECTRON_START_SCREEN_SHARING, startPromise)
            .catch((error: any) => {
                fnLogger.logFailure(`Failed to start LocalScreenShare. Error: ${error}`);
                this.stop(undefined, causeId);
                Promise.reject(error);
            });
    }

    public changeCropRegion(cropRegion: Rectangle): void {
        const sourceInfo = this.sourceToInfo(null);
        this.selectMonitorWithOptionalCrop(sourceInfo as MonitorInfo, cropRegion);
    }

    @callOperation(CALL_OPERATIONS_INTERNAL.ELECTRON_STOP_SCREEN_SHARING, {waitFor: CALL_OPERATIONS_INTERNAL.ELECTRON_START_SCREEN_SHARING})
    public stop(causeId: CauseId, negotiationTag?: string): Promise<void> {
        const fnLogger = this._logger.createFnLogger(CALL_OPERATIONS_INTERNAL.ELECTRON_STOP_SCREEN_SHARING, causeId);
        const always = () => {
            this._localVideoObjectId = 0;
            this.isStreaming = false;
            this._selectedMonitor = null;
            this._selectedRegion = null;
            this._selectedWindow = null;
            this._selectedCamera = null;
            this._editViewPPTWindowId = 0;
            this._appHooks.hideSharingIndicator();

            if (this._videoStatusChangedHandler) {
                this._videoStatusChangedHandler.dispose();
                this._videoStatusChangedHandler = null;
            }

            this.raiseChanged();
        };

        const stopPromise = Promise.resolve()
            .then(() => this._stopPreviewVideoObject())
            .then(() => {
                if (this._localVideoObjectId !== 0) {
                    fnLogger.info(`calling slimcore video stop`);
                    this.negotiationTag = negotiationTag;
                    this._slimcoreInstance.videoStop(this._localVideoObjectId, this.negotiationTag);
                }
            })
            .then(always, error => {
                fnLogger.logFailure(`Failed to stop LocalScreenShare. Error: ${error}`);
                always();
                throw error;
            });
        return this._callOperationHandler.registerPromise(CALL_OPERATIONS_INTERNAL.ELECTRON_STOP_SCREEN_SHARING, stopPromise);
    }

    public dispose(causeId: CauseId) {
        this._logger.info('Disposing of LocalScreenShare');
        if (this._localVideoObjectId !== 0 || this._previewVideoObjectId !== 0) {
            this.stop(causeId);
        }

        if (this._videoStatusChangedHandler) {
            this._videoStatusChangedHandler.dispose();
            this._videoStatusChangedHandler = null;
        }

        super.dispose();
    }

    private selectSource(source: ISharingSource, sourceInfo: MonitorInfo | WindowInfo | VideoDeviceDesc, cropRegion?: Rectangle) {
        if (source && source.getType() === SharingSourceType.Window) {
            this.selectWindow(sourceInfo as WindowInfo);
        } else if (source && source.getType() === SharingSourceType.Camera) {
            this._selectedCamera = sourceInfo as VideoDeviceDesc;
        } else {
            this.selectMonitorWithOptionalCrop(sourceInfo as MonitorInfo, cropRegion);
        }
    }

    private selectMonitorWithOptionalCrop(monitor: MonitorInfo, cropRegion?: Rectangle) {
        const region = cropRegion || monitor.region;
        this._logger.info(`Selected region with optional crop, x = ${region.x}, y = ${region.y}, width = ${region.width} height = ${region.height}`);
        this._showSharingIndicator(region);
        this._setCaptureRegionAndWindow(region);
        this._selectedMonitor = monitor;
        this._selectedRegion = cropRegion;
    }

    private selectWindow(window: WindowInfo) {
        this._appHooks.showSharingIndicator(window.windowId);
        this._setCaptureRegionAndWindow(undefined, window.windowId);
        this._selectedWindow = window;
    }

    private handleDisplaysChanged() {
        const causeId = generateCauseId();
        const fnLogger = this._logger.createFnLogger('HandleDisplaysChanged', causeId);
        if (!this._selectedMonitor) {
            fnLogger.logFailure(`On display change, monitor not seleted yet`);
            return;
        }
        const safeStringify = (inputSource: any) => {
            try { return JSON.stringify(inputSource); } catch (e) { return 'unable to stringify'; }
        };

        fnLogger.info(`On display change, currenlty selected monitor: ${safeStringify(this._selectedMonitor)} selected region:  ${safeStringify(this._selectedRegion)}`);

        if (!this._selectedRegion) {
            const monitors = this._screenScraper.getMonitorList() || [];
            for (const monitorInfo of monitors) {
                fnLogger.info(`On display change, new listed monitor: ${safeStringify(monitorInfo)}`);
            }
            const monitor = SlimCoreCommon.selectMonitorForScreenSharing(this._selectedMonitor, monitors);
            if (monitor) {
                fnLogger.info(`On display change, re-selected monitor: ${safeStringify(monitor)}`);
                this.selectMonitorWithOptionalCrop(monitor);
                return;
            }
        }
        fnLogger.info(`stopping screen share because displays changed and could not resolve monitor info`);
        SlimCoreCommon.forgetAndLog(this.stop(causeId), fnLogger, 'stopping screen share because sharing source lost');
        this.event('sharingSourceLost').raise();
    }

    private sourceToInfo(source: ISharingSource): MonitorInfo | WindowInfo | VideoDeviceDesc {
        if (!source) {
            if (!this._screenScraper) {
                this._logger.error('ScreenShare has no way to recover primary monitor');
                return null;
            } else {
                const sourceList = this._screenScraper.getMonitorList() || [];
                return _.find(sourceList, screen => screen.isPrimary);
            }
        }

        switch (source.getType()) {
            case SharingSourceType.Window: {
                const sourceList = this._screenScraper.getWindowList() || [];
                return _.find(sourceList, application => {
                    return application.windowId === source.getId();
                });
            }

            case SharingSourceType.Monitor: {
                if (!this._screenScraper) {
                    // For platforms that do not support enumeration take the region from source at face value
                    // as primary monitor dimensions intead of trying to recover the monitorInfo
                    return {
                        monitorId: 0,
                        name: source.getDescription(),
                        region: source.getBounds(),
                        isPrimary: true,
                        isInternal: true,
                        isDuplicated: false
                    };
                }
                const sourceList = this._screenScraper.getMonitorList() || [];
                return _.find(sourceList, screen => {
                    return screen.monitorId === source.getId() || _.isEqual(screen.region, source.getBounds());
                });
            }

            case SharingSourceType.Camera: {
                return this._deviceManager.getCamera(source.getDeviceId());
            }

            default:
                throw new Error(`Invalid source type: ${source.getType()}`);
        }
    }
}



