const MRI_ALIASES_REGEX: RegExp = /;aliases=.*$/i;

export const MRI_SKYPE_PREFIX = '8:';
export const MRI_ALIAS_PREFIX = ';aliases=';
export const MRI_SIP_PREFIX = '2:';

export function stripMriAliases(mri: string): string {
    // For SfB interop we are introducing alias'd MRIs eg. 8:orgid:<org_id>;aliases=2:<sip_uri>,4:<asserted_phone_num>
    // This is a temporary. Will be removed when Teams works on SfB interop item. Ping darsraj for Qs
    if (mri) {
        return mri.replace(MRI_ALIASES_REGEX, '');
    }

    return mri;
}

export function generateAliasedMri(mri: string, sipUri?: string): string {
    let aliasedMri = mri;

    if (sipUri) {
        aliasedMri += MRI_ALIAS_PREFIX + MRI_SIP_PREFIX + sipUri;
    }

    return aliasedMri;
}



