import * as _ from 'lodash';

import { CallParticipantStreamMap, ICallParticipantStream, StreamType } from 'skype-calling-interface';

import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronRemoteStream, { SlimCoreElectronRemoteStreamContext } from './SlimCoreElectronRemoteStream';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { noop } from '../../common/ts/fn';
import { IInternalLogger } from '../../common/ts/internalLogger';

// Manages remote streams for a participant and manages the roster state for creation/removal
// Needs to be created per participant
export default class SlimCoreElectronRemoteStreamManager extends SlimCoreElectronBase<{}> {
    private _onVideoCountChangedDefer: number;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _callHandler: SlimCoreCommon.CallHandler,
        private readonly _slimcoreParticipantObjectId: number,
        private _streams: CallParticipantStreamMap,
        private _streamContextMap: {[type: number]: SlimCoreElectronRemoteStreamContext},
        isObserving: boolean = false
    ) {
        super(logger.createChild(() => `SlimCoreElectronRemoteStreamManager[${this._slimcoreParticipantObjectId}]`), settings, engine);
        this._logger.info('constructor');

        if (!SlimCoreCommon.hasFeature(Enums.Feature.RemoteVideosCountChanged)) {
            // keeping it for legacy backward compat
            this._registerDisposable(this._slimcoreInstance.handle(
                'object-property-changed',
                { objectId: this._slimcoreParticipantObjectId, propKey: Enums.Property.ParticipantVideoCountChanged },
                data => this._onVideoCountChangedDeferred()));
        }

        this._registerDisposable(this._callHandler.handle(
            'remote-videos-count-changed',
            { participantObjectId: this._slimcoreParticipantObjectId },
            data => this._onVideoCountChangedDeferred()));

        if (!isObserving) {
            this._onVideoCountChangedDeferred();
        }
    }

    private _onVideoCountChangedDeferred(): void {
        if (!this._onVideoCountChangedDefer) {
            this._onVideoCountChangedDefer = _.defer(() => {
                delete this._onVideoCountChangedDefer;
                this._onVideoCountChanged();
            });
        }
    }

    private _onVideoCountChanged() {
        const videoObjectIds: number[] = this._callHandler.callGetParticipantVideos(this._slimcoreParticipantObjectId);
        if (!videoObjectIds) { return; }

        const videoObjectsByType: { [type: number]: SlimCoreCommon.IVideoObjectChangedEventData[] } = {};
        videoObjectsByType[Enums.VideoType.Video] = [];
        videoObjectsByType[Enums.VideoType.ScreenShare] = [];

        videoObjectIds.forEach(videoObjectId => {
            const mediaType = this._getIntProperty(this._callHandler, videoObjectId, Enums.Property.VideoType);
            const mediaStatus = this._getIntProperty(this._callHandler, videoObjectId, Enums.Property.VideoStatus);

            // Ignore Augmented for now
            if (mediaType === Enums.VideoType.Augmented) { return; }

            videoObjectsByType[mediaType].push({
                videoObjectId,
                videoType: mediaType,
                videoStatus: mediaStatus
            });
        });

        this._handleVideoCountChanged(Enums.VideoType.Video, videoObjectsByType[Enums.VideoType.Video]);
        this._handleVideoCountChanged(Enums.VideoType.ScreenShare, videoObjectsByType[Enums.VideoType.ScreenShare]);
        this.raiseChanged();
    }

    // VIDEO streams OPERATIONS
    private _handleVideoCountChanged(videoType: Enums.VideoType, videoObjects: SlimCoreCommon.IVideoObjectChangedEventData[]) {
        const streamType = SlimCoreElectronRemoteStreamManager.SlimcoreVideoTypeToStreamType(videoType);
        const streamsOfType = this._streams[streamType];
        const removals = _.filter(streamsOfType, s => !_.some(videoObjects, v => v.videoObjectId === s.id));

        _.each(removals, stream => {
            this._logger.info(`_handleVideoCountChanged: removing stream ${stream.id} (stream type: ${streamType})`);
            _.remove(streamsOfType, stream);
            stream.dispose();
        });

        // create objects for the _streams that exist in slimcore
        _.each(videoObjects, video => {
            if (!_.some(streamsOfType, s => s.id === video.videoObjectId)) {
                this._logger.info(`_handleVideoCountChanged: adding stream ${video.videoObjectId} (stream type: ${streamType})`);
                const newStream = new SlimCoreElectronRemoteStream(
                    this._logger,
                    this._settings,
                    this._slimcoreInstance,
                    this._slimcoreParticipantObjectId,
                    video.videoObjectId,
                    streamType,
                    this._callHandler,
                    this._streamContextMap[streamType]
                );
                this._streams[streamType].push(newStream);
                newStream.changed(() => this.raiseChanged());
            }
        });
    }

    public stopVideos(): Promise<void> {
        this._logger.info('stopVideos');
        const disposeStream = (stream: ICallParticipantStream): Promise<void> => {
            return (stream as SlimCoreElectronRemoteStream).disposeInternal();
        };
        return Promise.all(_.flatMap(_.values(this._streams), streams => _.map(streams, disposeStream))).then(noop);
    }

    public dispose(): void {
        this._logger.info('dispose');
        _.each(_.values(this._streams), _streams => _.each(_streams, s => s.dispose()));
        this._streams[StreamType.Video] = [];
        this._streams[StreamType.ScreenSharing] = [];
        super.dispose();
    }

    private static SlimcoreVideoTypeToStreamType(videoType: Enums.VideoType): StreamType {
        switch (videoType) {
            case Enums.VideoType.ScreenShare:
                return StreamType.ScreenSharing;
            case Enums.VideoType.Video:
                return StreamType.Video;
            default:
                return undefined;
        }
    }
}



