import { LogFactory, RootToolsManager } from '@skype/rt-js-bindings';
import { TsCallingLogger } from '../../../common/ts/logging/tscallingLogger';
import { RedirectingLogAppender } from '../../../common/ts/logging/ulLogAppender';
import { SlimCoreElectronStackConfig } from '../../typings/skype-calling-electron';
import * as SlimCoreCommon from '../SlimCoreCommon';
import { RootToolsManagerDelegate } from './ElectronRootToolsManagerDelegate';

export function initDelegate(slimcore: SlimCoreCommon.Engine, config: SlimCoreElectronStackConfig) : void {
    if (!RootToolsManager.isDelegateSet()) {    // if Teams, it's already wired up in S4L
        const rtDelegate = new RootToolsManagerDelegate(slimcore, config.logger);
        RootToolsManager.setDelegate(rtDelegate);
    }
}

export function patchLogger(config: SlimCoreElectronStackConfig) : void {
    if (!RootToolsManager.isDelegateSet()) {    // if Teams, it's already wired up in S4L
        if (config.logger) {
            const redirectingAppender = new RedirectingLogAppender(config.logger);
            LogFactory.instance().addAppender(redirectingAppender);
        }
    }
    config.logger = new TsCallingLogger('JS.TsCalling.Electron', false);
}

export function deinitLogging() : void {

    RootToolsManager.stopAsyncOperations();
}



