/// <reference path="../typings/slimcore-enums.d.ts" />

import * as _ from 'lodash';
import {
    CauseId, ElectronElement, FrameType, IRenderer, IRendererError, IRendererEvents, IRenderTarget, IVideoFrame,
    RendererErrorType, RendererStats, RendererType, ScalingMode, Size, StartStreamOptions
} from 'skype-calling-interface';
import { IElectronVideoFrame } from '../typings/skype-calling-electron';

import * as async from '../../common/ts/async';
import SlimCoreElectronBase from './SlimCoreElectronBase';

import { generateCauseId } from '../../common/ts/causeId';
import { IInternalLogger } from '../../common/ts/internalLogger';
import * as SlimCoreCommon from './SlimCoreCommon';

const VIDEO_ELEMENT_SIZE_POLL_INTERVAL = 2000;

export interface InternalRendererEvents extends IRendererEvents {
    renderStarted(): void;
    videoSizeChanged(width: number, height: number): void;
}

export default class SlimCoreElectronVideoRenderer extends SlimCoreElectronBase<InternalRendererEvents> implements IRenderer {
    public isRendering: boolean = false;
    public streamSize: Size;

    protected _videoBinding: SlimCore.VideoBindingRenderer;

    private _renderer: SlimCore.VideoRenderer;
    private _videoElement: HTMLVideoElement;
    private _paused: boolean = false;
    private _isFrameSinkSet = false;
    private _videoElementSizeChangeIntervalHandle = 0;
    private _isStartedLogged = false;

    public constructor(logger: IInternalLogger, settings: SlimCoreCommon.Settings, engine: SlimCoreCommon.Engine) {
        super(logger, settings, engine);
    }

    public dispose(causeId = generateCauseId()) {
        this._disposeVideoRenderer(causeId);
        super.dispose(causeId);
    }

    public get rendererType(): RendererType {
        return RendererType.VideoElement;
    }

    public get frameType(): FrameType {
        if (this._frameSink && this._frameSink.getFrameType) {
            return convertFrameType(this._frameSink.getFrameType());
        }

        return FrameType.Unknown;
    }

    public get paused(): boolean {
        return this._paused;
    }

    public set paused(value: boolean) {
        if (this._settings.platform !== SlimCoreCommon.PlatformType.Electron) {
            throw new Error('Only implemented for Electron renderer');
        }

        if (value === this._paused) { return; }
        if (this._frameSink) {
            this._controlFrameSink(!value);
        }
        this._paused = value;
    }

    public captureFrame(timeoutMs: number): Promise<IVideoFrame> {
        if (!this._videoBinding) {
            return Promise.resolve<IVideoFrame>();
        }
        return this._videoBinding.captureFrame({ timeout: timeoutMs })
            .then(frame => new VideoFrame(SlimCoreCommon.convertImageData(frame.image), frame.mirror));
    }

    public getStats(): Promise<RendererStats> {
        return async.asap(() => this._getStats());
    }

    public setScalingMode(scalingMode: ScalingMode): Promise<void> {
        if (this._renderer) {
            return this._renderer.setScalingMode(convertScalingMode(scalingMode));
        }

        if (this._videoElement) {
            return new Promise<void>(() => {
                this._videoElement.msZoom = (scalingMode === ScalingMode.Crop);
            });
        }

        return Promise.reject('No video renderer');
    }

    public dumpVideoSourceImages(): Promise<number> {
        return Promise.reject(new Error('Supposed to be overriden by preview video renderer. Not implemented for remote video renderer'));
    }

    protected _createVideoRenderer(target: IRenderTarget, options: StartStreamOptions, isLocalPreview: boolean) {
        switch (this._settings.platform) {
            case SlimCoreCommon.PlatformType.Electron:
                return this._createVideoRendererElectron(target, options);

            case SlimCoreCommon.PlatformType.Uwp:
                return this._createVideoRendererUwp(target, options, isLocalPreview);

            default:
                throw new Error(`Unsupported platform: ${this._settings.platform}`);
        }
    }

    private _createVideoRendererElectron(target: IRenderTarget, options: StartStreamOptions) {
        if (isElectronElement(target)) {
            this._renderer = target.renderer;
        } else {
            this._renderer = this._createVideoRendererImpl(target as HTMLElement, options);
        }

        this._renderer.on('video-size-changed', args => this._videoSizeChanged(args.width, args.height));

        if (this._frameSink) {
            this._frameSink.on('frame-type-changed', this._frameTypeChanged);
            this._frameSink.on('frame-size-changed', this._frameSizeChanged);
            this._frameSink.on('first-frame-rendered', this._firstFrameRendered);
            this._frameSink.on('full-frame-render-required', this._fullFrameRenderRequired);
            if (!_.isUndefined(options.ignoreMirroring) && this._frameSink.setIgnoreMirroring) {
                this._frameSink.setIgnoreMirroring(options.ignoreMirroring);
            }
            this._controlFrameSink(true);
        }
    }

    private _createVideoRendererUwp(target: IRenderTarget, options: StartStreamOptions, isLocalPreview: boolean) {
        this._logger.info(`Creating uwp video renderer with uri ${this._videoBindingRT.uri}`);
        this._videoBindingRT.on('video-uri-changed', this._videoUriChangedUwp);

        const container = (target as HTMLElement);

        this._videoElement = container.ownerDocument.createElement('video');
        container.appendChild(this._videoElement);

        this._videoElement.autoplay = true;
        this._videoElement.msHorizontalMirror = !options.ignoreMirroring ? isLocalPreview : false;
        this._videoElement.msZoom = (options.scalingMode === ScalingMode.Crop);
        this._videoElement.style.backgroundColor = options.transparent ? '' : 'black';
        this._videoElement.addEventListener('resize', this._videoResizedUwp);
        this._videoElement.src = this._videoBindingRT.uri || '';
        this._videoElement.addEventListener('pause', this._videoPausedUwp);

        if (!isLocalPreview && this._videoBindingRT.setVideoPreference) {
            let videoElementSize = { width: 0, height: 0 };
            this._videoElementSizeChangeIntervalHandle = setInterval(() => {
                const size: Size = { width: this._videoElement.offsetWidth, height: this._videoElement.offsetHeight };
                if (!_.isEqual(videoElementSize, size)) {
                    videoElementSize = size;
                    this._videoBindingRT.setVideoPreference(size.width, size.height);
                }
            }, VIDEO_ELEMENT_SIZE_POLL_INTERVAL);
        }
    }

    protected _disposeVideoRenderer(causeId: CauseId) {
        if (this._frameSink) {
            this._frameSink.removeListener('frame-type-changed', this._frameTypeChanged);
            this._frameSink.removeListener('frame-size-changed', this._frameSizeChanged);
            this._frameSink.removeListener('first-frame-rendered', this._firstFrameRendered);
            this._frameSink.removeListener('full-frame-render-required', this._fullFrameRenderRequired);
            this._controlFrameSink(false);
        }

        if (this._renderer) {
            this._renderer.removeAllListeners();
            this._renderer.dispose();
            this._renderer = null;
        }

        if (this._videoBindingRT) {
            this._videoBindingRT.removeListener('video-uri-changed', this._videoUriChangedUwp);
        }

        if (this._videoElementSizeChangeIntervalHandle) {
            clearInterval(this._videoElementSizeChangeIntervalHandle);
            this._videoElementSizeChangeIntervalHandle = 0;
        }
        if (this._videoElement) {
            this._videoElement.removeEventListener('resize', this._videoResizedUwp);
            this._videoElement.removeEventListener('pause', this._videoPausedUwp);
            this._videoElement.remove();
            this._videoElement = null;
        }
    }

    protected get _frameSink() {
        return this._renderer ? this._renderer.getFrameSink() : null;
    }

    private _videoSizeChanged(width: number, height: number) {
        this._logger.info(`video-size-changed: ${width} x ${height}`);

        this.streamSize = { width, height };
        this.isRendering = width > 0 && height > 0;
        if (!this._isStartedLogged && this.isRendering) {
            this._logger.info('renderStarted');
            this.event('renderStarted').raise();
            this._isStartedLogged = true;
        }
        this.event('videoSizeChanged').raise(width, height);
        this.raiseChanged();
    }

    private _frameTypeChanged = (args: SlimCore.FrameSink.Events.FrameTypeChangedArgs) => {
        this._logger.info(`frame-type-changed: ${args.type}`);

        this.event('frameTypeChanged').raise(convertFrameType(args.type));
    }

    private _frameSizeChanged = (args: SlimCore.FrameSink.Events.FrameSizeChangedArgs) => {
        this._logger.info(`frame-size-changed: ${args.width} x ${args.height}`);
    }

    private _firstFrameRendered = (args: SlimCore.FrameSink.Events.FirstFrameRenderedArgs) => {
        this._logger.info('first-frame-rendered');
        this.event('firstFrameRendered').raise();
    }

    private _fullFrameRenderRequired = (args: SlimCore.FrameSink.Events.FullFrameRenderRequiredArgs) => {
        this._logger.info(`full-frame-render-required: ${args.fullFrameRenderRequired}`);
        this.event('fullFrameRenderRequired').raise(args.fullFrameRenderRequired);
    }

    private _getStats() {
        if (!this._frameSink) {
            throw new Error('Renderer is not initialized');
        }

        if (this._frameSink.getStats) {
            return this._frameSink.getStats();
        } else {
            throw new Error('Renderer does not support stats');
        }
    }

    public get frameSinkMetadata(): SlimCore.FrameSink.Metadata {
        if (!this._frameSink || !this._frameSink.getMetadata) { return null; }
        return this._frameSink.getMetadata();
    }

    private _createVideoRendererImpl(container: HTMLElement, options: StartStreamOptions): SlimCore.VideoRenderer {
        const args: SlimCore.VideoRenderer.ConstructorArgs = {
            container,
            transparent: options.transparent,
            scalingMode: convertScalingMode(options.scalingMode),
            useBufferSharing: !!this._settings.enableBufferSharing,
            useFirstFrameRender: !!this._settings.enableFirstFrameRender,
            logger: this._logger
        };

        try {
            const frameSink = SlimCore.createChromiumFrameSink
                ? SlimCore.createChromiumFrameSink()
                : SlimCore.createPepperFrameSink();
            return VideoRenderer.createChromiumVideoRenderer(frameSink, args);
        } catch (error) {
            this._logger.error(`createChromiumVideoRenderer failed: ${error}`);
            throw new SlimCoreElectronRendererError(error, convertRendererError(error));
        }
    }

    private _videoUriChangedUwp = (args: SlimCoreRT.VideoBindingRenderer.Events.VideoUriChangedArgs) => {
        if (this._videoElement) {
            this._videoElement.src = args.uri || '';
        }
    }

    private _videoResizedUwp = () => {
        this._videoSizeChanged(this._videoElement.videoWidth, this._videoElement.videoHeight);
    }

    private _videoPausedUwp = () => {
        if (this._videoElement) {
            this._videoElement.src = this._videoBindingRT.uri || '';
            this._videoElement.play();
        }
    }

    private get _videoBindingRT(): SlimCoreRT.VideoBindingRenderer {
        return this._videoBinding as SlimCoreRT.VideoBindingRenderer;
    }

    private _controlFrameSink(shouldBeSet: boolean): void {
        // nothe this implementation does not check iff frame sink has changed or if binding has changed!
        if (this._videoBinding && this._isFrameSinkSet !== shouldBeSet) {
            this._isFrameSinkSet = shouldBeSet;
            if (shouldBeSet) {
                this._videoBinding.addFrameSink(this._frameSink);
            } else {
                this._videoBinding.removeFrameSink(this._frameSink);
            }
        }
    }
}

class VideoFrame implements IElectronVideoFrame {
    public constructor(private _imageData: ImageData, private _mirrored: boolean) {
    }

    public getSize(): Size {
        return { width: this._imageData.width, height: this._imageData.height };
    }

    public isMirrored(): boolean {
        return this._mirrored;
    }

    public getImageData(): ImageData {
        return this._imageData;
    }
}

class SlimCoreElectronRendererError implements IRendererError {
    public constructor(public reason: any, public errorType: RendererErrorType = RendererErrorType.Unknown) {
    }

    public toString() {
        return `Reason=${this.reason}, errorType=${this.errorType}`;
    }
}

function isElectronElement(target: IRenderTarget): target is ElectronElement {
    return (target as ElectronElement).renderer !== undefined;
}

function convertFrameType(type: SlimCore.FrameSink.FrameType): FrameType {
    switch (type) {
        case SlimCore.FrameSink.FrameType.None:
            return FrameType.None;
        case SlimCore.FrameSink.FrameType.Software:
            return FrameType.Software;
        case SlimCore.FrameSink.FrameType.Hardware:
            return FrameType.Hardware;
        default:
            return FrameType.Unknown;
    }
}

function convertRendererError(error: Error): RendererErrorType {
    const videoRendererError = error as SlimCore.VideoRendererError;

    switch (videoRendererError.reason) {
        case SlimCore.VideoRenderer.ErrorReason.ContextFailed:
            return RendererErrorType.RendererFailedGpuContext;
        case SlimCore.VideoRenderer.ErrorReason.Unavailable:
            return RendererErrorType.RendererFailedUnavailable;
        default:
            return RendererErrorType.RendererFailed;
    }
}

function convertScalingMode(scalingMode: ScalingMode): SlimCore.VideoRenderer.ScalingMode {
    switch (scalingMode) {
        case ScalingMode.Stretch:
            return SlimCore.VideoRenderer.ScalingMode.Stretch;
        case ScalingMode.Crop:
            return SlimCore.VideoRenderer.ScalingMode.Crop;
        case ScalingMode.Fit:
            return SlimCore.VideoRenderer.ScalingMode.Fit;
        default:
            return undefined;
    }
}



