export class Stopwatch {
    public msElapsed = 0;
    public isPaused = false;
    public get startTime() {
        return this._startTime;
    }

    private _startTime = new Date().getTime();

    public pause = (): void => {
        if (!this.isPaused) {
            this.msElapsed += new Date().getTime() - this._startTime;
            this.isPaused = true;
        }
    }

    public resume = (): void => {
        if (this.isPaused) {
            this.isPaused = false;
            this._startTime = new Date().getTime();
        }
    }

    public duration = (): number => {
        if (this.isPaused) {
            return this.msElapsed;
        }
        return this.msElapsed + new Date().getTime() - this._startTime;
    }

    public durationInMinutes = (): number => {
        const mins = this.duration() / 60000;
        return Math.ceil(mins);
    }

    public durationInSeconds = (): number => {
        const seconds = this.duration() / 1000;
        return Math.ceil(seconds);
    }
}

export function build(): Stopwatch {
    return new Stopwatch();
}



