import { CallStartOptions, CauseId, ICall, TerminatedReason } from 'skype-calling-interface';
import { CALL_OPERATIONS, CALL_OPERATIONS_INTERNAL, CONTENT_SHARING_OPERATIONS, PARTICIPANT_OPERATIONS } from './callConstants';
import { generateCauseId, validateCauseId } from './causeId';
import { noop } from './fn';
import OperationHandler from './operationHandler';

export enum OPERATION_TYPE {
    CHAINED = 'Chained',
    SYNC = 'Sync',
    ASYNC = 'Async'
}

const OPERATION_ID_INDEX = '_operationIdValue_';
const CAUSE_ID_INDEX = '_causeIdValue_';
const CALL_START_OPTIONS_INDEX = '_callStartOptionsValue_';

type OperationOptions = {
    type?: OPERATION_TYPE.CHAINED | OPERATION_TYPE.ASYNC;
    waitFor?: CALL_OPERATIONS | CALL_OPERATIONS_INTERNAL | PARTICIPANT_OPERATIONS | CONTENT_SHARING_OPERATIONS;
    singular?: boolean;
    triggerAttach?: boolean;
    operationIdKey?: string;
} | {
    type?: OPERATION_TYPE.SYNC;
    singular?: boolean;
    triggerAttach?: boolean;
    operationIdKey?: string;
};

export interface ICallInternal extends ICall {
    _callOperationHandler: OperationHandler;
    _participantOperationHandler: OperationHandler;
    processCallStartOptions(options: CallStartOptions, causeId: CauseId) : void;
}

enum OPERATION_HANDLER_TYPE {
    CALL,
    PARTICIPANT
}

export function callOperation(operationName: string, options?: OperationOptions) {
    return operationDecorator(OPERATION_HANDLER_TYPE.CALL, operationName, options);
}

export function participantOperation(operationName: string, options?: OperationOptions) {
    return operationDecorator(OPERATION_HANDLER_TYPE.PARTICIPANT, operationName, options);
}

/**
 * Use this decorator on parameter that will be used as a unique key for operation
 */
export const operationId = (target: any, key: string | symbol, index: number) => {
    if (!target[key][OPERATION_ID_INDEX]) {
        target[key][OPERATION_ID_INDEX] = index;
    } else {
        throw new Error('can not specify more than one operation id');
    }
};

/**
 * Use this decorator on parameter that will be used as a causeId string
 * it'll be used in logging and telemetry for current operation
 */
export const causeId = (target: any, key: string | symbol, index: number) => {
    if (!target[key][CAUSE_ID_INDEX]) {
        target[key][CAUSE_ID_INDEX] = index;
    } else {
        throw new Error('can not specify more than one cause id');
    }
};

/**
 * Use this decorator on parameter that will be used for call start options
 * it'll be used to process the options
 */
export const callStartOptions = (target: any, key: string | symbol, index: number) => {
    if (!target[key][CALL_START_OPTIONS_INDEX]) {
        target[key][CALL_START_OPTIONS_INDEX] = index;
    } else {
        throw new Error('can not specify options more than once');
    }
};

/**
 * Define decorator factory, that returns method decorator
 *  There are 2 decorators exported - @callOperation and @participantOperation
 *  both create and handle operations using their own OperationHandlers
 *
 *  Every method should be identified by unique OPERATION_NAME that is passed as a 1st argument
 *  Additionally if operation may be executed in parallel this can be achieved by specifying operation ID
 *  An operation ID can be a unique string | number input parameter
 *  to define which input parameter will be used as operation id use @operationId decorator
 *
 *
 *  !!! NOTE !!!
 *  To use this decorator make sure your class has a private fields:
 *  _callOperationHandler - if you're using @callOperation decorator
 * _participantOperationHandler - if you're using @participantOperation
 *
 *
 *  example:
 *  1) @callOperation('AAA')
 *  private methodA(): void
 *
 *  2) @participantOperation('AAA') // different from above because uses own OperationHandler
 *  public anotherMethod(param1: number): void
 *
 *  example with specifying operationId
 *  3) @callOperation('AAA')
 *  public thisIsOperationA(@operationId thisIsUniqueKeyForThatOperation: string)
 *
 *  Assuming that 1/2/3 are async - subsequent call to any of 1/2/3 before they are finished ( fulfilled or rejected )
 *  will result in a return of a pending promise returned by previous call
 *
 *  Decorator can be configured through OPTIONS ( 2nd arg )
 *  - type: OPERATION_TYPE ( CHAINED | SYNC | ASYNC )
 *   -  CHAINED - will chain all operations called using that decorator ( either callOperation or participantOperation )
 *   -  SYNC - will handle operation as if it's synchronous - so it doesn't return pending promise but rather returns result or throws expcetion
 *   -  ASYNC - ( default ) - will handle operation as if it's async - it has to return promise, but it will also handle sync exception in execution
 *
 *  - waitFor: specify a name of another operation that ( if pending ) should finish ( resolve/reject ) before current operation will be executed
 *  - TODO validCallState: CallState[] - an array of call states - operation will return rejected promise if call is not in one of the states
 *  - TODO invalidCallState: CallState[] - an array of call states - operation will return rejected promise if call is in one of the states
 *  - singular: boolean - if true , operation a subsequent call to execute given operation before previous is finished wil result in rejection ( transfer scenarios )
 */

 // tslint:disable:no-invalid-this

const operationDecorator = (operationHandlerType: OPERATION_HANDLER_TYPE, operationName: string, options?: OperationOptions) => {
    return (target: any, propertyKey: string, descriptor: PropertyDescriptor): any => {
        const originalMethod = descriptor.value;
        const operationIdIndex: number = target[propertyKey] && target[propertyKey][OPERATION_ID_INDEX];
        const causeIdIndex: number = target[propertyKey] && target[propertyKey][CAUSE_ID_INDEX];
        const callStartOptionsIndex: number = target[propertyKey] && target[propertyKey][CALL_START_OPTIONS_INDEX];
        const operationType = (options && options.type) || OPERATION_TYPE.ASYNC;
        descriptor.value = function (...args: any[]) {
            const callInternal = (this as ICallInternal);

            let operationId = operationIdIndex !== undefined ? args[operationIdIndex] : undefined;

            if (operationId !== undefined) {
                if (Array.isArray(operationId)) {
                    try {
                        // Build a single string containing a sorted list of array entries.
                        const s = operationId.map(o => o.toString()).sort().concat().toString();
                        operationId = getStringHash(s).toString();
                    } catch {
                        return Promise.reject(TerminatedReason.UnknownError);
                    }
                } else if (operationId && typeof operationId === 'object') {
                    try {
                        let temp = JSON.stringify(operationId);
                        if (options && options.operationIdKey && operationId.hasOwnProperty(options.operationIdKey)) {
                            temp = operationId[options.operationIdKey];
                        }
                        operationId = temp;
                    } catch {
                        return Promise.reject(TerminatedReason.UnknownError);
                    }
                }
            }

            let causeId = generateCauseId();
            if (causeIdIndex !== undefined) {
                if (args[causeIdIndex] && validateCauseId(args[causeIdIndex])) {
                    causeId = args[causeIdIndex];
                } else {
                    args[causeIdIndex] = causeId;
                }
            }

            const operationHandler = getOperationHandler(callInternal, operationHandlerType);
            operationHandler.logOperation(causeId, operationName);

            if (options && !!options.singular) {
                if (operationHandler.hasPendingOperation(operationName)) {
                    operationHandler.logOperation(causeId, operationName, 'Not allowed, pending operation exists');
                    return Promise.reject(TerminatedReason.ActionNotAllowed);
                }
            }

            try {
                if (callStartOptionsIndex !== undefined && args[callStartOptionsIndex]) {
                    callInternal.processCallStartOptions(args[callStartOptionsIndex], causeId);
                }
            } catch {
                return Promise.reject(TerminatedReason.UnknownError);
            }

            const execute = () => {
                if (operationType === OPERATION_TYPE.CHAINED) {
                    return operationHandler.executeChained(originalMethod.bind(this, ...args), operationName, operationId, causeId);
                } else if (operationType === OPERATION_TYPE.SYNC) {
                    return operationHandler.executeSync(originalMethod.bind(this, ...args), operationName, operationId, causeId);
                } else {
                    return operationHandler.execute(originalMethod.bind(this, ...args), operationName, operationId, causeId);
                }
            };

            if (operationName === CALL_OPERATIONS.START_CALL || operationName === CALL_OPERATIONS.JOIN_CALL) {
                if (!operationHandler.hasPendingOperation(CALL_OPERATIONS_INTERNAL.CALL_START_OR_JOIN_INITIATED)) {
                    operationHandler.createPendingOperation(CALL_OPERATIONS_INTERNAL.CALL_START_OR_JOIN_INITIATED).catch(noop);
                }
            }

            if (options && options.triggerAttach) {
                if (!operationHandler.hasPendingOperation(CALL_OPERATIONS_INTERNAL.ELECTRON_SLIMCORE_READY)) {
                    operationHandler.createPendingOperation(CALL_OPERATIONS_INTERNAL.ELECTRON_SLIMCORE_READY).catch(noop);
                }
            }

            if (options && options.type !== OPERATION_TYPE.SYNC) {
                const waitFor = options && options.waitFor;
                if (waitFor && operationHandler.hasPendingOperation(waitFor)) {
                    return operationHandler.waitForOperation(waitFor, undefined, causeId).then(execute, execute);
                }
            }

            return execute();
        };
        return descriptor;
    };
};

const getOperationHandler = (iCall: ICallInternal, operationHandlerType: OPERATION_HANDLER_TYPE): OperationHandler => {
    if (operationHandlerType === OPERATION_HANDLER_TYPE.CALL) {
        return iCall._callOperationHandler;
    } else if (operationHandlerType === OPERATION_HANDLER_TYPE.PARTICIPANT) {
        return iCall._participantOperationHandler;
    } else {
        throw new Error('Unsupported operation handler type!');
    }
};

function getStringHash(s: string): number {
    let hash = 0;
    for (let i = 0; i < s.length; i++) {
        const chr = s.charCodeAt(i);
        hash = ((hash << 5) - hash) + chr;
        hash |= 0; // Convert to 32bit integer
    }
    return hash;
}



