/// <reference path="../typings/slimcore.d.ts" />
/// <reference path="../typings/slimcore-enums.d.ts" />

import * as _ from 'lodash';
import {
    CauseId, IRenderTarget, PreviewVideoSelection, ScalingMode, StartStreamOptions, VideoDeviceDesc
} from 'skype-calling-interface';
import ChainedPromise from '../../common/ts/chainedPromise';

import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronDeviceManager from './SlimCoreElectronDeviceManager';
import SlimCoreElectronVideoRenderer from './SlimCoreElectronVideoRenderer';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { IInternalLogger } from '../../common/ts/internalLogger';
let _instanceCount = 0;

export default class SlimCoreElectronLocalVideoPreview extends SlimCoreElectronVideoRenderer {
    private readonly _id = _instanceCount++;

    private _previewVideoObjectId = 0;
    private _shouldRun = false;

    private _currentCamera: VideoDeviceDesc;
    private _chainedApiPromise: ChainedPromise;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _currentTarget: IRenderTarget,
        private _deviceManager: SlimCoreElectronDeviceManager,
        private _selection: PreviewVideoSelection,
        private _options: StartStreamOptions,
        causeId: CauseId
    ) {
        super(logger.createChild(() => `LocalVideoPreview[${this._id}][${this._selection.kind}][${this._previewVideoObjectId}]`), settings, engine);
        this._logger.info(`constructor device? ${this._selection.device} causeId: ${causeId}`);
        if (this._selection.kind !== 'camera' && (this._selection.kind !== 'sharing' || !_.isUndefined(this._selection.device))) {
            throw new Error(`invalid selection parameters causeId: ${causeId}`);
        }
        this._chainedApiPromise = new ChainedPromise(logger.createChild(() => `LocalVideoPreview[${this._id}][${this._selection.kind}][${this._previewVideoObjectId}]`));
        if (this._selection.kind === 'camera' && _.isUndefined(this._selection.device)) {
            this._registerDisposable(this._deviceManager.on('devicesChanged', () => this._devicesChanged(causeId)));
        }
    }

    private _devicesChanged(causeId: CauseId): void {
        this._logger.info(`_devicesChanged causeId: ${causeId}`);
        this._chainedApiPromise.chainPromise(() => this._swapVideoDevice(causeId), 'swapVideoDevice', causeId);
    }

    public startVideoAsync(causeId: CauseId): Promise<void> {
        this._logger.info(`startVideoAsync causeId: ${causeId}`);
        this._shouldRun = true;
        return this._chainedApiPromise.chainPromise(() => this._startVideo(causeId), 'startVideoAsync', causeId);
    }

    public stop(causeId: CauseId): Promise<void> {
        this._logger.info(`stop causeId: ${causeId}`);
        this._shouldRun = false;
        return this._chainedApiPromise.chainPromise(() => this._stopVideo(causeId, false), 'stopVideoAsync', causeId);
    }

    public dumpVideoSourceImages(): Promise<number> {
        if (!this._previewVideoObjectId) {
            return Promise.reject(new Error('Cannot dump video fx debug images when there is no preview video'));
        }
        return this._slimcoreInstance.dumpVideoSourceImages(this._previewVideoObjectId);
    }

    public dispose(causeId: CauseId): void {
        this._logger.info(`dispose causeId: ${causeId}`);
        this._shouldRun = false;
        this._chainedApiPromise.chainPromise(() => this._dispose(causeId), 'dispose', causeId);
    }

    private _isRunning(): boolean {
        return (this._previewVideoObjectId !== 0 && !!this._videoBinding);
    }

    private _dispose(causeId: CauseId): Promise<void> {
        return SlimCoreCommon.forgetAndLog(this._stopVideo(causeId, true), this._logger, `dispose causeId: ${causeId} stopping video preview failed`).then(() => {
            this._currentTarget = null;
            super.dispose(causeId);
        });
    }

    private async _swapVideoDevice(causeId: CauseId): Promise<void> {
        if (this._shouldRun && this._isRunning() && !!this._currentTarget) {
            const selectedCamera = this._deviceManager.getSelectedCamera();
            if (selectedCamera && this._currentCamera && selectedCamera.id !== this._currentCamera.id) {
                this._logger.info(`_swapVideoDevice causeId: ${causeId} will change video preview device: ${this._currentCamera.id} -> ${selectedCamera.id}`);
                await this._stopVideo(causeId, true);
                await this._startVideo(causeId);
            }
        } else {
            this._logger.info(`_swapVideoDevice causeId: ${causeId} noop, should run: ${this._shouldRun} is running: ${this._isRunning()} has target: ${!!this._currentTarget}`);
        }
    }

    private async _startVideo(causeId: CauseId): Promise<void> {
        if (this._shouldRun && !this._isRunning() && !!this._currentTarget) {
            let name = '';
            let path = '';
            let videoType = Enums.VideoType.ScreenShare;
            if (this._selection.kind === 'camera') {
                if (_.isUndefined(this._selection.device)) {
                    this._currentCamera = this._deviceManager.getSelectedCamera();
                } else {
                    this._currentCamera = this._deviceManager.getCamera(this._selection.device);
                }
                if (!this._currentCamera) {
                    throw new Error(`_startVideo causeId: ${causeId} no camera to start video preview`);
                }
                videoType = Enums.VideoType.Video;
                name = this._currentCamera.label;
                path = this._currentCamera.id;
            }
            this._logger.info(`_startVideo causeId: ${causeId} will start video preview now for device name: ${name} path: ${path}`);
            this._previewVideoObjectId = await this._deviceManager.getPreviewVideo(videoType, name, path);
            this._videoBinding = await this._deviceManager.getRendererBindingManager().acquire(this, this._previewVideoObjectId);

            const options = this._options || {
                scalingMode: ScalingMode.Crop,
                transparent: false
            };
            await this._createVideoRenderer(this._currentTarget, options, true);
        } else {
            this._logger.info(`_startVideo causeId: ${causeId} noop, should run: ${this._shouldRun} is running: ${this._isRunning()} has target: ${!!this._currentTarget}`);
        }
    }

    private async _stopVideo(causeId: CauseId, force: boolean): Promise<void> {
        if ((!this._shouldRun || force) && this._isRunning()) {
            this._logger.info(`_stopVideo causeId: ${causeId} force: ${force} will stop video preview now`);
            try {
                this._disposeVideoRenderer(causeId);
            } catch (error) {
                this._logger.warn(`_stopVideo causeId: ${causeId} _disposeVideoRenderer error: ${error} falied @ ${new Error().stack}`);
            }
            const releasePromise = this._deviceManager.getRendererBindingManager().release(this, this._previewVideoObjectId);
            this._previewVideoObjectId = 0;
            this._videoBinding = null;
            await releasePromise;
        } else {
            this._logger.info(`_stopVideo causeId: ${causeId} noop, force: ${force} should run: ${this._shouldRun} is running: ${this._isRunning()} has target: ${!!this._currentTarget}`);
        }
    }
}



