// Enum MetatagType is used for unpacking the event payload from the WinRTWrapper
// As this is specific only to RT, moving it to UWPEventWrapper file.
// We should look into what is the equivalent step on electron side.
// If  we decide to retain this design, we should then keep it common for electron as well
// and move it to slimcore-enums-d.ts
import * as _ from 'lodash';

enum MetatagType {
    Integer = 0,
    String = 1,
    Binary = 2
}

type ObjectPropertyChangedValue = {
    type: MetatagType;
    intVal: number;
    strVal: string;
    binVal: any;
};

type ObjectPropertyChangedArgs = {
    objectType: SlimCore.Enums.ObjectType;
    objectId: SlimCore.ObjectId;
    propKey: SlimCore.Enums.Property;
    value: ObjectPropertyChangedValue;
};

class UwpNativeWrapper<T> {
    public constructor(private _nativeObject: T) {
        _.forIn(_nativeObject, (v, k, o) => {
            if (_.isFunction(v)) {
                if (!this[k]) { //some methods are overridden by this wrapper
                    this[k] = function () { // tslint:disable-line:no-function-expression
                        return v.apply(o, arguments);
                    };
                }
            } else {
                Object.defineProperty(this, k, {
                    get() { return o[k]; },
                    set(nv) { o[k] = nv; }
                });
            }
        });
    }

    public get objectToWrap() {
        return this._nativeObject;
    }

    public get objectAsWrapped() {
        return this as any as T;
    }
}

class UwpEventWrapper<T, U> extends UwpNativeWrapper<T> implements SlimCore.EventEmitter<U> {
    private eventListenerArrays: any;

    public constructor(nativeObject: T) {
        super(nativeObject);
        this.eventListenerArrays = {};

        if ((nativeObject as any).oncommonwrapperevent !== undefined) {
            (nativeObject as any).oncommonwrapperevent = (eventPayload: SlimCoreRT.CommonWrapperEventPayload) => {
                const args = this.argsFromCommonWrapperEventPayload(eventPayload);
                const eventListeners: any[] = this.eventListenerArrays[eventPayload.target];
                if (eventListeners) {
                    eventListeners.forEach((listener: SlimCore.EventHandler<any>) => {
                        listener(args);
                    });
                }
            };
        }
    }

    private argsFromCommonWrapperEventPayload(eventPayload: SlimCoreRT.CommonWrapperEventPayload): any {
        if (eventPayload.target === 'object-property-changed') {
            const sourceArgs = eventPayload.detail[0] as ObjectPropertyChangedArgs;
            const args: SlimCore.Engine.Events.ObjectPropertyChangedArgs = {
                objectType: sourceArgs.objectType,
                objectId: sourceArgs.objectId,
                propKey: sourceArgs.propKey,
                value: undefined
            };
            switch (sourceArgs.value.type) {
                case MetatagType.Integer:
                    args.value = sourceArgs.value.intVal;
                    break;
                case MetatagType.String:
                    args.value = sourceArgs.value.strVal;
                    break;
                case MetatagType.Binary:
                    args.value = sourceArgs.value.binVal;
                    break;
                default:
            }
            return args;
        }
        return eventPayload.detail[0];
    }

    public addListener<P extends keyof U>(event: P, listener: SlimCore.EventHandler<U[P]>): this {
        let eventListeners: any[] = this.eventListenerArrays[event];
        if (!eventListeners) {
            eventListeners = this.eventListenerArrays[event] = [];
        }
        if (eventListeners.indexOf(listener) === -1) {
            eventListeners.push(listener);
        }
        return this;
    }

    public on<P extends keyof U>(event: P, listener: SlimCore.EventHandler<U[P]>): this {
        return this.addListener(event, listener);
    }

    public once<P extends keyof U>(event: P, listener: SlimCore.EventHandler<U[P]>): this {
        return this.addListener(event, listener);
    }

    public removeListener<P extends keyof U>(event: P, listener: SlimCore.EventHandler<U[P]>): this {
        const eventListeners: any[] = this.eventListenerArrays[event];
        if (eventListeners) {
            const index = eventListeners.indexOf(listener);
            if (index > -1) {
                eventListeners.splice(index, 1);
            }
        }
        return this;
    }

    public removeAllListeners<P extends keyof U>(event?: P): this {
        if (event) {
            let eventListeners: any[] = this.eventListenerArrays[event];
            if (eventListeners) {
                eventListeners = this.eventListenerArrays[event] = [];
            }
            return this;
        }
        Object.keys(this.eventListenerArrays).forEach(key => {
            this.eventListenerArrays[key] = [];
        });
        return this;
    }

    public getMaxListeners(): number {
        // TODO: fix this
        return 0;
    }

    public setMaxListeners(n: number): void {
        // TODO: fix this
    }

    public listeners<P extends keyof U>(event: P): Function[] {
        return this.eventListenerArrays[event];
    }

    public emit<P extends keyof U>(event: P, args: U[P]): boolean {
        const eventListeners: any[] = this.eventListenerArrays[event];
        if (eventListeners) {
            eventListeners.forEach((listener: SlimCore.EventHandler<any>) => {
                listener(args as any);
            });
        }
        return true;
    }
}

interface UwpVideoBindingWrapped {
    objectToWrap: SlimCore.VideoBinding;
}

class UwpVideoBinding<T extends SlimCore.VideoBinding, U> extends UwpEventWrapper<T, U> implements UwpVideoBindingWrapped {
    public constructor(nativeObject: T) {
        super(nativeObject);
    }
}

export class UwpEngineWrapped extends UwpEventWrapper<SlimCore.Engine, SlimCore.Engine.Events> {
    public constructor(nativeObject: SlimCore.Engine) {
        super(nativeObject);
    }

    public createCallInterface(): SlimCore.CallHandler {
        const object = this.objectToWrap.createCallInterface();
        const wrapped = new UwpEventWrapper<SlimCore.CallHandler, SlimCore.CallHandler.Events>(object);
        return wrapped.objectAsWrapped;
    }

    public getContentSharing(contentSharingObjectId: SlimCore.ObjectId): SlimCore.ContentSharing {
        const object = this.objectToWrap.getContentSharing(contentSharingObjectId);
        const wrapped = new UwpEventWrapper<SlimCore.ContentSharing, SlimCore.ContentSharing.Events>(object);
        return wrapped.objectAsWrapped;
    }

    public createVideoBindingRenderer(args: SlimCore.VideoBindingRendererArgs): SlimCoreRT.VideoBindingRenderer {
        // todo: move createVideoBindingRenderer out from slimcore.d.ts into platform specific file (also then remove as any)
        const object = (this.objectToWrap as any).createVideoBindingRenderer(args);
        const wrapped = new UwpVideoBinding<SlimCoreRT.VideoBindingRenderer, SlimCoreRT.VideoBindingRenderer.Events>(object);
        return wrapped.objectAsWrapped;
    }

    public createVideoBindingScreenShare(): SlimCore.VideoBindingScreenShare {
        const object = (this.objectToWrap as any).createVideoBindingScreenShare();
        const wrapped = new UwpVideoBinding<SlimCore.VideoBindingScreenShare, SlimCore.VideoBindingScreenShare.Events>(object);
        return wrapped.objectAsWrapped;
    }

    public videoCreateBinding(videoObjectId: SlimCore.ObjectId, videoBinding: SlimCore.VideoBinding): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            const videoBindingWrapped = videoBinding as any as UwpVideoBindingWrapped;
            const videoBindingEvent = videoBindingWrapped.objectToWrap as any;
            if (videoBindingEvent.onvideobindingcreatedevent !== undefined) {
                videoBindingEvent.onvideobindingcreatedevent = () => {
                    resolve();
                };
            }
            if (videoBindingEvent.onvideobindingfailedevent !== undefined) {
                videoBindingEvent.onvideobindingfailedevent = () => {
                    (this.objectToWrap as any).videoReleaseBinding(videoObjectId, videoBindingWrapped.objectToWrap);
                    reject('binding creation failed');
                };
            }
            (this.objectToWrap as any).videoCreateBinding(videoObjectId, videoBindingWrapped.objectToWrap);
        });
    }

    public videoReleaseBinding(videoObjectId: SlimCore.ObjectId, videoBinding: SlimCore.VideoBinding): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            const videoBindingWrapped = videoBinding as any as UwpVideoBindingWrapped;
            const videoBindingEvent = videoBindingWrapped.objectToWrap as any;
            if (videoBindingEvent.onvideobindingreleasedevent !== undefined) {
                videoBindingEvent.onvideobindingreleasedevent = () => {
                    resolve();
                };
            }
            (this.objectToWrap as any).videoReleaseBinding(videoObjectId, videoBindingWrapped.objectToWrap);
        });
    }

    public setMediaConfig(configuration: SlimCore.MediaConfiguration): void {
        (this.objectToWrap as any).setMediaConfig(JSON.stringify(configuration));
    }
}



