// executes exec function immediately as if it were an async call, and handles errors correctly
export function asap<T>(exec: () => T): Promise<T> {
    return new Promise<T>(resolve => {
        resolve(exec());
    });
}

export class TimeoutError extends Error { }

export class Timer {
    private timeoutId: number;

    constructor(private ontimeout: () => void) { }

    public start(timeout: number) {
        this.timeoutId = setTimeout(this.ontimeout, timeout);
    }

    public stop() {
        clearTimeout(this.timeoutId);
    }
}

export type Deferred<T> = {
    resolve(value?: T | Thenable<T>): void;
    reject(error: any): void;
    promise: Promise<T>;
};

export function defer<T>(): Deferred<T> {
    let resolve;
    let reject;
    const promise = new Promise<T>((resolveFn, rejectFn) => {
        resolve = resolveFn;
        reject = rejectFn;
    });
    return { promise, resolve, reject };
}

export function timedDefer<T>(timeout: number, timeoutMessage = `deferred timed out after ${timeout}ms`): Deferred<T> {
    let resolve;
    let reject: (error: any) => void;
    const rejectTimer = new Timer(() => reject(new TimeoutError(timeoutMessage)));
    const promise = new Promise<T>((resolveFn, rejectFn) => {
        resolve = (value?: T | Thenable<T>) => {
            rejectTimer.stop();
            resolveFn(value);
        };
        reject = (error: Error) => {
            rejectTimer.stop();
            rejectFn(error);
        };
    });
    rejectTimer.start(timeout);
    return { promise, resolve, reject };
}

export function delay(time: number): Promise<void> {
    // tslint:disable-next-line: no-string-based-set-timeout
    return new Promise(resolve => setTimeout(resolve, time));
}



