import { pii } from '@skype/rt-js-bindings';
import { CallEndDiagnosticsInfo, CallingLogger, CallOrigin, CallState, CallType, CauseId, FailureType, ParticipantRosterRole, PreheatFlags, TerminatedReason } from 'skype-calling-interface';
import { generateCauseId } from './causeId';
import { getPIISafeObject } from './logUtils';
import { scrubMriOrOmit } from './piiUtils';
import { getTsCallingVersion } from './tsCallingVersionProvider';

const MAX_IN_CALL_EVENTS = 150;

enum OperationStatus {
    Pending = 'Pending',
    Success = 'Success',
    Failure = 'Failure'
}

enum EventType {
    Event,
    Operation
}

export type IEvent = {
    [key: string]: {
        start: number;
        duration: number;
        status: OperationStatus;
        result?: any;
        data?: any;
    };
};

export interface ITelemetryEvent {
    recordSuccess<T>(result: T, resultCauseId: string): void;
    recordFailure(reason: any, resultCauseId: string): void;
    isPending(): boolean;
    getEvent(): IEvent;
}

class Event implements ITelemetryEvent {
    private result: any;
    private status = OperationStatus.Pending;
    private duration: number;
    private eventStartTimestamp = new Date().getTime();
    private data: any[] = [];
    private resultCauseId: string;
    private eventVariant: string;

    constructor(
        private eventName: string,
        private eventStartTime: number,
        private type: EventType,
        private causeId: CauseId,
        data?: any
    ) {
        if (this.type === EventType.Event) {
            delete this.status;
        }
        if (data) {
            this.data.push(data);
        }
    }

    public set variant(value: string) {
        this.eventVariant = value;
    }

    public recordSuccess(result: any, resultCauseId: string): void {
        this.recordResult(true, result, resultCauseId);
    }

    public recordFailure(error: any, resultCauseId: string): void {
        this.recordResult(false, error, resultCauseId);
    }

    public addData(data: any): void {
        this.data.push(data);
    }

    public isPending() {
        return this.status === OperationStatus.Pending;
    }

    public getEvent(): IEvent {
        const rawEvent = {
            start: this.eventStartTime,
            duration: this.duration,
            status: this.status,
            result: this.result,
            causeId: this.causeId,
            resultCauseId: this.resultCauseId,
            variant: this.eventVariant,
            data: this.data
                .map(d => this.prepareData(d, true))
                .filter(d => !!d)
        };

        if (this.type === EventType.Event) {
            delete rawEvent.status;
        }
        if (!rawEvent.data.length) {
            delete rawEvent.data;
        }
        if (!rawEvent.causeId) {
            delete rawEvent.causeId;
        }
        if (!rawEvent.resultCauseId) {
            delete rawEvent.resultCauseId;
        }
        if (!rawEvent.result) {
            delete rawEvent.result;
        }
        if (!rawEvent.variant) {
            delete rawEvent.variant;
        }

        return {
            [this.eventName]: rawEvent
        };
    }

    private recordResult(success: boolean, result: any, resultCauseId: string) {
        this.status = success ? OperationStatus.Success : OperationStatus.Failure;
        this.result = this.prepareData(result);
        if (this.causeId !== resultCauseId) {
            this.resultCauseId = resultCauseId;
        }
        this.duration = new Date().getTime() - this.eventStartTimestamp;
    }

    private prepareData(data: any, returnFullObject = false): string {
        const type = typeof data;
        if (data === undefined || data === null) {
            return '';
        } else if (type === 'string') {
            return scrubMriOrOmit(data);
        } else if (type === 'number' || type === 'boolean') {
            return String(data);
        } else if (type === 'function') {
            return '';
        } else if (data instanceof Error) {
            return data.toString();
        } else {
            try {
                if (Object.keys(data).length) {
                    return returnFullObject ? data : getPIISafeObject(data);
                }
                return '';
            } catch (e) {
                return 'invalid';
            }
        }
    }
}

export enum CALL_TERMINATING_END {
    LOCAL = 'Local',
    REMOTE = 'Remote'
}
export enum RESULT_VALUE {
    SUCCESS = 'Success',
    FAILURE = 'Failure'
}
export enum DIRECTION {
    INCOMING = 'Incoming',
    OUTGOING = 'Outgoing'
}

export enum SELF_PARTICIPANT_ROLE {
    CALLER = 'caller',
    CALLEE = 'callee',
    JOIN = 'join',
    SUBSCRIBE = 'SUBSCRIBE'
}

type BooleanToString = 'True' | 'False' | '';
type StackConfig = { [key: string]: string };
export type ICallTelemetryEvent = {
    CorrelationId: string;
    PreviousCorrelationId: string;
    ConsultativeCallId: string;
    EndpointId: string;
    ParticipantId: string;
    PreheatFlags: PreheatFlags;
    CallType: CallType;
    Direction: DIRECTION;
    Origin: CallOrigin;
    SelfParticipantRole: string;
    IsAnonymous: BooleanToString;
    ThreadId: string;
    MessageId: string;
    TenantId: string;
    FailureType: FailureType;
    IsCast: BooleanToString;
    IsEmergency: BooleanToString;
    TsCallingVersion: string;
    TerminationState: CallState;
    TerminationReason: TerminatedReason;
    StackConfig: StackConfig;
    EventTimestampBag: string;
    HostName: string;
    DiagnosticInfo: CallEndDiagnosticsInfo;
};

export interface ITelemetryUtilities {
    recordEvent(eventName: string, piiSafeData?: any, causeId?: CauseId): ITelemetryEvent;
    recordOperationSuccess(eventName: string, piiSafeData?: any, eventId?: string, causeId?: CauseId): void;
    recordOperationFailure(eventName: string, piiSafeData: any, eventId?: string, causeId?: CauseId): void;
    maybeRecordOperationSuccess(eventName: string, piiSafeData?: any, eventId?: string, causeId?: CauseId): void;
    maybeRecordOperationFailure(eventName: string, piiSafeData: any, eventId?: string, causeId?: CauseId): void;
    recordOperation(eventName: string, causeId: CauseId, eventId?: string): ITelemetryEvent;
    setOperationVariant(eventName: string, type: string, eventId?: string, causeId?: CauseId): void;
    updateOperationData(eventName: string, piiSafeData: any, causeId: CauseId, eventId?: string): void;
}

export interface ICallTelemetry extends ITelemetryUtilities {
    setCallId(callId: string): void;
    setConsultativeCallId(consultativeCallId: string): void;
    setEndpointId(endpointId: string): void;
    setParticipantId(participantId: string): void;
    setThreadId(threadId: string): void;
    setMesageId(messageId: string): void;
    setTenantId(tenantId: string): void;
    setFailureType(failureType: FailureType): void;
    setIsCast(isCast: boolean): void;
    setIsEmergency(isEmergency: boolean): void;
    setRole(role: ParticipantRosterRole): void;
    setCallType(callType: CallType): void;
    setDirection(direction: DIRECTION): void;
    setPreheatFlags(preheatFlags: PreheatFlags): void;
    setCallOrigin(origin: CallOrigin): void;
    setSelfParticipantRole(selfParticipantRole: SELF_PARTICIPANT_ROLE): void;
    setTerminationState(terminationState: CallState): void;
    setTerminationReason(terminationReason: TerminatedReason): void;
    getEvent(skipPendingOperations?: boolean): ICallTelemetryEvent;
    switchToInCallTelemetry(): void;
    setStackConfig(config: StackConfig): void;
    setCallEndDiagnosticInfo(diagnosticCode: CallEndDiagnosticsInfo): void;
}

export class TelemetryUtilities implements ITelemetryUtilities {
    protected recordedEvents: Event[] = [];
    private ongoingOperations: { [operationName: string]: { [operationId: string]: ITelemetryEvent } | ITelemetryEvent } = {};

    public constructor(
        protected logger: CallingLogger,
        protected startTime: number,
        private maxEventsExceeded: () => boolean
    ) {
    }

    public recordEvent(eventName: string, piiSafeData?: any, causeId = generateCauseId()): Event {
        this.logger.debug('Event:', eventName, pii.Omit(piiSafeData));

        const event: Event = new Event(eventName, this.getEventStartTime(), EventType.Event, causeId as CauseId, piiSafeData);
        this.pushToRecordedEvents(event);
        return event;
    }

    public recordOperationSuccess(eventName: string, piiSafeData: any = null, eventId?: string, causeId?: CauseId): void {
        this.logger.info('Event success:', causeId, eventName, pii.Omit(eventId), pii.Omit(piiSafeData));
        this.recordOperationResult(eventName, piiSafeData, true, eventId, causeId);
    }

    public recordOperationFailure(eventName: string, piiSafeData: any, eventId?: string, causeId?: CauseId): void {
        this.logger.info('Event failure:', eventName, pii.Omit(eventId), piiSafeData);
        this.recordOperationResult(eventName, piiSafeData, false, eventId, causeId);
    }

    public maybeRecordOperationSuccess(eventName: string, piiSafeData: any = null, eventId?: string, causeId?: CauseId): void {
        if (this.hasOngoingEvent(eventName, eventId)) {
            this.recordOperationSuccess(eventName, piiSafeData, eventId, causeId);
        }
    }

    public maybeRecordOperationFailure(eventName: string, piiSafeData: any, eventId?: string, causeId?: CauseId): void {
        if (this.hasOngoingEvent(eventName, eventId)) {
            this.recordOperationFailure(eventName, piiSafeData, eventId, causeId);
        }
    }

    private recordOperationResult(eventName: string, piiSafeData: any, success: boolean, eventId?: string, causeId?: CauseId): void {
        if (!this.hasOngoingEvent(eventName, eventId)) {
            this.recordOperation(eventName, causeId, eventId);
        }

        let event = this.ongoingOperations[eventName];
        if (eventId) {
            event = event[eventId];
        }

        if (success) {
            (event as Event).recordSuccess(piiSafeData, causeId);
        } else {
            (event as Event).recordFailure(piiSafeData, causeId);
        }

        if (eventId) {
            delete this.ongoingOperations[eventName][eventId];
        } else {
            delete this.ongoingOperations[eventName];
        }
    }

    public recordOperation(eventName: string, causeId: CauseId, eventId?: string): Event {
        this.logger.debug(`Event create: ${eventId}`, eventName, pii.Omit(eventId));

        const event: Event = new Event(eventName, this.getEventStartTime(), EventType.Operation, causeId);
        this.logger.debug(`Event create: ${getPIISafeObject(this.ongoingOperations)}`);
        if (!this.ongoingOperations[eventName]) {
            this.ongoingOperations[eventName] = {};
        }
        if (eventId) {
            this.ongoingOperations[eventName][eventId] = event;
        } else {
            this.ongoingOperations[eventName] = event;
        }

        this.pushToRecordedEvents(event);
        return event;
    }

    /*
     * Set an arbitrary string as variant for an operation. This allows
     * telemetry reports to look at particular variants separately.
     */
    public setOperationVariant(eventName: string, variant: string, eventId?: string): void {
        let event: Event;
        if (eventId) {
            event = this.ongoingOperations[eventName][eventId];
        } else {
            event = this.ongoingOperations[eventName] as Event;
        }
        if (event) {
            event.variant = variant;
        } else {
            this.logger.error(`Could not find operation ${eventName} to set variant ${variant}`);
        }
    }

    public updateOperationData(eventName: string, piiSafeData: any, causeId: CauseId, eventId?: string): void {
        try {
            if (!this.ongoingOperations[eventName]) {
                this.recordEvent(eventName, piiSafeData, causeId);
                return;
            }
            if (eventId) {
                if (this.ongoingOperations[eventName][eventId]) {
                    (this.ongoingOperations[eventName][eventId] as Event).addData(piiSafeData);
                }
            } else {
                (this.ongoingOperations[eventName] as Event).addData(piiSafeData);
            }
        } catch (e) {
            this.logger.info(`Unable to update operation: ${eventName} data`);
            return;
        }
    }

    private pushToRecordedEvents(event: Event): void {
        if (this.maxEventsExceeded && this.maxEventsExceeded()) {
            this.recordedEvents.shift();
        }
        this.recordedEvents.push(event);
    }

    private hasOngoingEvent(eventName: string, eventId: string): boolean {
        if (!this.ongoingOperations[eventName]) {
            this.logger.warn(`Unable to find event for name ${eventName}`);
            return false;
        }
        if (eventId && !this.ongoingOperations[eventName][eventId]) {
            this.logger.warn(`Unable to find event for name\\id ${eventName}\\${pii.Omit(eventId)}`);
            return false;
        }
        return true;
    }

    private getEventStartTime() {
        return new Date().getTime() - this.startTime;
    }

    protected getEventTimestampBag(skipPendingOperations?: boolean) {
        const eventTimestampBag = JSON.stringify({
            eventStart: this.startTime,
            events: this.recordedEvents
                .filter(e => skipPendingOperations ? !e.isPending() : true)
                .map(event => event.getEvent())
        });
        this.logger.info(`Call eventTimestampBag ${pii.Omit(eventTimestampBag)}`);
        return eventTimestampBag;
    }

}

export default class CallTelemetry extends TelemetryUtilities implements ICallTelemetry {
    private currentCallId: string;
    private previousCallId: string;
    private consultativeCallId: string;
    private endpointId: string;
    private participantId: string;
    private origin: CallOrigin;
    private callType: CallType;
    private direction: DIRECTION;
    private selfParticipantRole: SELF_PARTICIPANT_ROLE;
    private isAnonymous: BooleanToString;
    private terminationState: CallState;
    private terminationReason: TerminatedReason;
    private threadId: string;
    private messageId: string;
    private tenantId: string;
    private failureType: FailureType;
    private isCast: BooleanToString;
    private isEmergency: BooleanToString;
    private tsCallingVersion: string;
    private inCallMode = false;
    private stackConfig: StackConfig;
    private callEndDiagnosticInfo: CallEndDiagnosticsInfo;
    private preheatFlags: PreheatFlags;

    constructor(
        logger: CallingLogger,
        callStartTime: number
    ) {
        super(logger, callStartTime, () => { return this.inCallMode && this.recordedEvents.length > MAX_IN_CALL_EVENTS; });
        this.tsCallingVersion = getTsCallingVersion();
        this.logger = logger.createChild('CallTelemetry');
    }

    public setCallId(callId: string): void {
        if (this.currentCallId) {
            this.previousCallId = this.currentCallId;
        }
        this.currentCallId = callId;
    }

    public setConsultativeCallId(consultativeCallId: string) {
        this.consultativeCallId = consultativeCallId;
    }

    public setEndpointId(endpointId: string) {
        this.endpointId = endpointId;
    }

    public setParticipantId(participantId: string) {
        this.participantId = participantId;
    }

    public setPreheatFlags(preheatFlags: PreheatFlags) {
        this.preheatFlags = preheatFlags;
    }

    public setCallOrigin(origin: CallOrigin) {
        this.origin = origin;
    }

    public setThreadId(threadId: string) {
        this.threadId = threadId;
    }

    public setMesageId(messageId: string) {
        this.messageId = messageId;
    }

    public setTenantId(tenantId: string) {
        this.tenantId = tenantId;
    }

    public setFailureType(failureType: FailureType) {
        this.failureType = failureType;
    }

    public setIsCast(isCast: boolean) {
        this.isCast = isCast ? 'True' : 'False';
    }

    public setIsEmergency(isEmergency: boolean) {
        this.isEmergency = isEmergency ? 'True' : 'False';
    }

    public setRole(role: ParticipantRosterRole) {
        this.isAnonymous = role === 'admin' ? 'False' : 'True';
    }

    public setCallType(callType: CallType) {
        this.callType = callType;
    }

    public setDirection(direction: DIRECTION) {
        this.direction = direction;
    }

    public setSelfParticipantRole(selfParticipantRole: SELF_PARTICIPANT_ROLE) {
        this.selfParticipantRole = selfParticipantRole;
    }

    public setTerminationState(terminationState: CallState) {
        this.terminationState = terminationState;
    }

    public setTerminationReason(terminationReason: TerminatedReason) {
        this.terminationReason = terminationReason;
    }

    public setCallEndDiagnosticInfo(info: CallEndDiagnosticsInfo): void {
        this.callEndDiagnosticInfo = info;
    }

    public setStackConfig(config: StackConfig): void {
        this.stackConfig = config;
    }

    public getEvent(skipPendingOperations?: boolean): ICallTelemetryEvent {
        const getValue = (v: any) => v === undefined ? '' : v;

        return {
            CorrelationId: getValue(this.currentCallId),
            PreviousCorrelationId: getValue(this.previousCallId),
            ConsultativeCallId: getValue(this.consultativeCallId),
            EndpointId: getValue(this.endpointId),
            ParticipantId: getValue(this.participantId),
            PreheatFlags: getValue(this.preheatFlags),
            CallType: getValue(this.callType),
            Direction: getValue(this.direction),
            Origin: getValue(this.origin),
            SelfParticipantRole: getValue(this.selfParticipantRole),
            IsAnonymous: getValue(this.isAnonymous),
            ThreadId: getValue(this.threadId),
            MessageId: getValue(this.messageId),
            TenantId: getValue(this.tenantId),
            FailureType: getValue(this.failureType),
            IsCast: getValue(this.isCast),
            IsEmergency: getValue(this.isEmergency),
            TsCallingVersion: getValue(this.tsCallingVersion),
            TerminationState: getValue(this.terminationState),
            TerminationReason: getValue(this.terminationReason),
            StackConfig: getValue(this.stackConfig),
            EventTimestampBag: this.getEventTimestampBag(skipPendingOperations),
            HostName: this.getHostName(),
            DiagnosticInfo: this.callEndDiagnosticInfo
        };
    }

    public switchToInCallTelemetry() {
        this.recordedEvents = this.recordedEvents.filter(e => e.isPending());
        this.inCallMode = true;
    }

    private getHostName = () => {
        try {
            return location.host;
        } catch (e) {
            return 'unknown';
        }
    }

}

export const getEndpointInformationForTelemetry = (endpointDetails: { endpointId: string; participantId?: string }[]) => {
    if (!Array.isArray(endpointDetails)) {
        return null;
    }
    return endpointDetails.map(endpoint => {
        return { endpointId: endpoint.endpointId, participantId: endpoint.participantId };
    });
};



