import {
    AudioDeviceDesc, CallingLogger, CameraPosition, CompositeAudioDeviceDesc, CompositeAudioDeviceFormFactor, DeviceAccess, DeviceDesc,
    DeviceId, DeviceType, IDeviceManager, IDeviceManagerEvents, IRenderer, IRenderTarget, PreviewVideoSelection, SelectedDevices,
    StartStreamOptions, VideoCaptureConfig, VideoDeviceDesc, VideoEffectType
} from 'skype-calling-interface';
import { ISlimCoreElectronApplicationHooks } from '../typings/skype-calling-electron';

import * as _ from 'lodash';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import { SlimCoreElectronBindingManager } from './SlimCoreElectronBindingManager';
import * as EnumConverter from './SlimCoreElectronEnumConverter';
import SlimCoreElectronLocalVideoPreview from './SlimCoreElectronLocalVideoPreview';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { generateCauseId } from '../../common/ts/causeId';
import { IInternalLogger } from '../../common/ts/internalLogger';

type BrowserAwareDeviceInfo = {
    browserId?: string;
};

abstract class DeviceList<DeviceInfo extends SlimCore.DeviceInfo & BrowserAwareDeviceInfo> {
    protected _list: DeviceInfo[] = [];
    private _selectedDevice: DeviceId;

    public constructor(protected _kind: DeviceType, private _logger: CallingLogger) {
        this._logger.info('constructor');
    }

    public update(list: DeviceInfo[]) {
        this._list = list;
        this._logger.info(`update: ${JSON.stringify(this._list)}`);
    }

    public isAvailable(deviceId: string) {
        return !!(deviceId && _.find(this._list, device => device.id === deviceId));
    }

    public getBestDevice(): DeviceId {
        return this._getDeviceId(_.first(this._list));
    }

    public getSelectedDevice(): DeviceId {
        if (this.isAvailable(this._selectedDevice)) {
            this._logger.debug(`getSelectedDevice: ${this._selectedDevice}`);
            return this._selectedDevice;
        } else {
            const deviceId = this.getBestDevice();
            this._logger.debug(`getSelectedDevice - unknown device selected, returning preferred device: ${deviceId}`);
            return deviceId;
        }
    }

    public selectDevice(deviceId: DeviceId): void {
        if (this.isAvailable(deviceId)) {
            this._logger.info(`selectDevice: ${deviceId}`);
            this._selectedDevice = deviceId;
        } else {
            this._logger.info(`selectDevice - unknown device selected, selecting preferred device`);
            this._selectedDevice = null;
        }
    }

    protected _getDeviceId(device: DeviceInfo): DeviceId {
        return device ? device.id : null;
    }

    public abstract enumerate(): DeviceDesc[];
}

class VideoDeviceList extends DeviceList<SlimCore.VideoDeviceInfo & BrowserAwareDeviceInfo> {

    public getBestDevice(): DeviceId {
        const externalCamera = this._findCamera(Enums.CameraFacing.External);
        const frontCamera = this._findCamera(Enums.CameraFacing.Front);
        const leftFrontCamera = this._findCamera(Enums.CameraFacing.LeftFront);
        const rightFrontCamera = this._findCamera(Enums.CameraFacing.RightFront);
        const nonVirtualCamera = this._findCamera();
        const anyCamera = super.getBestDevice();

        return externalCamera || frontCamera || leftFrontCamera || rightFrontCamera || nonVirtualCamera || anyCamera;
    }

    public enumerate(): VideoDeviceDesc[] {
        return _.map(this._list, device => ({
            id: device.id,
            browserId: device.browserId,
            label: device.label,
            kind: this._kind,
            position: this._getCameraPosition(device.cameraFacing)
        }));
    }

    private _findCamera(facing?: Enums.CameraFacing): DeviceId {
        const device = _.find(this._list, device =>
            device.deviceType !== Enums.DeviceType.Virtual && (!facing || device.cameraFacing === facing));

        return this._getDeviceId(device);
    }

    private _getCameraPosition(cameraFacing: Enums.CameraFacing): CameraPosition {
        switch (cameraFacing) {
            case Enums.CameraFacing.Front:
                return CameraPosition.Front;
            case Enums.CameraFacing.LeftFront:
                return CameraPosition.LeftFront;
            case Enums.CameraFacing.RightFront:
                return CameraPosition.RightFront;
            case Enums.CameraFacing.Back:
                return CameraPosition.Back;
            case Enums.CameraFacing.External:
                return CameraPosition.External;
            default:
                return CameraPosition.Unknown;
        }
    }
}

class AudioDeviceList extends DeviceList<SlimCore.AudioDeviceInfo & BrowserAwareDeviceInfo> {

    public enumerate(): AudioDeviceDesc[] {
        const defaultDevice = _.find(this._list, device => device.isSystemDefault) || _.first(this._list);
        return _.map(this._list, device => ({
            id: device.id,
            browserId: device.browserId,
            label: device.label,
            kind: this._kind,
            productId: `${device.productId || ''}`,
            vendorId: `${device.vendorId || ''}`,
            isSystemDefault: device === defaultDevice
        }));
    }
}

class CompositeAudioDeviceList {
    protected _list: SlimCore.CompositeAudioDeviceInfo[] = [];

    public constructor(private _logger: CallingLogger) {
        this._logger.info('constructor');
    }

    public update(list: SlimCore.CompositeAudioDeviceInfo[]) {
        this._list = list;
        this._logger.info(`update: ${JSON.stringify(this._list)}`);
    }

    public enumerate(): CompositeAudioDeviceDesc[] {
        return _.map(this._list, device => ({
            id: null,
            browserId: null,
            label: device.label,
            kind: DeviceType.CompositeAudioDevice,
            microphoneId: device.microphoneId,
            speakerId: device.speakerId,
            formFactor: this._getCompositeDeviceFormFactor(device.deviceType),
            isPcInternalDevice: device.isPcInternalDevice
        }));
    }

    private _getCompositeDeviceFormFactor(type: Enums.CompositeAudioDeviceType): CompositeAudioDeviceFormFactor {
        switch (type) {
            case Enums.CompositeAudioDeviceType.Speakers:
                return CompositeAudioDeviceFormFactor.Speakers;
            case Enums.CompositeAudioDeviceType.Headphones:
                return CompositeAudioDeviceFormFactor.Headphones;
            case Enums.CompositeAudioDeviceType.Headset:
                return CompositeAudioDeviceFormFactor.Headset;
            case Enums.CompositeAudioDeviceType.Handset:
                return CompositeAudioDeviceFormFactor.Handset;
            case Enums.CompositeAudioDeviceType.Speakerphone:
                return CompositeAudioDeviceFormFactor.Speakerphone;
            default:
                return CompositeAudioDeviceFormFactor.Unknown;
        }
    }

}

function getMediaDeviceIdSubstring(mediaDeviceLabel: string, slimcoreDeviceLabel: string): string | undefined {
    const devicePostfix = mediaDeviceLabel.split(slimcoreDeviceLabel)[1].trim();
    const found = devicePostfix.match(/\((.+):(.+)\)/);
    if (found) {
        const [vid, pid] = found.slice(1);
        return `vid_${vid}&pid_${pid}`;
    }
    return undefined;
}

export default class SlimCoreElectronDeviceManager extends SlimCoreElectronBase<IDeviceManagerEvents> implements IDeviceManager {
    private readonly _cameras: VideoDeviceList;
    private readonly _microphones: AudioDeviceList;
    private readonly _speakers: AudioDeviceList;
    private readonly _compositeDevices: CompositeAudioDeviceList;
    private _browserDevicesPromise: Promise<void>;
    private readonly _devicesEnumeratedPromise: Promise<void>;
    private _browserDevices: MediaDeviceInfo[] = [];
    private _previewVideoManager: SlimCoreElectronBindingManager<void>;
    private _rendererBindingManager: SlimCoreElectronBindingManager<SlimCore.VideoBindingRenderer>;
    private _screenShareBindingManager: SlimCoreElectronBindingManager<SlimCore.VideoBindingScreenShare>;
    private _haveStartedMedia = false;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _mediaReadyPromise: Promise<SlimCore.Engine | SlimCore.CallHandler>,
        private _appHooks: ISlimCoreElectronApplicationHooks
    ) {
        super(logger.createChild('DeviceManager'), settings, engine);
        this._logger.info('constructor');

        this._cameras = new VideoDeviceList(DeviceType.Camera, this._logger.createChild('cameras'));
        this._microphones = new AudioDeviceList(DeviceType.Microphone, this._logger.createChild('microphones'));
        this._speakers = new AudioDeviceList(DeviceType.Speaker, this._logger.createChild('speakers'));

        this._compositeDevices = new CompositeAudioDeviceList(this._logger.createChild('compositeaudiodevices'));

        this._devicesEnumeratedPromise = this._mediaReadyPromise
            .then(() => this._updateBrowserDevices())
            .then(() => {
                this._updateAudioDevices();
                this._updateVideoDevices();
                this._handleDisplaysChanged();
            });
        this._previewVideoManager = new SlimCoreElectronBindingManager<void>(this._startPreviewVideo, this._stopPreviewVideo, this._logger);
        this._rendererBindingManager = new SlimCoreElectronBindingManager<SlimCore.VideoBindingRenderer>(this._createRendererBinding, this._releaseRendererBinding, this._logger);
        this._screenShareBindingManager = new SlimCoreElectronBindingManager<SlimCore.VideoBindingScreenShare>(this._createScreenShareBinding, this._releaseScreenShareBinding, this._logger);
        // hook device changed event
        this._registerDisposable(this._slimcoreInstance.handle('device-list-changed', undefined, eventData => this._slimCoreDevicesChangedHander(eventData)));
        this._registerDisposable(this._appHooks.onDisplaysChanged(() => this._handleDisplaysChanged()));
    }

    public getRendererBindingManager(): SlimCoreElectronBindingManager<SlimCore.VideoBindingRenderer> {
        return this._rendererBindingManager;
    }

    public getScreenShareBindingManager(): SlimCoreElectronBindingManager<SlimCore.VideoBindingScreenShare> {
        return this._screenShareBindingManager;
    }

    public getPreviewVideo(type: Enums.VideoType, name?: string, path?: string): Promise<number> {
        let videoObjectId = 0;
        return this._mediaReadyPromise.then(slimcoreObject => {
            videoObjectId = this._slimcoreInstance.createPreviewVideo(type, name, path);
            if (videoObjectId === 0) {
                throw new Error('SlimCore returned invalid video object ID 0');
            }
            this._logger.info(`Got preview video. Waiting for video to become AVAILABLE|STARTING|RUNNING`);
            // todo: with current slimcore we could bail out immediately when preview video state goes to NOT_AVAILABLE, not sure about old cl though...
            return SlimCoreCommon.waitForVideoObjectToBeAvailable(this._slimcoreInstance, slimcoreObject, videoObjectId, this._logger, SlimCoreCommon.videoObjectTimeout(this._settings));
        }).then(() => videoObjectId);
    }

    public get isAudioOutputSelectionSupported(): boolean {
        return true;
    }

    private _handleDisplaysChanged() {
        try {
            this._slimcoreInstance.setDeviceRotation(SlimCore.queryDeviceRotation());
        } catch (err) {
            this._logger.warn(`Displays Changed handling error: ${err}`);
        }
    }

    private _updateVideoDevices() {
        this._cameras.update(this._fillBrowserDeviceIds(this._slimcoreInstance.getCameraList(), 'videoinput'));
    }

    private _updateAudioDevices() {
        this._microphones.update(this._fillBrowserDeviceIds(this._slimcoreInstance.getMicrophoneList(), 'audioinput'));
        this._speakers.update(this._fillBrowserDeviceIds(this._slimcoreInstance.getSpeakerList(), 'audiooutput'));

        if (this._slimcoreInstance.getCompositeAudioDevices) {
            this._compositeDevices.update(this._slimcoreInstance.getCompositeAudioDevices());
        }

        this._selectAudioDevices();
    }

    private _updateBrowserDevices(): Promise<void> {
        if (typeof navigator !== 'object') {
            this._logger.warn('Browser-backed device list won\'t be fetched as navigator is not available');
            return Promise.resolve<void>();
        }

        // Guard against parallel calls
        if (this._browserDevicesPromise) {
            return this._browserDevicesPromise;
        }
        return this._browserDevicesPromise = navigator.mediaDevices.enumerateDevices().then((deviceList: MediaDeviceInfo[]) => {
            this._browserDevices = deviceList;
            this._browserDevicesPromise = undefined;
        });
    }

    private _fillBrowserDeviceIds<DeviceInfo extends SlimCore.DeviceInfo & BrowserAwareDeviceInfo>(deviceList: DeviceInfo[], kind: string): DeviceInfo[] {
        if (!deviceList) {
            return deviceList;
        }
        for (const device of deviceList) {
            const browserDevice = this._browserDevices.find(mediaDeviceInfo => {
                if (mediaDeviceInfo.kind !== kind) {
                    return false;
                }
                if (mediaDeviceInfo.label === device.label) {
                    return true;
                } else if (mediaDeviceInfo.label.indexOf(device.label) === 0) {
                    // Check for Windows device identifier match: vid_046d&pid_0843
                    const deviceIdSubstring = getMediaDeviceIdSubstring(mediaDeviceInfo.label, device.label);
                    return device.id.indexOf(deviceIdSubstring) > -1;
                } else {
                    return false;
                }
            });
            if (browserDevice) {
                device.browserId = browserDevice.deviceId;
            } else if (this._browserDevices.length > 0) {
                this._logger.warn(`No browser device found for device ${device.label}`);
            }
        }
        return deviceList;
    }

    private _selectAudioDevices() {
        const microphone = this._microphones.getSelectedDevice();
        const speaker = this._speakers.getSelectedDevice();

        this._logger.info(`SlimCore - selectAudioDevices: ${microphone}, ${speaker}`);
        this._slimcoreInstance.selectAudioDevices(microphone, speaker);
    }

    private _slimCoreDevicesChangedHander(args: SlimCore.Engine.Events.DeviceListChangedArgs) {
        this._logger.info(`SlimCore Devices changed: ${JSON.stringify(args)}`);

        this._updateBrowserDevices().then(() => {
            if (args.video) {
                this._updateVideoDevices();
            } else {
                this._updateAudioDevices();
            }

            this._raiseDevicesChanged();
            this._logger.info(`Raised device changed notification: ${JSON.stringify(args)}`);
        })
        .catch(error => {
            this._logger.error(`Failed to raise device changed notification, error = ${error}`);
        });
    }

    private _raiseDevicesChanged() {
        this.raiseChanged();
        this.event('devicesChanged').raise(this._enumerateDevices());
    }

    private _enumerateDevices(): DeviceDesc[] {
        const cameras = this._cameras.enumerate();
        const microphones = this._microphones.enumerate();
        const speakers = this._speakers.enumerate();
        const compositeAudioDevices = this._compositeDevices.enumerate();

        return [].concat(cameras, microphones, speakers, compositeAudioDevices);
    }

    public askDevicePermission(constraints?: DeviceAccess): Promise<DeviceAccess> {
        return Promise.resolve().then(() => {
            return { audio: true, video: true };
        });
    }

    private _triggerMediaInitOnce() {
        if (SlimCoreCommon.hasFeature(Enums.Feature.LazyMediaInit) && !this._haveStartedMedia) {
            this._slimcoreInstance.fireIntent(Enums.Intent.MediaInit);
            this._haveStartedMedia = true;
        }
    }

    public enumerateDevicesAsync(): Promise<DeviceDesc[]> {
        return new Promise((resolve, reject) => {
            this._triggerMediaInitOnce();
            this._devicesEnumeratedPromise.then(() => this._enumerateDevices()).then(resolve, reject);
        });
    }

    public selectDevices(devices: SelectedDevices): void {
        this._devicesEnumeratedPromise.then(() => {
            this._selectDevices(devices);
        });
    }

    public selectDevicesAsync(devices: SelectedDevices): Promise<void> {
        return Promise.reject(new Error('Not implemented'));
    }

    private _selectDevices(devices: SelectedDevices): void {
        this._logger.debug(`selectDevices - ${JSON.stringify(devices)}`);

        if (devices.camera !== undefined) {
            this._cameras.selectDevice(devices.camera);
        }

        if (devices.microphone !== undefined) {
            this._microphones.selectDevice(devices.microphone);
        }

        if (devices.speaker !== undefined) {
            this._speakers.selectDevice(devices.speaker);
        }

        this._selectAudioDevices();
        this._raiseDevicesChanged();
    }

    public setVideoCaptureConfigAsync(deviceId: DeviceId, config: VideoCaptureConfig): Promise<void> {
        return this._mediaReadyPromise
            .then(() => {
                if (this._slimcoreInstance.setVideoCaptureConfig) {
                    this._slimcoreInstance.setVideoCaptureConfig(deviceId, config);
                } else {
                    this._logger.warn('setVideoCaptureConfig is not available');
                }
            })
            .catch(error => {
                this._logger.error(`Failed to set video capture config, Error = ${error}`);
                throw error;
            });
    }

    public setDeviceEffectsAsync(deviceId: DeviceId, type: VideoEffectType): Promise<void> {
        return this._mediaReadyPromise
            .then(() => {
                if (this._slimcoreInstance.setDeviceEffects) {
                    this._slimcoreInstance.setDeviceEffects(deviceId, EnumConverter.convertVideoEffectType(type));
                } else {
                    this._logger.warn('setDeviceEffects is not available');
                }
            })
            .catch(error => {
                this._logger.error(`Failed to set Video Effects, Error = ${error}`);
                throw error;
            });
    }

    public getDeviceEffectsCapabilityAsync(deviceId: DeviceId, mask?: number): Promise<number> {
        return this._mediaReadyPromise
            .then(() => {
                if (this._slimcoreInstance.getDeviceEffectsCapability) {
                    return this._slimcoreInstance.getDeviceEffectsCapability(deviceId, mask);
                } else {
                    this._logger.warn('getDeviceEffectsCapability is not available');
                    return 0;
                }
            })
            .catch(error => {
                this._logger.error(`Error in getDeviceEffectsCapabilityAsync, error = ${error}`);
                throw error;
            });
    }

    public setBackgroundImageAsync(deviceId: DeviceId, imagePath: string): Promise<void> {
        return this._mediaReadyPromise
            .then(() => {
                if (this._slimcoreInstance.setBackgroundImage) {
                    this._slimcoreInstance.setBackgroundImage(deviceId, imagePath);
                } else {
                    this._logger.warn('setBackgroundImage is not available');
                }
            })
            .catch(error => {
                this._logger.error(`Failed to set background image, Error = ${error}`);
                throw error;
            });
    }

    public getSelectedDevices(): SelectedDevices {
        const result: SelectedDevices = {
            camera: this._cameras.getSelectedDevice(),
            microphone: this._microphones.getSelectedDevice(),
            speaker: this._speakers.getSelectedDevice()
        };

        this._logger.debug(`getSelectedDevices - ${JSON.stringify(result)}`);
        return result;
    }

    public getCamera(deviceId: DeviceId): VideoDeviceDesc {
        return _.find(this._cameras.enumerate(), device => device.id === deviceId);
    }

    public getSelectedCamera(): VideoDeviceDesc {
        const deviceId = this._cameras.getSelectedDevice();
        const device = this.getCamera(deviceId);
        this._logger.debug(`getSelectedCamera - ${JSON.stringify(device)}`);
        return device;
    }

    public async createPreview(target: IRenderTarget, selection: PreviewVideoSelection, options?: StartStreamOptions, causeId = generateCauseId()): Promise<IRenderer> {
        const renderer = new SlimCoreElectronLocalVideoPreview(
            this._logger,
            this._settings,
            this._slimcoreInstance,
            target,
            this,
            selection,
            options,
            causeId
        );
        await renderer.startVideoAsync(causeId);
        return renderer;
    }

    public createPreviewRenderer(target: IRenderTarget, options?: StartStreamOptions, causeId = generateCauseId()): Promise<IRenderer> {
        return this.createPreview(target, { kind: 'camera' }, options, causeId);
    }

    public createScreenSharingPreviewRenderer(target: IRenderTarget, options?: StartStreamOptions, causeId = generateCauseId()): Promise<IRenderer> {
        return this.createPreview(target, { kind: 'sharing' }, options, causeId);
    }

    public getDeviceNameAsync(deviceId: DeviceId): Promise<string> {
        return Promise.resolve()
            .then(() => {
                const device = _.find(this._enumerateDevices(), device => device.id === deviceId);
                if (!device) {
                    throw new Error(`Device with id: ${deviceId} not found`);
                }
                return device.label;
            });
    }

    public getSpeakerVolume(): Promise<number> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.getSpeakerVolume());
    }

    public getSpeakerSystemVolume(): Promise<number> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.getSpeakerSystemVolume());
    }

    public setSpeakerVolume(volume: number): Promise<void> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.setSpeakerVolume(volume));
    }

    public setSpeakerSystemVolume(volume: number): Promise<void> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.setSpeakerSystemVolume(volume));
    }

    public unmuteMicrophone(): Promise<void> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.unmuteMicrophone());
    }

    public unmuteSpeaker(): Promise<void> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.unmuteSpeaker());
    }

    public getNrgLevelsForDeviceTuner(microphone: string): Promise<number> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.getNrgLevelsForDeviceTuner(microphone));
    }

    public getMicrophoneVolume(): Promise<number> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.getMicrophoneVolume());
    }

    public setMicrophoneVolume(volume: number): Promise<void> {
        return this._mediaReadyPromise.then(() => this._slimcoreInstance.setMicrophoneVolume(volume));
    }

    public addProbeDevices(): Promise<boolean> {
        return this._mediaReadyPromise
            .then(() => {
                if (this._slimcoreInstance.addProbeDevices) {
                    return this._slimcoreInstance.addProbeDevices();
                } else {
                    this._logger.warn('addProbeDevices is not available');
                    return Promise.reject(false);
                }
            })
            .catch(error => {
                this._logger.error(`Error in addProbeDevices, Error = ${error}`);
                return Promise.reject(false);
            });
    }

    private _startPreviewVideo = (videoObjectId: number) => {
        // in AVAILABLE state video is fully stopped and no bindings on it
        return this._mediaReadyPromise.then(slimcoreObject => {
            this._logger.info(`Wait AVAILABLE for preview video ${videoObjectId}`);
            return SlimCoreCommon.waitForVideoObjectState({
                slimcoreInstance: this._slimcoreInstance,
                slimcoreObject,
                videoObjectId,
                logger: this._logger,
                resolveStates: [Enums.VideoStatus.Available],
                rejectStates: [Enums.VideoStatus.NotAvailable],
                timeout: SlimCoreCommon.videoObjectTimeout(this._settings)
            })
                .then(() => {
                    this._logger.info(`Starting preview video ${videoObjectId}`);
                    this._slimcoreInstance.videoStart(videoObjectId);
                })
                // in order to perform full stop that would bring video state back to AVAILABLE we need to wait for state
                // that is not AVAILABLE best place doing this is after start (alternativley could also be done before stop)
                .then(() => SlimCoreCommon.waitForVideoObjectState({
                    slimcoreInstance: this._slimcoreInstance,
                    slimcoreObject,
                    videoObjectId,
                    logger: this._logger,
                    resolveStates: [Enums.VideoStatus.Starting, Enums.VideoStatus.Running],
                    rejectStates: [Enums.VideoStatus.NotAvailable],
                    timeout: SlimCoreCommon.videoObjectTimeout(this._settings)
                }))
                .then(() => this._logger.info(`Now can use preview video ${videoObjectId}`));
        });
    }

    private _stopPreviewVideo = (videoObjectId: number) => {
        return Promise.resolve().then(() => {
            this._logger.info(`Stopping preview video ${videoObjectId}`);
            this._slimcoreInstance.videoStop(videoObjectId);
        });
    }

    private _createRendererBinding = (videoObjectId: number) => {
        let binding: SlimCore.VideoBindingRenderer;
        return this._previewVideoManager.acquire(this._rendererBindingManager, videoObjectId)
            .then(() => {
                // use new method if available
                if (this._slimcoreInstance.createVideoBindingRenderer) {
                    binding = this._slimcoreInstance.createVideoBindingRenderer({
                        enableDXVA: this._settings.enableDXVA,
                        isLocalPreview: true
                    });
                } else { // use old method for backwards compatibility
                    binding = SlimCore.createVideoBindingRenderer();
                }
                this._logger.info(`Create rendering binding for preview video ${videoObjectId}`);
                return this._slimcoreInstance.videoCreateBinding(videoObjectId, binding);
            }).then(() => {
                this._logger.info(`Created rendering binding for preview video ${videoObjectId}`);
                return binding;
            });
    }

    private _releaseRendererBinding = (videoObjectId: number, binding: SlimCore.VideoBindingRenderer) => {
        return Promise.resolve().then(() => {
            this._logger.info(`Release rendering binding for preview video ${videoObjectId}`);
            return this._slimcoreInstance.videoReleaseBinding(videoObjectId, binding);
        }).then(() => {
            this._logger.info(`Released rendering binding for preview video ${videoObjectId}`);
            return this._previewVideoManager.release(this._rendererBindingManager, videoObjectId);
        });
    }

    private _createScreenShareBinding = (videoObjectId: number) => {
        let binding: SlimCore.VideoBindingScreenShare;
        return this._previewVideoManager.acquire(this._screenShareBindingManager, videoObjectId)
            .then(() => {
                binding = this._createVideoBindingScreenShare();
                this._logger.info(`Create screenshare binding for preview video ${videoObjectId}`);
                return this._slimcoreInstance.videoCreateBinding(videoObjectId, binding);
            }).then(() => {
                this._logger.info(`Created screenshare binding for preview video ${videoObjectId}`);
                return binding;
            });
    }

    private _releaseScreenShareBinding = (videoObjectId: number, binding: SlimCore.VideoBindingScreenShare) => {
        return Promise.resolve().then(() => {
            this._logger.info(`Release screenshare binding for preview video ${videoObjectId}`);
            return this._slimcoreInstance.videoReleaseBinding(videoObjectId, binding);
        }).then(() => {
            this._logger.info(`Released screenshare binding for preview video ${videoObjectId}`);
            return this._previewVideoManager.release(this._screenShareBindingManager, videoObjectId);
        });
    }

    public hasVideoBindingScreenShare() {
        return !!(this._slimcoreInstance.createVideoBindingScreenShare || SlimCore.createVideoBindingScreenShare);
    }

    private _createVideoBindingScreenShare() {
        if (this._slimcoreInstance.createVideoBindingScreenShare) {
            return this._slimcoreInstance.createVideoBindingScreenShare();
        } else {
            return SlimCore.createVideoBindingScreenShare();
        }
    }
}



