import { CauseId } from 'skype-calling-interface';
const CAUSEID_LENGTH = 8;
/**
 * causeId.ts
 * Author: Richard Speyer
 * Copyright: Microsoft 2017
 *
 * Simple cause ID generator
 *
 * Cause IDs are basically random numbers that are passed along through asynchronous
 * operations and logged out at various points. They are extremely powerful in debugging
 * asynchronous codeflows and understanding data flows
 */

export function generateCauseId(): CauseId {
    /* tslint:disable:insecure-random */
    const alphanumerals = 'abcdefghijklmnopqrstuvwxyz0123456789';
    const lenAlphanumerals = alphanumerals.length;
    let causeId = '';
    for (let i = 0; i < CAUSEID_LENGTH; i++) {
        causeId += alphanumerals.charAt(Math.floor(Math.random() * lenAlphanumerals));
    }
    return causeId;
}

export function validateCauseId(causeId: string): causeId is CauseId {
    // expected format is CAUSEID_LENGTH lower case alphanumerals.
    const regExp = new RegExp('^[a-z0-9]{8}$');
    return regExp.test(causeId) && causeId.length === CAUSEID_LENGTH;
}



