import { CallingLogger, CallingLoggerPrefixFunction, CauseId } from 'skype-calling-interface';
import { getPrintableObject } from './logUtils';

type LogType = 'log' | 'debug' | 'info' | 'error' | 'warn';

export interface IInternalLogger extends CallingLogger {
    createChild(namespace: string | CallingLoggerPrefixFunction, debug?: boolean): IInternalLogger;
    createFnLogger(fnName: string, causeId: CauseId, ...additionalPrefix: string[]): IInternalLogger;
    logSuccess(piiSafeResult: string): void;
    logFailure(reason: string | number | boolean | Error): void;
}
export default class InternalLogger implements IInternalLogger {

    constructor(private _callingLogger: CallingLogger) { }

    private _maybeLog = (type: LogType, ...args: string[]) => {
        try {
            this._callingLogger[type](...args);
        } catch (e) {
            this._callingLogger.info('[failed to log]', getPrintableObject(e));
        }
    }

    private getPrefix(fnName?: string, causeId?: string) {
        let prefix = '';
        if (causeId) {
            prefix += `[${causeId}]`;
        }
        if (fnName) {
            prefix += `[${fnName}]`;
        }
        return prefix;
    }

    public createChild(namespace: string | CallingLoggerPrefixFunction, debug?: boolean): InternalLogger {
        return new InternalLogger(this._callingLogger.createChild(namespace, debug));
    }

    public createFnLogger(fnName: string, causeId: CauseId, ...additionalPrefix: string[]): InternalLogger {
        return new InternalLogger(this._callingLogger.createChild(this.getPrefix(fnName, causeId) + additionalPrefix.join(''), false));
    }

    public logSuccess(piiSafeResult: string): void {
        this._maybeLog('info', `success=${piiSafeResult}}`);
    }

    public logFailure(reason: string | number | boolean | Error): void {
        const message = getPrintableObject(reason);
        this._maybeLog('info', `failed=${message}}`);
    }

    public log(...values: any[]): void {
        this._maybeLog('log', ...values);
    }

    public debug(...values: any[]): void {
        this._maybeLog('debug', ...values);
    }

    public info(...values: any[]): void {
        this._maybeLog('info', ...values);
    }

    public warn(...values: any[]): void {
        this._maybeLog('warn', ...values);
    }

    public error(...values: any[]): void {
        this._maybeLog('error', ...values);
    }
}



