/// <reference path="../typings/slimcore-enums.d.ts" />

import {
    CallingLogger, CauseId, ICallParticipantStream, IRenderer, IRenderTarget, StartStreamOptions, StreamType
} from 'skype-calling-interface';

import * as async from '../../common/ts/async';
import { generateCauseId } from '../../common/ts/causeId';
import ChainedPromise from '../../common/ts/chainedPromise';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronRemoteVideoRenderer, { SlimCoreElectronRemoteVideoRendererTracker } from './SlimCoreElectronRemoteVideoRenderer';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { IInternalLogger } from '../../common/ts/internalLogger';
export class SlimCoreElectronRemoteStreamContext {
    public nrRunningStreams = 0;
    public videoStoppedEvent = new SlimCoreCommon.VideoStateCancelEvent();
    public constructor(logger: CallingLogger) { } // tslint:disable-line:no-empty
}

let NEXT_RANK = 0;

export default class SlimCoreElectronRemoteStream extends SlimCoreElectronBase<{}> implements ICallParticipantStream, SlimCoreElectronRemoteVideoRendererTracker {
    public rank: number;
    public endpointId: string;
    public participantId: string;
    public negotiationTag: string;

    private _remoteVideoId = 0;
    private _videoStatus: Enums.VideoStatus = Enums.VideoStatus.None;
    private _videoBinding: SlimCore.VideoBindingRenderer;

    private _mediaType: Enums.VideoType;
    private _stateCancelEvent = new SlimCoreCommon.VideoStateCancelEvent();
    private _shouldRun = false;
    private _isDisposed = false;
    private _nrRenderers = 0;
    private _nrOngoingStreamStarts = 0;
    private _msi = 0;
    private _chainedApiPromise: ChainedPromise;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private readonly _participantId: number,
        public id: number,
        public type: StreamType,
        private _callHandler: SlimCoreCommon.CallHandler,
        private _context: SlimCoreElectronRemoteStreamContext
    ) {
        super(logger.createChild(() => `RemoteStream(${this._context.nrRunningStreams})[${this._remoteVideoId}:${this.rank}:${this._mediaType}:${this._videoStatus}]`), settings, engine);
        this._chainedApiPromise = new ChainedPromise(logger.createChild(() => `RemoteStreamChain(${this._context.nrRunningStreams})`));
        this._remoteVideoId = id;
        this.rank = NEXT_RANK++;

        const props = this._getProperties(this._callHandler, this._remoteVideoId, {
            endpointId: { propKey: Enums.Property.VideoEndpointId },
            participantId: { propKey: Enums.Property.VideoParticipantLegId },
            negotiationTag: { propKey: Enums.Property.VideoNegotiationTag }
        }, {
            mediaType: { propKey: Enums.Property.VideoType },
            videoStatus: { propKey: Enums.Property.VideoStatus },
            msi: { propKey: Enums.Property.VideoRank }
        });

        this._mediaType = props.mediaType;
        this._videoStatus = props.videoStatus;
        const causeId = generateCauseId();
        this._logger.info(`constructor causeId: ${causeId}`);

        // Subscribe to object changes
        this._onVideoObjectPropertyChanged(Enums.Property.VideoStatus, data => this._onVideoObjectStateChanged(causeId));
        this._onVideoObjectPropertyChanged(Enums.Property.VideoNegotiationTag, data => this._onVideoNegotiationTagChanged(data.value as string));

        this.endpointId = props.endpointId;
        this.participantId = props.participantId;
        this._msi = props.msi;
        this.negotiationTag = props.negotiationTag;
    }

    public getDiagnosticData(): string {
        return JSON.stringify({
            id: this.id,
            rank: this.rank,
            type: this.type,
            isAvailable: this.isAvailable,
            isStreaming: this.isStreaming,
            negotiationTag: this.negotiationTag,
            video_status: this._videoStatus,
            msi: this._msi,
            shouldRun: this._shouldRun,
            nrRenderers: this._nrRenderers,
            isDisposed: this._isDisposed
        });
    }

    public get isAvailable(): boolean {
        return SlimCoreCommon.isVideoAvailable(this._videoStatus);
    }

    public get isStreaming(): boolean {
        return this._videoStatus === Enums.VideoStatus.Running;
    }

    public isActive(): boolean {
        return this.isAvailable && this.isStreaming;
    }

    public async start(target: IRenderTarget, options?: StartStreamOptions): Promise<IRenderer> {
        const startTime = Date.now();
        let waitForAvailableDoneTime = startTime;
        let startVideoDoneTime = startTime;
        const causeId = generateCauseId();
        this._shouldRun = true;
        this._nrOngoingStreamStarts++;
        this._logger.info(`start causeId: ${causeId}`);
        try {
            await SlimCoreCommon.waitForVideoObjectToBeAvailable(this._slimcoreInstance, this._callHandler, this._remoteVideoId, this._logger, 0, this._stateCancelEvent);
            waitForAvailableDoneTime = Date.now();
            const renderer = await this._chainedApiPromise.chainPromise(() => this._startVideo(causeId, target, options), this._chainActionFn('start'), causeId);
            startVideoDoneTime = Date.now();
            try {
                this._throwIfDisposed(causeId, 'disposed while in start');
                await SlimCoreCommon.waitForVideoObjectToBeRunning(this._slimcoreInstance, this._callHandler, this._remoteVideoId, this._logger, 0, this._stateCancelEvent);
                this._updateVideoStatus(causeId);
                const endTime = Date.now();
                this._logger.info(`start causeId: ${causeId} successful. duration: ${endTime - startTime} (${waitForAvailableDoneTime - startTime}+${startVideoDoneTime - waitForAvailableDoneTime}+${endTime - startVideoDoneTime})`);
                return renderer;
            } catch (error) {
                renderer.dispose();
                throw error;
            }
        } catch (error) {
            const endTime = Date.now();
            this._logger.warn(`start causeId: ${causeId} failed. duration: ${endTime - startTime} (${waitForAvailableDoneTime - startTime}+${startVideoDoneTime - waitForAvailableDoneTime}+${endTime - startVideoDoneTime}) error: ${error}`);
            throw error;
        } finally {
            this._nrOngoingStreamStarts--;
        }
    }

    public async stop(): Promise<void> {
        const causeId = generateCauseId();
        await this._stopWithCauseId(this._chainActionFn('stop'), causeId);
    }

    public dispose(): void {
        const causeId = generateCauseId();
        this._disposeWithCauseId(this._chainActionFn('dispose'), causeId);
    }

    public async disposeInternal(): Promise<void> {
        const causeId = generateCauseId();
        await this._disposeWithCauseId(this._chainActionFn('disposeInternal'), causeId);
    }

    private async _stopWithCauseId(action: () => string, causeId: CauseId): Promise<void> {
        this._shouldRun = false;
        this._stateCancelEvent.fire(); // make all waits that happen during start to get rejected, this does not guarantee that previous start can not complete after stop was called
        await this._chainedApiPromise.chainPromise(() => this._stopVideo(causeId, false), action, causeId);
    }

    public registerRenderer(object: SlimCoreElectronRemoteVideoRenderer): void {
        this._nrRenderers++;
    }

    public unregisterRenderer(object: SlimCoreElectronRemoteVideoRenderer): void {
        if (--this._nrRenderers === 0 && this._nrOngoingStreamStarts === 0) {
            const causeId = generateCauseId();
            this._stopWithCauseId(this._chainActionFn('renderers disposed'), causeId);
        }
    }

    private _chainActionFn(action: string): () => string {
        return () => `${action} [${this._participantId}:${this._remoteVideoId}:${this.rank}:${this._mediaType}:${this._videoStatus}]`;
    }

    private _isVideoStatusInStopping() : boolean {
        return this._videoStatus === Enums.VideoStatus.Stopping;
    }

    private _updateVideoStatus(causeId: CauseId): void {
        const videoStatus = this._getIntProperty(this._callHandler, this._remoteVideoId, Enums.Property.VideoStatus);
        if (this._videoStatus !== videoStatus) {
            this._logger.info(`VideoStateChanged: causeId: ${causeId} status: ${this._videoStatus} -> ${videoStatus}`);
            this._videoStatus = videoStatus;
            if (this._isVideoStatusInStopping()) {
                this._videoWasStopped(causeId);
            }
            this.raiseChanged();
        }
    }

    private async _disposeWithCauseId(action: () => string, causeId: CauseId): Promise<void> {
        this._isDisposed = true;
        this._shouldRun = false;
        this._stateCancelEvent.fire();
        await this._chainedApiPromise.chainPromise(() => this._dispose(causeId), action, causeId);
    }

    private _onVideoObjectPropertyChanged(propKey: Enums.Property, listener: SlimCore.EventHandler<SlimCore.Engine.Events.ObjectPropertyChangedArgs>) {
        this._onObjectPropertyChanged(this.id, propKey, listener);
    }

    private _onVideoObjectStateChanged(causeId: CauseId): void {
        // video object status change callback is called with delay to the actual property changes, thus we will not use the status change value from the event but rather poll current value
        // with this we can and will miss transitional states but we never get states from the past
        this._updateVideoStatus(causeId);
    }

    private _onVideoNegotiationTagChanged(value: string): void {
        if (this.negotiationTag !== value) {
            this.negotiationTag = value;
            this.raiseChanged();
        }
    }

    private _videoWasStopped(causeId: CauseId): void {
        // we might get here because of stopping, currently we don't know if video was stopped by remote endpoint or because UI called stop this is indistinguishable
        // on any case Stopping can only be taken from Starting or from Running
        // note we do not fire cancel event and will not set _shouldRun to false like _stopWithCauseId does because we do not want to interrupt ongoing start that happened after proper stop as this event could be fired with delay
        this._stopWithCauseId(this._chainActionFn('discovered video stopped'), causeId);
    }

    private _isRunning(): boolean {
        return !!this._videoBinding;
    }

    private async _stopVideo(causeId: CauseId, force: boolean): Promise<void> {
        //video status ends up stuck in stopping if remote endpoint stops sending video
        if ((!this._shouldRun || force || this._isVideoStatusInStopping()) && this._isRunning()) {
            this._logger.info(`_stopVideo causeId: ${causeId} force: ${force} will stop remote video now`);
            this._slimcoreInstance.videoStop(this._remoteVideoId); // stop video before releasing binding to avoid #275535
            // slimcore does not require that videoStop is called, it would call it itself when all bindings are released, but there is race in slimcore that on some circumstances when one remote video would internally stop the video sink
            // and then immediately let the sink go and if then some other remote video comes and starts consuming the same sink fast enougth by starting sink then the sink stop completion event from previous remote video
            // can land in new remote video and trigger there remote video STOPPING state transition, although it will need to go to RUNNING from STARTING...
            await SlimCoreCommon.forgetAndLog(this._slimcoreInstance.videoReleaseBinding(this._remoteVideoId, this._videoBinding), this._logger, `_stopVideo causeId: ${causeId} videoReleaseBinding on remote video failed`);
            this._videoBinding = null;
            await SlimCoreCommon.waitForVideoObjectToBeStopped(this._slimcoreInstance, this._callHandler, this._remoteVideoId, this._logger);
            this._context.nrRunningStreams--;
            this._logger.info(`_stopVideo causeId: ${causeId} force: ${force} remote video is stopped`);
            // note video Stop does change video status synchronously, otherwise we would need to wait until video status goes away from Running
            // It is possible that the current _videoStatus is not updated yet, due to _onVideoObjectStateChanged is not invoked yet.
            // In this case, if we resovle it right away, and check this.isActive(), it can still return true.
            // Set the current video status here to avoid this.
            this._updateVideoStatus(causeId);
            this._context.videoStoppedEvent.fire(); // notify about video sink back available so that sink acquisition can from now on happen again
        } else {
            this._logger.info(`_stopVideo causeId: ${causeId} noop, force: ${force} should run: ${this._shouldRun} is running: ${this._isRunning()} already disposed: ${this._isDisposed}`);
        }
    }

    private async _startVideo(causeId: CauseId, target: IRenderTarget, options?: StartStreamOptions): Promise<SlimCoreElectronRemoteVideoRenderer> {
        const videoWasJustStarted = await this._getStartedVideo(causeId);
        try {
            // It is possible that the current _videoStatus is not set to Running, due to _onVideoObjectStateChanged is
            // not invoked yet.
            // In this case, if we resovle it right away, and check this.isActive(), it can still return false.
            // Set the current video status here to avoid this.
            this._updateVideoStatus(causeId);
            this._logger.info(`_startVideo causeId: ${causeId} create renderer`);
            return new SlimCoreElectronRemoteVideoRenderer(
                this._logger,
                this._settings,
                null,
                target,
                this._mediaType,
                this._videoBinding,
                options,
                this);
        } catch (error) {
            if (videoWasJustStarted) {
                await SlimCoreCommon.forgetAndLog(this._stopVideo(causeId, true), this._logger, `_startVideo causeId: ${causeId} stopping remote video failed while got error while starting video`);
            }
            throw error;
        }
    }

    private async _dispose(causeId: CauseId): Promise<void> {
        try {
            await SlimCoreCommon.forgetAndLog(this._stopVideo(causeId, true), this._logger, `_dispose causeId: ${causeId} stopping remote video failed`);
        } finally {
            super.dispose();
        }
    }

    private async _getStartedVideo(causeId: string): Promise<boolean> {
        this._throwIfDisposed(causeId, 'disposed while in _getStartedVideo');
        if (!this._shouldRun) {
            throw new Error(`_getStartedVideo causeId: ${causeId} failing, should not run any more`);
        }
        if (!this._isRunning()) {
            this._logger.info(`_getStartedVideo causeId: ${causeId} will start remote video now`);
            const videoBinding = this._createVideoBindingRenderer(causeId);
            this._slimcoreInstance.videoStart(this._remoteVideoId);
            if (this._settings.enableRemoteStreamVideoBindingRetry) {
                await this._videoCreateBinding(causeId, this._remoteVideoId, videoBinding);
            } else {
                await this._slimcoreInstance.videoCreateBinding(this._remoteVideoId, videoBinding);
            }
            this._context.nrRunningStreams++;
            this._videoBinding = videoBinding;
            this._logger.info(`_getStartedVideo causeId: ${causeId} remote video is started`);
            return true;
        }
        return false;
    }

    private _throwIfDisposed(causeId: string, reason: string): void {
        if (this._isDisposed) {
            throw new Error(`_throwIfDisposed causeId: ${causeId} reason: ${reason}`);
        }
    }

    private _createVideoBindingRenderer(causeId: string): SlimCore.VideoBindingRenderer {
        // use new method if available
        if (this._slimcoreInstance.createVideoBindingRenderer) {
            this._logger.info(`_createVideoBindingRenderer causeId: ${causeId} createVideoBindingRenderer`);
            return this._slimcoreInstance.createVideoBindingRenderer({
                enableDXVA: this._settings.enableDXVA,
                isLocalPreview: false
            });
        } else { // use old method for backwards compatibility
            this._logger.info(`_createVideoBindingRenderer causeId: ${causeId} old createVideoBindingRenderer`);
            return SlimCore.createVideoBindingRenderer();
        }
    }

    private async _videoCreateBinding(causeId: string, videoObjectId: SlimCore.ObjectId, videoBinding: SlimCore.VideoBinding): Promise<void> {
        // HACK: we don't know exactly how many remote videos (video sinks) can be runnning simultanously, thus when we have consumed all available sinks, then stopping one video and starting
        // immediately another one could end up with SinkAcquisitionFailed error. We could add global synchronization but that would add penalty to all remote stream starts and stops as those starts and stops can happen in parallel
        // until all sinks are consumed
        const CREATE_BINDING_RETRY_COUNTER = 10;
        const CREATE_BINDING_RETRY_DELAY_MS = 200;
        // one cancel subsciption per create binding. This is for case when ui decides to stop stream while create binding tries to complete. On such case we can cancel (Reject promise) inbetween the retry
        const stateCancelRejectionDeferred = async.defer<void>();
        const stateCancelSub = this._stateCancelEvent.subscribe(() => {
            // we could get state cancel event after binding was created, on such case _videoCreateBinding function must resolve successfully (or binding would not get released later)
            stateCancelRejectionDeferred.reject(new Error(`_videoCreateBinding causeId: ${causeId} stateCancelEvent got signalled`));
        });
        let videoStoppedDeferred = async.defer<void>();
        // to reduce number of subscriptions subscribe only once for video stopped event
        const videoStoppedSub = this._context.videoStoppedEvent.subscribe(() => {
            this._logger.info(`_videoCreateBinding causeId: ${causeId} one of the videos was stopped will wake up for binding retry`);
            videoStoppedDeferred.resolve();
        });
        try {
            const tryCreateBinding: (retryCount: number) => Promise<void> = async (retryCount: number) => {
                // preconditions to create binding, we want to retry only for sink SinkAcquisitionFailed, but binding could also fail because of remote video not being in correct state
                // throw if disposed is redundant but would bring us out sooner (otherwise _stateCancelEvent would fail after we do retry)
                this._throwIfDisposed(causeId, `disposed while in _videoCreateBinding on retry attempt: ${retryCount}`);
                if (!SlimCoreCommon.isVideoAvailable(this._videoStatus)) {
                    throw new Error(`_videoCreateBinding causeId: ${causeId} video not available any more on retry attempt: ${retryCount}`);
                }
                const videoStoppedAlready = videoStoppedDeferred.promise;
                videoStoppedDeferred = async.defer<void>(); // for next retry use new deferred, we also memorized if we got event already otherwise our deferred promise gets saturated and would not trigger new retry after doing it once
                // with current implementation of videoStoppedAlready there is side effect where after first try, if wait race is won by videoStoppedDeferred then next race cycle would not wait but complete immediately because of videoStoppedAready
                // gets assigned from resolved promise (from previously resolved videoStoppedDeferred). But we can survive this because we do not want to miss videoStoppedDeferred events and that could happen if we reset videoStoppedDeferred
                // after promise race. I.e. consider case where async.delay would complete just before videoStoppedDeferred, then if we would reset videoStoppedDeferred in the then of promise race we would effectively miss one stopped event and wait for timeout
                this._logger.info(`_videoCreateBinding causeId: ${causeId} try to create video binding from slimcore on retry attempt: ${retryCount}`);
                try {
                    await this._slimcoreInstance.videoCreateBinding(videoObjectId, videoBinding);
                } catch (error) {
                    // retry preconditions
                    if (!(retryCount < CREATE_BINDING_RETRY_COUNTER)) {
                        throw new Error(`_videoCreateBinding causeId: ${causeId} retry attempt: ${retryCount} exceed`);
                    }
                    await Promise.race([videoStoppedAlready, videoStoppedDeferred.promise, stateCancelRejectionDeferred.promise, async.delay(CREATE_BINDING_RETRY_DELAY_MS)]);
                    await tryCreateBinding(retryCount + 1);
                }
            };
            await tryCreateBinding(1);
        } finally {
            stateCancelSub.dispose();
            videoStoppedSub.dispose();
        }
    }
}



