import { EventSubscription } from '../../common/ts/events';
import { InjectorConfig, InjectorMode, ISlimCoreElectronControlInjector, TelemetryService } from '../typings/skype-calling-electron';
import SlimCoreElectronCallParticipant from './SlimCoreElectronCallParticipant';
import SlimCoreElectronDataChannel, { DATA_SOURCE_ID_ANY, IMediaDataChannelUser } from './SlimCoreElectronDataChannel';

import * as _ from 'lodash';
import * as async from '../../common/ts/async';
