import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronRemoteVideoRenderer from './SlimCoreElectronRemoteVideoRenderer';

// Timeout when waiting for control request
const CONTROL_REQUEST_WAIT_TIMEOUT = 30000;

// Timeout when waiting for control acks
const CONTROL_ACK_WAIT_TIMEOUT = 10000;
const AVAILABLE_ACK_WAIT_TIMEOUT = 2000;
// Timeout when waiting for bulky bubbling input raw errors
const INPUT_RAW_ERROR_REPORTING_WAIT_TIMEOUT = 10000;
const AVAILABLE_RETRY_LIMIT = 6;

// Local participant mri
const LOCAL_PARTICIPANT_MRI = '';

export const enum ControlAction {
    Available,
    ControlRequest,
    CancelControlRequest,
    AcceptRequest,
    RejectRequest,
    Ack,
    GiveControl,
    TerminateNoAck,
    Terminate,
    AvailableAck,
    AvailableNack,
    RenderedAtViewer
}

type ControlDataChannelStatus = {
    id: number;
    readyForControl: boolean;
    state: MediaDataChannelState;
};

export type ControlMessage = {
    action: ControlAction;
    terminatedReason: ControlTerminatedReason;
    handshakeId?: string;
};

// Used to map between a participant leg (connected endpoint) and data sourceId here.
// note: sourceId for data might not be known when the participant is determined and it might change during retargets.
// therefore we look up the data sourceId when needed.
// The opposite is possible as well: we might know the sourceId from incoming data messages
// but can't map it to a participant / participantId due to a late roster update / stream info.
// For the available handshake this is currently mitigated via retries from the sender/viewer
// since the sharer would simply drop messages from unknown sourceIds.
// One could instead store the sourceId and try to identify it when we get a roster update.
type ParticipantLeg = {
    participant: ICallParticipant;
    participantState: ParticipantState;
    participantId: string;  // this refers to the connected endpoint while the participant includes all participant legs
};

enum VIEWER_CONTROL_SESSION_OPERATIONS {
    INITIATED = 'Initiated',
    CALL_STATE = 'CallState',
    SHARING_STARTED = 'SharingStarted',
    DATA_CHANNEL_AVAILABLE = 'DataChannelAvailable',
    DATA_CHANNEL_UNAVAILABLE = 'DataChannelUnavailable',
    AVAILABLE_HANDSHAKE = 'AvailableHandshake',
    SESSION_SHUTDOWN = 'Shutdown',
    SESSION_SHUTDOWN_BY_ANOTHER_SESSION = 'ShutdownByOtherSession'
}

enum SHARER_CONTROL_SESSION_OPERATIONS {
    INITIATED = 'Initiated',
    CALL_STATE = 'CallState',
    DATA_CHANNEL_AVAILABLE = 'DataChannelAvailable',
    DATA_CHANNEL_UNAVAILABLE = 'DataChannelUnavailable',
    CONTROL_REQUEST = 'ControlRequest',
    SESSION_SHUTDOWN = 'Shutdown',
    SESSION_SHUTDOWN_BY_ANOTHER_SESSION = 'ShutdownByOtherSession'
}

type SharerControlSessionEvent = {
    CorrelationId: string;
    ParticipantId: string;
    EndpointId: string;
    NegotiationTag: string;
    TsCallingVersion: string;
    TenantId: string;
    EventTimestampBag: string;
    ProtocolMessages: string;
};

type ViewerControlSessionEvent = {
    CorrelationId: string;
    ParticipantId: string;
    EndpointId: string;
    NegotiationTag: string;
    TsCallingVersion: string;
    TenantId: string;
    SharerParticipantId: string;
    SharerClientVersion: string;
    EventTimestampBag: string;
    ProtocolMessages: string;
};

class ControlSessionTelemetryBase extends TelemetryUtilities {
    protected tsCallingVersion: string;
    protected tenantId: string;
    public messagesFromUnknownSender: {[sourceId: number]: number};

    constructor(
        logger: CallingLogger,
        callStartTime: number,
        public negotiationTag: string,
        protected callId: string,
        protected participantId: string,
        protected endpointId: string
    ) {
        super(logger, callStartTime, () => { return this.recordedEvents.length > 100; });

        this.messagesFromUnknownSender = {};
        this.tsCallingVersion = getTsCallingVersion();
    }

    public recordProtocolMessage(sender: ICallParticipant, sourceId: number) {
        if (!sender) {
            if (!(sourceId in this.messagesFromUnknownSender)) {
                this.messagesFromUnknownSender[sourceId] = 0;
            }
            this.messagesFromUnknownSender[sourceId]++;
        }
    }

    public setCallId(callId: string) {
        this.callId = callId;
    }

    public setEndpointId(endpointId: string) {
        this.endpointId = endpointId;
    }

    public setParticipantId(participantId: string) {
        this.participantId = participantId;
    }

    public setTenantId(tenantId: string): void {
        this.tenantId = tenantId;
    }
}

class ViewerControlSessionTelemetry extends ControlSessionTelemetryBase {
    private sharerClientVersion: string;
    private sharerParticipantId: string;

    public constructor(
        logger: CallingLogger,
        callStartTime: number,
        negotiationTag: string,
        callId: string,
        participantId: string,
        endpointId: string,
        sharer: SlimCoreElectronCallParticipant
    ) {
        super(logger, callStartTime, negotiationTag, callId, participantId, endpointId);
        const sharerParticipantId = sharer.getSharingParticipantLeg();
        if (sharerParticipantId) {
            this.sharerParticipantId = sharerParticipantId;
            const d = _.find(sharer.endpoints.endpointDetails, e => e.participantId === sharerParticipantId);
            if (d) {
                this.sharerClientVersion = d.clientVersion;
            }
        }
    }

    public getEvent(): ViewerControlSessionEvent {
        const getValue = (v: any) => v === undefined ? '' : v;
        return {
            CorrelationId: getValue(this.callId),
            ParticipantId: getValue(this.participantId),
            EndpointId: getValue(this.endpointId),
            NegotiationTag: getValue(this.negotiationTag),
            TsCallingVersion: getValue(this.tsCallingVersion),
            TenantId: getValue(this.tenantId),
            SharerParticipantId: getValue(this.sharerParticipantId),
            SharerClientVersion: getValue(this.sharerClientVersion),
            EventTimestampBag: super.getEventTimestampBag(),
            ProtocolMessages: JSON.stringify({UnknownSender: this.messagesFromUnknownSender})
        };
    }
}

class SharerControlSessionTelemetry extends ControlSessionTelemetryBase {
    public constructor(
        logger: CallingLogger,
        callStartTime: number,
        negotiationTag: string,
        callId: string,
        participantId: string,
        endpointId: string
    ) {
        super(logger, callStartTime, negotiationTag, callId, participantId, endpointId);
    }

    public getEvent(): SharerControlSessionEvent {
        const getValue = (v: any) => v === undefined ? '' : v;
        return {
            CorrelationId: getValue(this.callId),
            ParticipantId: getValue(this.participantId),
            EndpointId: getValue(this.endpointId),
            NegotiationTag: getValue(this.negotiationTag),
            TsCallingVersion: getValue(this.tsCallingVersion),
            TenantId: getValue(this.tenantId),
            EventTimestampBag: super.getEventTimestampBag(),
            ProtocolMessages: JSON.stringify({UnknownSender: this.messagesFromUnknownSender})
        };
    }
}

export default class SlimCoreElectronScreenSharingControl extends SlimCoreElectronBase<IScreenSharingControlEvents>
    implements IScreenSharingControl, IMediaDataChannelUser {

    public controlState: ControlState = ControlState.None;
    public role: SharingControlRole = SharingControlRole.Unknown;
    public controlDataChannelStatus: ControlDataChannelStatus = { id: null, readyForControl: false, state: MediaDataChannelState.Unknown };

    private _enabled: boolean = false;
    private _availableAckEnabled: boolean = true;
    private _allowControlForUser: Function;
    private _dataChannel: SlimCoreElectronDataChannel;
    private _dataChannelActive: boolean = false;
    private _dataChannelAvailableSent: boolean = false;
    private _protocolDataSource: SlimCore.DataSource = null;
    private _controlDataSource: SlimCore.DataSource = null;
    private _controlDataSink: SlimCore.DataSink = null;
    private _controlRequest: ParticipantLeg = null;
    private _controlRequestSubscription: IDisposable = null;
    private _controllerParticipant: ParticipantLeg = null;
    private _controllerParticipantSubscription: IDisposable = null;
    private _screenSharingVideoRenderer: SlimCoreElectronRemoteVideoRenderer = null;
    private _screenSharingVideoRendererSubscriptions: IDisposable[] = [];
    private _sharerParticipant: ICallParticipant = null; //TODO(robedum) this should be a ParticipantLeg. see initControlForViewer
    private _controlCapturer: ISlimCoreElectronControlCapturer = null;
    private _captureEventSubscription: EventSubscription = null;
    private _mouseControlEventSubscription: EventSubscription = null;
    private _keyboardControlEventSubscription: EventSubscription = null;
    private _requestControlTimer: number = null;
    private _grantControlTimer: number = null;
    private _acceptControlTimer: number = null;
    private _terminateControlTimer: number = null;
    private _availableAckTimer: number = null;
    private _retryAttempt: number = 0;
    private _injectRawInputErrorTimer: number = null;
    private _lastInjectRawInputErrorCount: number = 0;
    private _raiseRenderedAtViewer: Function = null;
    private _participantSubscriptions: { [id: string]: IDisposable } = {};

    private _enableControlSessionTelemetry: boolean;
    private _currentViewerSessionTelemetry: ViewerControlSessionTelemetry;
    private _currentNegotiationTag: string;
    private _completedViewerSessions: string[] = [];
    private _currentSharerSessionTelemetry: SharerControlSessionTelemetry;
    private _callId: string;
    private _participantId: string;
    private _endpointId: string;
    private _callState: CallState;

    private _terminateAvailableHandshake: (reason?: AvailableHandshakeTerminatedReason, ackHandshakeId?: string) => void;

    // viewer's action promise
    private _requestControlPromise: async.Deferred<void>;

    // sharer's action promise
    private _grantControlPromise: async.Deferred<void>;
    private _acceptControlPromise: async.Deferred<void>;

    // both viewer's and sharer's promise
    private _terminateControlPromise: async.Deferred<void>;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        call: ICall,
        private _controlInjector: ISlimCoreElectronControlInjector,
        private _telemetryService: TelemetryService
    ) {
        super(logger.createChild(() => `SlimCoreElectronScreenSharingControl`), settings, engine);
        this._logger.info('constructor');

        this._callId = call.callId;
        call.on('callIdChanged', (id: string) => {
            this._callId = id;
            if (this._currentViewerSessionTelemetry) {
                this._currentViewerSessionTelemetry.setCallId(id);
            }
            if (this._currentSharerSessionTelemetry) {
                this._currentSharerSessionTelemetry.setCallId(id);
            }
        });
        this._participantId = call.participantId;
        call.on('callLegIdChanged', (id: string) => {
            this._participantId = id;
            if (this._currentViewerSessionTelemetry) {
                this._currentViewerSessionTelemetry.setParticipantId(id);
            }
            if (this._currentSharerSessionTelemetry) {
                this._currentSharerSessionTelemetry.setParticipantId(id);
            }
        });
        this._endpointId = call.endpointId;

        if (this._slimcoreInstance.ecsGetSettingAsBool) {
            this._enableControlSessionTelemetry = this._slimcoreInstance.ecsGetSettingAsBool('MediaAgent', 'EnableGtcTelemetry', false);
        } else {
            this._enableControlSessionTelemetry = false;
        }
    }

    // this is the function on the sharer side to turn on/off the available ack
    public enableScreenSharingControl(enabled: boolean, reason?: SharingControlDisableReason, detail?: any, _allowControlForUser?: Function) {
        if (this._availableAckEnabled === enabled && this._allowControlForUser === _allowControlForUser) {
            return;
        }
        this._allowControlForUser = _allowControlForUser;

        if (!enabled) {
            if (reason === SharingControlDisableReason.SetInjectionFailed) {
                const controlError: SharingControlError = {
                    reason: SharingControlErrorReason.SetInjectionRectError,
                    detail
                };
                this.event('sharingControlError').raise(controlError);
            }
            this._raiseScreenSharingControlCapableEvent(false, LOCAL_PARTICIPANT_MRI);
        } else {
            this._raiseScreenSharingControlCapableEvent(true, LOCAL_PARTICIPANT_MRI);
        }
        this._availableAckEnabled = enabled;
    }

    public setRaiseRenderedAtViewer(_raiseRenderedAtViewer: Function) {
        this._raiseRenderedAtViewer = _raiseRenderedAtViewer;
    }

    // feature flag
    public setScreenSharingControlFeatureFlag(enabled: boolean) {
        this._enabled = enabled;
        this._availableAckEnabled = enabled;
    }

    public isScreenSharingControlEnabled(): boolean {
        return this._enabled;
    }

    private _disposeRenderer() {
        this._disposeControlCapturer();
        for (const s of this._screenSharingVideoRendererSubscriptions) {
            s.dispose();
        }
        this._screenSharingVideoRendererSubscriptions = [];
        this._screenSharingVideoRenderer = null;
    }

    public setRenderer(renderer: IRenderer, target?: IRenderTarget): Promise<void> {
        this._disposeRenderer();

        if (!renderer) { return Promise.resolve<void>(); }

        const videoRenderer = renderer as SlimCoreElectronRemoteVideoRenderer;
        const container = (!!target ? target : videoRenderer.renderTarget) as HTMLElement;
        //jyr: remove renderTarget prop from SlimCoreElectronRemoteVideoRenderer when target is made mandatonary
        return async.asap(() => this._setViewingRenderer(videoRenderer, container));
    }

    public setDataChannel(channel: SlimCoreElectronDataChannel): Promise<void> {
        return async.asap(() => this._setDataChannel(channel));
    }

    private _setDataChannel(channel: SlimCoreElectronDataChannel): void {
        this._logger.info(`Setting DataChannel ${channel && channel.id}`);
        if (this._dataChannel && !channel) {
            this._detachAndDeleteControlDevices();
        }
        this._dataChannel = channel;
        if (channel) {
            this.controlDataChannelStatus = {
                id: channel.id,
                readyForControl: false,
                state: SlimCoreElectronDataChannel._mapDataChannelStatus(channel._lastDataChannelState)
            };
            this.event('controlDataChannelStateChanged').raise(this.controlDataChannelStatus);
        }
    }

    public setProtocolDataSource(source: SlimCore.DataSource): Promise<void> {
        return async.asap(() => this._setProtocolDataSource(source));
    }

    private _setProtocolDataSource(source: SlimCore.DataSource): void {
        this._protocolDataSource = source;
    }

    private _raiseScreenSharingControlCapableEvent(isCapable: boolean, participantId: string, disabledBySharer?: boolean): void {
        if (this._enabled) {
            // Notify app that local participant can use the control/pointer feature
            this._logger.info(`_raiseScreenSharingControlCapableEvent() enabled is true, raising control capability, capable=${isCapable}`);
            const capable: SharingControlCapability = { capable: isCapable, id: participantId, disabledBySharer };
            this.event('sharingControlCapable').raise(capable);
        } else {
            this._logger.info('_raiseScreenSharingControlCapableEvent() enabled is false, disabling control capability');
            const capable: SharingControlCapability = { capable: false, id: participantId };
            this.event('sharingControlCapable').raise(capable);
        }
    }

    public initControlForSharer(negotiationTag: string): void {
        this._logger.info('initControlForSharer()');

        // Reset some state
        this._resetControlState();

        // Sharer doesn't need renderer / capturer:
        this._screenSharingVideoRenderer = null;
        this._disposeControlCapturer();

        this.role = SharingControlRole.Sharer;
        this._dataChannelAvailableSent = false; // this is a viewer only flag, should be false for sharer

        // Set seamless mode which tells injector
        // we want multiple cursors
        // @TODO: for now, set to disabled until we
        // enable pointing
        this._setInjectorMode(InjectorMode.Disabled);

        // Notify app that local participant can use the control/pointer feature
        this._raiseScreenSharingControlCapableEvent(true, LOCAL_PARTICIPANT_MRI);

        this._initiateSharerSessionTelemetry(negotiationTag);

    }

    public shutdownControlForSharer(): void {
        this._logger.info('shutdownControlForSharer()');

        // Check if there is any pending control requests...reject them here:
        if (this._controlRequest) {
            this._logger.warn('Rejecting pending control requests');

            const controlRequesterSourceId = this._getDataSourceIdForParticipantLeg(this._controlRequest);
            const controlRequest = this._controlRequest;
            this._clearControlRequest();
            this._raiseControlRequestCanceled(controlRequest);

            this._sendRequest(
                { action: ControlAction.RejectRequest, terminatedReason: ControlTerminatedReason.SharerTerminated }, controlRequesterSourceId);
        }

        this.role = SharingControlRole.Unknown;

        if (this._controllerParticipant) {
            // Notify app
            const controlInfo: SharingControlInfo = {
                inControl: false,
                id: this._controllerParticipant.participant.id,
                terminatedReason: ControlTerminatedReason.SharerTerminated
            };
            this.event('sharingControlChanged').raise(controlInfo);
        }

        this._finalizeSharerSessionTelemetry(SHARER_CONTROL_SESSION_OPERATIONS.SESSION_SHUTDOWN);

        // Reset some state
        this._teardownSharerRemoteControl();
        this._resetControlState();
        this._resetParticipantSubscriptions();
        this._availableAckEnabled = this._enabled;
    }

    private _resetParticipantSubscriptions(): void {
        this._logger.info('resetParticipantSubscriptions(): Stopping tracking all participant state changes');
        _.each(this._participantSubscriptions, id => { id.dispose(); });
        this._participantSubscriptions = {};
    }

    private _initiateViewerSessionTelemetry(sharer: ICallParticipant, negotiationTag: string, alwaysCreate: boolean): ViewerControlSessionTelemetry {
        if (!alwaysCreate && !negotiationTag) {
            return null;
        }
        if (!alwaysCreate && _.find(this._completedViewerSessions, s => negotiationTag === s)) {
            return null;
        }
        if (this._currentViewerSessionTelemetry) {
            if (this._currentViewerSessionTelemetry.negotiationTag === negotiationTag) {
                return null;
            }
            this._finalizeViewerSessionTelemetry(VIEWER_CONTROL_SESSION_OPERATIONS.SESSION_SHUTDOWN_BY_ANOTHER_SESSION);
        }

        this._logger.info(`initiating new viewer session telemetry for ${negotiationTag}`);
        this._currentViewerSessionTelemetry = new ViewerControlSessionTelemetry(
            this._logger,
            new Date().getTime(),
            negotiationTag,
            this._callId,
            this._participantId,
            this._endpointId,
            (sharer as SlimCoreElectronCallParticipant)
        );

        this._currentViewerSessionTelemetry.recordEvent(VIEWER_CONTROL_SESSION_OPERATIONS.CALL_STATE, {state: this._callState});
        if (this._dataChannel && this._dataChannelActive) {
            this._currentViewerSessionTelemetry.recordEvent(VIEWER_CONTROL_SESSION_OPERATIONS.DATA_CHANNEL_AVAILABLE);
        }
        return this._currentViewerSessionTelemetry;
    }

    private _recordViewerSessionTelemetry(f: (t: ViewerControlSessionTelemetry) => void, shouldHaveExistingSession: boolean = true): void {
        if (!this._currentViewerSessionTelemetry) {
            if (shouldHaveExistingSession) {
                this._logger.error(`Cannot record telemetry without current session`);
            }
            return;
        }

        if (f) {
            f(this._currentViewerSessionTelemetry);
        }
    }

    private _finalizeViewerSessionTelemetry(reason: VIEWER_CONTROL_SESSION_OPERATIONS) : void {
        if (!this._currentViewerSessionTelemetry) {
            return;
        }
        this._currentViewerSessionTelemetry.recordEvent(reason);
        const event: ViewerControlSessionEvent = this._currentViewerSessionTelemetry.getEvent();
        this._sendTelemetryEvent('mdsc_gtc_viewer_session', event);
        if (this._currentViewerSessionTelemetry.negotiationTag) {
            this._completedViewerSessions.push(this._currentViewerSessionTelemetry.negotiationTag);
        }
        if (this._currentViewerSessionTelemetry.messagesFromUnknownSender &&
            Object.keys(this._currentViewerSessionTelemetry.messagesFromUnknownSender).length) {
            RootToolsManager.logExternalForDDL(`ScreenSharingControl: messages received from unkonwn sender => ${event.ProtocolMessages}`);
        }
        this._currentViewerSessionTelemetry = null;
    }

    private _initiateSharerSessionTelemetry(negotiationTag: string): void {
        if (this._currentSharerSessionTelemetry) {
            if (this._currentSharerSessionTelemetry.negotiationTag === negotiationTag) {
                return null;
            }
            this._finalizeSharerSessionTelemetry(SHARER_CONTROL_SESSION_OPERATIONS.SESSION_SHUTDOWN_BY_ANOTHER_SESSION);
        }

        this._logger.info(`initiating new sharer session telemetry for ${negotiationTag}`);
        this._currentSharerSessionTelemetry = new SharerControlSessionTelemetry(
            this._logger,
            new Date().getTime(),
            negotiationTag,
            this._callId,
            this._participantId,
            this._endpointId);
        if (this._dataChannel && this._dataChannelActive) {
            this._currentSharerSessionTelemetry.recordEvent(SHARER_CONTROL_SESSION_OPERATIONS.DATA_CHANNEL_AVAILABLE);
        }
    }

    private _recordSharerSessionTelemetry(f: (t: SharerControlSessionTelemetry) => void, shouldHaveExistingSession: boolean = true): void {
        if (!this._currentSharerSessionTelemetry) {
            if (shouldHaveExistingSession) {
                this._logger.error(`Cannot record telemetry without current session`);
            }
            return;
        }

        if (f) {
            f(this._currentSharerSessionTelemetry);
        }
    }

    private _finalizeSharerSessionTelemetry(reason: SHARER_CONTROL_SESSION_OPERATIONS) : void {
        if (!this._currentSharerSessionTelemetry) {
            return;
        }
        this._currentSharerSessionTelemetry.recordEvent(reason);
        const event: SharerControlSessionEvent = this._currentSharerSessionTelemetry.getEvent();
        this._sendTelemetryEvent('mdsc_gtc_sharer_session', event);

        if (this._currentSharerSessionTelemetry.messagesFromUnknownSender &&
            Object.keys(this._currentSharerSessionTelemetry.messagesFromUnknownSender).length) {
            RootToolsManager.logExternalForDDL(`ScreenSharingControl: messages received from unkonwn sender => ${event.ProtocolMessages}`);
        }
        this._currentSharerSessionTelemetry = null;
    }

    private _sendTelemetryEvent(eventName: string, event: any): void {
        if (this._enableControlSessionTelemetry) {
            this._logger.info(`sending event ${eventName}: `);
        } else {
            this._logger.info(`${eventName}: `);
        }
        Object.keys(event).forEach(
            key  => {
                this._logger.info(`     ${key} => ${event[key]}`);
            }
        );
        if (this._enableControlSessionTelemetry && this._telemetryService) {
            this._telemetryService.sendEvent(eventName, event);
        }
    }

    private _recordProtocolMessage(sender: ICallParticipant, sourceId: number): void {
        if (this._currentViewerSessionTelemetry) {
            this._currentViewerSessionTelemetry.recordProtocolMessage(sender, sourceId);
        }
        if (this._currentSharerSessionTelemetry) {
            this._currentSharerSessionTelemetry.recordProtocolMessage(sender, sourceId);
        }
    }

    public reportSharingSessionChangeForViewer(sharer: ICallParticipant, negotiationTag: string): void {
        if (negotiationTag) {
            this._initiateViewerSessionTelemetry(sharer, negotiationTag, false);
        }
    }

    public initControlForViewer(sharer: ICallParticipant, negotiationTag: string): void {
        this._logger.info(`initControlForViewer(${negotiationTag})`);

        const newSharingSession = (!this._sharerParticipant || this._sharerParticipant && this._sharerParticipant.id !== sharer.id || negotiationTag !== this._currentNegotiationTag);

        this._currentNegotiationTag = negotiationTag;
        this.role = SharingControlRole.Viewer;

        // TODO(robedum) this should be based on participantId (the caller could pass in the sharer's IStream).
        // we should determine the sharing participant leg once and stick with it until the participant leg changes.
        // for now we attempt to determine the sharer's data source id on the fly when we need it.

        // Check if sharer has changed and needs to be updated
        if (this._sharerParticipant) {
            if (this._sharerParticipant.id !== sharer.id) {
                this._recordViewerSessionTelemetry(e => e.recordEvent(VIEWER_CONTROL_SESSION_OPERATIONS.SESSION_SHUTDOWN_BY_ANOTHER_SESSION, null, negotiationTag));

                this._logger.info(`initControlForViewer() sharer switched, new sharer id=${piiUtils.scrubMriOrOmit(sharer.id)}`);
                // Sharer switched, we should send ControlAction.Available to the new sharer
                this._dataChannelAvailableSent = false;

                // And give up control if we had it
                if (this.controlState === ControlState.LocalControlling) {
                    this._terminateControl(false);
                }

                // TBC: no _resetControlState here, if data channel is not active, _sharerParticipant not changed, is it intentional?
            }
        } else {
            this._logger.info(`initControlForViewer() setting sharer to id=${piiUtils.scrubMriOrOmit(sharer.id)}`);
            this._sharerParticipant = sharer;
            this._dataChannelAvailableSent = false;
        }

        this._initiateViewerSessionTelemetry(sharer, negotiationTag, true);
        if (newSharingSession) {
            this._recordViewerSessionTelemetry(e => e.recordEvent(VIEWER_CONTROL_SESSION_OPERATIONS.SHARING_STARTED));
        }

        if (this._dataChannel && this._dataChannelActive && !this._dataChannelAvailableSent) {
            // Reset some state
            this._resetControlState();

            this.role = SharingControlRole.Viewer;
            this._sharerParticipant = sharer;

            // Remote participant started screen sharing
            // Send an 'Available' message to sharer
            this._logger.info('initControlForViewer() sending Available message to sharer');
            this._startAvailableHandshake();
        }
    }

    public shutdownControlForViewer(sharer?: ICallParticipant): void {
        this._logger.info('shutdownControlForViewer()');

        let shouldShutdownControlForViewer: boolean = false;
        if (!sharer ||
            (this._sharerParticipant && sharer.id === this._sharerParticipant.id)) {
            // We should shutdown control for viewer if sharer param is null, or
            // sharer matches our sharer
            this._logger.info('shutdownControlForViewer: setting shouldShutdownControlForViewer to true');
            shouldShutdownControlForViewer = true;
        }

        if (shouldShutdownControlForViewer && this._isViewing()) {

            if (this.controlState === ControlState.RequestSent) {

                if (this._sharerParticipant) {
                    const sharerSourceId: number = this._getSharerDataSourceId();
                    this._sendRequest(
                        { action: ControlAction.CancelControlRequest, terminatedReason: ControlTerminatedReason.ViewerTerminated }, sharerSourceId);
                } else {
                    this._logger.warn('shutdownControlForViewer: sharer participant is null');
                }

                // Raise an event notifying that the request was 'cancelled'
                const controlInfo: SharingControlInfo = {
                    inControl: false,
                    id: LOCAL_PARTICIPANT_MRI, // local participant
                    terminatedReason: ControlTerminatedReason.ViewerTerminated
                };

                if (this._requestControlPromise) {
                    this._requestControlPromise.reject(new Error(JSON.stringify({ controlInfo, details: '' })));
                } else {
                    this._logger.error('Unexpected, no promise could be resolved - state =ControlState.RequestSent action =shutdownControlForViewer');
                }
            } else if (this.controlState === ControlState.LocalControlling) {
                // Notify app
                const controlInfo: SharingControlInfo = {
                    inControl: false,
                    id: LOCAL_PARTICIPANT_MRI,
                    terminatedReason: ControlTerminatedReason.ViewerTerminated
                };
                this.event('sharingControlChanged').raise(controlInfo);
            }

            // Reset some state
            this._teardownViewerRemoteControl();
            this._resetControlState();

            this._screenSharingVideoRenderer = null;
            this._disposeControlCapturer();
            if (this._terminateAvailableHandshake) {
                this._terminateAvailableHandshake(AvailableHandshakeTerminatedReason.Shutdown);
            }

            this._dataChannelAvailableSent = false;

            this._finalizeViewerSessionTelemetry(VIEWER_CONTROL_SESSION_OPERATIONS.SESSION_SHUTDOWN);

            // Raise an event indicating that local user cannot do control/pointers
            this._raiseScreenSharingControlCapableEvent(false, LOCAL_PARTICIPANT_MRI);
        }
    }

    private _resetControlRequest(request: ParticipantLeg): void {
        this._clearControlRequest();
        this._controlRequest = request;
        this._registerParticipantListener(request);
        // there may be a race where the participant has not yet been set to connected
        // even though the data channel has made a control request. Wait until we are connected
        // to notify
        if (request.participantState === ParticipantState.Connected) {
            this.event('sharingIncomingControlRequest').raise(request.participant.id);
        }
    }

    private _clearControlRequest(): void {
        this._stopListeningToParticipantChanges(this._controlRequest);
        this._controlRequest = null;
    }

    private _stopListeningToParticipantChanges(previousRequest: ParticipantLeg): void {
        if (this._controlRequestSubscription) {
            this._logger.info(`_stopListeningToParticipantChanges: stopping tracking changes for ${previousRequest.participant.id}`);
            this._controlRequestSubscription.dispose();
            this._controlRequestSubscription = null;
        }
    }

    private _registerParticipantListener(request: ParticipantLeg): void {
        this._logger.info(`_registerParticipantListener: tracking changes for ${request.participant.id}`);
        this._controlRequestSubscription = request.participant.changed(() => {
            if (request.participant.state !== request.participantState) {
                request.participantState = request.participant.state;
                switch (request.participantState) {
                    case ParticipantState.Connected:
                        this.event('sharingIncomingControlRequest').raise(request.participant.id);
                        break;
                    case ParticipantState.None:
                    case ParticipantState.OnHold:
                    case ParticipantState.Disconnected:
                        this.denyControlRequest().catch(err => {
                            this._logger.info(`it is ok if denyControlRequest rejects here as screensharing may have just been stopped. Err is ${err}`);
                        });
                        this._logger.info(`_registerParticipantListener: participant ${request.participant.id} is no longer connected. resetting`);
                        this._clearControlRequest();
                        this._raiseControlRequestCanceled(request);
                        break;
                    case ParticipantState.Connecting:
                    case ParticipantState.Ringing:
                    case ParticipantState.EarlyMedia:
                    case ParticipantState.InLobby:
                        break;
                    default:
                        this._logger.error(`unexpected participantState {request.participantState} unhandled`);
                }
            }
        });
    }

    private _resetControllerParticipant(participantLeg?: ParticipantLeg): void {
        if (this._controllerParticipantSubscription) {
            this._logger.info(`_resetControllerParticipant: stopping tracking changes for ${this._controllerParticipant.participant.id}`);
            this._controllerParticipantSubscription.dispose();
            this._controllerParticipantSubscription = null;
        }
        if (participantLeg) {
            this._logger.info(`_resetControllerParticipant: tracking changes for ${participantLeg.participant.id}`);
            this._controllerParticipantSubscription = participantLeg.participant.changed(() => {
                if (participantLeg.participant.state !== ParticipantState.Connected) {
                    this.handleParticipantRemoved(participantLeg.participant.id);
                }
            });
        }
        this._controllerParticipant = participantLeg;
    }

    public handleParticipantRemoved(participantId: string): void {
        if (this._participantSubscriptions[participantId]) {
            this._logger.info(`handleParticipantRemoved(): Stopping tracking state changes for ${participantId}`);
            this._participantSubscriptions[participantId].dispose();
            delete this._participantSubscriptions[participantId];
        }

        // A participant was removed, check if it is controller and if so, teardown the control session
        if (this._isSharing()) {
            if (this._controllerParticipant && this._controllerParticipant.participant.id === participantId) {  // note: this is not the per endpoint participantId but MRI
                this._logger.info('handleParticipantRemoved: controller left call, tearing down control session');
                // The controller left the call, teardown the control session
                this._teardownSharerRemoteControl();
                this.controlState = ControlState.None;
                this._resetControllerParticipant();

                // Notify app that control terminated
                const controlInfo: SharingControlInfo = {
                    inControl: false,
                    id: participantId,
                    terminatedReason: ControlTerminatedReason.SharerTerminated
                };
                this.event('sharingControlChanged').raise(controlInfo);
            }

            // Notify app that this participant is no longer control capable
            this._raiseScreenSharingControlCapableEvent(false, participantId);
        } else {
            // viewing
            if (this._sharerParticipant && this._sharerParticipant.id === participantId && this.controlState === ControlState.LocalControlling) {
                // The sharer left the call while we were in control, do some cleanup
                this._logger.info('handleParticipantRemoved: sharer left call, tearing down control session');
                this._teardownViewerRemoteControl();
                this.controlState = ControlState.None;

                // Notify app
                const controlInfo: SharingControlInfo = {
                    inControl: false,
                    id: LOCAL_PARTICIPANT_MRI,
                    terminatedReason: ControlTerminatedReason.ViewerTerminated
                };
                this.event('sharingControlChanged').raise(controlInfo);
            }
        }
    }

    public callStateChanged(state: CallState) {
        this._callState = state;
        this._recordViewerSessionTelemetry(e => e.recordEvent(VIEWER_CONTROL_SESSION_OPERATIONS.CALL_STATE, {state}), false);
        this._recordSharerSessionTelemetry(e => e.recordEvent(SHARER_CONTROL_SESSION_OPERATIONS.CALL_STATE, {state}), false);

        if (state === CallState.Disconnected) {
            this._handleCallDisconnected();
        }
    }

    private _handleCallDisconnected(): void {
        this._logger.info('handleCallDisconnected()');

        // Handle any cleanup when call has disconnected
        if (this._isSharing()) {
            this.shutdownControlForSharer();
        } else {
            this.shutdownControlForViewer();
        }
    }

    private _videoSizeChanged(width: number, height: number) {
        if (this._controlCapturer != null) {
            this._controlCapturer.updateVideoSize(width, height);
        }
    }

    private _onRenderStarted(): void {
        const data = { action: ControlAction.RenderedAtViewer, terminatedReason: ControlTerminatedReason.None };
        if (this._sharerParticipant) {
            const sharerSourceId = this._getSharerDataSourceId();
            this._sendRequest(data, sharerSourceId);
        } else {
            this._logger.error(`Attempted to send sharing rendered message to null sharerParticipant`);
        }
    }

    private _setViewingRenderer(screenSharingRenderer: SlimCoreElectronRemoteVideoRenderer, container: HTMLElement): void {
        try {
            // Save the new renderer and create the capturer
            this._screenSharingVideoRenderer = screenSharingRenderer;
            this._screenSharingVideoRendererSubscriptions.push(
                this._screenSharingVideoRenderer.on('videoSizeChanged', (width, height) => this._videoSizeChanged(width, height))
            );
            this._screenSharingVideoRendererSubscriptions.push(
                this._screenSharingVideoRenderer.on('renderStarted', () => this._onRenderStarted())
            );
            this._controlCapturer = new SlimCoreElectronControlCapturer(this._logger, container);

            if (this.controlState === ControlState.LocalControlling) {
                // We were already in control (could be that user switched from viewing
                // while in control, to chat), and the renderer has changed, let's continue
                // being in control
                this._logger.info('_setViewingRenderer: detected local state is controlling');
                this._setupViewerRemoteControl();
            } else {
                // @TODO: For now, go to disabled - eventually go to detection mode
                // when we support pointers
                this._teardownViewerRemoteControl();
            }
        } catch (error) {
            this._logger.error(`_setViewingRenderer: unable to set _screenSharingVideoRenderer error = ${error}`);
        }
    }

    private _isSharing(): boolean {
        return (this.role === SharingControlRole.Sharer);
    }

    private _isViewing(): boolean {
        return (this.role === SharingControlRole.Viewer);
    }

    private _createAndAttachControlDevices(): void {
        this._logger.info('_createAndAttachControlDevices()');
        if (!SlimCoreCommon.hasCreateDataSource(this._slimcoreInstance) || !SlimCoreCommon.hasCreateDataSink(this._slimcoreInstance)) {
            this._logger.warn('createDataSource or createDataSink not implemented');
            return;
        }

        if (this._dataChannel) {

            let registerDataSource: Promise<void> = Promise.resolve<void>();
            let registerDataSink: Promise<void> = Promise.resolve<void>();
            // Create/register source/sink
            if (!this._controlDataSource) {
                this._controlDataSource = SlimCoreCommon.createDataSource(this._slimcoreInstance, SlimCore.Enums.DataDeviceId.Control);
                this._logger.info('created Control DataSource');

                registerDataSource = this._dataChannel.registerDataSource(this._controlDataSource)
                    .then(() => this._logger.info('registered Control DataSource'))
                    .catch(error => this._logger.error(`registerDataSource threw exception: ${error}`));
            }

            if (!this._controlDataSink) {
                this._controlDataSink = SlimCoreCommon.createDataSink(this._slimcoreInstance, SlimCore.Enums.DataDeviceId.Control);
                this._logger.info('created Control DataSink');

                registerDataSink = this._dataChannel.registerDataSink(this._controlDataSink)
                    .then(() => this._logger.info('registered Control DataSink'))
                    .catch(error => this._logger.error(`registerDataSink threw exception: ${error}`));
            }

            Promise.all([registerDataSource, registerDataSink]).then(() => {
                this.controlDataChannelStatus.readyForControl = true;
                this.event('controlDataChannelStateChanged').raise(this.controlDataChannelStatus);
            });

            this._controlDataSink.on('data', ((args: SlimCore.DataSink.Events.DataArgs) => {
                this._controlInjector.injectRawInput(args.data, args.sourceID).catch(error => {
                    this._lastInjectRawInputErrorCount++;
                    if (this._injectRawInputErrorTimer === null) {
                        this._injectRawInputErrorTimer = setTimeout(() => {

                            const controlError: SharingControlError = {
                                reason: SharingControlErrorReason.InjectRawInputError,
                                detail: JSON.stringify({ dataChannelId: this._dataChannel.id, sourceId: args && args.sourceID, count: this._lastInjectRawInputErrorCount, errorMsg: error })
                            };
                            this.event('sharingControlError').raise(controlError);

                            this._lastInjectRawInputErrorCount = 0;
                            this._injectRawInputErrorTimer = null;
                        }, INPUT_RAW_ERROR_REPORTING_WAIT_TIMEOUT);
                    }
                });
            }));
        } else {
            this._logger.warn('_createAndAttachControlDevices() null dataChannel');
        }
    }

    private _detachAndDeleteControlDevices(): void {
        this._logger.info('_detachAndDeleteControlDevices()');

        // Remove data callback
        if (this._controlDataSink) {
            this._controlDataSink.removeAllListeners('data');
        }

        if (this._dataChannel) {
            // Unregister the source/sink
            if (this._controlDataSource) {

                this._dataChannel.unregisterDataSource(this._controlDataSource)
                    .then(() => this._logger.info('Control DataSource unregistered'))
                    .catch(error => this._logger.warn(`unregisterDataSource threw exception: ${error}`));

                this._controlDataSource = null;
                this._logger.info('deleted Control data source');
            }

            if (this._controlDataSink) {

                this._dataChannel.unregisterDataSink(this._controlDataSink)
                    .then(() => this._logger.info('Control DataSink unregistered'))
                    .catch(error => this._logger.warn(`unregisterDataSink threw exception: ${error}`));

                this._controlDataSink = null;
                this._logger.info('deleted Control data sink');
            }
        } else {
            this._logger.warn('_detachAndDeleteControlDevices() null dataChannel');
        }

    }

    public onDataChannelStatusChanged(dataChannelStatus: number): Promise<void> {
        return async.asap(() => this._onDataChannelStatusChanged(dataChannelStatus));
    }

    private _onDataChannelStatusChanged(dataChannelStatus: number): void {
        this._logger.info(`Data channel status = ${dataChannelStatus}`);
        this.controlDataChannelStatus.readyForControl = this.controlDataChannelStatus.readyForControl && dataChannelStatus === SlimCore.Enums.DataChannelStatus.Active;
        this.controlDataChannelStatus.state = SlimCoreElectronDataChannel._mapDataChannelStatus(dataChannelStatus);
        this.event('controlDataChannelStateChanged').raise(this.controlDataChannelStatus);

        // 1. When call is established, sharer/viewer will get DataChannelStatus.Available status change
        // 2. When sharer calls Start(), sharer gets status change to 'Starting', then 'Active',
        // while viewer gets status change to 'Active'
        // 3. When sharer calls Stop(), sharer gets status change to 'Stopping', then 'Available',
        // while viewer gets status change to 'Available'
        // So, this handler effectively only handles 'Active' and 'Available' status changes since both
        // will get raised on sharer & viewer:

        if (dataChannelStatus === SlimCore.Enums.DataChannelStatus.Active) {

            if (this._enabled) {
                if (this._dataChannel && !this._dataChannelActive) {
                    this._dataChannelActive = true;
                    this._recordViewerSessionTelemetry(e => e.recordEvent(VIEWER_CONTROL_SESSION_OPERATIONS.DATA_CHANNEL_AVAILABLE), false);
                    this._recordSharerSessionTelemetry(e => e.recordEvent(SHARER_CONTROL_SESSION_OPERATIONS.DATA_CHANNEL_AVAILABLE), false);
                    // create and attach devices
                    this._createAndAttachControlDevices();

                    if (this._isViewing()) {
                        if (!this._dataChannelAvailableSent) {
                            if (this._sharerParticipant) {
                                // Send an 'Available' message to sharer
                                this._logger.info('_onDataChannelStatusChanged() sending Available message to sharer');
                                this._startAvailableHandshake();
                            } else {
                                this._logger.error('Unexpected null _sharerParticipant trying to send Available to sharer');
                            }
                        }
                    } else if (this._isSharing()) {
                        // Notify app that local participant can use the control/pointer feature
                        this._raiseScreenSharingControlCapableEvent(true, LOCAL_PARTICIPANT_MRI);
                    }
                }
            }
        } else {
            if (this._dataChannel && this._dataChannelActive) {
                this._recordViewerSessionTelemetry(e => e.recordEvent(VIEWER_CONTROL_SESSION_OPERATIONS.DATA_CHANNEL_UNAVAILABLE), false);
                this._recordSharerSessionTelemetry(e => e.recordEvent(SHARER_CONTROL_SESSION_OPERATIONS.DATA_CHANNEL_UNAVAILABLE), false);

                // reset some state
                this._dataChannelActive = false;
                this._dataChannelAvailableSent = false;
                if (this._terminateAvailableHandshake) {
                    this._terminateAvailableHandshake(AvailableHandshakeTerminatedReason.DataChannel);
                }

                // detach any devices
                this._detachAndDeleteControlDevices();

                // Raise an event indicating that local user cannot do control/pointers
                this._raiseScreenSharingControlCapableEvent(false, LOCAL_PARTICIPANT_MRI);
            }
        }
    }

    // Rejecting Ack will have either SharerControlDisabled, SharerDenied, UnknownSender as ControlTerminatedReason
    private _handleAvailableRequest(controlMessage: ControlMessage, sender: ICallParticipant, senderSourceId: number): void {
        let rejectAck = false;  // if true, reject available request: AvailableNack
        let controlTerminatedReason: ControlTerminatedReason = ControlTerminatedReason.None;

        if (this._enabled === false) {
            this._logger.info('feature flag is off. no need to answer the available ack');
            rejectAck = true;
            controlTerminatedReason = ControlTerminatedReason.SharerControlDisabled;
        } else if (this._availableAckEnabled === false) {
            this._logger.info('available ack is off. no need to answer the available ack');
            rejectAck = true;
            controlTerminatedReason = ControlTerminatedReason.SharerControlDisabled;
        } else {
            if (sender && sender.id) {
                if (this._allowControlForUser && typeof (this._allowControlForUser) === typeof (Function)) {
                    this._allowControlForUser(sender)
                        .then((allowControl: boolean) => {
                            if (!allowControl) {
                                rejectAck = true;
                                controlTerminatedReason = ControlTerminatedReason.SharerDenied;
                            }

                            this._sendResponseToAvailableRequest(controlMessage, sender, senderSourceId, rejectAck, controlTerminatedReason);
                        });
                    return;
                }
                // else if this._allowControlForUser is not valid we allow the ack
            } else {
                rejectAck = true;
                controlTerminatedReason = ControlTerminatedReason.UnknownSender;
            }
        }
        this._sendResponseToAvailableRequest(controlMessage, sender, senderSourceId, rejectAck, controlTerminatedReason);
    }

    private _sendResponseToAvailableRequest(
        controlMessage: ControlMessage,
        sender: ICallParticipant,
        senderSourceId: number,
        rejectAck: boolean,
        controlTerminatedReason: ControlTerminatedReason): void {
        if (rejectAck) {
            this._logger.info(`Rejecting Available for handShake:${senderSourceId}:${controlMessage.handshakeId}`);
            this.event('controlAvailableHandshake').raise(controlMessage.handshakeId, senderSourceId, AvailableHandshakeStatus.Rejecting, this._getAvailableHandshakeTerminatedFromControlTerminated(controlTerminatedReason));
            this._sendRequest(
                { action: ControlAction.AvailableNack, terminatedReason: controlTerminatedReason, handshakeId: controlMessage.handshakeId }, senderSourceId);
        } else {
            // A participant's data channel is ready to enable pointers/control
            this._raiseScreenSharingControlCapableEvent(true, sender.id);
            if (!this._participantSubscriptions[sender.id]) {
                this._logger.info(`Tracking participant status for ${sender.id}`);
                this._participantSubscriptions[sender.id] = sender.changed(() => {
                    if (sender.state !== ParticipantState.Connected) {
                        this._logger.info(`Participant ${sender.id} is not connected. removing`);
                        this.handleParticipantRemoved(sender.id);
                    }
                });
            }

            this._logger.info(`Acking Available for handShake:${senderSourceId}:${controlMessage.handshakeId}`);
            this.event('controlAvailableHandshake').raise(controlMessage.handshakeId, senderSourceId, AvailableHandshakeStatus.Acking);

            // Send an ack
            this._sendRequest(
                { action: ControlAction.AvailableAck, terminatedReason: ControlTerminatedReason.None, handshakeId: controlMessage.handshakeId }, senderSourceId);
        }
    }

    public processProtocolMessage(message: string, sender: ICallParticipant, senderParticipantId: string, senderSourceId: number): Promise<void> {
        return async.asap(() => this._processProtocolMessage(message, sender, senderParticipantId, senderSourceId));
    }

    private _raiseControlRequestCanceled(controlRequest: ParticipantLeg): void {
        this.event('sharingIncomingControlRequestCancelled').raise(controlRequest.participant.id);
    }

    public _processProtocolMessage(message: string, sender: ICallParticipant, senderParticipantId: string, senderSourceId: number): void {
        // Parse the message:
        let controlMessage: ControlMessage;

        this._recordProtocolMessage(sender, senderSourceId);

        try {
            controlMessage = JSON.parse(message);
        } catch (err) {
            this._logger.error('Error parsing controlMessage');
            return;
        }

        this._logger.info(`_processProtocolMessage action=${controlMessage.action} sender=${senderSourceId}`);
        if (!sender) {
            this._logger.warn(`protocol message from unknown sender with sourceId=${senderSourceId}`);
        }

        if (controlMessage.action === ControlAction.Available) {
            this._handleAvailableRequest(controlMessage, sender, senderSourceId);
        } else if (controlMessage.action === ControlAction.ControlRequest) {
            if (this._enabled === false) {
                this._logger.warn('Sharer control is disabled - rejecting request');
                this._sendRequest(
                    { action: ControlAction.RejectRequest, terminatedReason: ControlTerminatedReason.SharerControlDisabled }, senderSourceId);
            } else if (this._controlRequest) {
                this._logger.warn('Sharer is already processing a control request...rejecting new requests');
                this._sendRequest(
                    { action: ControlAction.RejectRequest, terminatedReason: ControlTerminatedReason.SharerBusy }, senderSourceId);
            } else if (this._controllerParticipant && this._controllerParticipant.participant === sender) {
                if (this._controllerParticipant.participantId && this._controllerParticipant.participantId !== senderParticipantId) {
                    // TODO(robedum) allow controller to switch / use multiple endpoints?
                    // for now we just reject the second endpoint. the user must stop control on the other endpoint first.
                    this._logger.warn(`rejecting control request from second endpoint. participantId: ${senderParticipantId}`);
                    this._sendRequest(
                        { action: ControlAction.RejectRequest, terminatedReason: ControlTerminatedReason.SharerBusy }, senderSourceId);
                } else {
                    this._logger.warn('Got control request for someone already in control');
                    this._sendRequest(
                        { action: ControlAction.AcceptRequest, terminatedReason: ControlTerminatedReason.None }, senderSourceId);
                }
            } else {
                if (sender) {
                    this._resetControlRequest({ participant: sender, participantState: sender.state, participantId: senderParticipantId });
                } else {
                    this._logger.error('Got ControlRequest message but could not find a sender participant, ignoring');
                }
            }
        } else if (controlMessage.action === ControlAction.AcceptRequest) {

            if (this.controlState === ControlState.RequestSent) {

                // Send ack
                const sent: boolean = this._sendRequest(
                    { action: ControlAction.Ack, terminatedReason: ControlTerminatedReason.None }, senderSourceId);

                if (sent) {
                    this._setupViewerRemoteControl();
                    this.controlState = ControlState.LocalControlling;

                    if (this._requestControlPromise) {
                        this._requestControlPromise.resolve();
                    } else {
                        this._logger.error(`Unexpected, no promise could be resolved - state =ControlState.RequestSent action =${controlMessage.action}`);
                    }
                } else {
                    if (this._requestControlPromise) {
                        this._requestControlPromise.reject(new Error(JSON.stringify({ controlInfo: undefined, details: 'request control is accepted, but fail to send ack' })));
                    } else {
                        this._logger.error(`Unexpected, no promise could be resolved - state =${this.controlState} action =${controlMessage.action}`);
                    }
                }
                this._cancelRequestControlTimer();

            } else {
                this._logger.warn(`AcceptRequest message received in controlState=${this.controlState}`);
            }
        } else if (controlMessage.action === ControlAction.RejectRequest) {

            if (this.controlState === ControlState.RequestSent) {

                this.controlState = ControlState.None;

                const controlInfo: SharingControlInfo = {
                    inControl: false,
                    id: LOCAL_PARTICIPANT_MRI, // local participant
                    terminatedReason: controlMessage.terminatedReason
                };

                if (this._requestControlPromise) {
                    this._requestControlPromise.reject(new Error(JSON.stringify({ controlInfo, details: '' })));
                } else {
                    this._logger.error(`Unexpected, no promise could be resolved - state =ControlState.RequestSent action =${controlMessage.action}`);
                }

                this._cancelRequestControlTimer();
            } else {
                this._logger.warn(`RejectRequest message received in controlState=${this.controlState} reason=${controlMessage.terminatedReason}`);
            }
        } else if (controlMessage.action === ControlAction.CancelControlRequest) {
            if (this._controlRequest) {
                //TODO(robedum) we should check the sender here
                //even without malicious activity another user might concurrently send a request (which is rejected because of the first user's request)
                //and immediately cancel, which would instead cancel the first users request.
                const canceledRequest = this._controlRequest;
                this._clearControlRequest();
                this._raiseControlRequestCanceled(canceledRequest);
            }
        } else if (controlMessage.action === ControlAction.GiveControl) {

            // Send ack
            const sent: boolean = this._sendRequest(
                { action: ControlAction.Ack, terminatedReason: ControlTerminatedReason.None }, senderSourceId);

            if (sent) {
                this._setupViewerRemoteControl();
                this.controlState = ControlState.LocalControlling;

                const controlInfo: SharingControlInfo = {
                    inControl: true,
                    id: LOCAL_PARTICIPANT_MRI, // local participant
                    terminatedReason: controlMessage.terminatedReason
                };
                this.event('sharingControlChanged').raise(controlInfo);
            }

        } else if (controlMessage.action === ControlAction.Ack) {

            if (this.controlState === ControlState.WaitingForControlAck) {

                if (this._controllerParticipant && this._controllerParticipant.participant === sender) {

                    const controllerSourceId: number = this._getDataSourceIdForParticipantLeg(this._controllerParticipant);

                    if (senderSourceId !== controllerSourceId) {
                        // note: even if the ack comes from the same participant, source ids could change during retargets.
                        // TODO(robedum) ensure control uses correct source id
                        this._logger.warn(`acknowledging source id: ${senderSourceId} does not match requesting source id: ${controllerSourceId}`);
                    }

                    this._logger.info(`Setting up remote control for controller source id=${controllerSourceId}`);

                    this._setupSharerRemoteControl(controllerSourceId);
                    this.controlState = ControlState.RemoteControlling;

                    const controlInfo: SharingControlInfo = {
                        inControl: true,
                        id: this._controllerParticipant.participant.id,
                        terminatedReason: controlMessage.terminatedReason
                    };
                    this.event('sharingControlChanged').raise(controlInfo);
                    if (this._grantControlPromise) {
                        this._grantControlPromise.resolve();
                        this._cancelGrantControlTimer();
                    } else if (this._acceptControlPromise) {
                        this._acceptControlPromise.resolve();
                        this._cancelAcceptControlTimer();
                    } else {
                        this._logger.error(`Unexpected, no promise could be resolved - state =ControlState.WaitingForControlAck action =${controlMessage.action}`);
                    }
                } else if (sender) {
                    this._logger.warn(`Ignoring ack from participant that does not match controller id = ${piiUtils.scrubMriOrOmit(sender.id)}`);
                } else {
                    this._logger.warn(`Ignoring ack from null participant source id = ${senderSourceId}`);
                }
            } else if (this.controlState === ControlState.WaitingForTerminateAck) {
                this.controlState = ControlState.None;
                this._resetControllerParticipant();

                if (this._terminateControlPromise) {
                    this._terminateControlPromise.resolve();
                    this._cancelTerminateControlTimer();
                } else {
                    this._logger.error(`Unexpected, no promise could be resolved - state =ControlState.WaitingForTerminateAck action =${controlMessage.action}`);
                }
            } else {
                this._logger.warn(`Unexpected ack in control state = ${this.controlState}`);
            }
        } else if (controlMessage.action === ControlAction.TerminateNoAck ||
            controlMessage.action === ControlAction.Terminate) {

            let controllerId: string;
            if (this._isViewing()) {
                this._teardownViewerRemoteControl();
                controllerId = LOCAL_PARTICIPANT_MRI; // local participant
            } else {
                this._teardownSharerRemoteControl();
                if (sender) {
                    controllerId = sender.id;
                } else {
                    this._logger.error('Got terminate message but could not find a sender participant!');
                }
            }

            this.controlState = ControlState.None;
            this._resetControllerParticipant();

            const controlInfo: SharingControlInfo = {
                inControl: false,
                id: controllerId,
                terminatedReason: controlMessage.terminatedReason
            };
            this.event('sharingControlChanged').raise(controlInfo);

            if (controlMessage.action === ControlAction.Terminate) {
                // Send ack
                this._sendRequest(
                    { action: ControlAction.Ack, terminatedReason: ControlTerminatedReason.None }, senderSourceId);
            }
        } else if (controlMessage.action === ControlAction.AvailableAck) {
            if (this._terminateAvailableHandshake) {
                this._terminateAvailableHandshake(AvailableHandshakeTerminatedReason.Succeeded, controlMessage.handshakeId);
            }

            // Notify app that local participant can use the control/pointer feature
            this._raiseScreenSharingControlCapableEvent(true, LOCAL_PARTICIPANT_MRI);
        } else if (controlMessage.action === ControlAction.AvailableNack) {
            this._recordViewerSessionTelemetry(e => e.updateOperationData(VIEWER_CONTROL_SESSION_OPERATIONS.AVAILABLE_HANDSHAKE, { nackReason: controlMessage.terminatedReason }, controlMessage.handshakeId));
            if (controlMessage.terminatedReason === ControlTerminatedReason.UnknownSender) {
                return;
            }
            if (this._terminateAvailableHandshake) {
                this._terminateAvailableHandshake(this._getAvailableHandshakeTerminatedFromControlTerminated(controlMessage.terminatedReason), controlMessage.handshakeId);
            }
            if (controlMessage.terminatedReason === ControlTerminatedReason.SharerDenied) {
                this._raiseScreenSharingControlCapableEvent(true, LOCAL_PARTICIPANT_MRI, true);
            } else {
                this._raiseScreenSharingControlCapableEvent(false, LOCAL_PARTICIPANT_MRI);
            }
        } else if (controlMessage.action === ControlAction.RenderedAtViewer) {
            if (this._raiseRenderedAtViewer) {
                if (sender) {
                    this._raiseRenderedAtViewer(sender.id);
                } else {
                    this._logger.error('Got RenderedAtViewer message but could not find a sender participant!');
                }
            }
        } else {
            this._logger.error(`Unknown request action received on control protocol data sink, action=${controlMessage.action}`);
        }
    }

    private _getAvailableHandshakeTerminatedFromControlTerminated(reason: ControlTerminatedReason): AvailableHandshakeTerminatedReason {
        switch (reason) {
            case ControlTerminatedReason.UnknownSender:
                return AvailableHandshakeTerminatedReason.UnknownSender;

            case ControlTerminatedReason.SharerDenied:
                return AvailableHandshakeTerminatedReason.SharerDenied;

            case ControlTerminatedReason.SharerControlDisabled:
            default:
                return AvailableHandshakeTerminatedReason.Unknown;
        }
    }

    // Returns true if sent
    private _sendRequest(data: ControlMessage, sourceID: number): boolean {

        let returnVal: boolean = false;
        const dataSource = this._protocolDataSource;

        if (sourceID < 0) {
            this._logger.error(`Unexpected: invalid sourceID=${sourceID}`);
            return returnVal;
        }

        if (dataSource) {
            this._logger.info(`sending control message action=${data.action} to sourceID=${sourceID}`);

            const protocolMessage: DataChannelProtocolMessage = {
                type: DataChannelProtocolType.ScreenSharingControl,
                message: JSON.stringify(data)
            };

            try {
                const recipients: number[] = [sourceID];
                dataSource.sendData(SlimCoreCommon.stringToBuffer(JSON.stringify(protocolMessage)), recipients);
                returnVal = true;
            } catch (error) {
                this._logger.error(`Unexpected: failure in call to sendData. Error=${error}`);

                const controlError: SharingControlError = {
                    reason: SharingControlErrorReason.SendDataError,
                    detail: JSON.stringify({ sourceId: sourceID, errorMsg: error })
                };
                this.event('sharingControlError').raise(controlError);
            }
        } else {
            this._logger.error('Unexpected: null dataSource in _sendRequest');
        }
        return returnVal;
    }

    private _resetControlState() {
        this._logger.info('_resetControlState()');
        this.controlState = ControlState.None;
        this._resetControllerParticipant();
        this._sharerParticipant = null;
        this._clearControlRequest();
        this._cancelRequestControlTimer();
        this._cancelAcceptControlTimer();
        this._cancelGrantControlTimer();
        this._cancelTerminateControlTimer();
    }

    private _setupSharerRemoteControl(sourceId: number) {

        this._logger.info(`_setupSharerRemoteControl() sourceId=${sourceId}`);

        // @TODO: temp code to enter seamless mode
        this._setInjectorMode(InjectorMode.Seamless);

        this._controlInjector.allowSingleController(sourceId).catch(error => {
            const controlError: SharingControlError = {
                reason: SharingControlErrorReason.AllowSingleControllerError,
                detail: JSON.stringify({ sourceId, errorMsg: error })
            };
            this.event('sharingControlError').raise(controlError);
        });
    }

    private _teardownSharerRemoteControl() {

        this._logger.info('_teardownSharerRemoteControl()');

        // Remove the controller from injector
        this._controlInjector.allowSingleController(0).catch(error => {
            const controlError: SharingControlError = {
                reason: SharingControlErrorReason.AllowSingleControllerError,
                detail: JSON.stringify({ sourceId: 0, errorMsg: error })
            };
            this.event('sharingControlError').raise(controlError);
        });

        // @TODO: temporary workaround to remove the avatars
        // once control ends:
        this._setInjectorMode(InjectorMode.Disabled);
    }

    private _setupViewerRemoteControl() {

        this._logger.info('_setupViewerRemoteControl()');

        this._setCapturerMode(CtrlCaptureMode.Control);
    }

    private _teardownViewerRemoteControl() {

        this._logger.info('_teardownViewerRemoteControl()');

        // When viewer stops controlling, we go to detection mode
        // @TODO: For now, go to disabled
        this._setCapturerMode(CtrlCaptureMode.Disabled);
    }

    private _setInjectorMode(injectorMode: InjectorMode) {

        this._logger.info(`_setInjectorMode() mode=${injectorMode}`);

        const injectorConfig: InjectorConfig = {
            mode: injectorMode,
            noMotionTimeoutMs: 0, // don't timeout - always show
            noMotionSharerTimeoutMs: 0 // don't timout - always show
        };
        this._controlInjector.setInjectorConfig(injectorConfig).catch(error => {
            const controlError: SharingControlError = {
                reason: SharingControlErrorReason.SetInjectorConfigError,
                detail: JSON.stringify({ mode: injectorMode, errorMsg: error })
            };
            this.event('sharingControlError').raise(controlError);
        });
    }

    private _clearCapturerEventSubscriptions() {
        this._logger.info('_clearCapturerEventSubscriptions()');

        if (this._captureEventSubscription) {
            this._captureEventSubscription.dispose();
            this._captureEventSubscription = null;
        }
        if (this._mouseControlEventSubscription) {
            this._mouseControlEventSubscription.dispose();
            this._mouseControlEventSubscription = null;
        }
        if (this._keyboardControlEventSubscription) {
            this._keyboardControlEventSubscription.dispose();
            this._keyboardControlEventSubscription = null;
        }
    }

    // Tighten the typing when Teams upgrades to tsc 3.1.6 or above
    // public sendRemoteControlEvent<K extends keyof ISlimCoreElectronControlCapturerEvents>
    //     (type: K, ...data: Parameters<ISlimCoreElectronControlCapturerEvents[K]>) {
    public sendRemoteControlEvent(type: keyof ISlimCoreElectronControlCapturerEvents,
        data: CtrlMouseData | CtrlKbdData) {
        if (!data) {
            this._logger.error('sendRemoteControlEvent: Invalid argument.');
            return;
        }
        switch (type) {
            case 'mouseControlEvent':
                this._sendControlEvent(SlimCoreElectronControlCapturer.formatMouseEvent(data as CtrlMouseData));
                break;
            case 'keyboardControlEvent':
                this._sendControlEvent(SlimCoreElectronControlCapturer.formatKeyboardEvent(data as CtrlKbdData));
                break;
            default:
                this._logger.error('sendRemoteControlEvent: Invalid type = ', type);
                return;
        }
    }

    private _sendControlEvent(data: Uint8Array) {
        if (!this._controlDataSource || !this._sharerParticipant || this.controlState !== ControlState.LocalControlling) {
            this._logger.error('_sendControlEvent: Data channel not set up / Not in control');
            return;
        }

        const sharerSourceId = this._getSharerDataSourceId();
        const recipients: number[] = [sharerSourceId];
        this._controlDataSource.sendData(data, recipients);
    }

    private _setCapturerMode(capturerMode: CtrlCaptureMode) {
        this._logger.info(`_setCapturerMode() capturerMode=${capturerMode}`);

        if (this._controlCapturer) {
            this._controlCapturer.setCaptureMode(capturerMode);

            // Remove existing callbacks
            this._clearCapturerEventSubscriptions();

            if (capturerMode === CtrlCaptureMode.Detection) {
                this._captureEventSubscription = this._controlCapturer.on('ctrlCaptureEvent', ((event: CtrlCaptureEvent) => {
                    if (event === CtrlCaptureEvent.MouseClick) {
                        this.event('sharingRendererClicked').raise();
                    }
                }));
            } else if (capturerMode === CtrlCaptureMode.Pointer) {
                this._captureEventSubscription = this._controlCapturer.on('ctrlCaptureEvent', ((event: CtrlCaptureEvent) => {
                    if (event === CtrlCaptureEvent.MouseEntering) {
                        this.event('sharingRendererMouseEntering').raise();
                    } else if (event === CtrlCaptureEvent.MouseLeaving) {
                        this.event('sharingRendererMouseLeaving').raise();
                    }
                }));
            } else if (capturerMode === CtrlCaptureMode.Control) {
                this._mouseControlEventSubscription = this._controlCapturer.on('mouseControlEvent', ((event: CtrlMouseData) => this._sendControlEvent(SlimCoreElectronControlCapturer.formatMouseEvent(event))));
                this._keyboardControlEventSubscription = this._controlCapturer.on('keyboardControlEvent', ((event: CtrlKbdData) => this._sendControlEvent(SlimCoreElectronControlCapturer.formatKeyboardEvent(event))));
                this._captureEventSubscription = this._controlCapturer.on('ctrlCaptureEvent', ((event: CtrlCaptureEvent) => {
                    if (event === CtrlCaptureEvent.MouseEntering) {
                        this.event('sharingRendererMouseEntering').raise();
                    } else if (event === CtrlCaptureEvent.MouseLeaving) {
                        this.event('sharingRendererMouseLeaving').raise();
                    }
                }));
            }
        } else {
            this._logger.warn('_controlCapturer not found when trying to setup viewer capture mode');
        }
    }

    public setPointerImage(participant: ICallParticipant, base64str: string): Promise<void> {
        this._logger.warn('setPointerImage is deprecated. Use setLocalPointerImage or setRemotePointerImage');

        if (!participant) {
            return this.setLocalPointerImage(base64str);
        } else {
            return this.setRemotePointerImage(participant, base64str);
        }
    }

    public setLocalPointerImage(base64str: string): Promise<void> {
        if (base64str.length === 0) {
            return Promise.reject(new Error('setLocalPointerImage invalid image length'));
        }

        return this._setPointerImage(0, base64str);
    }

    public async setRemotePointerImage(participant: ICallParticipant, base64str: string): Promise<void> {
        if (!participant) {
            throw new Error('setRemotePointerImage participant is null');
        }

        if (base64str.length === 0) {
            throw new Error('setRemotePointerImage invalid image length');
        }

        if (!(participant as SlimCoreElectronCallParticipant).enableParticipantLegToSourceIdMapping) {
            // fallback to old logic: we will only support one endpoint
            const sourceId = this._mapParticipantToSourceId(participant);
            if (sourceId < 0) {
                throw new Error('setRemotePointerImage unable to map participant to sourceId');
            }

            await this._setPointerImage(sourceId, base64str);
            return;
        }

        const sourceIds = this._getParticipantsDataSourceIds(participant);

        if (sourceIds.length === 0) {
            throw new Error('setRemotePointerImage unable to map participant to sourceIds');
        }

        this._logger.info(`set pointer image for ${sourceIds.length} sources`);

        // TODO(robedum) update when necessary
        //
        // this is called when an available handshake is acked and we raise SharingControlCapability.
        // currently we just set the image for all endpoints of the user since we don't know the sourceId.
        //
        // we could add the sourceId to the SharingControlCapability event to allow it to be passed back to us
        // or we could store the sourceId list to take a diff between the current and the last time and update accordingly.
        // we might also want to update the sourceIds if they change during a retarget (diff when we do a roster update).
        //
        // alternatively we could set the pointer image only once an endpoint takes control.
        // we'd save resources and it we'd obviously know which sourceId to use.
        for (const sourceId of sourceIds) {
            await this._setPointerImage(sourceId, base64str);
        }

        // we might want to use Promise.all instead if setAvatar is reentrant
        // or another setRemotePointerImage might sneak in while above loop yields.
        //await Promise.all(sourceIds.map(sourceId => this._setPointerImage(sourceId, base64str)));
    }

    private _setPointerImage(sourceId: number, base64str: string): Promise<void> {

        if (!this._isSharing()) {
            this._logger.warn('_setPointerImage called when not sharing');
        }
        return this._controlInjector.setAvatar(SlimCoreCommon.stringToBuffer(base64str), sourceId);
    }

    public startPointerMode(): Promise<void> {

        if (!this._dataChannelActive) {
            return Promise.reject(new Error('startPointerMode when data channel not active'));
        } else if (this.controlState !== ControlState.None) {
            return Promise.reject(new Error(`startPointerMode called in bad control state=${this.controlState}`));
        } else if (!this._isViewing()) {
            return Promise.reject(new Error('startPointerMode when not viewing'));
        }
        return async.asap(() => this._setCapturerMode(CtrlCaptureMode.Pointer));
    }

    public stopPointerMode(): Promise<void> {

        if (this.controlState !== ControlState.None) {
            return Promise.reject(new Error(`stopPointerMode called in bad control state=${this.controlState}`));
        } else if (!this._isViewing()) {
            return Promise.reject(new Error('stopPointerMode when not viewing'));
        }

        return async.asap(() => this._setCapturerMode(CtrlCaptureMode.Detection));
    }

    private _startAvailableHandshake(): void {
        // Caller makes sure we are in an acceptable state to send
        const sharerSourceId: number = this._getSharerDataSourceId();

        if (sharerSourceId < 0) {
            this._logger.warn(`sharer: ${this._sharerParticipant} has no data channel`);
            // return; TODO(robedum) we should retry only once the sharer has a data channel.
            // For now we just ignore this to let the retry logic fix this.
            // Simply bailing here might break the case in which the sharer adds the data channel later.
        }

        const handshakeId: string = generateGuid();
        this._logger.info(`_startAvailableHandshake() - handshakeId: ${sharerSourceId}:${handshakeId}`);
        this.event('controlAvailableHandshake').raise(handshakeId, sharerSourceId, AvailableHandshakeStatus.Sending);

        if (this._terminateAvailableHandshake) {
            this._logger.warn('Replacing existing availble handshake');
            this._terminateAvailableHandshake(AvailableHandshakeTerminatedReason.Replaced);
        }

        this._recordViewerSessionTelemetry(e => e.recordOperation(VIEWER_CONTROL_SESSION_OPERATIONS.AVAILABLE_HANDSHAKE, handshakeId));

        const sent: boolean = this._sendRequest(
            { action: ControlAction.Available, terminatedReason: ControlTerminatedReason.None, handshakeId }, sharerSourceId);
        if (!sent) {
            this._logger.error('Failed to send Available message to sharer, will retry after backoff');
        }
        this._dataChannelAvailableSent = true; // set to true even if send failed so we know we are trying to send

        // Start a timer in case we never get a response - so we know to retry
        this._waitForAvailableAck(handshakeId);

        this._terminateAvailableHandshake = (reason = AvailableHandshakeTerminatedReason.Unknown, ackHandshakeId?: string) => {
            this._logger.info(`_terminateAvailableHandshake() - handshakeId: ${sharerSourceId}:${handshakeId}, status: ${reason}`);

            this._recordViewerSessionTelemetry(e => e.updateOperationData(VIEWER_CONTROL_SESSION_OPERATIONS.AVAILABLE_HANDSHAKE, { tries: this._retryAttempt }, ackHandshakeId));
            if (reason === AvailableHandshakeTerminatedReason.Succeeded) {
                this._recordViewerSessionTelemetry(e => e.recordOperationSuccess(VIEWER_CONTROL_SESSION_OPERATIONS.AVAILABLE_HANDSHAKE, null, '', ackHandshakeId));
            } else {
                this._recordViewerSessionTelemetry(e => e.recordOperationFailure(VIEWER_CONTROL_SESSION_OPERATIONS.AVAILABLE_HANDSHAKE, {  reason }, '', ackHandshakeId));
            }

            this._terminateAvailableHandshake = null;

            this.event('controlAvailableHandshake').raise(
                handshakeId,
                sharerSourceId,
                reason === AvailableHandshakeTerminatedReason.Succeeded ? AvailableHandshakeStatus.Succeeded : AvailableHandshakeStatus.Terminated,
                reason
            );

            if (ackHandshakeId && ackHandshakeId !== handshakeId) {
                this._logger.warn(`AvailableSeries: Got handshake from another series - expected: ${handshakeId}, received: ${ackHandshakeId}`);
            }

            if (this._availableAckTimer) {
                clearTimeout(this._availableAckTimer);
                this._availableAckTimer = null;
            }
            this._retryAttempt = 0;
        };
    }

    private _waitForAvailableAck(handshakeId: string): void {
        this._logger.info('_waitForAvailableAck()');

        this._availableAckTimer = setTimeout(() => {
            this._logger.warn(`No Ack received for Available message from attempt=${this._retryAttempt}`);
            this._availableAckTimer = null;

            const sharerSourceId: number = this._getSharerDataSourceId();
            if (this._retryAttempt <= AVAILABLE_RETRY_LIMIT) {
                // Let's continue retrying until we reach the retry limit
                this._logger.info(`Re-sending Available message handshakeId: ${sharerSourceId}:${handshakeId}`);
                this.event('controlAvailableHandshake').raise(handshakeId, sharerSourceId, AvailableHandshakeStatus.Resending);

                this._sendRequest(
                    { action: ControlAction.Available, terminatedReason: ControlTerminatedReason.None, handshakeId }, sharerSourceId);
                this._waitForAvailableAck(handshakeId);
            } else {
                this._logger.error('Reached Available retry limit! Control capability will be false');
                if (this._terminateAvailableHandshake) {
                    this._terminateAvailableHandshake(AvailableHandshakeTerminatedReason.TimedOut);
                } else {
                    // Raise generic error when handshake already terminated
                    const controlError: SharingControlError = {
                        reason: SharingControlErrorReason.AvailableAckTimeout,
                        detail: JSON.stringify({ sourceId: sharerSourceId, errorMsg: `Timed out after ${this._retryAttempt} tries` })
                    };
                    this.event('sharingControlError').raise(controlError);
                }
            }
        }, (AVAILABLE_ACK_WAIT_TIMEOUT * ++this._retryAttempt));
    }

    public requestControl(): Promise<void> {

        if (!this._dataChannelActive) {
            return Promise.reject(new Error('requestControl when data channel not active'));
        } else if (this.controlState !== ControlState.None) {
            return Promise.reject(new Error(`requestControl called in bad state=${this.controlState}`));
        } else if (!this._isViewing()) {
            return Promise.reject(new Error('requestControl when not viewing'));
        } else if (!this._sharerParticipant) {
            return Promise.reject(new Error('requestControl unexpected null sharer'));
        }

        if (this._requestControlPromise) {
            return Promise.reject(new Error('request control promise has not been resolved yet while requesting control'));
        }
        if (this._terminateControlPromise) {
            return Promise.reject(new Error('terminate control promise has not been resolved yet while requesting control'));
        }

        return this._requestControl();
    }

    private _requestControl(): Promise<void> {

        this._logger.info('_requestControl()');

        this._requestControlPromise = async.defer<void>();
        const always = () => {
            this._requestControlPromise = null;
        };
        this._requestControlPromise.promise.then(always, always);

        // find the sharer's source id:
        const sharerSourceId: number = this._getSharerDataSourceId();

        const sent: boolean = this._sendRequest(
            { action: ControlAction.ControlRequest, terminatedReason: ControlTerminatedReason.None }, sharerSourceId);
        if (!sent) {
            // Failed to send the request
            const controlInfo: SharingControlInfo = {
                inControl: false,
                id: LOCAL_PARTICIPANT_MRI, // local participant
                terminatedReason: ControlTerminatedReason.DataChannelError
            };

            this._requestControlPromise.reject(new Error());
            return Promise.reject(new Error(JSON.stringify({ controlInfo, details: '' })));
        }

        this.controlState = ControlState.RequestSent;

        // Start a timer in case we never get a response
        this._requestControlTimer = setTimeout(() => {
            if (this.controlState === ControlState.RequestSent) {

                this.controlState = ControlState.None;

                // Didn't get any response, let's cancel the request
                this._logger.warn('No response to control request - canceling');
                const sent: boolean = this._sendRequest(
                    { action: ControlAction.CancelControlRequest, terminatedReason: ControlTerminatedReason.SharerNoResponse }, sharerSourceId);
                if (!sent) {
                    this._logger.error('fail to send the cancel control request');
                }

                // Raise an event notifying that the request was 'rejected'
                const controlInfo: SharingControlInfo = {
                    inControl: false,
                    id: LOCAL_PARTICIPANT_MRI, // local participant
                    terminatedReason: ControlTerminatedReason.SharerNoResponse
                };

                if (this._requestControlPromise) {
                    this._requestControlPromise.reject(new Error(JSON.stringify({ controlInfo, details: 'we did not get any response to the control - terminating control' })));
                } else {
                    this._logger.error('Unexpected, no promise could be resolved - controlState = ControlState.RequestSent');
                }
            } else {
                if (this._requestControlPromise) {
                    this._requestControlPromise.reject(new Error(JSON.stringify({ controlInfo: undefined, details: 'request control - times out in bad state - do nothing' })));
                } else {
                    this._logger.error(`Unexpected, no promise could be resolved - controlState = ${this.controlState}`);
                }
            }
            this._requestControlTimer = null;
        }, CONTROL_REQUEST_WAIT_TIMEOUT);

        return this._requestControlPromise.promise;
    }

    private _cancelRequestControlTimer(): void {
        if (this._requestControlTimer) {
            if (this._requestControlPromise) {
                this._requestControlPromise.reject(new Error('cancelled by _cancelRequestControlTimer'));
            }
            clearTimeout(this._requestControlTimer);
            this._requestControlTimer = null;
        }
    }

    public cancelControl(): Promise<void> {

        if (!this._dataChannelActive) {
            return Promise.reject(new Error('cancelControl when data channel not active'));
        } else if (this.controlState !== ControlState.RequestSent) {
            return Promise.reject(new Error(`cancelControl called in bad state=${this.controlState}`));
        } else if (!this._isViewing()) {
            return Promise.reject(new Error('cancelControl when not viewing'));
        } else if (!this._sharerParticipant) {
            return Promise.reject(new Error('cancelControl unexpected null sharer'));
        }

        // cancel only could happen if request control promise is not resolved yet.
        if (!this._requestControlPromise) {
            return Promise.reject(new Error('request control promise has been resolved while cancelling control'));
        }
        if (this._terminateControlPromise) {
            return Promise.reject(new Error('terminate control promise has not been resolved yet while cancelling control'));
        }

        return this._cancelControl();
    }

    private _cancelControl(): Promise<void> {

        this._logger.info('_cancelControl()');

        // find the sharer's source id:
        const sharerSourceId: number = this._getSharerDataSourceId();

        if (this.controlState === ControlState.RequestSent) {

            this._cancelRequestControlTimer();

            this.controlState = ControlState.None;

            // Viewer cancel the control
            this._logger.info('Viewer cancel the control');
            const sent: boolean = this._sendRequest(
                { action: ControlAction.CancelControlRequest, terminatedReason: ControlTerminatedReason.ViewerCancelled }, sharerSourceId);

            // Raise an event notifying that the request was 'cancelled'
            if (sent) {
                const controlInfo: SharingControlInfo = {
                    inControl: false,
                    id: LOCAL_PARTICIPANT_MRI, // local participant
                    terminatedReason: ControlTerminatedReason.ViewerCancelled
                };

                this._requestControlPromise.reject(new Error(JSON.stringify({ controlInfo, details: '' })));
            } else {
                return Promise.reject(new Error('fail to send cancel control request'));
            }
        } else {
            return Promise.reject(new Error('cancel control - in bad state - do nothing'));
        }

        return new Promise<void>((resolve, reject) => { resolve(); });
    }

    public acceptControlRequest(): Promise<void> {

        if (!this._dataChannelActive) {
            return Promise.reject(new Error('acceptControlRequest when data channel not active'));
        } else if (!this._controlRequest) {
            return Promise.reject(new Error('acceptControlRequest while no control request pending'));
        } else if (!this._isSharing()) {
            return Promise.reject(new Error('acceptControlRequest when not sharing'));
        }

        if (this._acceptControlPromise) {
            return Promise.reject(new Error('accept control promise has not been resolved yet while accepting control'));
        }
        if (this._grantControlPromise) {
            return Promise.reject(new Error('grant control promise has not been resolved yet while accepting control'));
        }
        if (this._terminateControlPromise) {
            return Promise.reject(new Error('terminate control promise has not been resolved yet while accepting control'));
        }

        return this._acceptControlRequest();
    }

    private _acceptControlRequest(): Promise<void> {

        this._logger.info('_acceptControlRequest()');

        this._acceptControlPromise = async.defer<void>();
        const always = () => {
            this._acceptControlPromise = null;
        };
        this._acceptControlPromise.promise.then(always, always);

        if (this._controllerParticipant) {
            // We are trying to accept someone's control request while someone already has control
            this._logger.warn('_acceptControlRequest called while another participant already has control - terminating control');

            // Stop control on sharer side
            this._teardownSharerRemoteControl();

            const controllerSourceId: number = this._getDataSourceIdForParticipantLeg(this._controllerParticipant);

            const sent: boolean = this._sendRequest(
                { action: ControlAction.TerminateNoAck, terminatedReason: ControlTerminatedReason.SharerTerminated }, controllerSourceId);
            if (!sent) {
                this._logger.error('fail to send the terminateNoAck request');
            }
            this._resetControllerParticipant();
        }

        const controlRequesterSourceId: number = this._getDataSourceIdForParticipantLeg(this._controlRequest);
        const controlRequesterId: string = this._controlRequest.participant.id;

        // Send the accept control message
        const sent: boolean = this._sendRequest(
            { action: ControlAction.AcceptRequest, terminatedReason: ControlTerminatedReason.None }, controlRequesterSourceId);

        if (!sent) {
            this._logger.error('fail to send the accept control request');
            this._acceptControlPromise.reject(new Error());
            return Promise.reject(new Error('fail to send the accept control request!'));
        }

        this._resetControllerParticipant(this._controlRequest);
        this._clearControlRequest();
        this.controlState = ControlState.WaitingForControlAck;

        // Start ack timer before we setup the injector
        this._acceptControlTimer = setTimeout(() => {
            if (this.controlState === ControlState.WaitingForControlAck) {

                this.controlState = ControlState.None;
                this._resetControllerParticipant();

                // Didn't get any ack, we shouldn't give the control
                this._logger.error('No ack received when accepting control request - terminating control');
                const sent: boolean = this._sendRequest(
                    { action: ControlAction.TerminateNoAck, terminatedReason: ControlTerminatedReason.AckTimeout }, controlRequesterSourceId);
                if (!sent) {
                    this._logger.error('fail to send the TerminateNoAck request');
                }

                const controlInfo: SharingControlInfo = {
                    inControl: false,
                    id: controlRequesterId,
                    terminatedReason: ControlTerminatedReason.AckTimeout
                };
                this.event('sharingControlChanged').raise(controlInfo);
                if (this._acceptControlPromise) {
                    this._acceptControlPromise.reject(new Error('No ack received when accept control - terminating control'));
                } else {
                    this._logger.error('Unexpected, no promise could be resolved - controlState = ControlState.WaitingForControlAck');
                }
            } else {
                if (this._acceptControlPromise) {
                    this._acceptControlPromise.reject(new Error('accept control - times out in bad state - do nothing'));
                } else {
                    this._logger.error(`Unexpected, no promise could be resolved - controlState = ${this.controlState}`);
                }
            }
            this._acceptControlTimer = null;
        }, CONTROL_ACK_WAIT_TIMEOUT);

        return this._acceptControlPromise.promise;
    }

    private _cancelAcceptControlTimer(): void {
        if (this._acceptControlTimer) {
            if (this._acceptControlPromise) {
                this._acceptControlPromise.reject(new Error('cancelled by _cancelAcceptControlTimer'));
            }
            clearTimeout(this._acceptControlTimer);
            this._acceptControlTimer = null;
        }
    }

    public denyControlRequest(): Promise<void> {

        if (!this._dataChannelActive) {
            return Promise.reject(new Error('denyControlRequest when data channel not active'));
        } else if (!this._controlRequest) {
            return Promise.reject(new Error('denyControlRequest while no control request pending'));
        } else if (!this._isSharing()) {
            return Promise.reject(new Error('denyControlRequest when not sharing'));
        }

        if (this._acceptControlPromise) {
            return Promise.reject(new Error('accept control promise has not been resolved yet while denying control'));
        }
        if (this._grantControlPromise) {
            return Promise.reject(new Error('grant control promise has not been resolved yet while denying control'));
        }
        if (this._terminateControlPromise) {
            return Promise.reject(new Error('terminate control promise has not been resolved yet while denying control'));
        }

        return this._denyControlRequest();
    }

    private _denyControlRequest(): Promise<void> {

        this._logger.info('_denyControlRequest()');

        const controlRequesterSourceId: number = this._getDataSourceIdForParticipantLeg(this._controlRequest);
        this._clearControlRequest();

        // Send the reject control message
        const sent: boolean = this._sendRequest(
            { action: ControlAction.RejectRequest, terminatedReason: ControlTerminatedReason.SharerDenied }, controlRequesterSourceId);

        if (!sent) {
            return Promise.reject(new Error('fail to send the deny control requst'));
        }

        return new Promise<void>((resolve, reject) => { resolve(); });
    }

    public grantControl(participant: ICallParticipant): Promise<void> {

        if (!this._dataChannelActive) {
            return Promise.reject(new Error('grantControl when data channel not active'));
        } else if (this._controlRequest) {
            return Promise.reject(new Error('grantControl called while control request pending'));
        } else if (!this._isSharing()) {
            return Promise.reject(new Error('grantControl when not sharing'));
        } else if (!participant) {
            return Promise.reject(new Error('grantControl null participant'));
        }

        if (this._acceptControlPromise) {
            return Promise.reject(new Error('accept control promise has not been resolved yet while granting control'));
        }
        if (this._grantControlPromise) {
            return Promise.reject(new Error('grant control promise has not been resolved yet while granting control'));
        }
        if (this._terminateControlPromise) {
            return Promise.reject(new Error('terminate control promise has not been resolved yet while granting control'));
        }

        return this._grantControl(participant);
    }

    private _grantControl(participant: ICallParticipant): Promise<void> {

        this._logger.info('_grantControl()');

        this._grantControlPromise = async.defer<void>();
        const always = () => {
            this._grantControlPromise = null;
        };
        this._grantControlPromise.promise.then(always, always);

        if (this.controlState === ControlState.RemoteControlling) {
            if (this._controllerParticipant.participant === participant) {
                // note if this api would specify the participantId, we'd have to update the comparison here to allow switching endpoint.
                // this does not seem like a reasonable scenario instead the controller should be able to decide which endpoint to use.

                // We are trying to grant control to someone who already has control
                this._logger.warn('grantControl called for participant who already has control');
                this._grantControlPromise.resolve();
                return new Promise<void>((resolve, reject) => { resolve(); });
            } else {
                // Someone else has control, we have to terminate their control before we can
                // give control to another
                this._logger.warn('_grantControl called while another participant already has control - terminating control');

                // Stop control on sharer side
                this._teardownSharerRemoteControl();

                const controllerSourceId: number = this._getDataSourceIdForParticipantLeg(this._controllerParticipant);
                const sent: boolean = this._sendRequest(
                    { action: ControlAction.TerminateNoAck, terminatedReason: ControlTerminatedReason.SharerTerminated }, controllerSourceId);
                if (!sent) {
                    this._logger.error('fail to send the terminateNoAck request');
                }
            }
        }

        // TODO(robedum) we need to know which endpoint or we need to let the controller decide which endpoint to use.
        // we could send a request to all of the specified user's endpoints and let him decide or use multiple endpoints.
        //
        // for now we just randomly choose the first endpoint with a data channel and stick with it.
        const participantSourceId: number = this._mapParticipantToSourceId(participant);

        // Send the give control message
        const sent: boolean = this._sendRequest(
            { action: ControlAction.GiveControl, terminatedReason: ControlTerminatedReason.None }, participantSourceId);

        if (!sent) {
            this._logger.error('fail to send the grant control request');
            this._grantControlPromise.reject(new Error());
            return Promise.reject(new Error('fail to send the grant control request!'));
        }

        // define the controlling participant leg based on the chosen source id
        const participantId: string = this._getParticipantIdForDataSourceId(participant, participantSourceId);
        this._resetControllerParticipant({ participant, participantState: participant.state, participantId });
        this.controlState = ControlState.WaitingForControlAck;

        // Start ack timer before we setup the injector
        this._grantControlTimer = setTimeout(() => {
            if (this.controlState === ControlState.WaitingForControlAck) {

                this.controlState = ControlState.None;
                this._resetControllerParticipant();

                // Didn't get any ack, we shouldn't give the control
                this._logger.error('No ack received when granting control - terminating control');
                const sent: boolean = this._sendRequest(
                    { action: ControlAction.TerminateNoAck, terminatedReason: ControlTerminatedReason.AckTimeout }, participantSourceId);
                if (!sent) {
                    this._logger.error('fail to send the terminateNoAck request');
                }

                if (this._grantControlPromise) {
                    this._grantControlPromise.reject(new Error('No ack received when granting control - terminating control'));
                } else {
                    this._logger.error('Unexpected, no promise could be resolved - controlState = ControlState.WaitingForControlAck');
                }
            } else {
                if (this._grantControlPromise) {
                    this._grantControlPromise.reject(new Error('grant control - times out in bad state - do nothing'));
                } else {
                    this._logger.error(`Unexpected, no promise could be resolved - controlState = ${this.controlState}`);
                }
            }
            this._grantControlTimer = null;
        }, CONTROL_ACK_WAIT_TIMEOUT);

        return this._grantControlPromise.promise;
    }

    private _cancelGrantControlTimer(): void {
        if (this._grantControlTimer) {
            if (this._grantControlPromise) {
                this._grantControlPromise.reject(new Error('cancelled by _cancelGrantControlTimer'));
            }
            clearTimeout(this._grantControlTimer);
            this._grantControlTimer = null;
        }
    }

    private _mapParticipantToSourceId(participant: ICallParticipant): number {

        // note: the check for _dataChannel is a refactoring artifact.
        // previously this was wrapped by the SlimCoreElectronDataChannel.
        // TODO(robedum) we should be able to remove it if we verify that this does not cause regressions.

        if (this._dataChannel) {
            return participant.getSourceIdForMediaType(MediaType.Data);
        } else {
            this._logger.error('Null data channel cannot map participant to source id');
            return -1;
        }
    }

    private _getParticipantsDataSourceIds(participant: ICallParticipant): number[] {

        const result : number[] = [];

        try {
            if (participant && participant.endpoints && participant.endpoints.endpointDetails) {

                // return DATA_SOURCE_ID_ANY if we're in a 1-1 call
                // TODO(robedum) we should base this on call type rather than the
                // old logic inspired from SlimCoreElectronCallParticipant.getSourceIdForMediaType
                if (participant.endpoints.endpointDetails.length === 1) {
                    const endpoint = participant.endpoints.endpointDetails[0];
                    if (!endpoint || !endpoint.mediaStreams) {
                        result.push(DATA_SOURCE_ID_ANY);
                        return result;
                    }
                }

                for (const endpoint of participant.endpoints.endpointDetails) {
                    if (endpoint && endpoint.mediaStreams) {
                        for (const stream of endpoint.mediaStreams) {
                            if (SlimCoreCommon.mapMediaTypeStringToMediaType(stream.type) === MediaType.Data) {
                                result.push(stream.sourceId);

                                if (result.length >= 8) {
                                    this._logger.error('excessive amount of data streams');  // being paranoid
                                    return result;
                                }

                                break;
                            }
                        }
                    }
                }
            }

        } catch (error) {
            this._logger.error(`_getParticipantsDataSourceIds caught exception error=${error}`);
        }

        return result;
    }

    private _getParticipantIdForDataSourceId(participant: ICallParticipant, sourceId: number): string {
        const p: SlimCoreElectronCallParticipant = participant as SlimCoreElectronCallParticipant;
        return p.getParticipantIdForSourceId(MediaType.Data, sourceId);
    }

    private _getDataSourceIdForParticipantId(participant: ICallParticipant, participantId: string): number {

        // note: the check for _dataChannel is a refactoring artifact.
        // previously this was wrapped by the SlimCoreElectronDataChannel.
        // TODO(robedum) we should be able to remove it if we verify that this does not cause regressions.

        if (this._dataChannel) {
            const p: SlimCoreElectronCallParticipant = participant as SlimCoreElectronCallParticipant;
            return p.getSourceId(participantId, MediaType.Data);
        } else {
            this._logger.error('Null data channel cannot map participant to source id');
            return -1;
        }
    }

    private _getDataSourceIdForParticipantLeg(participantLeg: ParticipantLeg): number {
        return this._getDataSourceIdForParticipantId(participantLeg.participant, participantLeg.participantId);
    }

    private _getSharerDataSourceId(): number {
        const p: SlimCoreElectronCallParticipant = this._sharerParticipant as SlimCoreElectronCallParticipant;
        const participantId =  p.getSharingParticipantLeg();

        if (!participantId) {
            // fall back to old logic to randomly pick the first endpoint with a data channel
            // TODO(robedum) remove?
            this._logger.warn('unable to identify the sharer participantId, falling back to first data channel');
            return this._mapParticipantToSourceId(this._sharerParticipant);
        }

        return this._getDataSourceIdForParticipantId(this._sharerParticipant, participantId);
    }

    public terminateControl(): Promise<void> {

        if (this._isViewing()) {
            if (this.controlState !== ControlState.LocalControlling) {
                return Promise.reject(new Error('terminateControl called while not in control'));
            } else if (!this._sharerParticipant) {
                return Promise.reject(new Error('terminateControl called but _sharerParticipant is null'));
            }
        } else if (this._isSharing()) {
            if (!this._controllerParticipant) {
                return Promise.reject(new Error('terminateControl called but controllerParticipant is null'));
            }
        } else {
            return Promise.reject(new Error('terminateControl called but not sharing nor viewing'));
        }

        if (this._requestControlPromise) {
            return Promise.reject(new Error('request control promise has not been resolved yet while terminating control'));
        }

        if (this._acceptControlPromise) {
            return Promise.reject(new Error('accept control promise has not been resolved yet while terminating control'));
        }
        if (this._grantControlPromise) {
            return Promise.reject(new Error('grant control promise has not been resolved yet while terminating control'));
        }

        if (this._terminateControlPromise) {
            return Promise.reject(new Error('terminate control promise has not been resolved yet while terminating control'));
        }

        return this._terminateControl();
    }

    private _terminateControl(waitForAck = true): Promise<void> {

        this._logger.info('_terminateControl()');

        this._terminateControlPromise = async.defer<void>();
        const always = () => {
            this._terminateControlPromise = null;
        };
        this._terminateControlPromise.promise.then(always, always);

        let reason: ControlTerminatedReason = ControlTerminatedReason.None;
        const controlAction: ControlAction = waitForAck ? ControlAction.Terminate : ControlAction.TerminateNoAck;
        let recipientSourceId: number;
        let controllerId: string;

        if (this._isViewing()) {
            if (this.controlState === ControlState.LocalControlling) {
                // Local participant is viewing
                this._teardownViewerRemoteControl();
                reason = ControlTerminatedReason.ViewerTerminated;

                // We should send the terminate message to sharer
                recipientSourceId = this._getSharerDataSourceId();
                controllerId = LOCAL_PARTICIPANT_MRI; // local participant is controller
            } else {
                this._logger.error('Local viewer called terminateControl when not controlling');
                this._terminateControlPromise.reject(new Error());
                return Promise.reject(new Error('Local viewer called terminateControl when not controlling'));
            }
        } else {
            if (this.controlState === ControlState.RemoteControlling &&
                this._controllerParticipant) {
                // Stop control on sharer side
                this._teardownSharerRemoteControl();

                // Local participant is sharing
                reason = ControlTerminatedReason.SharerTerminated;

                // We should send the terminate message to sharer
                recipientSourceId = this._getDataSourceIdForParticipantLeg(this._controllerParticipant);
                controllerId = this._controllerParticipant.participant.id; // controller
            } else {
                this._logger.error('Local sherer called terminateControl when no one is in control');
                this._terminateControlPromise.reject(new Error());
                return Promise.reject(new Error('Local sherer called terminateControl when no one is in control'));
            }
        }

        const sent: boolean = this._sendRequest(
            { action: controlAction, terminatedReason: reason }, recipientSourceId);
        if (!sent) {
            if (waitForAck) {
                this._logger.error('fail to send the terminate control request');
                this._terminateControlPromise.reject(new Error());
                return Promise.reject(new Error('fail to send the grant control request!'));
            } else {
                this._logger.error('fail to send the terminateNoAck request');
            }
        }

        this.controlState = waitForAck ? ControlState.WaitingForTerminateAck : ControlState.None;

        const controlInfo: SharingControlInfo = {
            inControl: false,
            id: controllerId,
            terminatedReason: reason
        };
        this.event('sharingControlChanged').raise(controlInfo);

        if (waitForAck) {
            this._terminateControlTimer = setTimeout(() => {
                if (this.controlState === ControlState.WaitingForTerminateAck) {

                    this.controlState = ControlState.None;
                    this._resetControllerParticipant();

                    // Didn't get any ack, we'll just resend the terminate
                    this._logger.error('No ack received when terminating control - resending terminate request');
                    const sent: boolean = this._sendRequest(
                        { action: ControlAction.TerminateNoAck, terminatedReason: ControlTerminatedReason.AckTimeout }, recipientSourceId);
                    if (!sent) {
                        this._logger.error('fail to send the terminateNoAck request');
                    }

                    if (this._terminateControlPromise) {
                        this._terminateControlPromise.reject(new Error('No ack received when terminating control - resending terminate request'));
                    } else {
                        this._logger.error('Unexpected, no promise could be resolved - controlState = ControlState.WaitingForTerminateAck');
                    }
                } else {
                    if (this._terminateControlPromise) {
                        this._terminateControlPromise.reject(new Error('terminate control - times out in bad state - do nothing'));
                    } else {
                        this._logger.error(`Unexpected, no promise could be resolved - controlState = ${this.controlState}`);
                    }
                }
                this._terminateControlTimer = null;
            }, CONTROL_ACK_WAIT_TIMEOUT);
        } else {
            this._terminateControlPromise.resolve();
            return new Promise<void>((resolve, reject) => { resolve(); });
        }

        return this._terminateControlPromise.promise;
    }

    private _cancelTerminateControlTimer(): void {
        if (this._terminateControlTimer) {
            if (this._terminateControlPromise) {
                this._terminateControlPromise.reject(new Error('cancelled by _cancelTerminateControlTimer'));
            }
            clearTimeout(this._terminateControlTimer);
            this._terminateControlTimer = null;
        }
    }

    private _disposeControlCapturer() {
        this._logger.info('_disposeControlCapturer');

        this._clearCapturerEventSubscriptions();

        if (this._controlCapturer) {
            this._controlCapturer.dispose();
            this._controlCapturer = null;
        }
    }

    public dispose() {
        this._logger.info('SlimCoreElectronScreenSharingControl.dispose()');

        this._detachAndDeleteControlDevices();
        this._resetControlState();
        if (this._dataChannel) {
            // Don't dispose here, SlimCoreElectronDataChannel owns
            // the SlimCore.DataChannel and will call dispose
            this._dataChannel = null;
        }
        this._disposeRenderer();

        this._cancelRequestControlTimer();
        this._cancelAcceptControlTimer();
        this._cancelGrantControlTimer();
        this._cancelTerminateControlTimer();

        if (this._terminateAvailableHandshake) {
            this._terminateAvailableHandshake(AvailableHandshakeTerminatedReason.Shutdown);
        }

        this._resetParticipantSubscriptions();

        this._finalizeViewerSessionTelemetry(VIEWER_CONTROL_SESSION_OPERATIONS.SESSION_SHUTDOWN);
        this._finalizeSharerSessionTelemetry(SHARER_CONTROL_SESSION_OPERATIONS.SESSION_SHUTDOWN);

        super.dispose();
    }
}



