import { LogComponent, LogFactory } from '@skype/rt-js-bindings';
import { isFunction, isObject } from 'lodash';
import { CallingLogger } from 'skype-calling-interface';

export class TsCallingLogger implements CallingLogger {
    private _getPrefix: () => string;
    private logComponent: LogComponent;

    public constructor(private ulLogComponent: string, private ulSafeComponent: boolean, prefix: string | (() => string) = '') {
        this._getPrefix = isFunction(prefix) ? prefix : (() => prefix);
        this.logComponent = LogFactory.instance().component(this.ulLogComponent);
        LogFactory.instance().declareComponentSafe(this.ulLogComponent, ulSafeComponent);
    }

    public createChild(prefix: string | (() => string)): CallingLogger {
        const getChildPrefix = isFunction(prefix) ? prefix : (() => prefix);
        return new TsCallingLogger(
            this.ulLogComponent,
            this.ulSafeComponent,
            () => this._getPrefix() ? `${this._getPrefix()}/${getChildPrefix()}` : getChildPrefix()
        );
    }

    public log(...args: any[]): void {
        this._apply(log => this.logComponent.debug2(null, log), args);
    }

    public debug(...args: any[]): void {
        this._apply(log => this.logComponent.debug4(null, log), args);
    }

    public info(...args: any[]): void {
        this._apply(log => this.logComponent.debug1(null, log), args);
    }

    public warn(...args: any[]): void {
        this._apply(log => this.logComponent.warn(null, log), args);
    }

    public error(...args: any[]): void {
        this._apply(log => this.logComponent.error(null, log), args);
    }

    private _apply(logFunction: (log: string) => void, args: any[] = []): void {
        this._addPrefix(args);
        const message = args
            .map(arg => (arg instanceof DOMException) ? arg.toString() : arg)
            .map(arg => isObject(arg) ? JSON.stringify(arg) : arg)
            .join(', ');
        logFunction(message);
    }

    private _addPrefix(args: any[]): void {
        if (args && args[0]) {
            const format = `${this._getPrefix()} ${args[0]}`;
            args[0] = format;
        }
    }
}



