/// <reference path="../typings/slimcore-enums.d.ts" />

import {
    CauseId, IDisposable, VideoDeviceDesc
} from 'skype-calling-interface';

import * as _ from 'lodash';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronDeviceManager from './SlimCoreElectronDeviceManager';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { generateCauseId } from '../../common/ts/causeId';
import { IInternalLogger } from '../../common/ts/internalLogger';
import SlimCoreElectronCall from './SlimCoreElectronCall';
const VIDEO_OBJECT_WAIT_TIMEOUT = 500;

export default class SlimCoreElectronLocalVideo extends SlimCoreElectronBase<{}> implements IDisposable {

    public id: number;
    public negotiationTag: string;

    private _videoObjectId = 0;
    private _currentStartPromise: Promise<void>;
    private _currentStopPromise: Promise<void>;
    private _currentCamera: VideoDeviceDesc;
    private _internalVideoStartPromise: Promise<void>;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _call: SlimCoreElectronCall,
        private _callObjectId: number,
        private _callHandler: SlimCoreCommon.CallHandler,
        private _deviceManager: SlimCoreElectronDeviceManager
    ) {
        super(logger.createChild(() => `LocalVideo[${this._videoObjectId}]`), settings, engine);
        this._logger.info('constructor');

        this._registerDisposable(this._deviceManager.on('devicesChanged', () => this._devicesChanged()));
    }

    private _devicesChanged() {
        const causeId = generateCauseId();
        const fnLogger = this._logger.createFnLogger('devicesChanged', causeId);
        fnLogger.info('Devices changed - checking if video needs to be restarted');
        const selectedDevices = this._deviceManager.getSelectedDevices();

        if (this._currentCamera && selectedDevices.camera && selectedDevices.camera !== this._currentCamera.id) {
            fnLogger.info(`Changing video device to ${selectedDevices.camera}`);

            this.stop(undefined, causeId)
                .then(() => this.start(causeId, this.negotiationTag))
                .catch(e => {
                    fnLogger.logFailure(`Caught error while trying to stop\start local video after device change. Error: ${e}`);
                });
        }
    }

    private _executeOperationAndExpectVideoStateTransition(transitionTriggerFunc: Function, videoObjectId: number, expectedFinalVideoStates: number[], causeId: CauseId) {
        const fnLogger = this._logger.createFnLogger('LocalVideoOperation', causeId);
        return new Promise<void>((resolve, reject) => {
            let done = false;
            let videoStatusChangedCallback: IDisposable = null;

            const onVideoObjectStateChanged = (value: Enums.VideoStatus) => {
                fnLogger.info(`Video state changed for monitored video ID[${videoObjectId}] -> ${value} (${Enums.VideoStatus[value]})`);

                if (!done && _.indexOf(expectedFinalVideoStates, value) !== -1) {
                    done = true;
                    if (videoStatusChangedCallback) {
                        videoStatusChangedCallback.dispose();
                        videoStatusChangedCallback = null;
                    }
                    resolve();
                }
            };

            setTimeout(() => {
                if (!done) {
                    if (videoStatusChangedCallback) {
                        videoStatusChangedCallback.dispose();
                        videoStatusChangedCallback = null;
                    }

                    const currentState = this._getIntProperty(this._callHandler, videoObjectId, Enums.Property.VideoStatus);
                    fnLogger.info(`_executeOperationAndExpectVideoStateTransition[${videoObjectId}] time out. Current State = ${currentState}.`);

                    if (_.indexOf(expectedFinalVideoStates, currentState) !== -1) {
                        resolve();
                    } else {
                        const errorMessage = `_executeOperationAndExpectVideoStateTransition[${videoObjectId}] time out. Video not in expected final state`;
                        fnLogger.info(errorMessage);
                        reject(errorMessage);
                    }
                }
            }, VIDEO_OBJECT_WAIT_TIMEOUT);

            // Subscribe to object changes
            videoStatusChangedCallback = this._slimcoreInstance.handle(
                'object-property-changed',
                { objectId: videoObjectId, propKey: Enums.Property.VideoStatus },
                data => onVideoObjectStateChanged(data.value as number));

            transitionTriggerFunc();
        });
    }

    private _startInternalVideo(negotiationTag: string, causeId: CauseId) {
        if (this._internalVideoStartPromise) {
            return this._internalVideoStartPromise;
        }
        const fnLogger = this._logger.createFnLogger('startInternalVideo', causeId);
        this._internalVideoStartPromise = Promise.resolve<void>(undefined)
            .then(() => {
                const selectedCamera = this._deviceManager.getSelectedCamera();
                if (!selectedCamera) { throw new Error('No camera selected to start video on'); }

                fnLogger.info(`Starting local video using camera: ${JSON.stringify(selectedCamera)}`);

                this._currentCamera = selectedCamera;

                this._videoObjectId = this._slimcoreInstance.createLocalVideo(Enums.VideoType.Video, selectedCamera.label, selectedCamera.id);
                if (this._videoObjectId === 0) {
                    fnLogger.logFailure('SlimCore returned invalid video object ID 0');
                    throw new Error('SlimCore returned invalid video object ID 0');
                }

                this._callHandler.callAttachSendVideo(this._callObjectId, this._videoObjectId);
            })
            .then(() => SlimCoreCommon.waitForVideoObjectToBeAvailable(this._slimcoreInstance, this._callHandler, this._videoObjectId, this._logger))
            .then(() => {
                fnLogger.info(`Local Video is AVAILABLE. Starting Video`);

                // reject promise if local video does not go to Running state
                return this._executeOperationAndExpectVideoStateTransition(() => {
                    if (negotiationTag) {
                        this.negotiationTag = negotiationTag;
                    }
                    this._slimcoreInstance.videoStart(this._videoObjectId, this.negotiationTag);
                }, this._videoObjectId, [Enums.VideoStatus.Running, Enums.VideoStatus.Starting], causeId);
            });

        return this._internalVideoStartPromise;
    }

    public start(causeId: CauseId, negotiationIag?: string): Promise<void> {
        const fnLogger = this._logger.createFnLogger('start', causeId);
        fnLogger.info('Local video START requested');

        if (!this._call.callStateCanToggleVideo()) {
            fnLogger.logFailure('Local video start requested but call is not connected, skipping operation');
            return Promise.resolve(null);
        }

        if (this._currentStartPromise) {
            fnLogger.logSuccess(`Start requested but pending start exists. Returning previous promise`);
            return this._currentStartPromise;
        }

        let initialPromise: Promise<void>;
        // do a synchronous check to avoid start/stop deadlock, if there's a pending stop, wait for that to finish first
        if (this._currentStopPromise) {
            fnLogger.info(`Start requested but pending stop exists. Waiting for previous stop promise`);
            initialPromise = this._currentStopPromise;
        } else {
            initialPromise = Promise.resolve<void>();
        }

        this._currentStartPromise = initialPromise
            .then(() => this._startInternalVideo(negotiationIag, causeId))
            .then(() => {
                this._currentStartPromise = null;
            })
            .catch(error => {
                fnLogger.logFailure(error);
                this._currentStartPromise = null;
                this.stop(undefined, causeId);
                throw error;
            });

        return this._currentStartPromise;
    }

    public stop(causeId: CauseId, negotiationIag?: string): Promise<void> {
        const fnLogger = this._logger.createFnLogger('stop', causeId);
        fnLogger.info('Local video STOP requested');

        const always = () => {

            this._videoObjectId = 0;

            this._currentCamera = null;
            this._currentStopPromise = null;
            this._currentStartPromise = null;
            this._internalVideoStartPromise = null;
        };

        if (this._currentStopPromise) {
            fnLogger.info(`Stop requested but pending stop exists. Returning previous promise`);
            return this._currentStopPromise;
        }

        let initialPromise: Promise<void>;
        // do a synchronous check to avoid start/stop deadlock, if there's a pending start, wait for that to finish first
        if (this._currentStartPromise) {
            fnLogger.info(`Waiting for start promise to finish.`);
            initialPromise = this._currentStartPromise;
        } else {
            initialPromise = Promise.resolve<void>();
        }

        this._currentStopPromise = initialPromise
            .then(() => {
                this._logger.info('Stopping SlimCore video objects');

                if (this._videoObjectId !== 0) {
                    this.negotiationTag = negotiationIag;
                    this._slimcoreInstance.videoStop(this._videoObjectId, this.negotiationTag);
                }
            })
            .then(() => {
                if (this._call.callStateCanToggleVideo()) {
                    return SlimCoreCommon.waitForVideoObjectToBeAvailable(this._slimcoreInstance, this._callHandler, this._videoObjectId, this._logger);
                } else {
                    return Promise.resolve<void>();
                }
            })
            .then(() => {
                fnLogger.info('VideoBinding was released');
            })
            .then(always, error => {
                fnLogger.logFailure(error);
                always();
                throw error;
            });

        return this._currentStopPromise;
    }

    public dumpVideoSourceImages(): Promise<number> {
        if (this._videoObjectId === 0) {
            return Promise.reject(new Error('Cannot dump video effects debug images when video is not created'));
        }

        return this._slimcoreInstance.dumpVideoSourceImages(this._videoObjectId);
    }

    public dispose(causeId: CauseId) {
        const always = () => super.dispose();
        Promise.resolve(() => this._videoObjectId !== 0 ? this.stop(undefined, causeId) : true)
            .then(always).catch(always);
    }
}



