import { CauseId, IDisposable } from 'skype-calling-interface';

import { IInternalLogger } from '../../common/ts/internalLogger';
import ObservableBase from '../../common/ts/observableBase';
import * as SlimCoreCommon from './SlimCoreCommon';

type ObjectPropertyChangedCallback = SlimCore.EventHandler<SlimCore.Engine.Events.ObjectPropertyChangedArgs>;

abstract class SlimCoreElectronBase<T> extends ObservableBase<T> implements IDisposable {

    private _disposables: IDisposable[] = [];
    private _objectPropertyChangedHandlers: Map<SlimCore.ObjectId, Map<SlimCore.Enums.Property, ObjectPropertyChangedCallback>> = new Map();

    protected constructor(
        protected _logger: IInternalLogger,
        protected _settings: SlimCoreCommon.Settings,
        protected _slimcoreInstance: SlimCoreCommon.Engine
    ) {
        super(_logger);
    }

    public dispose(causeId?: CauseId) {
        this._logger.info('dispose');

        this._disposables.forEach(disposable => disposable.dispose(causeId));
        this._disposables = [];

        this._objectPropertyChangedHandlers.clear();

        super.dispose();
    }

    protected _registerDisposable(disposable: IDisposable) {
        if (!disposable) {
            this._logger.error(`Attempt to register null disposable, ignoring`);
            return;
        }
        this._disposables.push(disposable);
    }

    protected _getProperties<T extends SlimCore.StrProperties, U extends SlimCore.IntProperties>(callHandler: SlimCore.CallHandler, objectId: SlimCore.ObjectId, strProperties: T, intProperties: U): SlimCore.PropertyResults<T, U>  {
        if (callHandler.getProperties && this._settings.enableNativeGetProperties) {
            return callHandler.getProperties(objectId, strProperties, intProperties);
        }

        const result = {};

        for (const key of Object.keys(strProperties)) {
            const item = strProperties[key];
            result[key] = SlimCoreCommon.getStrProperty(callHandler, this._logger, item.objectId || objectId, item.propKey, item.fallback);
        }

        for (const key of Object.keys(intProperties)) {
            const item = intProperties[key];
            result[key] = SlimCoreCommon.getIntProperty(callHandler, this._logger, item.objectId || objectId, item.propKey, item.fallback);
        }

        return result as SlimCore.PropertyResults<T, U>;
    }

    protected _getStrProperty(callHandler: SlimCore.CallHandler, objectId: SlimCore.ObjectId, propertyKey: SlimCore.Enums.Property, fallback?: string): string {
        return SlimCoreCommon.getStrProperty(callHandler, this._logger, objectId, propertyKey, fallback);
    }

    protected _getIntProperty(callHandler: SlimCore.CallHandler, objectId: SlimCore.ObjectId, propertyKey: SlimCore.Enums.Property, fallback?: number): number {
        return SlimCoreCommon.getIntProperty(callHandler, this._logger, objectId, propertyKey, fallback);
    }

    protected _onObjectPropertyChanged(objectId: SlimCore.ObjectId, propKey: SlimCore.Enums.Property, listener: ObjectPropertyChangedCallback) {
        if (!this._objectPropertyChangedHandlers.has(objectId)) {
            this._objectPropertyChangedHandlers.set(objectId, new Map());

            this._registerDisposable(this._slimcoreInstance.handle(
                'object-property-changed',
                { objectId },
                data => this._onObjectPropertyChangedCallback(data)
            ));
        }

        const handlers = this._objectPropertyChangedHandlers.get(objectId);
        handlers.set(propKey, listener);
    }

    protected _onObjectPropertyChangedCallback(data: SlimCore.Engine.Events.ObjectPropertyChangedArgs) {
        const handlers = this._objectPropertyChangedHandlers.get(data.objectId);
        if (!handlers) { return; }

        const handler = handlers.get(data.propKey);

        if (this._settings.enableSlimCoreEventsCounting) {
            const propKey = SlimCore.Enums.Property[data.propKey] || data.propKey;
            const objectType = SlimCore.Enums.ObjectType[data.objectType] || data.objectType;
            this._logger.debug(`_onObjectPropertyChangedCallback - objectId: ${data.objectId}, objectType: ${objectType}, propKey: ${propKey}, value: ${data.value}, handler: ${!!handler}`);
        }

        if (handler) {
            handler.call(this, data);
        }
    }

    protected raiseChanged(): void {
        try {
            super.raiseChanged();
        } catch (err) {
            this._logger.error(`Change handling error: ${err}`);
        }
    }

    protected _raiseEventImpl(eventName: string, ...args: any[]): void {
        try {
            super._raiseEventImpl(eventName, ...args);
        } catch (err) {
            this._logger.error(`Event '${eventName}' handling error: ${err}`);
        }
    }
}

export default SlimCoreElectronBase;



