import { CallParticipantEndpoints } from 'skype-calling-interface';

export const getStreamInformationFromEndpoints = (endpoints: CallParticipantEndpoints) => {
        const data: { [key: string]: any } = [];
        if (!endpoints || !endpoints.endpointDetails.length) {
            return null;
        }
        try {
            endpoints.endpointDetails
                .filter(endpoint => !!endpoint.mediaStreams && endpoint.mediaStreams.length)
                .forEach(endpoint => {
                    const streams = endpoint.mediaStreams.reduce((acc, stream) => {
                        acc[stream.type] = stream.direction;
                        return acc;
                    }, {});
                    data.push({
                        endpointId: endpoint.endpointId,
                        participantId: endpoint.participantId,
                        ...streams
                    });
                });
        } catch (e) {
            return [];
        }
        return data;
    };



