import { CallingLogger, DataSendFunction, ICallParticipant, IDataChannelAdapter, IDataChannelHandler } from 'skype-calling-interface';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronDataChannel, { IMediaDataChannelUser } from './SlimCoreElectronDataChannel';
interface HandlerMap {
    [dataId: number]: HandlerDetails;
}

// Holder object for data channel
class HandlerDetails {
    public dataSource: SlimCore.DataSource;
    public dataSink: SlimCore.DataSink;
    public isAttached: boolean = false;
    public listener: SlimCore.EventHandler<SlimCore.DataSink.Events.DataArgs>;
    public constructor(public readonly dataId: number, public handler: IDataChannelHandler, public readonly sourceSinkInternallyCreated: boolean) {

    }
}

export interface IInternalDataChannelAdapter extends IDataChannelAdapter {
    setInternalSourceSink(dataId: number, dataSource: SlimCore.DataSource, dataSink: SlimCore.DataSink): void;
}

// Adapter class to expose an interface for the clients to
// manage data channel implementations at their end rather than having to add business logic at this layer
export default class SlimCoreElectronDataChannelAdapter implements IMediaDataChannelUser, IInternalDataChannelAdapter {
    private _dataChannel: SlimCoreElectronDataChannel = null;
    private _dataChannelStatus: number = SlimCore.Enums.DataChannelStatus.Unknown;
    private _handlers: HandlerMap = {};
    private _availableHandlers = 0; // Should track complete details
    private _dataChannelStartCalled = false;
    private _negotiationTag: string;

    public constructor(private logger: CallingLogger, private _slimcoreInstance: SlimCoreCommon.Engine) {
        this.setInternalSourceSink = this.setInternalSourceSink.bind(this);
    }
    // Client requests for the data channel handler to be added
    public addHandler(dataId: number, handler: IDataChannelHandler, skipDataSourceSinkCreation: boolean = false, negotiationTag?: string): Promise<void> {
        if (dataId < 0 || dataId >= 64) {
            return Promise.reject(new Error(`Data Id ${dataId} is outside the valid range of 0-63`));
        }
        if (this._handlers.hasOwnProperty(dataId) && this._handlers[dataId].handler) {
            return Promise.reject(new Error(`Data Id ${dataId} is already registered to a non-null handler`));
        }

        this.logger.info(`Adding handler to dataId: ${dataId}, ${skipDataSourceSinkCreation ? 'skipping source sink creation' : 'making source sink'}`);

        let details: HandlerDetails = null;
        let shouldAttach = false;
        if (dataId in this._handlers) { // if handlerDetails exists, it was added internally
            details = this._handlers[dataId];
            details.handler = handler;
            shouldAttach = true;
        } else {
            details = new HandlerDetails(dataId, handler, skipDataSourceSinkCreation);
            this._handlers[dataId] = details;
            if (!skipDataSourceSinkCreation) {
                shouldAttach = true;
            }
        }

        if (shouldAttach) { // if we've completed a handler
            this._availableHandlers++;

            // This may be the first handler after data channel became available, we should start data channel
            // - note that once we start the data channel, the handler(s) will be attached in onDataChannelStatusChanged()
            if (this._isDataChannelAvailable) {
                // Only the last negotiationTag is cached even if addHandler is called multiple times. The negotiationTag
                // is anyway generated by the client and data channel is never stopped and restarted in a call so this should be OK.
                // So essentially if data channel is never started, then the tag used will be from the first call to addHandler.
                // If data channel is already started then the last cached tag is used. The client must ensure to update the tag
                // any time the tag changes.
                this._negotiationTag = negotiationTag;
                this.start();
            }

            // If the data channel is already active, just attach this handler
            if (this._isDataChannelActive) {
                return this._attachHandler(details);
            }
        }
        return Promise.resolve<void>();
    }

    // Client requests for the data channel handler to be removed and stop the data channel once thee are no more attached handlers!
    public removeHandler(dataId: number): Promise<void> {
        const details = this._handlers[dataId];
        if (!details) {
            return Promise.reject(new Error(`Handler associated with Data Id ${dataId} wasn't attached in the first place`));
        }

        this._detachHandler(details);
        delete this._handlers[dataId];
        this._availableHandlers--;

        // Ideally client should be responsible for handling start and stop of data channel. As of now if call is ending, then calling stop would be redundant.
        // This can end up slowing down ending call as stopping data channel would be queued on media stack and media stack will try and generate an offer which
        // it will soon throw away anyway because call is ending. This can block wasting time that could've been used to end the call.If we can find a better way
        // to avoid the above scenario, then we should check here if the LAST handler has just been removed, and then stop data channel.
        // this.stop();

        return Promise.resolve<void>();
    }

    public updateNegotiationTag(negotiationTag: string): Promise<void> {
        this._negotiationTag = negotiationTag;
        return Promise.resolve<void>();
    }

    public dispose() {
        this._detachAllHandlers();
        if (this._dataChannel) {
            this._dataChannel = null;
        }
    }

    public setDataChannel(channel: SlimCoreElectronDataChannel): Promise<void> {
        if (!!this._dataChannel) {
            throw new Error('Unexpected setting of data channel when we already have one');
        }

        this._dataChannel = channel;
        return Promise.resolve<void>();
    }

    // Attaches or detaches the handlers in response to the data channel status change.
    // If the data channel becomes available (only when not previously active), and there are some handlers that we'd like to attach, we'll also start the data channel.
    public onDataChannelStatusChanged(dataChannelStatus: number): Promise<void> {
        if (dataChannelStatus === SlimCore.Enums.DataChannelStatus.Active && !this._isDataChannelActive) {
            this._attachAllHandlers()
                .catch(error => {
                    this.logger.error(`Failed to attach all handlers error: ${error}`);
                });
        } else if (dataChannelStatus === SlimCore.Enums.DataChannelStatus.Available) {
            if (this._isDataChannelActive || this._isDataChannelStopping) {
                this._dataChannelStartCalled = false; // reset
                // Becoming available when it was already active - remove previously attached handlers
                this._detachAllHandlers();
            } else {

                // If tag is changing for any reason, the client must pass the updated tag by calling updateDataChannelTag.
                // Becoming available when it wasn't active before (data channel has just become available, let's start it)
                // Note: the handlers will be attached when this method is called again (as at that point the channel is expected to become Active)
                if (this._availableHandlers > 0) {
                    this.start();
                }
            }
        }

        this._dataChannelStatus = dataChannelStatus;

        return Promise.resolve<void>();
    }

    public setProtocolDataSource(source: SlimCore.DataSource): Promise<void> {
        // Not used
        return Promise.resolve<void>();
    }

    public processProtocolMessage(message: string, senderParticipant: ICallParticipant, senderParticipantId: string, senderSourceId: number): Promise<void> {
        // Not used
        return Promise.resolve<void>();
    }

    private get _isDataChannelAvailable() {
        return !!this._dataChannel && this._dataChannelStatus === SlimCore.Enums.DataChannelStatus.Available;
    }

    private get _isDataChannelActive() {
        return !!this._dataChannel && this._dataChannelStatus === SlimCore.Enums.DataChannelStatus.Active;
    }

    private get _isDataChannelStopping() {
        return !!this._dataChannel && this._dataChannelStatus === SlimCore.Enums.DataChannelStatus.Stopping;
    }

    private _attachAllHandlers(): Promise<void[]> {
        return Promise.all(Object.keys(this._handlers).map(dataId => {
            const details = this._handlers[dataId];
            // if internal, only attach if details completed
            if (details && ((details.handler && details.dataSource && details.dataSink) || !details.sourceSinkInternallyCreated)) {
                return this._attachHandler(details);
            } else {
                return Promise.resolve(null);
            }
        }));
    }

    // Other ts-calling sources and sinks can be exposed to the public through this internal dictionary (Namely, protocol)
    public setInternalSourceSink(dataId: number, dataSource: SlimCore.DataSource, dataSink: SlimCore.DataSink): void {
        if (dataId < 0 || dataId >= 64) {
            throw new Error(`Data Id ${dataId} is outside the valid range of 0-63`);
        }
        if (this._handlers.hasOwnProperty(dataId) && (this._handlers[dataId].dataSource || this._handlers[dataId].dataSink)) {
            this.logger.info(`Data Id ${dataId} for internal source sink is already registered`);
            return;
        }

        this.logger.info(`Setting internal source sink for dataId: ${dataId}`);
        if (dataId in this._handlers) { // A handler is already associated (added via adapter)
            this._handlers[dataId].dataSource = dataSource;
            this._handlers[dataId].dataSink = dataSink;
            // We've completed the details, attach
            this.logger.info(`Completed adapter details for dataId: ${dataId}`);
            this._availableHandlers++; // Protocol handler attaches through onDataChannelStatusChanged, other handlers should attach themselves
        } else { // No handler yet
            const internalDetails = new HandlerDetails(dataId, null, true);
            internalDetails.dataSource = dataSource;
            internalDetails.dataSink = dataSink;
            this._handlers[dataId] = internalDetails;
        }
    }

    private _attachHandler(details: HandlerDetails): Promise<void> {
        if (details.isAttached) {
            return Promise.resolve<void>();
        }

        try {
            this.logger.info(`Attaching handler for dataId: ${details.dataId}, ${details.sourceSinkInternallyCreated ? 'skipping source sink creation' : 'making source sink'}`);

            if (!details.sourceSinkInternallyCreated) { // Internal details should not register more source sinks
                details.dataSource = SlimCoreCommon.createDataSource(this._slimcoreInstance, details.dataId);
                this._dataChannel.registerDataSource(details.dataSource);

                details.dataSink = SlimCoreCommon.createDataSink(this._slimcoreInstance, details.dataId);
                this._dataChannel.registerDataSink(details.dataSink);
            } else if (!details.dataSource || !details.dataSink) {
                this.logger.error('Attaching handler with null datasource or datasink');
                return Promise.reject(new Error('Attaching handler with null datasource or datasink'));
            }

            details.listener = (args: SlimCore.DataSink.Events.DataArgs) => {
                try {
                    details.handler.onDataReceived(details.dataId, args.data, args.sourceID);
                } catch (error) {
                    this.logger.error(`Exception thrown from onDataReceived for data id ${details.dataId}, error: ${error}`);
                }
            };
            details.dataSink.on('data', details.listener);

            const sendFunction: DataSendFunction = (data: Uint8Array, recipients?: number[]): Promise<void> => {
                try {
                    details.dataSource.sendData(data, recipients);
                    return Promise.resolve<void>();
                } catch (error) {
                    return Promise.reject(error);
                }
            };

            details.isAttached = true;
            details.handler.onStarted(details.dataId, sendFunction);
            return Promise.resolve<void>();
        } catch (error) {
            this.logger.error(`Attaching handler for data id ${details.dataId} threw exception: ${error}`);
            return Promise.reject(error);
        }
    }

    private _detachAllHandlers() {
        Object.keys(this._handlers).forEach(dataId => {
            this._detachHandler(this._handlers[dataId]);
        });
    }

    private _detachHandler(details: HandlerDetails) {
        if (!details.isAttached) {
            return;
        }

        try {
            this.logger.info(`Detaching data handler dataid: ${details.dataId}`);

            if (details.handler) {
                details.handler.onStopped(details.dataId)
                    .catch(error => {
                        this.logger.warn(`Error in handler onStopped :${error}`);
                    });
            }

            if (details.sourceSinkInternallyCreated) {  // if internal, adapter may not interfere with source/sink/other listeners
                if (!!details.dataSink) {
                    try {
                        details.dataSink.removeListener('data', details.listener);
                    } catch (error) {
                        this.logger.warn(`Error removing internal listener, call probably ended: ${error}`);
                    }
                }
            } else {
                if (!!details.dataSource) {
                    this._dataChannel.unregisterDataSource(details.dataSource)
                        .catch(error => {
                            this.logger.warn(`Failed to unregister data source, call probably ended: ${error}`);
                        })
                        .then(() => {
                            details.dataSource.dispose();
                            details.dataSource = null;
                        });
                }

                if (!!details.dataSink) {
                    details.dataSink.removeAllListeners('data');
                    this._dataChannel.unregisterDataSink(details.dataSink)
                        .catch(error => {
                            this.logger.warn(`Failed to unregister data sink, call probably ended: ${error}`);
                        })
                        .then(() => {
                            details.dataSink.dispose();
                            details.dataSink = null;
                        });
                }
            }
            details.isAttached = false;

        } catch (error) {
            this.logger.error(`Exception thrown while detaching handler for data id ${details.dataId}, error: ${error}`);
        }
    }

    // Start/Stop functionalities have been intentionally kept private in order to be managed
    // internally by public APIs add/remove handlers.
    private start(): Promise<void> {
        this.logger.info('Data channel start called from adapter');
        if (!this._dataChannel) {
            return Promise.reject(new Error('Attempted to call start without a data channel'));
        } else if (this._dataChannelStartCalled) { // if data channel is still transitioning from available to active and we put in multiple starts
            this.logger.warn('Data channel start called more than once');
            return Promise.resolve();
        } else {
            this._dataChannelStartCalled = true;
            return this._dataChannel.start(this._negotiationTag);
        }
    }

    // Not calling stop on data channel anymore... Refer to the above comment around the same.
    // private stop(): Promise<void> {
    //     if (!this._dataChannel) {
    //         return Promise.reject(new Error('Attempted to call stop without a data channel'));
    //     }
    //     return this._dataChannel.stop();
    // }
}



