import * as _ from 'lodash';
import { CallingLogger, CauseId, IDisposable } from 'skype-calling-interface';

// Event subscriptions are disposable objects
export interface EventSubscription extends IDisposable { }

// Defines the interface for event sources
export interface EventSource<E> extends IDisposable {
    subscribe(eventHandler: E): EventSubscription;
}

export class EventSourceImpl<E> implements EventSource<E> {
    private eventLogger: CallingLogger | undefined;
    constructor(logger?: CallingLogger) {
        this.eventLogger = logger;
    }
    // Array of event subscriptions
    protected subscriptions: EventSubscriptionImpl<E>[] = [];

    public subscribe(eventHandler: E): EventSubscription {
        return new EventSubscriptionImpl(this.subscriptions, eventHandler);
    }

    public dispose(causeId?: CauseId) {
        this.subscriptions = [];
    }

    // Raise the specified event with the associated event data
    protected raiseEvents(exec: (eventHandler: E) => void) {
        const subscriptions = this.subscriptions.slice();
        subscriptions.forEach(subscription => {
            try {
                subscription.eventHandler !== undefined && exec(subscription.eventHandler);
            } catch (e) {
                if (this.eventLogger && this.eventLogger.warn) {
                    this.eventLogger.warn('Event handler exception caught!', e);
                }

            }
        });
    }
}

class EventSubscriptionImpl<E> implements EventSubscription {
    constructor(
        private subscriptions: EventSubscriptionImpl<E>[],
        public eventHandler: E | undefined) {

        this.subscriptions.push(this);
    }

    public dispose() {
        _.remove(this.subscriptions, subscription => subscription === this);
        this.eventHandler = undefined;
    }
}



