import {
    AccountConfiguration, CallingIntent, CallingLogger, CallingStackInitOptions,
    CallState, CauseId, ICall, ICallRegistry,
    ICallRegistryEvents, IDisposable,
    IEcsProvider, ISetup, ProxiedPushNotificationEvent, SkypeIdentity
} from 'skype-calling-interface';
import { ISlimCoreElectronApplicationHooks, TelemetryService } from '../typings/skype-calling-electron';

import * as _ from 'lodash';
import * as async from '../../common/ts/async';
import { noop } from '../../common/ts/fn';
import generateGuid from '../../common/ts/guid';
import * as piiUtils from '../../common/ts/piiUtils';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronCall from './SlimCoreElectronCall';
import SlimCoreElectronDeviceManager from './SlimCoreElectronDeviceManager';
import SlimCoreElectronEcsProvider from './SlimCoreElectronEcsProvider';
import SlimCoreElectronSetup from './SlimCoreElectronSetup';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { generateCauseId } from '../../common/ts/causeId';
import InternalLogger, { IInternalLogger } from '../../common/ts/internalLogger';

export default class SlimCoreElectronCallRegistry extends SlimCoreElectronBase<ICallRegistryEvents> implements ICallRegistry {
    public calls: SlimCoreElectronCall[] = [];
    public identity: string;
    private _skypeIdentity: SkypeIdentity;
    private _slimcoreCallHandler: SlimCoreCommon.CallHandler;
    private _loginPromiseDefered = async.defer<SlimCoreCommon.CallHandler>();
    private _logoutPromiseDefered = async.defer<void>();
    private _startSlimcorePromiseDefered = async.defer<SlimCoreCommon.CallHandler>();
    public isDisposing = false;
    private _disposedPromise = async.defer<void>();
    private slimcoreInstanceStartCalled = false;
    private libStatus: Enums.LibStatus;
    private _setup: SlimCoreElectronSetup;
    private _ecsProvider: SlimCoreElectronEcsProvider;
    private _account: SlimCoreCommon.Account;

    public constructor(
        callingLogger: CallingLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _deviceManager: SlimCoreElectronDeviceManager,
        private _appHooks: ISlimCoreElectronApplicationHooks,
        private _telemetryService?: TelemetryService
    ) {
        super(new InternalLogger(callingLogger).createChild('CallRegistry'), settings, engine);
        this._logger.info('constructor');

        this._slimcoreInstance.setMaxListeners(0);
        this._ecsProvider = new SlimCoreElectronEcsProvider(this._slimcoreInstance);
    }

    public async dispose(causeId = generateCauseId()): Promise<void> {
        const fnLogger = this._logger.createFnLogger('dispose', causeId);
        fnLogger.info('disposing call registry');

        if (this.isDisposing) {
            fnLogger.warn('call registry is already in disposing stage!');
            return this._disposedPromise.promise;
        }
        this.isDisposing = true;

        const always = () => {
            this.calls = [];
            if (this._slimcoreCallHandler) {
                this._slimcoreCallHandler.dispose();
                this._slimcoreCallHandler = undefined;
            }
            fnLogger.info('callhandler is disposed');
            if (this._account) {
                this._account.dispose();
                this._account = undefined;
            }
            fnLogger.info('account is disposed');
            this.event('disposed').raise();
            super.dispose();
            this._disposedPromise.resolve();
        };

        const callStopPromises = this.calls.map(call => call.stop());
        const logout = () => this.logout(causeId);
        Promise.all(callStopPromises).then(logout, logout).then(always, always);
        return this._disposedPromise.promise;
    }

    public get loginPromise(): Promise<SlimCoreCommon.CallHandler> {
        return this._loginPromiseDefered.promise;
    }

    public get disposePromise(): Promise<void> {
        return this._disposedPromise.promise;
    }

    private _initCall(call: SlimCoreElectronCall, callObjectId: number, causeId: CauseId): void {
        const fnLogger = this._logger.createFnLogger(`initCall[callId=${call.callId}][participantId=${call.participantId}]`, causeId);
        fnLogger.info(`callObjectId=${callObjectId}`);
        call.incomingCallInit(callObjectId, causeId);
    }

    private _incomingCallMonitor(data: SlimCore.Engine.Events.ObjectPropertyChangedArgs, causeId: CauseId) {
        const fnLogger = this._logger.createFnLogger('incomingCallMonitor', causeId);
        fnLogger.info(`_incomingCallMonitor for objectPropertyChange objectId=${data.objectId}`);
        const callId = this._getStrProperty(this._slimcoreCallHandler, data.objectId, Enums.Property.CallName);
        if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport)) {
            const userId = this._getStrProperty(this._slimcoreCallHandler, data.objectId, Enums.Property.CallAccountIdentity);
            if (userId !== this.identity) {
                fnLogger.info(`incoming call(${callId}) for user(${piiUtils.mriOrId(userId)}) but current user is ${piiUtils.mriOrId(this.identity)}`);
                return;
            }
        }
        const localParticipantId = this._getStrProperty(this._slimcoreCallHandler, data.objectId, Enums.Property.CallLegId);

        if (!this._getCall(callId, localParticipantId, causeId)) {
            fnLogger.logFailure(`getCall failed to find the call(callId: ${callId}, localParticipantId: ${localParticipantId})`);
        }
    }

    public init(skypeIdentity: SkypeIdentity, initOptions: CallingStackInitOptions = { block: true }, causeId = generateCauseId()): Promise<void> {
        const fnLogger = this._logger.createFnLogger('init', causeId);
        fnLogger.info('init');

        if (!initOptions.block && !SlimCoreCommon.hasFeature(Enums.Feature.AsyncStart)) {
            initOptions.block = true;
        }

        let accountStatusCallback: IDisposable = null;

        if (!this.slimcoreInstanceStartCalled) {
            if (!initOptions.block) {

                this._registerDisposable(this._slimcoreInstance.handle(
                    'lib-status-changed',
                    { libStatus: SlimCore.Enums.LibStatus.Running },
                    data => this._onLibStatusChanged(data)));
            }

            this._slimcoreInstance.setupSetStr('*Lib/SCT/Telemetry/DiagnosticLevel', initOptions.telemetryDiagnosticLevel || '');

            this._slimcoreInstance.start(initOptions.block);
            fnLogger.info(`starting slimcoreInstance block=${initOptions.block}`);
            this.slimcoreInstanceStartCalled = true;

            if (initOptions.block) {
                // Blocking call finished so we can resolve promise immediately,
                // and manually set libstatus to running
                this.libStatus = Enums.LibStatus.Running;
                this._startSlimcorePromiseDefered.resolve();
            }
        } else {
            fnLogger.info('skipping slimcoreInstance start, because it was already started');
        }

        this._skypeIdentity = skypeIdentity;
        this.identity = skypeIdentity.id;
        this._skypeIdentity.tokenProvider()
            .then((token: string) => {
                // Set up platform callbacks for AccountStatus
                fnLogger.info('setting up stack callbacks');
                accountStatusCallback = this._slimcoreInstance.handle('object-property-changed', { propKey: Enums.Property.AccountStatus, value: Enums.AccountStatus.LoggedIn }, () => {
                    const callHandler = this._slimcoreInstance.createCallInterface();
                    this._slimcoreCallHandler = SlimCoreCommon.wrap(callHandler, this._settings);
                    fnLogger.info('createCallInterface returned');
                    this._loginPromiseDefered.resolve(this._slimcoreCallHandler);

                    if (accountStatusCallback) {
                        accountStatusCallback.dispose();
                        accountStatusCallback = null;
                    }

                    this._hookCallRegistryEvents();
                });

                this._slimcoreInstance.handle(
                    'object-property-changed',
                    { propKey: Enums.Property.CallStatus, value: Enums.CallStatus.RingingIn },
                    eventData => this._incomingCallMonitor(eventData, causeId));

                this._initializeSlimCore(token);
                this._logger.info('initialize returned');
            })
            .catch((err: any) => {
                fnLogger.logFailure(err);
                this._loginPromiseDefered.reject(err);
            });

        return this.loginPromise.then(noop);
    }

    public uninit(): Promise<void> {
        if (!this.slimcoreInstanceStartCalled) {
            this._logoutPromiseDefered.reject(new Error('CallRegistry uninit called without slimcore::start()'));
        } else if (this.libStatus !== Enums.LibStatus.Running) {
            this._startSlimcorePromiseDefered.reject(new Error('Callregistry uninit called before slimcore reached Running state'));
            this._logoutPromiseDefered.reject(new Error('Callregistry uninit called before slimcore reached Running state'));
        }

        let accountStatusCallback: IDisposable = null;
        try {
            this._logger.info('uninit: setting up stack callbacks');
            accountStatusCallback = this._slimcoreInstance.handle('object-property-changed', { propKey: Enums.Property.AccountStatus, value: Enums.AccountStatus.LoggedOut }, () => {
                this._logoutPromiseDefered.resolve();
                if (accountStatusCallback) {
                    accountStatusCallback.dispose();
                    accountStatusCallback = null;
                }
            });
            this._logger.info('uninit(): calling slimcore.logout()');
            this._slimcoreInstance.logout();
        } catch (e) {
            this._logger.error('slimcore.logout() failed.', e);
            this._logoutPromiseDefered.reject(e);
        }

        // reset the start bool, so that slimcore can be started again if necessary
        this.slimcoreInstanceStartCalled = false;

        return this._logoutPromiseDefered.promise.then(noop);
    }

    public login(skypeIdentity: SkypeIdentity, causeId = generateCauseId(), configuration?: string): Promise<void> {
        const fnLogger = this._logger.createFnLogger('login', causeId);
        fnLogger.info('login');

        let accountStatusCallback: IDisposable = null;

        this._skypeIdentity = skypeIdentity;
        this.identity = skypeIdentity.id;
        this._setup = new SlimCoreElectronSetup(this._slimcoreInstance, this.identity);

        this._skypeIdentity.tokenProvider()
            .then((token: string) => {
                // Set up platform callbacks for AccountStatus
                fnLogger.info('setting up stack callbacks');
                accountStatusCallback = this._slimcoreInstance.handle(
                    'object-property-changed', { propKey: Enums.Property.AccountStatus, value: Enums.AccountStatus.LoggedIn }, eventData => {
                        if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport)) {
                            if (this._account.getObjectId() !== eventData.objectId) {
                                fnLogger.error(`accountObjectId: ${this._account.getObjectId} does not match with ${eventData.objectId}`);
                                return;
                            }
                        }
                        const callHandler = this._createCallHandler();
                        this._slimcoreCallHandler = SlimCoreCommon.wrap(callHandler, this._settings);
                        fnLogger.info('createCallInterface returned');
                        this._loginPromiseDefered.resolve(this._slimcoreCallHandler);

                        if (accountStatusCallback) {
                            accountStatusCallback.dispose();
                            accountStatusCallback = null;
                        }
                        this._hookCallRegistryEvents();
                    });

                this._slimcoreInstance.handle(
                    'object-property-changed',
                    { propKey: Enums.Property.CallStatus, value: Enums.CallStatus.RingingIn },
                    eventData => this._incomingCallMonitor(eventData, causeId));
                this._login(token, skypeIdentity.displayName, fnLogger, configuration);
            })
            .catch((err: any) => {
                fnLogger.logFailure(err);
                this._loginPromiseDefered.reject(err);
            });

        return this.loginPromise.then(() => this.setAdditionalIdentities());
    }

    private _login(skypeToken: string, displayName: string, logger: IInternalLogger, configuration?: string): void {
        if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport)) {
            // HODOR
            // Facilitating the duplicate login for updating displayname till UI moves to updateDisplayName API
            if (this._account) {
                logger.info('already logged in. updating displayname and skypetoken!');
                this._account.updateDisplayName(displayName);
                this._account.updateSkypeToken(skypeToken);
                return;
            }
            const account = this._slimcoreInstance.getAccount(this.identity);
            this._account = SlimCoreCommon.wrap(account, this._settings);
            this._account.login(skypeToken, displayName, SlimCoreCommon.mapToEmptyStrIfFalsy(configuration));
        } else {
            this._slimcoreInstance.login(this.identity, skypeToken, displayName);
        }
    }

    private _createCallHandler(): SlimCore.CallHandler {
        if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport)) {
            return this._slimcoreInstance.getCallHandler(this._account.getObjectId());
        }
        return this._slimcoreInstance.createCallInterface();
    }

    public logout(causeId = generateCauseId()): Promise<void> {
        const fnLogger = this._logger.createFnLogger('logout', causeId);
        fnLogger.info('logout');

        let accountStatusCallback: IDisposable = null;
        try {
            fnLogger.info('setting up stack callbacks');
            accountStatusCallback = this._slimcoreInstance.handle(
                'object-property-changed', { propKey: Enums.Property.AccountStatus, value: Enums.AccountStatus.LoggedOut }, eventData => {
                    if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport)) {
                        if (this._account.getObjectId() !== eventData.objectId) {
                            return;
                        }
                    }
                    this._logoutPromiseDefered.resolve();
                    if (accountStatusCallback) {
                        accountStatusCallback.dispose();
                        accountStatusCallback = null;
                    }
                });
            fnLogger.info('calling slimcore.logout()');
            this._logout();
        } catch (e) {
            fnLogger.error('slimcore.logout() failed.', e);
            this._logoutPromiseDefered.reject(e);
        }
        return this._logoutPromiseDefered.promise.then(noop);
    }

    private _logout(): void {
        if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport)) {
            this._account.logout();
        } else {
            this._slimcoreInstance.logout();
        }
    }

    public setRing(ring: string): Promise<void> {
        return Promise.resolve().then(() => {
            if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) && this._account) {
                return this._account.setRing(ring);
            } else {
                //HODOR
                return this._setup.setString('*Lib/Call/NG/Ring', ring);
            }
        });
    }

    public setTenantId(tenantId: string): Promise<void> {
        return Promise.resolve().then(() => {
            if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) && this._account) {
                return this._account.setTenantId(tenantId);
            } else {
                //HODOR
                return this._setup.setString('*Lib/Call/NG/TenantId', tenantId);
            }
        });
    }

    public setConfiguration(configuration: AccountConfiguration): Promise<void> {
        return Promise.resolve().then(() => {
            if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) && this._account &&
                this._account.setConfiguration) {

                this._account.setConfiguration(JSON.stringify(configuration));
            }
        });
    }

    public updateDisplayName(displayName: string): Promise<void> {
        return Promise.resolve().then(() => {
            if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) && this._account) {
                return this._account.updateDisplayName(displayName);
            } else {
                return this._skypeIdentity.tokenProvider()
                    .then(skypeToken => this._login(skypeToken, displayName, this._logger));
            }
        });
    }

    public updateSkypeToken(skypeToken: string): void {
        if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) && this._account) {
            this._account.updateSkypeToken(skypeToken);
        } else {
            this._slimcoreInstance.updateSkypeToken(skypeToken);
        }
    }

    public fireIntent(intent: CallingIntent, identity?: string): Promise<void> {
        return Promise.resolve().then(() => {
            if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport)) {
                this._slimcoreInstance.fireIntent(Enums.Intent.CallPush, identity, undefined, this.identity);
            }
        });
    }

    public getSetup(): ISetup {
        return this._setup;
    }

    public getEcsProvider(): IEcsProvider {
        return this._ecsProvider;
    }

    public createCall(threadId: string, callId?: string, localParticipantId?: string, messageId?: string, causeId = generateCauseId()): ICall {
        const fnLogger = this._logger.createFnLogger('createCall', causeId);
        fnLogger.info(`threadId:${threadId}, callId:${callId}, localParticipantId: ${localParticipantId}, messageId:${messageId}`);

        if (this._slimcoreCallHandler === null || this._slimcoreCallHandler === undefined) {
            fnLogger.logFailure('Stack not initialized yet');
            throw new Error('Stack not initialized yet');
        }

        // Make sure skype token is up-to-date before creating a new call
        this._skypeIdentity.tokenProvider().then(token => {
            if (token) {
                fnLogger.info('Updating Skype Token...');
                this.updateSkypeToken(token);
                fnLogger.info('Token update complete');
            }
        });

        let call = null;

        // Within the same thread, a new call can be created only if the callId is different and if messageId is different
        if (threadId) {
            call = _.find(this.calls, call => call.threadId === threadId);
            if (call) {
                if (callId && call.callId === callId) {
                    fnLogger.logFailure(`Call Registry already has an entry for callId: ${call.callId}, threadId: ${call.threadId}`);
                    return call;
                } else if (messageId && call.messageId === messageId) {
                    fnLogger.logFailure(`Call Registry already has an entry for threadId: ${call.threadId}, messageId: ${call.messageId}`);
                    return call;
                }
            }
        }

        call = this._getCall(callId, localParticipantId, causeId);
        if (call) {
            return call;
        }

        call = new SlimCoreElectronCall(
            this._logger,
            this._settings,
            this._slimcoreInstance,
            this._slimcoreCallHandler,
            this._deviceManager,
            this._appHooks,
            threadId,
            callId || generateGuid(),
            this._skypeIdentity,
            localParticipantId,
            this._telemetryService,
            messageId);

        this.calls.push(call);
        this.event('callAdded').raise(call);
        this.raiseChanged();

        return call;
    }

    public getCall(callId: string, localParticipantId: string, causeId = generateCauseId()): ICall {
        return this._getCall(callId, localParticipantId, causeId);
    }

    private _getCall(callId: string, participantId: string, causeId: CauseId): SlimCoreElectronCall {
        const fnLogger = this._logger.createFnLogger(`getCall[callId=${callId}][participantId=${participantId}]`, causeId);

        if (!callId && !participantId) {
            fnLogger.logFailure('callId & participantId is undefined');
            return null;
        }

        if (this._slimcoreCallHandler === null || this._slimcoreCallHandler === undefined) {
            fnLogger.logFailure('Stack not initialized yet');
            throw new Error('Stack not initialized yet');
        }

        // Initing observing calls always so that if there is a ringing call in slimcore,
        // it attaches and starts to process it. This optimizes the observing call + join flow.
        let call = _.find(this.calls, call => call.callId === callId);
        if (call) {
            if (call.state === CallState.Observing) {
                this._initCall(call, call.slimcoreCallId, causeId);
                return call;
            } else {
                fnLogger.info(`Call Registry already has an entry for this call`);
                return call;
            }
        }

        const slimCoreCallObjectId = _.find(this._slimcoreCallHandler.getActiveCalls(), callObjectId => (this._slimcoreCallHandler.getStrProperty(callObjectId, Enums.Property.CallName) === callId) &&
            (this._slimcoreCallHandler.getStrProperty(callObjectId, Enums.Property.CallLegId) === participantId));

        if (!slimCoreCallObjectId) {
            fnLogger.logFailure('slimCoreCallObjectId not found');
            return null;
        }

        const threadId = this._slimcoreCallHandler.getStrProperty(slimCoreCallObjectId, Enums.Property.CallThreadId);
        const messageId = this._slimcoreCallHandler.getStrProperty(slimCoreCallObjectId, Enums.Property.CallMessageId);

        call = new SlimCoreElectronCall(
            this._logger,
            this._settings,
            this._slimcoreInstance,
            this._slimcoreCallHandler,
            this._deviceManager,
            this._appHooks,
            threadId,
            callId,
            this._skypeIdentity,
            participantId,
            this._telemetryService,
            messageId);

        this._initCall(call, slimCoreCallObjectId, causeId);
        this.calls.push(call);
        this.event('callAdded').raise(call);
        this.raiseChanged();
        return call;
    }

    public deleteCall(call: ICall, final = true, causeId = generateCauseId()): boolean {
        this._logger.info(`deleteCall ${call.callId}`);

        let deletedCalls = false;
        _.each(_.remove(this.calls, c => c === call), c => {
            deletedCalls = true;
            if (final) {
                this.event('callRemoved').raise(call);
                if (c.state !== CallState.Disconnected) {
                    c.dispose(causeId);
                }
            }
        });

        if (deletedCalls) {
            this.raiseChanged();
            return true;
        }

        return false;
    }

    public debugInformation(call?: ICall): Promise<string> {
        let content = this._slimcoreCallHandler.getDebugInformation('/callinfo');
        if (call && (!content || _.isEmpty(content))) {
            content = `CallInformation\n * CallId=${call.callId}`;
        }

        return Promise.resolve(content);
    }

    // Ideally to be set as soon as you login.
    // Can be of the format - <rc_id1>:<userId1>;aliases=<rc_id2>:<userId2>,<rc_id3>:<userId3>
    // Example: 8:identity;aliases=2:sipId@microsoft.com,4:+100000001
    private setAdditionalIdentities(): Promise<void> {
        return Promise.resolve().then(() => {
            if (this._skypeIdentity.sipUri) {
                const additionalIdentity = `2:${this._skypeIdentity.sipUri}`;
                if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) && this._account) {
                    this._account.setAdditionalIdentities(additionalIdentity);
                } else {
                    this._slimcoreInstance.setAdditionalIdentities(additionalIdentity);
                }
            }
        });
    }

    private _initializeSlimCore(token: string) {
        try {
            this._startSlimcorePromiseDefered.promise.then(() => {
                // LibStatus can be undefined if we made a blocking call to SlimCore.start(),
                // but if it was async, then libStatus should be Running when we attempt to login
                this._logger.info(`logging in to slimcore libStatus=${this.libStatus}`);
                this._slimcoreInstance.login(this._skypeIdentity.id, token, this._skypeIdentity.displayName);
            });
        } catch (e) {
            this._logger.error('initialization failed.', e);
        }
    }

    private _onSkypeTokenRequired(eventData: SlimCore.Engine.Events.SkypeTokenRequiredArgs) {
        this.event('skypeTokenRequired').raise(eventData.invalidToken);
    }

    private _onLibStatusChanged(eventData: SlimCore.Engine.Events.LibStatusChangedArgs) {
        this._logger.info(`_onLibStatusChanged from ${this.libStatus} => ${eventData.libStatus}`);
        if (eventData.libStatus === Enums.LibStatus.Running) {
            this._startSlimcorePromiseDefered.resolve();
        } else if (eventData.libStatus === Enums.LibStatus.FatalError &&
            this.libStatus === Enums.LibStatus.Constructed) {
            // Handle fatal error during setup
            this._startSlimcorePromiseDefered.reject(new Error('Slimcore FatalError during start'));
        }
        this.libStatus = eventData.libStatus;
    }

    private _onProxiedPushNotification(eventData: SlimCore.Engine.Events.ProxiedPushNotificationArgs) {
        const pushEvent: ProxiedPushNotificationEvent = {
            eventId: eventData.eventId,
            payload: eventData.payload
        };

        this.event('proxiedPushNotification').raise(pushEvent);
    }

    private _hookCallRegistryEvents() {
        if (SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) && this._account) {
            this._registerDisposable(this._account.handle('skype-token-required', undefined, data => this._onSkypeTokenRequired(data)));
            this._registerDisposable(this._slimcoreCallHandler.handle('proxied-push-notification', undefined, data => this._onProxiedPushNotification(data)));
        } else {
            this._registerDisposable(this._slimcoreInstance.handle('skype-token-required', undefined, data => this._onSkypeTokenRequired(data)));
            this._registerDisposable(this._slimcoreInstance.handle('proxied-push-notification', undefined, data => this._onProxiedPushNotification(data)));
        }
    }
}



