import {
    CallingLogger, DeviceType, ImageInfo, IScreenSharingManager, IScreenSharingManagerEvents, ISharingSource, Rectangle, SharingSourceType, VideoDeviceDesc
} from 'skype-calling-interface';
import { ISlimCoreElectronApplicationHooks } from '../typings/skype-calling-electron';

import * as _ from 'lodash';
import * as async from '../../common/ts/async';
import { IInternalLogger } from '../../common/ts/internalLogger';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronDeviceManager from './SlimCoreElectronDeviceManager';
import SlimCoreElectronScreenScraper from './SlimCoreElectronScreenScraper';

export default class SlimCoreElectronScreenSharingManager extends SlimCoreElectronBase<IScreenSharingManagerEvents> implements IScreenSharingManager {
    private _screenScraper: SlimCoreElectronScreenScraper;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _appHooks: ISlimCoreElectronApplicationHooks,
        private _deviceManager : SlimCoreElectronDeviceManager
    ) {
        super(logger.createChild('ScreenSharingManager'), settings, engine);
        this._logger.info('constructor');

        try {
            this._screenScraper = new SlimCoreElectronScreenScraper(this._slimcoreInstance);
        } catch (error) {
            this._logger.warn(`${error}`);
        }

        this._registerDisposable(this._appHooks.onDisplaysChanged(() => this._raiseScreensChanged()));
    }

    public enumerateScreensAsync(): Promise<ISharingSource[]> {
        return async.asap(() => this._enumerateScreens());
    }

    public enumerateWindowsAsync(): Promise<ISharingSource[]> {
        return async.asap(() => this._enumerateWindows());
    }

    public enumerateCamerasAsync(): Promise<ISharingSource[]> {
        try {
            if (this._deviceManager) {
                return this._deviceManager.enumerateDevicesAsync()
                    .then(devices => {
                        const cameras = devices.filter(device => device.kind === DeviceType.Camera);
                        this._logger.debug(`enumerateCameras() result: ${JSON.stringify(cameras)}`);
                        return _.map(cameras, camera => new SlimcoreElectronCameraSharingSource(camera as VideoDeviceDesc));
                    });
            } else {
                return Promise.resolve([]);
            }
        } catch (error) {
            this._logger.error(`enumerateCameras() failed: ${error}`);
            return Promise.reject(`enumerateCameras() failed: ${error}`);
        }
    }

    private _enumerateScreens(): ISharingSource[] {
        try {
            const monitors = this._screenScraper && this._screenScraper.getMonitorList() || [];
            this._logger.debug(`ScreenScraper.getMonitorList() result: ${JSON.stringify(monitors)}`);

            return _.map(monitors, monitorInfo => new SlimCoreElectronScreenSharingSource(monitorInfo, this._logger, this._screenScraper));
        } catch (error) {
            this._logger.error(`ScreenScraper.getMonitorList() failed: ${error}`);
            throw error;
        }
    }

    private _enumerateWindows(): ISharingSource[] {
        try {
            const windows = this._screenScraper && this._screenScraper.getWindowList() || [];
            this._logger.debug(`ScreenScraper.getWindowList() result: ${JSON.stringify(windows)}`);

            return _.map(windows, windowInfo => new SlimcoreElectronWindowSharingSource(windowInfo, this._logger, this._screenScraper));
        } catch (error) {
            this._logger.error(`ScreenScraper.getWindowList() failed: ${error}`);
            throw error;
        }
    }

    private _raiseScreensChanged() {
        this._logger.debug('raising onScreensChanged');
        this.event('screensChanged').raise();
    }
}

class SlimCoreElectronScreenSharingSource implements ISharingSource {
    public constructor(private _monitorInfo: SlimCore.MonitorInfo, private _logger: CallingLogger, private _screenScraper: SlimCoreElectronScreenScraper) {
    }

    public getId(): number {
        return this._monitorInfo.monitorId;
    }

    public getDeviceId(): string {
        return String(this._monitorInfo.monitorId);
    }

    public getType(): SharingSourceType {
        return SharingSourceType.Monitor;
    }

    public getPreview(width?: number, height?: number, asImage?: boolean): Promise<ImageInfo> {
        return Promise.reject('not implemented');
    }

    public getPreviewAsync(width?: number, height?: number, asImage?: boolean): Promise<ImageData> {
        return new Promise<ImageData>(resolve => {
            resolve(this._screenScraper.getMonitorPreview(this.getId(), width, height, asImage, this._monitorInfo.region));
        })
            .then(result => {
                this._logger.debug(`ScreenScraper.getMonitorPreview(${this.getId()}) result: ${result.width}x${result.height}`);
                return result;
            }, error => {
                this._logger.error(`ScreenScraper.getMonitorPreview(${this.getId()}) failed: ${error}`);
                throw error;
            });
    }

    public getDescription(): string {
        return this._monitorInfo.name;
    }

    public getIcon(width?: number, height?: number): Promise<ImageData> {
        return Promise.resolve(null);
    }

    public getBounds(): Rectangle {
        return {
            x: this._monitorInfo.region.x,
            y: this._monitorInfo.region.y,
            width: this._monitorInfo.region.width,
            height: this._monitorInfo.region.height
        };
    }
}

class SlimcoreElectronWindowSharingSource implements ISharingSource {
    public constructor(private _windowInfo: SlimCore.WindowInfo, private _logger: CallingLogger, private _screenScraper: SlimCoreElectronScreenScraper) {
    }

    public getId(): number {
        return this._windowInfo.windowId;
    }

    public getDeviceId(): string {
        return String(this._windowInfo.windowId);
    }

    public getType(): SharingSourceType {
        return SharingSourceType.Window;
    }

    public getPreview(width?: number, height?: number, asImage?: boolean): Promise<ImageInfo> {
        return Promise.reject('not implemented');
    }

    public getPreviewAsync(width?: number, height?: number, asImage?: boolean): Promise<ImageData> {
        return new Promise<ImageData>(resolve => {
            resolve(this._screenScraper.getWindowPreview(this.getId(), width, height, asImage));
        })
            .then(result => {
                this._logger.debug(`ScreenScraper.getWindowPreview(${this.getId()}) result: ${result.width}x${result.height}`);
                return result;
            }, error => {
                this._logger.error(`ScreenScraper.getWindowPreview(${this.getId()}) failed: ${error}`);
                throw error;
            });
    }

    public getDescription(): string {
        return this._windowInfo.title;
    }

    public getAppName(): string {
        return this._windowInfo.applicationName;
    }

    public getIcon(width?: number, height?: number, asImage?: boolean): Promise<ImageData> {
        return new Promise<ImageData>(resolve => {
            resolve(this._screenScraper.getWindowIcon(this.getId(), width, height, asImage));
        })
            .then(result => {
                this._logger.debug(`ScreenScraper.getWindowIcon(${this.getId()}) result: ${result.width}x${result.height}`);
                return result;
            }, error => {
                this._logger.error(`ScreenScraper.getWindowIcon(${this.getId()}) failed: ${error}`);
                throw error;
            });
    }

    public getBounds(): Rectangle {
        return undefined;
    }
}

class SlimcoreElectronCameraSharingSource implements ISharingSource {
    public constructor(private _cameraDescription: VideoDeviceDesc) {
    }

    public getId(): number {
        return null;
    }

    public getDeviceId(): string {
        return this._cameraDescription.id;
    }

    public getType(): SharingSourceType {
        return SharingSourceType.Camera;
    }

    public getPreview(width?: number, height?: number, asImage?: boolean): Promise<ImageInfo> {
        return Promise.reject('not implemented');
    }

    public getPreviewAsync(width?: number, height?: number, asImage?: boolean): Promise<ImageData> {
        return Promise.reject('not implemented');
    }

    public getDescription(): string {
        return this._cameraDescription.label;
    }

    public getIcon(width?: number, height?: number, asImage?: boolean): Promise<ImageData> {
        return Promise.reject('not implemented');
    }

    public getBounds(): Rectangle {
        return undefined;
    }
}



