import * as _ from 'lodash';
import { CallingLogger, CauseId, TerminatedReason } from 'skype-calling-interface';
import { noop } from '../../common/ts/fn';
import * as piiUtils from '../../common/ts/piiUtils';
import * as async from './async';
import { generateCauseId } from './causeId';
import { getPrintableObject } from './logUtils';

type DeferredOperations<T> = { [key: string]: { deferred: async.Deferred<T>; causeId: CauseId } };
type DeferredOperationName = string;
type DeferredOperationId = string;
export default class AsyncOperationHandler {
    private _endOperationDeferred: async.Deferred<void>;

    private _deferredOperations: DeferredOperations<any>;
    private _deferredOperationsWithOperationId: { [key: string]: { [key: string]: { deferred: async.Deferred<any>; causeId: CauseId } } };
    private _deferredOperationsWithCauseId: { [causeId: string]: { [operationId: string]: async.Deferred<any> } };

    protected _logger: CallingLogger;
    private _disposed = false;

    constructor(logger: CallingLogger) {
        this._logger = logger.createChild('[ASYNC]');
        this._endOperationDeferred = async.defer<void>();
        this._endOperationDeferred.promise.catch(error => {
            this._logger.info('Rejected all operations');
        });
        this._deferredOperations = {};
        this._deferredOperationsWithOperationId = {};
        this._deferredOperationsWithCauseId = {};
        this._logger = logger;
    }

    public registerPromise(asyncOperationName: string, asyncOperation: Promise<void>) {
        let asyncOperationDone = false;
        const always = () => { asyncOperationDone = true; };
        asyncOperation.then(always, always);

        const endOperationPromise = this._endOperationDeferred.promise.catch(error => {
            if (!asyncOperationDone) {
                throw new Error(`Operation ${asyncOperationName} did not complete, reason:${error}`);
            }
        });
        return Promise.race([endOperationPromise, asyncOperation]);
    }

    /*
    update all occurances of operation with the new id.
    */
    public updateOperationId(previousId: string, newId: string) {
        this._logger.info(`updateOperationId ${previousId} ${newId}`);

        Object.keys(this._deferredOperationsWithOperationId).forEach(operationName => {
            if (operationName && this._deferredOperationsWithOperationId[operationName]) {
                this._logger.info(`_deferredOperationsWithOperationId ${operationName}`);
                Object.keys(this._deferredOperationsWithOperationId[operationName]).forEach(id => {
                    if (id && this._deferredOperationsWithOperationId[operationName][id]) {
                        this._logger.info(`_deferredOperationsWithOperationId ${id} ${newId}`);
                        if (id === previousId) {
                            this._deferredOperationsWithOperationId[operationName][newId]
                                = this._deferredOperationsWithOperationId[operationName][id];
                            delete this._deferredOperationsWithOperationId[operationName][id];
                        }
                    }
                });
            }
        });

        Object.keys(this._deferredOperationsWithCauseId).forEach(causeId => {
            if (causeId && this._deferredOperationsWithCauseId[causeId]) {
                this._logger.info(`_deferredOperationsWithCauseId ${causeId}`);
                Object.keys(this._deferredOperationsWithCauseId[causeId]).forEach(id => {
                    if (id && this._deferredOperationsWithCauseId[causeId][id]) {
                        this._logger.info(`_deferredOperationsWithCauseId ${id} ${newId}`);
                        if (id === previousId) {
                            this._deferredOperationsWithOperationId[causeId][newId]
                                = this._deferredOperationsWithOperationId[causeId][id];
                            delete this._deferredOperationsWithOperationId[causeId][id];
                        }
                    }
                });
            }
        });
    }

    public createPendingOperation<T= undefined>(operationName: DeferredOperationName, operationId?: DeferredOperationId, causeId = generateCauseId()): Promise<T> {
        const deferredOperation = async.defer<T>();
        deferredOperation.promise.catch(noop);
        const operationInfo = this.getOperationInfoForLogging(causeId, operationName, operationId);
        this._logger.info(`${operationInfo}[creating...]`);
        if (this._disposed) {
            this._logger.info(`${operationInfo}[create failed, call is disposed]`);
            return Promise.reject(TerminatedReason.ActionNotAllowed);
        }
        if (operationId) {
            if (!this._deferredOperationsWithOperationId[operationName]) {
                this._deferredOperationsWithOperationId[operationName] = {};
            }
            if (this._deferredOperationsWithOperationId[operationName][operationId]) {
                this._logger.warn(`${operationInfo} Deferred operation with given name and operation id is already defined`);
                return Promise.reject(new Error(`Deferred operation with given name is already defined:${operationName}/${piiUtils.scrubMriOrOmit(operationId)}`));
            } else {
                this._deferredOperationsWithOperationId[operationName][operationId] = { deferred: deferredOperation, causeId };
                if (!this._deferredOperationsWithCauseId[causeId]) {
                    this._deferredOperationsWithCauseId[causeId] = {};
                }
                this._deferredOperationsWithCauseId[causeId][operationId] = deferredOperation;
            }

        } else {
            if (!this._deferredOperations[operationName]) {
                if (!this._deferredOperationsWithCauseId[causeId]) {
                    this._deferredOperationsWithCauseId[causeId] = {};
                }
                this._deferredOperationsWithCauseId[causeId][''] = deferredOperation;
                this._deferredOperations[operationName] = { deferred: deferredOperation, causeId };
            } else {
                this._logger.warn(`${operationInfo} Deferred operation with given name is already defined`);
                return Promise.reject(new Error(`Deferred operation with given name is already defined:${operationName}`));
            }
        }
        const endOperationPromise = this._endOperationDeferred.promise.catch<T>(error => {
            if (this.hasPendingOperation(operationName, operationId)) {
                return Promise.reject(new Error(`Operation ${operationName} did not complete, reason:${error}`));
            }

            return Promise.resolve<T>(undefined);
        });

        return Promise.race<T>([endOperationPromise as Promise<T>, deferredOperation.promise]);
    }

    public hasPendingOperation(operationName: string, operationId?: string): boolean {
        if (!operationId) {
            return !!this._deferredOperations[operationName];
        } else if (this._deferredOperationsWithOperationId[operationName]) {
            return !!this._deferredOperationsWithOperationId[operationName][operationId];
        }
        return false;
    }

    public hasPendingOperationWithCauseId(causeId: string, operationId?: string): boolean {
        if (!operationId) {
            operationId = ''; // tslint:disable-line:no-parameter-reassignment
        }
        return !!this._deferredOperationsWithCauseId[causeId][operationId];
    }

    public getPendingOperationWithCauseId<T = void>(causeId: string, operationId?: string): Promise<T> {
        if (this.hasPendingOperationWithCauseId(causeId, operationId)) {
            return this._deferredOperationsWithCauseId[causeId][operationId].promise;
        }
        return Promise.reject(`[${causeId}][${operationId}]: getPendingOperationWithCauseId for given causeId, operationId`);
    }

    public resolveOperationWithCauseId<T>(causeId: string, operationId?: string, resolveResult?: T): Promise<T> {
        if (this.hasPendingOperationWithCauseId(causeId)) {
            if (!operationId) {
                operationId = ''; // tslint:disable-line:no-parameter-reassignment
            }
            const deferredPromise = this._deferredOperationsWithCauseId[causeId][operationId];
            const promise = deferredPromise.promise;
            deferredPromise.resolve(resolveResult);
            this._logger.info(`[${causeId}][${operationId}] resolved, result=${getPrintableObject(resolveResult) || `void`}`);
            delete this._deferredOperationsWithCauseId[causeId][operationId];
            return promise;
        }
        return Promise.reject(`[${causeId}][${operationId}] resolveOperationWithCauseId failed, operation not found`);
    }

    public rejectOperationWithCauseId<T>(causeId: string, operationId?: string, resolveResult?: T): Promise<T> {
        if (this.hasPendingOperationWithCauseId(causeId)) {
            if (!operationId) {
                operationId = ''; // tslint:disable-line:no-parameter-reassignment
            }
            const deferredPromise = this._deferredOperationsWithCauseId[causeId][operationId];
            const promise = deferredPromise.promise;
            promise.catch(noop);
            deferredPromise.reject(resolveResult);
            this._logger.info(`[${causeId}][${operationId}] rejected, result=${getPrintableObject(resolveResult) || `void`}`);
            delete this._deferredOperationsWithCauseId[causeId][operationId];
            return promise;
        }
        return Promise.reject(`[${causeId}][${operationId}] rejectOperationWithCauseId failed, operation not found`);
    }

    public maybeResolveOperationWithCauseId<T>(causeId: string, operationId?: string, resolveResult?: T): void {
        if (this.hasPendingOperationWithCauseId(causeId, operationId)) {
            this.resolveOperationWithCauseId(causeId, operationId, resolveResult).catch(e => {
                this._logger.info(`${causeId}maybeResolveOperationWithCauseId failed, reason=${getPrintableObject(e)}`);
            });
        } else {
            this._logger.info(`${causeId}maybeResolveOperationWithCauseId ignored, operation does not exist`);
        }
    }

    public maybeRejectOperationWithCauseId<T>(causeId: string, operationId?: string, resolveResult?: T): void {
        if (this.hasPendingOperationWithCauseId(causeId, operationId)) {
            this.rejectOperationWithCauseId(causeId, operationId, resolveResult).catch(noop);
        } else {
            this._logger.info(`${causeId}maybeRejectOperationWithCauseId ignored, operation does not exist`);
        }
    }

    public getPendingOperation<T = void>(operationName: string, operationId?: string, causeId = generateCauseId()): Promise<T> {
        if (!operationId && this._deferredOperations[operationName]) {
            return this._deferredOperations[operationName].deferred.promise;
        } else if (this._deferredOperationsWithOperationId[operationName] &&
                   this._deferredOperationsWithOperationId[operationName][operationId]) {
            return this._deferredOperationsWithOperationId[operationName][operationId].deferred.promise;
        }

        return Promise.reject(`${this.getOperationInfoForLogging(causeId, operationName, operationId)}: Deferred operation with given name and operation does not exist`);
    }

    public waitForOperation<T = void>(operationName: string, operationId?: string, causeId = generateCauseId()): Promise<T> {
        if (this.hasPendingOperation(operationName)) {
            return this._deferredOperations[operationName].deferred.promise;

        } else if (this.hasPendingOperation(operationName, operationId)) {
            return this._deferredOperationsWithOperationId[operationName][operationId].deferred.promise;
        }

        return Promise.reject(`${this.getOperationInfoForLogging(causeId, operationName, operationId)}failed=wait failed, operation not found`);
    }

    public resolveOperation<T>(operationName: DeferredOperationName, resolveResult?: T, operationId?: DeferredOperationName, causeId = generateCauseId(), persistOperationData = false): Promise<T> {
        if (!operationName) {
            return Promise.reject('Unable to resolve deferred operation with empty name');
        }

        if (this.hasPendingOperation(operationName)) {
            const operation = this._deferredOperations[operationName];
            const promise = this._deferredOperations[operationName].deferred.promise;
            this._deferredOperations[operationName].deferred.resolve(resolveResult);
            this._logger.info(`${this.getOperationInfoForLogging(operation.causeId, operationName, operationId)}resolved, result=${getPrintableObject(resolveResult) || `void`}`);
            if (!persistOperationData) {
                delete this._deferredOperations[operationName];
            }
            return promise;
        }

        if (this.hasPendingOperation(operationName, operationId)) {
            const operation = this._deferredOperationsWithOperationId[operationName][operationId];
            const promise = operation.deferred.promise;
            operation.deferred.resolve(resolveResult);
            this._logger.info(`${this.getOperationInfoForLogging(operation.causeId, operationName, operationId)}resolved, result=${getPrintableObject(resolveResult) || `void`}`);
            if (!persistOperationData) {
                delete this._deferredOperationsWithOperationId[operationName][operationId];
            }
            return promise;
        }
        return Promise.reject(`${this.getOperationInfoForLogging(causeId, operationName, operationId)}failed=resolve failed, operation not found`);
    }

    public rejectOperation<T>(operationName: DeferredOperationName, reason?: T, operationId?: DeferredOperationId, causeId = generateCauseId()): Promise<T> {
        if (!operationName) {
            return Promise.reject('Unable to reject deferred operation with empty name');
        }
        if (this.hasPendingOperation(operationName)) {
            const operation = this._deferredOperations[operationName];
            const promise = operation.deferred.promise;
            promise.catch(noop);
            operation.deferred.reject(reason);
            this._logger.info(`${this.getOperationInfoForLogging(operation.causeId, operationName, operationId)}rejected, reason=${getPrintableObject(reason) || `void`}`);
            delete this._deferredOperations[operationName];
            return promise;
        }

        if (this.hasPendingOperation(operationName, operationId)) {
            const operation = this._deferredOperationsWithOperationId[operationName][operationId];
            const promise = operation.deferred.promise;
            promise.catch(noop);
            operation.deferred.reject(reason);
            this._logger.info(`${this.getOperationInfoForLogging(operation.causeId, operationName, operationId)}rejected, reason=${getPrintableObject(reason) || `void`}`);
            delete this._deferredOperationsWithOperationId[operationName][operationId];
            return promise;
        }
        return Promise.reject(`${this.getOperationInfoForLogging(causeId, operationName, operationId)}failed=reject failed, operation not found`);
    }

    public maybeResolveOperation(operationName: DeferredOperationName, resolveResult?: any, operationId?: DeferredOperationName, causeId = generateCauseId()): void {
        const operationInfo = this.getOperationInfoForLogging(causeId, operationName, operationId);
        if (this.hasPendingOperation(operationName, operationId)) {
            this.resolveOperation(operationName, resolveResult, operationId, causeId).catch(e => {
                this._logger.info(`${operationInfo}maybeResolveOperation failed, reason=${getPrintableObject(e)}`);
            });
        } else {
            this._logger.info(`${operationInfo}maybeResolveOperation ignored, operation does not exist`);
        }
    }

    public maybeRejectOperation(operationName: DeferredOperationName, reason?: any, operationId?: DeferredOperationName, causeId = generateCauseId()): void {
        const operationInfo = this.getOperationInfoForLogging(causeId, operationName, operationId);
        if (this.hasPendingOperation(operationName, operationId)) {
            this.rejectOperation(operationName, reason, operationId, causeId).catch(noop);
        } else {
            this._logger.info(`${operationInfo}maybeRejectOperation ignored, operation does not exist`);
        }
    }

    public rejectPendingOperations(reason: any, causeId = generateCauseId()): void {
        this._disposed = true;
        this._logger.info(`[${causeId}]rejectPendingOperations=${reason}`);
        Object.keys(this._deferredOperations).forEach((operationName: string) => {
            this.maybeRejectOperation(operationName, reason, undefined, causeId);
            if (this._deferredOperationsWithOperationId[causeId]) {
                delete this._deferredOperationsWithOperationId[causeId][''];
            }
        });
        Object.keys(this._deferredOperationsWithOperationId).forEach((operationName: string) => {
            Object.keys(this._deferredOperationsWithOperationId[operationName]).forEach((operationId: string) => {
                this.maybeRejectOperation(operationName, reason, operationId, causeId);
                if (this._deferredOperationsWithOperationId[causeId]) {
                    delete this._deferredOperationsWithOperationId[causeId][operationId];
                }
            });
        });
        this._deferredOperations = {};
        this._deferredOperationsWithOperationId = {};
        this._endOperationDeferred.reject(reason);
    }

    protected getOperationInfoForLogging(causeId: CauseId, operationName: string, operationId = '') {
        const operationInfo = operationId ? `[${piiUtils.scrubMriOrOmit(operationId)}]` : ``;
        return `[${causeId}][${operationName}]${operationInfo}`;
    }
}



