import { IDisposable, MediaType, RemoveEndpointScope } from 'skype-calling-interface';
import { SlimCoreSettings } from '../typings/skype-calling-electron';

import * as _ from 'lodash';

import { EventSourceImpl } from '../../common/ts/events';

_.defaults(window, { SlimCore: {} });
_.defaults(SlimCore, { Enums: {} });

if (isSlimCoreRTModuleAvailable()) {
    const enumsRt = (window as any).SlimCoreRT.SlimCore.Enums;
    copyAllEnumObjects(enumsRt, SlimCore.Enums);
    correctAllEnumObjectsForValueNames(SlimCore.Enums);
}

if (Proxy) {
    SlimCore.Enums = new Proxy(SlimCore.Enums, {
        get: (target, name) => (name in target) ? target[name] : {}
    });
}

import Enums = SlimCore.Enums;
import { IInternalLogger } from '../../common/ts/internalLogger';

export function isSlimCoreRTModuleAvailable(): boolean {
    return !!((window as any).SlimCoreRT
        && (window as any).SlimCoreRT.SlimCore
        && (window as any).SlimCoreRT.SlimCore.Enums);
}

function firstLetterToUpperCase(inputString: string): string {
    return inputString[0].toUpperCase() + inputString.substr(1);
}

function correctEnumObjectValueName(enumObject: Object, valueName: string): void {
    const correctedValueName = firstLetterToUpperCase(valueName);
    if (correctedValueName !== valueName) {
        const value = enumObject[valueName];
        enumObject[value] = correctedValueName;
        delete enumObject[valueName];
        enumObject[correctedValueName] = value;
    }
}

function correctEnumObjectValueNames(enumObject: Object): void {
    Object.keys(enumObject).forEach(key => {
        correctEnumObjectValueName(enumObject, key);
    });
}

function correctAllEnumObjectsForValueNames(enumObjects: Object): void {
    Object.keys(enumObjects).forEach(key => {
        correctEnumObjectValueNames(enumObjects[key]);
    });
}

function copyEnumObject(sourceKey: string, sourceValue: Object, destination: Object): void {
    destination[sourceKey] = {};
    Object.keys(sourceValue).forEach(key => {
        destination[sourceKey][key] = sourceValue[key];
    });
}

function copyAllEnumObjects(source: Object, destination: Object): void {
    Object.keys(source).forEach(key => {
        copyEnumObject(key, source[key], destination);
    });
}

const AVAILABLE_VIDEO_STATES = [Enums.VideoStatus.Available, Enums.VideoStatus.Starting, Enums.VideoStatus.Running];

export type Wrapped<T, U> = T & Required<SlimCore.EventEmitter<U>>;

function isMatch<T>(value: T, pattern: Partial<T>) {
    // tslint:disable-next-line:no-for-in
    for (const key in pattern) {
        if (!pattern.hasOwnProperty(key)) {
            continue;
        }
        if (value[key] !== pattern[key]) {
            return false;
        }
    }
    return true;
}

function makeFilteredListener<U>(listener: SlimCore.EventHandler<U[keyof U]>, filter: Partial<U[keyof U]>) {
    return (args: U[keyof U]) => {
        if (isMatch(args, filter)) {
            listener(args);
        }
    };
}

export function wrap<T extends SlimCore.EventEmitter<U>, U>(target: T, settings: Partial<SlimCoreSettings> = {}): Wrapped<T, U> {
    const handle = (event: keyof U, filter: Partial<U[keyof U]>, listener: SlimCore.EventHandler<U[keyof U]>) => {

        const callback = filter ? makeFilteredListener(listener, filter) : listener;

        target.addListener(event, callback);

        return {
            dispose: () => {
                target.removeListener(event, callback);
            }
        };
    };

    if (settings.enableNativeHandle && 'handle' in target) {
        return target as Wrapped<T, U>;
    }

    return _.assign(target, { handle });
}

export type Engine = Wrapped<SlimCore.Engine, SlimCore.Engine.Events>;
export type Account = Wrapped<SlimCore.Account, SlimCore.Account.Events>;
export type CallHandler = Wrapped<SlimCore.CallHandler, SlimCore.CallHandler.Events>;
export type ContentSharing = Wrapped<SlimCore.ContentSharing, SlimCore.ContentSharing.Events>;

export enum PlatformType {
    Electron = 1,
    Uwp = 2
}

export type Settings = SlimCoreSettings & {
    platform: PlatformType;
};

export interface IVideoObjectChangedEventData {
    videoObjectId: number;
    videoType: Enums.VideoType;
    videoStatus: Enums.VideoStatus;
}

export interface IParticipantVideosChangedEventData {
    videos: { [type: number]: IVideoObjectChangedEventData[] };
    participantId: string;
}

export function forgetAndLog(promise: Promise<any>, _logger: IInternalLogger, logMessage: string): Promise<any> {
    if (promise) {
        _logger.info(`Promise executing: ${logMessage}`);
        return promise.catch(err => {
            _logger.logFailure(`Promise execution failed: ${err}`);
        });
    }

    _logger.logFailure(`Undefined promise @ ${new Error().stack}`);
    return Promise.resolve<void>();
}

export function getStrProperty(slimcoreObject: SlimCore.CallHandler | SlimCore.Engine, logger: IInternalLogger, objectId: SlimCore.ObjectId, propertyKey: SlimCore.Enums.Property, fallback?: string): string {
    try {
        return slimcoreObject.getStrProperty(objectId, propertyKey);
    } catch (error) {
        logger.logFailure(`getStrProperty failed for objectId=${objectId}, propertyKey=${propertyKey}: ${error}`);
        return fallback;
    }
}

export function getIntProperty(slimcoreObject: SlimCore.CallHandler | SlimCore.Engine, logger: IInternalLogger, objectId: SlimCore.ObjectId, propertyKey: SlimCore.Enums.Property, fallback?: number): number {
    try {
        return slimcoreObject.getIntProperty(objectId, propertyKey);
    } catch (error) {
        logger.logFailure(`getIntProperty failed: ${error}`);
        return fallback;
    }
}

export function hasFeature(feature: Enums.Feature): boolean {
    return feature in Enums.Feature;
}

type CancelEventHandler = () => void;
export class VideoStateCancelEvent extends EventSourceImpl<CancelEventHandler> {
    public fire(): void {
        this.raiseEvents(e => e());
    }
}

export interface VideoObjectStateWaitOptions {
    slimcoreInstance: Engine;
    slimcoreObject: any; // HODOR
    videoObjectId: number;
    logger: IInternalLogger;
    resolveStates: Enums.VideoStatus[];
    rejectStates?: Enums.VideoStatus[];
    timeout?: number;
    cancelEvent?: VideoStateCancelEvent;
}

/*
 * Hooks up a property change callback and awaits (with a timeout or indefinitely) for
 * video available state. If timeout hits, looks up value once before rejecting promise
 */
export function waitForVideoObjectState(options: VideoObjectStateWaitOptions): Promise<void> {
    return new Promise<void>((resolve: () => void, reject: (error?: any) => void) => {
        let done = false;
        let videoStateChangedCallback: IDisposable = null;
        let timeoutId = 0;
        let cancelSub: IDisposable;

        const _cleanupAndReturn = (error?: string) => {
            done = true;
            if (videoStateChangedCallback) {
                videoStateChangedCallback.dispose();
                videoStateChangedCallback = null;
            }
            if (timeoutId) {
                clearTimeout(timeoutId);
                timeoutId = 0;
            }
            if (cancelSub) {
                cancelSub.dispose();
            }
            if (error) {
                reject(error);
            } else {
                resolve();
            }
        };

        const onVideoObjectStateChanged = (value: Enums.VideoStatus) => {
            if (done) { return; }

            options.logger.info(`waitForVideoObjectState[${options.videoObjectId}] state changed -> ${value} (${Enums.VideoStatus[value]})`);

            if (isVideoInState(value, options.resolveStates)) {
                _cleanupAndReturn();
            } else if (options.rejectStates && isVideoInState(value, options.rejectStates)) {
                _cleanupAndReturn(`waitForVideoObjectState[${options.videoObjectId}] rejecting state ${value} (${Enums.VideoStatus[value]})`);
            }
        };

        const timeoutHandler = () => {
            if (done) { return; }

            options.logger.info(`waitForVideoObjectState[${options.videoObjectId}] timeout. Checking state again.`);

            const videoStatus = getIntProperty(options.slimcoreObject, options.logger, options.videoObjectId, Enums.Property.VideoStatus);

            if (isVideoInState(videoStatus, options.resolveStates)) {
                _cleanupAndReturn();
            } else if (options.rejectStates && isVideoInState(videoStatus, options.rejectStates)) {
                _cleanupAndReturn(`waitForVideoObjectState[${options.videoObjectId}] rejecting state ${videoStatus}`);
            } else {
                _cleanupAndReturn(`waitForVideoObjectState[${options.videoObjectId}] timeout.`);
            }
        };

        if (options.timeout) {
            timeoutId = setTimeout(timeoutHandler, options.timeout);
        }

        if (options.cancelEvent) {
            cancelSub = options.cancelEvent.subscribe(() => {
                options.logger.info(`waitForVideoObjectState[${options.videoObjectId}] canceled.`);
                _cleanupAndReturn(`waitForVideoObjectState[${options.videoObjectId}] canceled.`);
            });
        }

        videoStateChangedCallback = options.slimcoreInstance.handle(
            'object-property-changed',
            { objectId: options.videoObjectId, propKey: Enums.Property.VideoStatus },
            data => onVideoObjectStateChanged(data.value as number));

        const videoStatus = getIntProperty(options.slimcoreObject, options.logger, options.videoObjectId, Enums.Property.VideoStatus);
        onVideoObjectStateChanged(videoStatus);
    });
}

export function isVideoInState(state: Enums.VideoStatus, expected: Enums.VideoStatus[]): boolean {
    return expected.indexOf(state) !== -1;
}

export function videoObjectTimeout(settings: Settings): number {
    return (settings.platform === PlatformType.Uwp) ? 5000 : 500;
}

export function waitForVideoObjectToBeAvailable(
    slimcoreInstance: Engine,
    slimcoreObject: any, //HODOR
    videoObjectId: number,
    logger: IInternalLogger,
    timeout?: number,
    cancelEvent?: VideoStateCancelEvent): Promise<void> {
    return waitForVideoObjectState({
        slimcoreInstance,
        slimcoreObject,
        videoObjectId,
        logger,
        resolveStates: AVAILABLE_VIDEO_STATES,
        rejectStates: [],
        timeout,
        cancelEvent
    });
}

// This is for remote video to assure video is running
export function waitForVideoObjectToBeRunning(
    slimcoreInstance: Engine,
    slimcoreObject: any, //HODOR
    videoObjectId: number,
    logger: IInternalLogger,
    timeout?: number,
    cancelEvent?: VideoStateCancelEvent): Promise<void> {
    return waitForVideoObjectState({
        slimcoreInstance,
        slimcoreObject,
        videoObjectId,
        logger,
        resolveStates: [Enums.VideoStatus.Running],
        rejectStates: [Enums.VideoStatus.Stopping],
        timeout,
        cancelEvent
    });
}

// note this is for remote video to assure that when remote video gets stopped and binding released that remote video did actually release the underlying video sink to the available sinks pool
// waiting for not started, not available but also available because some transitions can happen sequentially inside slimcore
// when remote end stops sending then we end up in not started state, if we just stopped then video goes back to available (because remote still sends)
// not available is on case of sink aquisition falure (non fatal) but for paranoia we monitor that aswell
export function waitForVideoObjectToBeStopped(
    slimcoreInstance: Engine,
    slimcoreObject: any, //HODOR
    videoObjectId: number,
    logger: IInternalLogger,
    timeout?: number,
    cancelEvent?: VideoStateCancelEvent): Promise<void> {
    return waitForVideoObjectState({
        slimcoreInstance,
        slimcoreObject,
        videoObjectId,
        logger,
        resolveStates: [ Enums.VideoStatus.NotStarted, Enums.VideoStatus.Available, Enums.VideoStatus.NotAvailable],
        rejectStates: [],
        timeout,
        cancelEvent
    });
}

export function isVideoAvailable(state: Enums.VideoStatus) {
    return isVideoInState(state, AVAILABLE_VIDEO_STATES);
}

export function bufferToString(buf: Uint8Array): string {
    return String.fromCharCode.apply(null, buf);
}

export function stringToBuffer(msg: string): Uint8Array {
    const buf = new ArrayBuffer(msg.length);
    const bufView = new Uint8Array(buf);
    for (let i = 0; i < msg.length; i++) {
        bufView[i] = msg.charCodeAt(i);
    }
    return bufView;
}

export function mapMediaTypeStringToMediaType(mediaTypeStr: string): MediaType {
    switch (mediaTypeStr) {
        case 'audio':
            return MediaType.Audio;
        case 'video':
            return MediaType.Video;
        case 'panoramic-video':
            return MediaType.PanoramicVideo;
        case 'applicationsharing-video':
            return MediaType.ScreenShare;
        case 'data':
            return MediaType.Data;
        default:
            return undefined;
    }
}

export function mapToEmptyStrIfFalsy(inputString: string): string {
    if (inputString) {
        return inputString;
    } else {
        return '';
    }
}

export function hasCreateDataSource(engine: Engine) {
    return !!(engine.createDataSource || SlimCore.createDataSource);
}

export function hasCreateDataSink(engine: Engine) {
    return !!(engine.createDataSink || SlimCore.createDataSink);
}

export function createDataSource(engine: Engine, dataId: number) {
    if (engine.createDataSource) {
        return engine.createDataSource(dataId);
    } else {
        return SlimCore.createDataSource(dataId);
    }
}

export function createDataSink(engine: Engine, dataId: number) {
    if (engine.createDataSink) {
        return engine.createDataSink(dataId);
    } else {
        return SlimCore.createDataSink(dataId);
    }
}

export function convertImageData(image: SlimCore.ImageData) {
    if (image.isImage) {
        return (image as any) as ImageData;
    } else {
        const array = image.data as Uint8Array;
        const data = new Uint8ClampedArray(array.buffer, array.byteOffset, array.byteLength);
        return new ImageData(data, image.width, image.height);
    }
}

export function convertEndpointScope(value: RemoveEndpointScope): Enums.RemoveEndpointScope {
    switch (value) {
        case RemoveEndpointScope.Specified:
            return Enums.RemoveEndpointScope.RemoveEndpointScopeSpecified;
        case RemoveEndpointScope.Other:
            return Enums.RemoveEndpointScope.RemoveEndpointScopeOther;
        default:
            return undefined;
    }
}

export function selectMonitorForScreenSharing(selectedMonitor: SlimCore.MonitorInfo, monitors: SlimCore.MonitorInfo[]): SlimCore.MonitorInfo {
    const pickSameMonitor = (monitors: SlimCore.MonitorInfo[]) => {
        return _.find(monitors, monitor => {
            return monitor.monitorId === selectedMonitor.monitorId || _.isEqual(monitor.region, selectedMonitor.region);
        });
    };

    const handleDuplicateToExtend = (monitors: SlimCore.MonitorInfo[]) => {
        // if display configuration changes from duplicate to extend:
        // desktop will share the primary screen, there is no internal concept
        // laptop will share the extenal screen
        if (selectedMonitor.isDuplicated && isExtendConfiguration(monitors)) {
            // continue sharing the external screen
            return _.find(monitors, monitor => !monitor.isInternal);
        }
        return null;
    };

    const handleExtendToDuplicate = (monitors: SlimCore.MonitorInfo[]) => {
        // if display configuration changes from extend to duplicate
        if (!selectedMonitor.isDuplicated && isDuplicateConfiguration(monitors)) {
            // continue sharing the primary monitor
            return _.find(monitors, monitor => monitor.isPrimary);
        }
        return null;
    };
    return (!selectedMonitor) ? null : handleDuplicateToExtend(monitors) || handleExtendToDuplicate(monitors) || pickSameMonitor(monitors);
}

function isExtendConfiguration(monitors: SlimCore.MonitorInfo[]): boolean {
    const duplicated = count(monitors, monitor => monitor.isDuplicated);
    const primary = count(monitors, monitor => monitor.isPrimary);
    return duplicated === 0 && primary === 1 && monitors.length > 1;
}

function isDuplicateConfiguration(monitors: SlimCore.MonitorInfo[]): boolean {
    const duplicated = count(monitors, monitor => monitor.isDuplicated);
    const primary = count(monitors, monitor => monitor.isPrimary);
    return duplicated === 1 && primary === 1;
}

function count<T>(collection: T[], predicate: (value: T) => boolean): number {
    return _.reduce(collection, (result, item) => predicate(item) ? result + 1 : result, 0);
}



