import { AbstractLogAppender, LogLevel, LogLineMetaData, SLF_Flags, StandardLogFormatter } from '@skype/rt-js-bindings';
import { CallingLogger } from 'skype-calling-interface';

export class RedirectingLogAppender extends AbstractLogAppender {
    public constructor(private _logger: CallingLogger) {
        super(new StandardLogFormatter(SLF_Flags.Component));
    }
    public log(md: LogLineMetaData, logId: number, fmt: string, args: any[]): void {
        if (!this._logger) {
            return;
        }

        //tslint:disable:ban
        const loggerFunc = (md.level <= LogLevel.Debug4) ? this._logger.debug :
                    (md.level <= LogLevel.Debug2) ? this._logger.log :
                    (md.level <= LogLevel.Debug1) ? this._logger.info :
                    (md.level <= LogLevel.Warning) ? this._logger.warn :
                    this._logger.error;

        if (loggerFunc) {
            loggerFunc.apply(this._logger, [this.formatter().format(md, logId, fmt, args)]);
        }
    }
}



