import { pii } from '@skype/rt-js-bindings';

export function scrubMriOrOmit(mri: any) {
    if (typeof mri === 'string') {
        return pii.Mri(mri);
    } else {
        return pii.Omit(mri as string);
    }
}

export function scrubMriOrOmitList(mriList: any[]) {
    return mriList.forEach(scrubMriOrOmit);
}

export function mriOrId(mriOrId: string): string;
export function mriOrId(mriOrId: undefined): undefined;
export function mriOrId(mriOrId: string | undefined): string | undefined;
export function mriOrId(mriOrId: string | undefined): string | undefined {
    return mriOrId && pii.Mri(mriOrId);
}



