import {
    AccountConfiguration, CallingIntent, CallingStackInitOptions, CallSupport, E911Info, ICallingStack, ICallingStackError,
    ICallingStackEvents, IDeviceManager, IEcsProvider, IScreenSharingManager, ISetup, MediaConfiguration, MediaStatus, SkypeIdentity
} from 'skype-calling-interface';
import { ISlimCoreElectronApplicationHooks, ISlimCoreElectronStackFactory, ISlimCoreUwpStackFactory, SlimCoreElectronStackConfig, SlimCoreSettings, TelemetryService } from '../typings/skype-calling-electron';

import * as _ from 'lodash';
import * as async from '../../common/ts/async';
import * as piiUtils from '../../common/ts/piiUtils';
import { getOvb, getTsCallingVersion } from '../../common/ts/tsCallingVersionProvider';
import { deinitLogging, initDelegate, patchLogger } from './logging/electronInitLogging';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronCallRegistry from './SlimCoreElectronCallRegistry';
import SlimCoreElectronDeviceManager from './SlimCoreElectronDeviceManager';
import SlimCoreElectronEcsProvider from './SlimCoreElectronEcsProvider';
import SlimCoreElectronScreenSharingManager from './SlimCoreElectronScreenSharingManager';
import SlimCoreElectronSetup from './SlimCoreElectronSetup';
import { UwpEngineWrapped } from './SlimCoreUwpEventWrapper';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { generateCauseId } from '../../common/ts/causeId';
import { noop } from '../../common/ts/fn';
import InternalLogger from '../../common/ts/internalLogger';

const REQUIRED_API_VERSION = 7;

class SlimCoreElectronStackError extends Error implements ICallingStackError {
    public readonly callSupport: CallSupport;

    public constructor(message: string, callSupport: CallSupport = CallSupport.BrowserNotSupported) {
        super(message);
        this.callSupport = callSupport;
    }
}

export class SlimCoreElectronStack extends SlimCoreElectronBase<ICallingStackEvents> implements ICallingStack {
    public callRegistries: SlimCoreElectronCallRegistry[] = [];
    private _deviceManager: SlimCoreElectronDeviceManager;
    private _screenSharingManager: SlimCoreElectronScreenSharingManager;
    private _setup: SlimCoreElectronSetup;
    private _ecsProvider: SlimCoreElectronEcsProvider;
    private _startSlimcorePromiseDefered = async.defer<SlimCoreCommon.Engine>();
    private _libStatus: Enums.LibStatus;
    private _appHooks: ISlimCoreElectronApplicationHooks;
    private _telemetryService: TelemetryService;
    private _stackLoginPromise = async.defer<SlimCoreCommon.CallHandler>();
    private _mediaStartDeferredPromise = async.defer<SlimCoreCommon.Engine>();

    public constructor(config: SlimCoreElectronStackConfig) {
        super(new InternalLogger(config.logger).createChild('CallingStack'),
            _.assign<SlimCoreSettings, { platform: SlimCoreCommon.PlatformType }>(config.settings, { platform: SlimCoreCommon.PlatformType.Electron }),
            SlimCoreCommon.wrap(config.slimCoreInstance, config.settings));
        if (!isModuleAvailable()) {
            throw new SlimCoreElectronStackError('slimcore module is not available', CallSupport.BrowserNotSupported);
        }

        if (getApiVersion() < REQUIRED_API_VERSION) {
            throw new SlimCoreElectronStackError(`slimcore module API version ${getApiVersion()} incompatible with required ${REQUIRED_API_VERSION}`, CallSupport.IncompatibleVersions);
        }

        if (config.settings.enableTsCallingULLogging) {
            initDelegate(this._slimcoreInstance, config);
        }
        this._appHooks = config.appHooks;
        this._telemetryService = config.telemetryService;

        this._setup = new SlimCoreElectronSetup(this._slimcoreInstance);
        this._ecsProvider = new SlimCoreElectronEcsProvider(this._slimcoreInstance);

        //HODOR
        this._stackLoginPromise.promise.then(() => this._startSlimcorePromiseDefered.resolve(this._slimcoreInstance));

        const internalLogger = new InternalLogger(config.logger);
        const initPromise: Promise<SlimCoreCommon.CallHandler | SlimCoreCommon.Engine> = SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) ? this._startSlimcorePromiseDefered.promise : this._stackLoginPromise.promise;
        //by contract media init promise could complete before slimcore init promise, thus having guard in here (kind of paranoia, but we are guaranteed that slimcore is running and media is ready)
        const mediaReadyPromise = SlimCoreCommon.hasFeature(Enums.Feature.LazyMediaInit) ? initPromise.then(() => this._mediaStartDeferredPromise.promise) : initPromise;
        this._deviceManager = new SlimCoreElectronDeviceManager(internalLogger, this._settings, this._slimcoreInstance, mediaReadyPromise, config.appHooks);
        this._screenSharingManager = new SlimCoreElectronScreenSharingManager(internalLogger, this._settings, this._slimcoreInstance, config.appHooks, this._deviceManager);
        this._registerDisposable(this._slimcoreInstance.handle('e911-info-changed', undefined, data => this._onE911Info(data)));
        this._registerDisposable(this._slimcoreInstance.handle('media-status-changed', undefined, data => this._onMediaStatus(data)));
        this._registerDisposable(this._slimcoreInstance.handle('probe-devices-status-changed', undefined, data => this._onProbeDevicesStatusChanged(data)));
    }

    public dispose(causeId = generateCauseId()) {
        const fnLogger = this._logger.createFnLogger('dispose', causeId);
        fnLogger.info(`dispose`);

        if (this._libStatus !== Enums.LibStatus.Running) {
            this._startSlimcorePromiseDefered.reject(new Error('CallingStack dispose called before slimcore reached Running state'));
        }
        this._mediaStartDeferredPromise.reject(new Error('CallingStack dispose called before media was initialized'));

        const always = () => {
            this.callRegistries = [];
            fnLogger.info('call registries are disposed');
            this._screenSharingManager.dispose(causeId);
            this._screenSharingManager = undefined;
            fnLogger.info('screensharing manager is disposed');
            this._deviceManager.dispose(causeId);
            this._deviceManager = undefined;
            fnLogger.info('device manager is disposed');
            deinitLogging();
            this._slimcoreInstance.dispose();
            this._slimcoreInstance = undefined;
            fnLogger.info('slimcore instance is disposed');
            super.dispose();
        };
        const callRegistryDisposePromises = this.callRegistries.map(callRegistry => callRegistry.dispose(causeId));
        Promise.all(callRegistryDisposePromises).then(always, always);
    }

    public async init(initOptions: CallingStackInitOptions = { block: true }, causeId = generateCauseId()): Promise<void> {
        const fnLogger = this._logger.createFnLogger('init', causeId);
        fnLogger.info(`init`);

        if (!this._slimcoreInstance) {
            return Promise.reject(new Error('slimcore instance is already disposed'));
        }

        if (!SlimCoreCommon.hasFeature(Enums.Feature.AsyncStart)) {
            initOptions.block = true;
        }

        if (!initOptions.block) {
            this._registerDisposable(this._slimcoreInstance.handle(
                'lib-status-changed',
                { libStatus: SlimCore.Enums.LibStatus.Running },
                data => this._onLibStatusChanged(data)));
        }

        await Promise.all([
            this.getSetup().setNumber('*Lib/LazyMediaInit',
                SlimCoreCommon.hasFeature(Enums.Feature.LazyMediaInit) && !!initOptions.lazyMediaInit  ? 1 : 0),
            this.getSetup().setString('*Lib/SCT/Telemetry/DiagnosticLevel', initOptions.telemetryDiagnosticLevel || '')
        ]);

        this._slimcoreInstance.start(initOptions.block);
        fnLogger.info(`starting slimcoreInstance block=${initOptions.block}`);

        if (initOptions.block) {
            // Blocking call finished so we can resolve promise immediately,
            // and manually set libstatus to running
            this._libStatus = Enums.LibStatus.Running;
            this._startSlimcorePromiseDefered.resolve(this._slimcoreInstance);
        }
        return this._startSlimcorePromiseDefered.promise.then(noop);
    }

    private _onLibStatusChanged(eventData: SlimCore.Engine.Events.LibStatusChangedArgs) {
        this._logger.info(`_onLibStatusChanged from ${this._libStatus} => ${eventData.libStatus}`);
        if (eventData.libStatus === Enums.LibStatus.Running) {
            this._startSlimcorePromiseDefered.resolve(this._slimcoreInstance);
        } else if (eventData.libStatus === Enums.LibStatus.FatalError &&
            this._libStatus === Enums.LibStatus.Constructed) {
            // Handle fatal error during setup
            this._startSlimcorePromiseDefered.reject(new Error('Slimcore FatalError during start'));
        }
        this._libStatus = eventData.libStatus;
    }

    public getCallRegistry(identity?: SkypeIdentity, causeId = generateCauseId(), configuration?: AccountConfiguration): any {
        const id = identity ? piiUtils.mriOrId(identity.id) : undefined;
        const fnLogger = this._logger.createFnLogger(`getCallRegistry[identity=${id}]`, causeId);

        // old way of getCallRegistry(): ICallRegistry
        if (!identity) {
            fnLogger.info('returning default callRegistry');
            if (this.callRegistries[0]) {
                return this.callRegistries[0];
            }
            return this.createCallRegistry();
        }

        let callRegistry = this.callRegistries.find(callRegistry => callRegistry.identity === identity.id);
        if (callRegistry && !callRegistry.isDisposing) {
            fnLogger.info(`callRegistry already exists for ${piiUtils.mriOrId(identity.id)}`);
            return Promise.resolve(callRegistry);
        }

        const disposePromise = callRegistry && callRegistry.isDisposing ? callRegistry.disposePromise : Promise.resolve().then(noop);

        return this._startSlimcorePromiseDefered.promise
            .then(async () => {
                await disposePromise;
                callRegistry = this.createCallRegistry();
                await callRegistry.login(identity, causeId, SlimCoreCommon.mapToEmptyStrIfFalsy(JSON.stringify(configuration))).catch(() => callRegistry.dispose(causeId));
                return callRegistry;
            });
    }

    private createCallRegistry() : SlimCoreElectronCallRegistry {
        const callRegistry = new SlimCoreElectronCallRegistry(this._logger, this._settings, this._slimcoreInstance, this._deviceManager, this._appHooks, this._telemetryService);
        callRegistry.on('disposed', () => { this.callRegistries.splice(this.callRegistries.indexOf(callRegistry), 1); });
        callRegistry.loginPromise.then(this._stackLoginPromise.resolve, this._stackLoginPromise.reject);
        this.callRegistries.push(callRegistry);
        return callRegistry;
    }

    public getDeviceManager(): IDeviceManager {
        return this._deviceManager;
    }

    public getScreenSharingManager(): IScreenSharingManager {
        return this._screenSharingManager;
    }

    public getSetup(): ISetup {
        return this._setup;
    }

    public getEcsProvider(): IEcsProvider {
        return this._ecsProvider;
    }

    public fireIntent(callingIntent: CallingIntent, identity?: string) {
        if (!this._slimcoreInstance) {
            return;
        }

        const slimcoreIntent = SlimCoreElectronStack._callingIntentToSlimcoreIntent(callingIntent);
        if (!slimcoreIntent) {
            return;
        }

        this._slimcoreInstance.fireIntent(slimcoreIntent, identity);
    }

    public getVersion() {
        return {
            build: getTsCallingVersion(),
            ovb: getOvb()
        };
    }

    private static _callingIntentToSlimcoreIntent(callingIntent: CallingIntent): Enums.Intent {
        switch (callingIntent) {
            case CallingIntent.Incoming:
                return Enums.Intent.CallPush;
            case CallingIntent.Outgoing:
                return Enums.Intent.CallUser;
            case CallingIntent.WarmPush:
                return Enums.Intent.CallPreheat;
            case CallingIntent.MediaInit:
                return Enums.Intent.MediaInit;
            default:
                return undefined;
        }
    }

    private _slimcoreMediaStatusToCallingMediaStatus(slMediaStatus: Enums.MediaStatus): MediaStatus {
        switch (slMediaStatus) {
            case Enums.MediaStatus.Initialized:
                return MediaStatus.Initialized;
            case Enums.MediaStatus.NotAvailable:
                return MediaStatus.NotAvailable;
            case Enums.MediaStatus.Failed:
                return MediaStatus.Failed;
            case Enums.MediaStatus.Uninitialized:
                return MediaStatus.Uninitialized;
            default:
                return undefined;
        }
    }

    public getMediaStatus(): MediaStatus {
        return this._slimcoreMediaStatusToCallingMediaStatus(this._slimcoreInstance.getMediaStatus());
    }

    public setMediaConfig(configuration: MediaConfiguration) : Promise<void> {
        return new Promise(resolve => {
            // allow to call this before slimcore starts (then all config values get applied before csa can create new conference)
            this._setMediaConfig(configuration, this._startSlimcorePromiseDefered.promise);
            resolve();
        });
    }

    private _setMediaConfig(configuration: MediaConfiguration, mediaReady: Promise<SlimCoreCommon.Engine>) : void {

        const mediaConfigApi = !!this._slimcoreInstance.setMediaConfig;

        if (mediaConfigApi) {
            this._logger.info('Set Media Configuration, using setMediaConfig');
            this._slimcoreInstance.setMediaConfig(configuration as any);
        } else {
            // following slimcore methods can be called before slimcore started
            const mediaPortRangesApi = !!this._slimcoreInstance.setMediaPortRanges;
            const enableMediaQosApi = !!this._slimcoreInstance.enableMediaQoS;
            const createMediaConfigApi = !!this._slimcoreInstance.createMediaConfig;
            const openCameraApi = !!this._slimcoreInstance.setOpenCameraInMaxResolution;
            // following slimcore methods must be called after slimcore started!
            const enableTtySupportApi = !!this._slimcoreInstance.enableTtySupport;
            const enableAGCApi = !!this._slimcoreInstance.enableAGC;

            const hasPortRanges = configuration.hasOwnProperty('mediaPortRanges');
            const hasQos = configuration.hasOwnProperty('enableMediaQoS');
            const hasSetOpenCamera = configuration.hasOwnProperty('openCameraInMaxResolution');
            const hasEnableTtySupport = configuration.hasOwnProperty('enableTtySupport');
            const hasEnableAGC = configuration.hasOwnProperty('enableAGC');

            if (openCameraApi && hasSetOpenCamera) {
                this._logger.info('Set Media Configuration, using OpenCameraInMaxResolution');
                this._slimcoreInstance.setOpenCameraInMaxResolution(configuration.openCameraInMaxResolution);
            }
            if (hasQos || hasPortRanges) {
                if (enableMediaQosApi && mediaPortRangesApi) {
                    if (hasQos) {
                        this._logger.info('Set Media Configuration, using set enableMediaQos');
                        this._slimcoreInstance.enableMediaQoS(configuration.enableMediaQoS);
                    }
                    if (hasPortRanges) {
                        this._logger.info('Set Media Configuration, using set Media port ranges');
                        this._slimcoreInstance.setMediaPortRanges(configuration.mediaPortRanges);
                    }
                } else if (createMediaConfigApi) {
                    this._logger.info('Set Media Configuration, using CreateMediaConfig');
                    const mediaConfig = this._slimcoreInstance.createMediaConfig();
                    if (hasQos) {
                        mediaConfig.enableMediaQoS(configuration.enableMediaQoS);
                    }
                    if (hasPortRanges) {
                        mediaConfig.setMediaPortRanges(configuration.mediaPortRanges);
                    }
                } else {
                    this._logger.error('Set Media Configuration, failed no suitable methods found for qos or port ranges');
                }
            }
            if (hasEnableTtySupport || hasEnableAGC) {
                //will apply following settings only after media gets initialized, or they fail inside slimcore
                this._logger.info('Set Media Configuration, queuing operation to run when media is ready');
                mediaReady.then(() => {
                    if (hasEnableTtySupport) {
                        if (enableTtySupportApi) {
                            this._logger.info('Set Media Configuration, using enableTtySupport');
                            this._slimcoreInstance.enableTtySupport(configuration.enableTtySupport);
                        } else if (createMediaConfigApi) {
                            const mediaConfig = this._slimcoreInstance.createMediaConfig();
                            if (mediaConfig.enableTtySupport) {
                                this._logger.info('Set Media Configuration, using CreateMediaConfig.enableTtySupport');
                                mediaConfig.enableTtySupport(configuration.enableTtySupport);
                            } else {
                                this._logger.error('Set Media Configuration, failed no suitable methods found for enableTtySupport on CreateMediaConfig');
                            }
                        } else {
                            this._logger.error('Set Media Configuration, failed no suitable methods found for enableTtySupport');
                        }
                    }
                    if (hasEnableAGC && enableAGCApi) {
                        this._logger.info('Set Media Configuration, using enableAGC');
                        this._slimcoreInstance.enableAGC(configuration.enableAGC);
                    }
                });
            }
        }
    }

    public getE911Info(): Promise<E911Info> {
        if (!this._slimcoreInstance) {
            return Promise.reject(new Error('slimcore instance is already disposed'));
        }

        if (!this._slimcoreInstance.getE911Info) {
            return Promise.reject(new Error('getE911Info is not available'));
        }

        return this._startSlimcorePromiseDefered.promise.then(() => {
            const infoJson = this._slimcoreInstance.getE911Info();
            try {
                return JSON.parse(infoJson) as E911Info;

            } catch (e) {
                return Promise.reject(new Error('failed to parse e911Info'));
            }
        });
    }

    private _onE911Info(eventData: SlimCore.Engine.Events.E911InfoArgs) {
        let info: E911Info;
        try {
            info = JSON.parse(eventData.info) as E911Info;
        } catch (e) {
            this._logger.error('failed to parse e911Info');
            return;
        }
        this.event('e911InfoChanged').raise(info);
    }

    private _onMediaStatus(eventData: SlimCore.Engine.Events.MediaStatusChangedArgs) {
        const status = this._slimcoreMediaStatusToCallingMediaStatus(eventData.mediaStatus);
        this.event('mediaStatusChanged').raise(status);
        if (status === MediaStatus.Initialized) {
            this._mediaStartDeferredPromise.resolve(this._slimcoreInstance);
        } else if (status === MediaStatus.Failed) {
            this._mediaStartDeferredPromise.reject(new Error('Media initialization FatalError'));
        }
    }

    private _onProbeDevicesStatusChanged(eventData: SlimCore.Engine.Events.ProbeDevicesStatusChangedArgs) {
        this.event('probeDevicesStatusChanged').raise(eventData.ready);
    }
}

export class SlimCoreUwpStack extends SlimCoreElectronBase<ICallingStackEvents> implements ICallingStack {
    public callRegistries: SlimCoreElectronCallRegistry[] = [];
    private _deviceManager: SlimCoreElectronDeviceManager;
    private _setup: SlimCoreElectronSetup;
    private _screenSharingManager: SlimCoreElectronScreenSharingManager;
    private _startSlimcorePromiseDefered = async.defer<SlimCoreCommon.Engine>();
    private _libStatus: Enums.LibStatus;
    private _appHooks: ISlimCoreElectronApplicationHooks;
    private _telemetryService: TelemetryService;
    private _stackLoginPromise = async.defer<SlimCoreCommon.CallHandler>(); //HODOR
    private _mediaStartDeferredPromise = async.defer<SlimCoreCommon.Engine>();

    public constructor(config: SlimCoreElectronStackConfig) {
        super(new InternalLogger(config.logger).createChild('CallingStack', true),
            _.assign<SlimCoreSettings, { platform: SlimCoreCommon.PlatformType }>(config.settings, { platform: SlimCoreCommon.PlatformType.Uwp }),
            SlimCoreCommon.wrap(new UwpEngineWrapped(config.slimCoreInstance).objectAsWrapped, config.settings));

        if (!SlimCoreCommon.isSlimCoreRTModuleAvailable()) {
            throw new SlimCoreElectronStackError('slimcore module is not available', CallSupport.BrowserNotSupported);
        }
        this._appHooks = config.appHooks;
        this._telemetryService = config.telemetryService;
        this._setup = new SlimCoreElectronSetup(this._slimcoreInstance);

        // HODOR
        this._stackLoginPromise.promise.then(() => this._startSlimcorePromiseDefered.resolve(this._slimcoreInstance));

        const initPromise: Promise<SlimCoreCommon.CallHandler | SlimCoreCommon.Engine> = SlimCoreCommon.hasFeature(Enums.Feature.MultiUserSupport) ? this._startSlimcorePromiseDefered.promise : this._stackLoginPromise.promise;
        const mediaReadyPromise = SlimCoreCommon.hasFeature(Enums.Feature.LazyMediaInit) ? initPromise.then(() => this._mediaStartDeferredPromise.promise) : initPromise;
        this._deviceManager = new SlimCoreElectronDeviceManager(this._logger, this._settings, this._slimcoreInstance, mediaReadyPromise, config.appHooks);
        this._screenSharingManager = new SlimCoreElectronScreenSharingManager(this._logger, this._settings, this._slimcoreInstance, config.appHooks, this._deviceManager);
        this._registerDisposable(this._slimcoreInstance.handle('media-status-changed', undefined, data => this._onMediaStatus(data)));
    }

    public dispose(causeId = generateCauseId()) {
        const fnLogger = this._logger.createFnLogger('dispose', causeId);
        fnLogger.info(`dispose`);

        if (this._libStatus !== Enums.LibStatus.Running) {
            this._startSlimcorePromiseDefered.reject(new Error('CallingStack dispose called before slimcore reached Running state'));
        }
        this._mediaStartDeferredPromise.reject(new Error('CallingStack dispose called before media was initialized'));

        const always = () => {
            this.callRegistries = [];
            fnLogger.info('call registries are disposed');
            this._screenSharingManager.dispose(causeId);
            this._screenSharingManager = undefined;
            fnLogger.info('screensharing manager is disposed');
            this._deviceManager.dispose(causeId);
            this._deviceManager = undefined;
            fnLogger.info('device manager is disposed');
            this._slimcoreInstance.dispose();
            this._slimcoreInstance = undefined;
            fnLogger.info('slimcore instance is disposed');
            super.dispose();
        };
        const callRegistryDisposePromises = this.callRegistries.map(callRegistry => callRegistry.dispose(causeId));
        Promise.all(callRegistryDisposePromises).then(always, always);
    }

    public async init(initOptions: CallingStackInitOptions = { block: true }, causeId = generateCauseId()): Promise<void> {
        const fnLogger = this._logger.createFnLogger('init', causeId);
        fnLogger.info(`init`);

        if (!this._slimcoreInstance) {
            return Promise.reject(new Error('slimcore instance is already disposed'));
        }

        if (!SlimCoreCommon.hasFeature(Enums.Feature.AsyncStart)) {
            initOptions.block = true;
        }

        if (!initOptions.block) {
            this._registerDisposable(this._slimcoreInstance.handle(
                'lib-status-changed',
                { libStatus: SlimCore.Enums.LibStatus.Running },
                data => this._onLibStatusChanged(data)));
        }

        await Promise.all([
            this.getSetup().setNumber('*Lib/LazyMediaInit',
                SlimCoreCommon.hasFeature(Enums.Feature.LazyMediaInit) && !!initOptions.lazyMediaInit  ? 1 : 0),
            this.getSetup().setString('*Lib/SCT/Telemetry/DiagnosticLevel', initOptions.telemetryDiagnosticLevel || '')
        ]);

        this._slimcoreInstance.start(initOptions.block);
        fnLogger.info(`starting slimcoreInstance block=${initOptions.block}`);

        if (initOptions.block) {
            // Blocking call finished so we can resolve promise immediately,
            // and manually set libstatus to running
            this._libStatus = Enums.LibStatus.Running;
            this._startSlimcorePromiseDefered.resolve(this._slimcoreInstance);
        }
        return this._startSlimcorePromiseDefered.promise.then(noop);
    }

    private _onLibStatusChanged(eventData: SlimCore.Engine.Events.LibStatusChangedArgs) {
        this._logger.info(`_onLibStatusChanged from ${this._libStatus} => ${eventData.libStatus}`);
        if (eventData.libStatus === Enums.LibStatus.Running) {
            this._startSlimcorePromiseDefered.resolve(this._slimcoreInstance);
        } else if (eventData.libStatus === Enums.LibStatus.FatalError &&
            this._libStatus === Enums.LibStatus.Constructed) {
            // Handle fatal error during setup
            this._startSlimcorePromiseDefered.reject(new Error('Slimcore FatalError during start'));
        }
        this._libStatus = eventData.libStatus;
    }

    public getCallRegistry(identity?: SkypeIdentity, causeId = generateCauseId(), configuration?: AccountConfiguration): any {
        const id = identity ? piiUtils.mriOrId(identity.id) : undefined;
        const fnLogger = this._logger.createFnLogger(`getCallRegistry[identity=${id}]`, causeId);

        // old way of getCallRegistry(): ICallRegistry
        if (!identity) {
            fnLogger.info('returning default callRegistry');
            if (this.callRegistries[0]) {
                return this.callRegistries[0];
            }
            return this.createCallRegistry();
        }
        let callRegistry = this.callRegistries.find(callRegistry => callRegistry.identity === identity.id);
        if (callRegistry && !callRegistry.isDisposing) {
            fnLogger.info(`callRegistry already exists for ${piiUtils.mriOrId(identity.id)}`);
            return Promise.resolve(callRegistry);
        }

        const disposePromise = callRegistry && callRegistry.isDisposing ? callRegistry.disposePromise : Promise.resolve().then(noop);

        return this._startSlimcorePromiseDefered.promise
            .then(async () => {
                await disposePromise;
                callRegistry = this.createCallRegistry();
                await callRegistry.login(identity, causeId, SlimCoreCommon.mapToEmptyStrIfFalsy(JSON.stringify(configuration))).catch(() => callRegistry.dispose(causeId));
                return callRegistry;
            });
    }

    private createCallRegistry() : SlimCoreElectronCallRegistry {
        const callRegistry = new SlimCoreElectronCallRegistry(this._logger, this._settings, this._slimcoreInstance, this._deviceManager, this._appHooks, this._telemetryService);
        callRegistry.on('disposed', () => { this.callRegistries.splice(this.callRegistries.indexOf(callRegistry), 1); });
        callRegistry.loginPromise.then(this._stackLoginPromise.resolve, this._stackLoginPromise.reject);
        this.callRegistries.push(callRegistry);
        return callRegistry;
    }

    public getDeviceManager(): IDeviceManager {
        this._logger.info('retrieving device manager');
        return this._deviceManager;
    }

    public getScreenSharingManager(): IScreenSharingManager {
        // TODO: implement, waiting for support in wrapper
        return this._screenSharingManager;
    }

    public getSetup(): ISetup {
        return this._setup;
    }

    public getEcsProvider(): IEcsProvider {
        // TODO: implement, waiting for support in wrapper
        return null;
    }

    public fireIntent(callingIntent: CallingIntent, identity?: string) {
        // TODO: implement once wrapper supports this
    }

    public getVersion() {
        return {
            build: getTsCallingVersion(),
            ovb: getOvb()
        };
    }

    public getMediaStatus(): MediaStatus {
        return null;
    }

    public setMediaConfig(configuration: MediaConfiguration): Promise<void> {
        return new Promise(resolve => {
            // allow to call this before slimcore starts (then all config values get applied before csa can create new conference)
            this._logger.info('Set Media Configuration, using new default method');
            this._slimcoreInstance.setMediaConfig(configuration as any);
            resolve();
        });
    }

    private _slimcoreMediaStatusToCallingMediaStatus(slMediaStatus: Enums.MediaStatus): MediaStatus {
        switch (slMediaStatus) {
            case Enums.MediaStatus.Initialized:
                return MediaStatus.Initialized;
            case Enums.MediaStatus.NotAvailable:
                return MediaStatus.NotAvailable;
            case Enums.MediaStatus.Failed:
                return MediaStatus.Failed;
            case Enums.MediaStatus.Uninitialized:
                return MediaStatus.Uninitialized;
            default:
                return undefined;
        }
    }

    private _onMediaStatus(eventData: SlimCore.Engine.Events.MediaStatusChangedArgs) {
        const status = this._slimcoreMediaStatusToCallingMediaStatus(eventData.mediaStatus);
        this.event('mediaStatusChanged').raise(status);
        if (status === MediaStatus.Initialized) {
            this._mediaStartDeferredPromise.resolve(this._slimcoreInstance);
        } else if (status === MediaStatus.Failed) {
            this._mediaStartDeferredPromise.reject(new Error('Media initialization FatalError'));
        }

    }
}

export const slimCoreElectronStackFactory: ISlimCoreElectronStackFactory = {
    build(config: SlimCoreElectronStackConfig): Promise<ICallingStack> {
        return Promise.resolve()
            .then(() => {
                if (config.settings.enableTsCallingULLogging) {
                    patchLogger(config);
                }
                return new SlimCoreElectronStack(config);
            }).catch((error: Error) => {
                if (error instanceof SlimCoreElectronStackError) {
                    throw error;
                } else {
                    throw new SlimCoreElectronStackError(`${error}`);
                }
            });
    },
    getVersion() {
        return {
            build: getTsCallingVersion(),
            ovb: getOvb()
        };
    }
};

export const slimCoreUwpStackFactory: ISlimCoreUwpStackFactory = {
    build(config: SlimCoreElectronStackConfig): Promise<ICallingStack> {
        return Promise.resolve()
            .then(() => new SlimCoreUwpStack(config))
            .catch((error: Error) => {
                if (error instanceof SlimCoreElectronStackError) {
                    throw error;
                } else {
                    throw new SlimCoreElectronStackError(`${error}`);
                }
            });
    },
    getVersion() {
        return {
            build: getTsCallingVersion(),
            ovb: getOvb()
        };
    }
};

function getApiVersion(): number {
    return SlimCore.getApiVersion && SlimCore.getApiVersion();
}

function isModuleAvailable(): boolean {
    return !!(SlimCore && SlimCore.getVersion);
}



