/// <reference path="../typings/slimcore-enums.d.ts" />

import { IRenderTarget, ScalingMode, StartStreamOptions } from 'skype-calling-interface';

import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronVideoRenderer from './SlimCoreElectronVideoRenderer';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { IInternalLogger } from '../../common/ts/internalLogger';
let _instanceCount = 0;

export interface SlimCoreElectronRemoteVideoRendererTracker {
    registerRenderer(object: SlimCoreElectronRemoteVideoRenderer): void;
    unregisterRenderer(object: SlimCoreElectronRemoteVideoRenderer): void;
}

export default class SlimCoreElectronRemoteVideoRenderer extends SlimCoreElectronVideoRenderer {
    private readonly _id = _instanceCount++;

    public get renderTarget() {
        return this._renderTarget;
    }

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _renderTarget: IRenderTarget,
        private _videoType: Enums.VideoType,
        videoBinding: SlimCore.VideoBindingRenderer,
        private _options: StartStreamOptions,
        private _rendererTracker: SlimCoreElectronRemoteVideoRendererTracker
    ) {
        super(logger.createChild(() => `RemoteVideoRenderer[${this._id}]`), settings, engine);
        this._logger.info('constructor');

        const isScreenShare = (_videoType === Enums.VideoType.ScreenShare);
        this._videoBinding = videoBinding;

        this._options = _options || {
            scalingMode: isScreenShare ? ScalingMode.Fit : ScalingMode.Crop,
            transparent: isScreenShare,
            ignoreMirroring: false
        };

        this._createVideoRenderer(this._renderTarget, this._options, false);
        this._rendererTracker.registerRenderer(this); //this has to be last because if constructor fails there is no way to dispose the instance
    }

    public clone(renderTarget: IRenderTarget): SlimCoreElectronRemoteVideoRenderer {
        this._logger.info('clone');
        return new SlimCoreElectronRemoteVideoRenderer(
            this._logger,
            this._settings,
            this._slimcoreInstance,
            renderTarget,
            this._videoType,
            this._videoBinding,
            this._options,
            this._rendererTracker
        );
    }

    public dispose() {
        this._logger.info('dispose');
        try {
            super.dispose();
        } finally {
            this._renderTarget = null;
            if (this._rendererTracker) {
                this._rendererTracker.unregisterRenderer(this);
                this._rendererTracker = null; // to be sure we unregister once!
            }
        }
    }
}



