import { IRootToolsManagerDelegate } from '@skype/rt-js-bindings';
import { CallingLogger } from 'skype-calling-interface';
import * as SyncTasks from 'synctasks';
import * as SlimCoreCommon from '../SlimCoreCommon';

export class RootToolsManagerDelegate implements IRootToolsManagerDelegate {

    public constructor(private _slimcore: SlimCoreCommon.Engine, private _logger: CallingLogger) {}

    public fetchEcsConfig<T>(ecsNs: string, ecsKey: string): SyncTasks.Promise<T> {
        return SyncTasks.Resolved<T>(undefined);
    }

    public sendTelemetry(tenant: string, data: {[name: string]: string}): void {
        return;
    }

    public sendLoggingEventToNative(message: string, auxiliaryPayload: string): void {
        if (!this._slimcore.handleLoggingEvent) {
            this._logger.warn('slimcore.handleLoggingEvent is not available');
            return;
        }
        this._slimcore.handleLoggingEvent(message, auxiliaryPayload);
    }

    public setNativeLoggingEventCallback(callback: (message: string, auxiliaryPayload: string) => void): void {
        if (!callback || typeof callback !== 'function') {
            this._logger.warn('RootToolsManagerDelegate.setNativeLoggingEventCallback invalid callback provided');
            return;
        }

        this._slimcore.handle('logging-event', undefined, (data: SlimCore.Engine.Events.LoggingEventArgs) => {
            this._logger.debug(`RootToolsManagerDelegate: LoggingNativeEvent msg:${data.message} aux${data.auxiliaryPayload}`);
            callback(data.message, data.auxiliaryPayload);
        });
    }
}



