import { CallingLogger, CauseId, EventCallback, IDisposable, IObservableObject, ObservableChangedCallback } from 'skype-calling-interface';
import { EventSourceImpl } from './events';

export interface ObservableEventCallback {
    name: string;
    handler: EventCallback;
}

export interface Callback {
    changed: ObservableChangedCallback | undefined;
    on: ObservableEventCallback | undefined;
}

export default class ObservableBase<T> extends EventSourceImpl<Callback> implements IObservableObject<T> {
    constructor(logger?: CallingLogger) {
        super(logger);
    }

    public changed(callback: ObservableChangedCallback): IDisposable {
        return this.subscribe({
            changed: callback,
            on: undefined
        });
    }

    public on<P extends keyof T>(eventName: P, callback: T[P]): IDisposable {
        return this.subscribe({
            changed: undefined,
            on: {
                name: String(eventName),
                handler: this._toEventCallback<P>(callback)
            }
        });
    }

    public once<P extends keyof T>(eventName: P, callback: T[P], causeId?: CauseId): IDisposable {
        let subscription: IDisposable;
        const onceSubscription = (...args: any[]) => {
            subscription.dispose(causeId);
            this._toEventCallback<P>(callback)(...args);
        };

        return subscription = this.on(eventName, this._fromEventCallback<P>(onceSubscription));
    }

    protected raiseChanged(): void {
        this.raiseEvents(callback => callback.changed && callback.changed());
    }

    protected event<P extends keyof T>(eventName: P): { raise: T[P] } {
        return {
            raise: this._fromEventCallback<P>((...args: any[]) => this._raiseEventImpl(String(eventName), ...args))
        };
    }

    protected _raiseEventImpl(eventName: string, ...args: any[]): void {
        this.raiseEvents(callback => callback.on && callback.on.name === eventName && callback.on.handler(...args));
    }

    private _toEventCallback<P extends keyof T>(callback: T[P]): EventCallback {
        return callback as any;
    }

    private _fromEventCallback<P extends keyof T>(callback: EventCallback): T[P] {
        return callback as any;
    }
}



