import * as _ from 'lodash';
import {
    CallingLogger,
    CauseId,
    ContentSharingStatus,
    SessionTerminatedReason,
    TerminatedReason
} from 'skype-calling-interface';

import * as async from '../../common/ts/async';
import InternalLogger, { IInternalLogger } from './internalLogger';

interface ContentSharingResultEvent {
    failureReason: number;
    code: number;
    subCode: number;
}

interface ContentSharingSessionStateUpdateResultEvent {
    id: string;
    failureReason: number;
    code: number;
    subCode: number;
}

const CONSTANTS = {
    CANCELED_TERMINATION_REASON: {
        terminatedReason: TerminatedReason.Cancelled,
        terminatedReasonCode: 0,
        terminatedReasonSubCode: 0,
        errorMessage: 'Content sharing is disconnected'
    },
    ACTION_NOT_ALLOWED: (errorMessage: string) => {
        return {
            terminatedReason: TerminatedReason.ActionNotAllowed,
            terminatedReasonCode: 0,
            terminatedReasonSubCode: 0,
            errorMessage
        };
    }
};

export interface ContentSharingPlatformImpl {
    noFailureReason: number;
    contentSharingStatus: ContentSharingStatus;
    contentSharingGuid: string;
    contentSharingTerminationReason: SessionTerminatedReason;
    convertFailureReason(reason: number): TerminatedReason;
    convertContentSharingStatus(status: number): ContentSharingStatus;

    callStartContentSharing(): Promise<void>;
    callStopContentSharing(): Promise<void>;
    callUpdateContentSharingSessionState(commandId: string, sessionState: string): Promise<void>;
    callTakeContentSharingControl(): Promise<void>;
    callUpdateContentSharingParticipantState(): Promise<void>;
}

export class ContentSharingSession {
    private _pendingDeferreds: async.Deferred<void>[] = [];
    private _updateContentSharingSessionStateDeferredPromises: { [id: string]: async.Deferred<void> } = {};
    private _takeControlDeferredPromise: async.Deferred<void>;
    private _updateParticipantStateDeferredPromise: async.Deferred<void>;
    private _stopContentSharingDeferredPromise: async.Deferred<void>;
    private _joinContenSharingDeferredPromise: async.Deferred<void>;
    private _startContentSharingDeferredPromise: async.Deferred<void>;
    private _logger: IInternalLogger;
    public constructor(
        private _platformImpl: ContentSharingPlatformImpl,
        logger: CallingLogger) {
            this._logger = new InternalLogger(logger);
        }

    public statusChanged(status: ContentSharingStatus): void {
        if (status === ContentSharingStatus.Done || status === ContentSharingStatus.Failed) {
            if (this._stopContentSharingDeferredPromise) {
                this._invokeDeferred(this._stopContentSharingDeferredPromise);
            }

            if (this._startContentSharingDeferredPromise) {
                const terminatedReason = this._platformImpl.contentSharingTerminationReason && this._platformImpl.contentSharingTerminationReason.terminatedReason !== TerminatedReason.Undefined ?
                    this._platformImpl.contentSharingTerminationReason :
                    CONSTANTS.CANCELED_TERMINATION_REASON;
                this._rejectDeferred(this._startContentSharingDeferredPromise, terminatedReason);
            }

            this._logger.info('Content sharing is disconnected');
            this._rejectPendingPromises(CONSTANTS.CANCELED_TERMINATION_REASON);
        } else if (status === ContentSharingStatus.Ringing || status === ContentSharingStatus.Viewing || status === ContentSharingStatus.Connected) {

            if (this._stopContentSharingDeferredPromise) {
                const errorMessage = 'Content sharing cannot be ended when not presenting';
                const terminatedReason = CONSTANTS.ACTION_NOT_ALLOWED(errorMessage);
                this._logger.error(errorMessage);
                this._rejectDeferred(this._stopContentSharingDeferredPromise, terminatedReason);
            }
        } else if (status === ContentSharingStatus.Presenting) {
            if (this._startContentSharingDeferredPromise) {
                this._invokeDeferred(this._startContentSharingDeferredPromise);
            }

            if (this._takeControlDeferredPromise) {
                this._invokeDeferred(this._takeControlDeferredPromise);
            }
        }
    }

    // Start a new content sharing session
    public startContentSharing(causeId: CauseId): Promise<void> {
        const fnLogger = this._logger.createFnLogger('startContentSharing', causeId);
        if (this._startContentSharingDeferredPromise) {
            return this._startContentSharingDeferredPromise.promise;
        }

        // Ensure we are in the initial state
        if (this._platformImpl.contentSharingStatus !== ContentSharingStatus.None) {
            const errorMessage = 'Content sharing cannot be started as it is not in the initial state';
            const terminatedReason = CONSTANTS.ACTION_NOT_ALLOWED(errorMessage);
            fnLogger.logFailure(errorMessage);
            return Promise.reject(terminatedReason);
        }

        fnLogger.info(`Start content sharing for : ${this._platformImpl.contentSharingGuid}`);

        this._startContentSharingDeferredPromise = async.defer<void>();
        const always = () => {
            this._startContentSharingDeferredPromise = null;
        };
        this._startContentSharingDeferredPromise.promise.then(always, always);

        // Promise resolves when we go to presenting state
        return Promise.resolve(undefined)
            .then(() => this._platformImpl.callStartContentSharing())
            .then(() => this._pendingDeferreds.push(this._startContentSharingDeferredPromise))
            .then(() => this._startContentSharingDeferredPromise.promise)
            .catch(error => {
                this.handleContentSharingOperationError(error, 'startContentSharing', fnLogger);
            });
    }

    // Join an incoming content sharing session
    public joinContentSharing(causeId: CauseId): Promise<void> {
        const fnLogger = this._logger.createFnLogger('joinContentSharing', causeId);
        if (this._joinContenSharingDeferredPromise) {
            return this._joinContenSharingDeferredPromise.promise;
        }

        // Ensure we are in the ringing state
        if (this._platformImpl.contentSharingStatus !== ContentSharingStatus.Ringing) {
            const errorMessage = 'Content sharing cannot be joined as it is not in ringing state';
            const terminatedReason = CONSTANTS.ACTION_NOT_ALLOWED(errorMessage);
            fnLogger.logFailure(errorMessage);
            return Promise.reject(terminatedReason);
        }

        fnLogger.info(`Join content sharing for : ${this._platformImpl.contentSharingGuid}`);

        this._joinContenSharingDeferredPromise = async.defer<void>();
        const always = () => {
            this._joinContenSharingDeferredPromise = null;
        };
        this._joinContenSharingDeferredPromise.promise.then(always, always);

        // Promise resolves when join status result comes back and state goes to connected
        return Promise.resolve(undefined)
            .then(() => this._platformImpl.callStartContentSharing())
            .then(() => this._pendingDeferreds.push(this._joinContenSharingDeferredPromise))
            .then(() => this._joinContenSharingDeferredPromise.promise)
            .catch(error => {
                this.handleContentSharingOperationError(error, 'joinContentSharing', fnLogger);
            });
    }

    public onJoinResultEvent = (event: ContentSharingResultEvent) => {
        if (!this._joinContenSharingDeferredPromise) {
            this._logger.warn(`No pending promise for join. Event data: ${event}`);
            return;
        }

        this._logger.info(`JoinContentSharing completed for: ${this._platformImpl.contentSharingGuid}, status: ${JSON.stringify(event)}`);

        if (event.failureReason === this._platformImpl.noFailureReason) {
            this._invokeDeferred(this._joinContenSharingDeferredPromise);
        } else {
            const terminatedReasonCode = event.code;
            const terminatedReasonSubCode = event.subCode;
            const terminatedReason = this._platformImpl.convertFailureReason(event.failureReason);
            const errorMessage = `Join failed with reason ${terminatedReason}`;
            this._logger.error(errorMessage);
            this._rejectDeferred(this._joinContenSharingDeferredPromise, { terminatedReason, terminatedReasonCode, terminatedReasonSubCode, errorMessage });
        }
    }

    // Update content sharing participant state for an incoming session. This will cause the
    // endpoint to appear as viewer for the session in the roster update to other participants
    public updateContentSharingParticipantStateToViewer(causeId: CauseId): Promise<void> {
        const fnLogger = this._logger.createFnLogger('updateContentSharingParticipantStateToViewer', causeId);
        if (this._updateParticipantStateDeferredPromise) {
            return this._updateParticipantStateDeferredPromise.promise;
        }

        // Ensure we are in the connected state
        if (this._platformImpl.contentSharingStatus !== ContentSharingStatus.Connected) {
            const errorMessage = 'Cannot update participant state as content sharing is not in connected state';
            const terminatedReason = CONSTANTS.ACTION_NOT_ALLOWED(errorMessage);
            fnLogger.logFailure(errorMessage);
            return Promise.reject(terminatedReason);
        }

        fnLogger.info(`Update content sharing participant state for : ${this._platformImpl.contentSharingGuid}`);

        this._updateParticipantStateDeferredPromise = async.defer<void>();
        const always = () => {
            this._updateParticipantStateDeferredPromise = null;
        };
        this._updateParticipantStateDeferredPromise.promise.then(always, always);

        // Promise resolves when result comes back and state goes to viewing
        return Promise.resolve(undefined)
            .then(() => this._platformImpl.callUpdateContentSharingParticipantState())
            .then(() => this._pendingDeferreds.push(this._updateParticipantStateDeferredPromise))
            .then(() => this._updateParticipantStateDeferredPromise.promise)
            .catch(error => {
                this.handleContentSharingOperationError(error, 'joinContentSharing', fnLogger);
            });
    }

    public onUpdateParticipantStateResultEvent = (event: ContentSharingResultEvent) => {
        if (!this._updateParticipantStateDeferredPromise) {
            this._logger.warn(`No pending promise for update participant state. Event data: ${event}`);
            return;
        }

        this._logger.info(`UpdateContentSharingParticipantStateToViewer completed for: ${this._platformImpl.contentSharingGuid}, status: ${JSON.stringify(event)}`);
        if (event.failureReason === this._platformImpl.noFailureReason) {
            this._invokeDeferred(this._updateParticipantStateDeferredPromise);
        } else {
            const terminatedReasonCode = event.code;
            const terminatedReasonSubCode = event.subCode;
            const terminatedReason = this._platformImpl.convertFailureReason(event.failureReason);
            const errorMessage = `Update participant state failed with reason ${terminatedReason}`;
            this._logger.error(errorMessage);
            this._rejectDeferred(this._updateParticipantStateDeferredPromise, { terminatedReason, terminatedReasonCode, terminatedReasonSubCode, errorMessage });
        }
    }

    // Update metadata for the session if presenting
    public updateContentSharingSessionState(id: string, sessionState: string, causeId: CauseId): Promise<void> {
        const fnLogger = this._logger.createFnLogger('updateContentSharingSessionState', causeId);
        if (this._updateContentSharingSessionStateDeferredPromises[id]) {
            return this._updateContentSharingSessionStateDeferredPromises[id].promise;
        }

        // Ensure we are in the presenting state
        if (this._platformImpl.contentSharingStatus !== ContentSharingStatus.Presenting) {
            const errorMessage = 'Cannot update session state as content sharing is not in presenting state';
            const terminatedReason = CONSTANTS.ACTION_NOT_ALLOWED(errorMessage);
            fnLogger.logFailure(errorMessage);
            return Promise.reject(terminatedReason);
        }

        const deferred = async.defer<void>();
        this._updateContentSharingSessionStateDeferredPromises[id] = deferred;
        const always = () => {
            delete this._updateContentSharingSessionStateDeferredPromises[id];
        };
        this._updateContentSharingSessionStateDeferredPromises[id].promise.then(always, always);

        fnLogger.info(`Update content sharing session state for : ${this._platformImpl.contentSharingGuid}`);

        // Promise resolves when result comes back
        return Promise.resolve(undefined)
            .then(() => this._platformImpl.callUpdateContentSharingSessionState(id, sessionState))
            .then(() => this._updateContentSharingSessionStateDeferredPromises[id].promise)
            .catch(error => {
                this.handleContentSharingOperationError(error, 'updateContentSharingSessionState', fnLogger);
            });
    }

    public onSessionStateResultEvent = (event: ContentSharingSessionStateUpdateResultEvent) => {
        this._logger.info(`UpdateContentSharingSessionState completed for: ${this._platformImpl.contentSharingGuid}, status: ${JSON.stringify(event)}`);

        if (this._updateContentSharingSessionStateDeferredPromises.hasOwnProperty(event.id)) {
            if (event.failureReason === this._platformImpl.noFailureReason) {
                this._updateContentSharingSessionStateDeferredPromises[event.id].resolve();
            } else {
                const terminatedReasonCode = event.code;
                const terminatedReasonSubCode = event.subCode;
                const terminatedReason = this._platformImpl.convertFailureReason(event.failureReason);
                const errorMessage = `Update SessionState failed with reason ${terminatedReason}`;
                this._logger.error(errorMessage);
                this._updateContentSharingSessionStateDeferredPromises[event.id].reject({ terminatedReason, terminatedReasonCode, terminatedReasonSubCode, errorMessage });
            }
        }
    }

    // Take control of the session to become a presenter if you are currently an attendee
    public takeContentSharingControl(causeId: CauseId): Promise<void> {
        const fnLogger = this._logger.createFnLogger('takeContentSharingControl', causeId);
        if (this._takeControlDeferredPromise) {
            return this._takeControlDeferredPromise.promise;
        }

        // Ensure we are in the viewing state
        if (this._platformImpl.contentSharingStatus !== ContentSharingStatus.Viewing) {
            const errorMessage = 'Cannot take control of content sharing as it is not in viewing state';
            const terminatedReason = CONSTANTS.ACTION_NOT_ALLOWED(errorMessage);
            fnLogger.logFailure(errorMessage);
            return Promise.reject(terminatedReason);
        }

        fnLogger.info(`Taking control of content sharing for : ${this._platformImpl.contentSharingGuid}`);

        this._takeControlDeferredPromise = async.defer<void>();
        const always = () => {
            this._takeControlDeferredPromise = null;
        };
        this._takeControlDeferredPromise.promise.then(always, always);

        // Promise resolves when result comes back or when we go to presenting state
        return Promise.resolve(undefined)
            .then(() => this._platformImpl.callTakeContentSharingControl())
            .then(() => this._pendingDeferreds.push(this._takeControlDeferredPromise))
            .then(() => this._takeControlDeferredPromise.promise)
            .catch(error => {
                this.handleContentSharingOperationError(error, 'takeContentSharingControl', fnLogger);
            });
    }

    public onTakeControlResultEvent = (event: ContentSharingResultEvent) => {
        if (!this._takeControlDeferredPromise) {
            this._logger.warn(`No pending promise for take control. Event data: ${event}`);
            return;
        }

        this._logger.info(`TakeContentSharingControl completed for: ${this._platformImpl.contentSharingGuid}, ${JSON.stringify(event)}`);

        if (event.failureReason === this._platformImpl.noFailureReason) {
            this._invokeDeferred(this._takeControlDeferredPromise);
        } else {
            const terminatedReasonCode = event.code;
            const terminatedReasonSubCode = event.subCode;
            const terminatedReason = this._platformImpl.convertFailureReason(event.failureReason);
            const errorMessage = `Take content sharing control failed with reason ${terminatedReason}`;
            this._logger.error(errorMessage);
            this._rejectDeferred(this._takeControlDeferredPromise, { terminatedReason, terminatedReasonCode, terminatedReasonSubCode, errorMessage });
        }
    }

    // Stop the session
    public stopContentSharing(causeId: CauseId): Promise<void> {
        const fnLogger = this._logger.createFnLogger('stopContentSharing', causeId);
        if (this._stopContentSharingDeferredPromise) {
            return this._stopContentSharingDeferredPromise.promise;
        }

        // Ensure we are not in the viewing/ringing/connected state
        if (this._platformImpl.contentSharingStatus === ContentSharingStatus.Ringing ||
            this._platformImpl.contentSharingStatus === ContentSharingStatus.Connected ||
            this._platformImpl.contentSharingStatus === ContentSharingStatus.Viewing) {
            const errorMessage = 'Incoming content sharing cannot be stopped';
            const terminatedReason = CONSTANTS.ACTION_NOT_ALLOWED(errorMessage);
            fnLogger.logFailure(errorMessage);
            return Promise.reject(terminatedReason);
        }

        this._stopContentSharingDeferredPromise = async.defer<void>();
        const always = () => {
            this._stopContentSharingDeferredPromise = null;
        };
        this._stopContentSharingDeferredPromise.promise.then(always, always);

        fnLogger.info(`Stop content sharing for : ${this._platformImpl.contentSharingGuid}`);

        // Promise resolves when we go to finished state
        return Promise.resolve(undefined)
            .then(() => this._platformImpl.callStopContentSharing())
            .then(() => this._pendingDeferreds.push(this._stopContentSharingDeferredPromise))
            .then(() => this._stopContentSharingDeferredPromise.promise)
            .catch(error => {
                this.handleContentSharingOperationError(error, 'stopContentSharing', fnLogger);
            });
    }

    private handleContentSharingOperationError(error: any, functionName: string, fnLogger: IInternalLogger) {
        const errorMessage = (error instanceof Error) ? error.toString() : JSON.stringify(error);
        fnLogger.logFailure(`Error in ${functionName}(), contentSharingGuid = ${this._platformImpl.contentSharingGuid}, error = ${errorMessage}`);
        if (error && error.hasOwnProperty('terminatedReason')) {
            throw error;
        } else {
            throw CONSTANTS.ACTION_NOT_ALLOWED(errorMessage);
        }
    }

    private _rejectPendingPromises(reason: SessionTerminatedReason) {
        this._logger.info('rejectPendingPromises');

        this._pendingDeferreds.forEach(deferred => {
            deferred.reject(reason);
        });
        this._pendingDeferreds = [];

        _.forEach(this._updateContentSharingSessionStateDeferredPromises, deferred => {
            deferred.reject(reason);
        });
        this._updateContentSharingSessionStateDeferredPromises = {};
    }

    private _invokeDeferred(deferred: async.Deferred<void>) {
        this._logger.info('_invokeDeferred');

        if (deferred) {
            _.pull(this._pendingDeferreds, deferred);
            deferred.resolve();
        }
    }

    private _rejectDeferred(deferred: async.Deferred<void>, terminatedReason: SessionTerminatedReason) {
        this._logger.info('_rejectDeferred');

        if (deferred) {
            _.pull(this._pendingDeferreds, deferred);
            deferred.reject(terminatedReason);
        }
    }
}



