import {
    CallingLogger, CtrlCaptureEvent, CtrlCaptureMode, CtrlCaptureRegion,
    CtrlEventType, CtrlKbdCodeType, CtrlKbdData, CtrlMouseButtonType,
    CtrlMouseData, CtrlMouseEventType, ISlimCoreElectronControlCapturerEvents
} from 'skype-calling-interface';
import ObservableBase from '../../common/ts/observableBase';

const MOUSE_EVENT_SIZE = 7;
const KEYBOARD_EVENT_SIZE = 3;
const CTRL_MOUSE_MAXPOS = 65535;
const POLL_TIME = 3000; // three seconds
const REACT_TIME = 250; // 250ms
const CTRL_VK_MENU = 0x12;
const CTRL_VK_CONTROL = 0x11;
const CTRL_VK_SHIFT = 0x10;
const CTLR_VK_WINCMD = 0x5B;

type VideoSize = {
    width: number;
    height: number;
};

/*
struct CtrlMouseData {
    // The followng data fields take a byte
    uint8_t type : 2;
    uint8_t buttonType : 3;
    bool fButtonDown : 1;
    bool fWheelButtonDown : 1;
    uint8_t reserved : 1;
    //*************************************
    uint16_t xPos : 16;
    uint16_t yPos : 16;
    int8_t wheelRotation : 8;
};

struct CtrlKbdData {
    // The followng data fields take a byte
    uint8_t codeType : 2;
    uint8_t reserved : 2;
    bool fKeyUp : 1;
    bool fRepeat : 1;
    bool fExtended : 1;
    bool fExtended1 : 1;
    //*************************************
    uint8_t code : 8;
};

struct CtrlClipboardData {
    uint32_t size;
    uint8_t data;
};

struct CtrlInputData {
    uint8_t type; // CtrlEventType
    union {
        CtrlMouseData mouse;
        CtrlKbdData kbd;
        CtrlClipboardData clipboard;
    };
};
*/

export class SlimCoreElectronControlCapturer extends ObservableBase<ISlimCoreElectronControlCapturerEvents> {
    private _videoSize: VideoSize;
    private _origElementSize: VideoSize;
    private _captureRegion: CtrlCaptureRegion;
    private _mouseMoveCount: number = 0;
    private _isMouseOnRenderer: boolean = false;
    private _captureMode: CtrlCaptureMode = CtrlCaptureMode.Disabled;
    private _pollTimerID: number = 0;
    private _resizeTimerID: number = 0;

    public get captureMode(): CtrlCaptureMode {
        return this._captureMode;
    }

    public get captureRegion(): CtrlCaptureRegion {
        return this._captureRegion;
    }

    public constructor(
        private _logger: CallingLogger,
        private _element: HTMLElement,
        private _captureRegionPreserveAspectRatio: boolean = true
    ) {
        super();
        this._ensureCanReceiveKeyboardInput();
        if (this._element.ownerDocument && this._element.ownerDocument.defaultView) {
            this._element.ownerDocument.defaultView.addEventListener('resize', this._handleResizeEvents, false);
        }
        this._captureRegion = { left: 0, top: 0, width: this._element.clientWidth, height: this._element.clientHeight };
        this._videoSize = { width: 640, height: 360 };
        this._origElementSize = { width: 0, height: 0 };
        this._checkElementSize();
    }

    public dispose() {
        this._logger.info('Disposing of Control Capturer');
        window.clearTimeout(this._pollTimerID);
        if (this._element.ownerDocument && this._element.ownerDocument.defaultView) {
            this._element.ownerDocument.defaultView.removeEventListener('resize', this._handleResizeEvents, false);
        }
    }

    public updateVideoSize(width: number, height: number) {
        this._videoSize.width = width;
        this._videoSize.height = height;
        this.updateCaptureRegion();
    }

    public updateCaptureRegion() {
        // Scale to show the whole video (screen sharing), while preserving aspect ratio
        if (this._captureRegionPreserveAspectRatio) {
            this._captureRegion.width = this._element.clientWidth;
            this._captureRegion.height = this._element.clientWidth * this._videoSize.height / this._videoSize.width;

            if (this._captureRegion.height > this._element.clientHeight) {
                this._captureRegion.width = this._element.clientHeight * this._videoSize.width / this._videoSize.height;
                this._captureRegion.height = this._element.clientHeight;
                this._captureRegion.left = (this._element.clientWidth - this._captureRegion.width) / 2;
                this._captureRegion.top = 0;
            } else {
                this._captureRegion.left = 0;
                this._captureRegion.top = (this._element.clientHeight - this._captureRegion.height) / 2;
            }
        } else { // Fit and crop the video (webcam, for holographic interaction)
            this._captureRegion.width = this._element.clientWidth;
            this._captureRegion.height = this._element.clientHeight;
            this._captureRegion.top = 0;
            this._captureRegion.left = 0;
        }
    }

    public setCaptureMode(mode: CtrlCaptureMode) {
        if (this._captureMode === CtrlCaptureMode.Control && mode !== CtrlCaptureMode.Control) {
            this._syncKeyStates(true); // When user is swtiching mode out of control while losing focus
        }

        this._captureMode = mode;
        if (mode !== CtrlCaptureMode.Disabled) {
            this._element.addEventListener('click', this._handleClick);

            // pointer events (mouse, touch, stylus)
            this._element.addEventListener('pointermove', this._handleMouseMove);
            this._element.addEventListener('pointerdown', this._handleMouseDown);
            this._element.addEventListener('pointerup', this._handleMouseUp);
            this._element.addEventListener('pointerenter', this._handleMouseEnter);
            this._element.addEventListener('pointerleave', this._handleMouseLeave);

            if (mode === CtrlCaptureMode.Control) {
                this._element.addEventListener('wheel', this._handleWheel);
                this._element.addEventListener('keydown', this._handleKeyDown);
                this._element.addEventListener('keyup', this._handleKeyUp);
                this._element.addEventListener('contextmenu', this._handleContextMenu);  // to prevent browser default right click menu
                this._element.addEventListener('blur', this._handleLosingFocus);
            } else {
                this._element.removeEventListener('wheel', this._handleWheel);
                this._element.removeEventListener('keydown', this._handleKeyDown);
                this._element.removeEventListener('keyup', this._handleKeyUp);
                this._element.removeEventListener('contextmenu', this._handleContextMenu);
                this._element.removeEventListener('blur', this._handleLosingFocus);
            }
        } else {
            this._element.removeEventListener('pointermove', this._handleMouseMove);
            this._element.removeEventListener('pointerdown', this._handleMouseDown);
            this._element.removeEventListener('pointerup', this._handleMouseUp);
            this._element.removeEventListener('pointerenter', this._handleMouseEnter);
            this._element.removeEventListener('pointerleave', this._handleMouseLeave);

            this._element.removeEventListener('click', this._handleClick);
            this._element.removeEventListener('wheel', this._handleWheel);
            this._element.removeEventListener('keydown', this._handleKeyDown);
            this._element.removeEventListener('keyup', this._handleKeyUp);
            this._element.removeEventListener('contextmenu', this._handleContextMenu);
            this._element.removeEventListener('blur', this._handleLosingFocus);
        }
    }

    private _ensureCanReceiveKeyboardInput() {
        this._element.tabIndex = this._element.tabIndex;
    }

    private _handleResizeEvents = () => {
        clearTimeout(this._resizeTimerID);
        this._resizeTimerID = window.setTimeout(() => this.updateCaptureRegion(), REACT_TIME);
    }

    private _checkElementSize() {
        if (this._origElementSize.width !== this._element.clientWidth || this._origElementSize.height !== this._element.clientHeight) {
            this.updateCaptureRegion();
            this._origElementSize.width = this._element.clientWidth;
            this._origElementSize.height = this._element.clientHeight;
        }
        this._pollTimerID = window.setTimeout(() => this._checkElementSize(), POLL_TIME);
    }

    private _handleMouseLeave = (event: PointerEvent) => {
        if (!this._isOnScreenContent(event) && this._isMouseOnRenderer) {
            this._raiseCaptureEvent(CtrlCaptureEvent.MouseLeaving);
            this._logger.info('Mouse leaving render region');
        }
        this._isMouseOnRenderer = false;
    }

    private _handleMouseEnter = (event: PointerEvent) => {
        if (!this._isOnScreenContent(event)) { return; }
        this._isMouseOnRenderer = true;
        this._raiseCaptureEvent(CtrlCaptureEvent.MouseEntering);
        this._logger.info('Mouse entering render region');
    }

    private _handleLosingFocus = (event: FocusEvent) => {
        this._syncKeyStates(true);
    }

    private _handleClick = (event: MouseEvent) => {
        if (!this._isOnScreenContent(event)) { return; }
        this._raiseCaptureEvent(CtrlCaptureEvent.MouseClick);
        this._logger.info('Mouse clicked');
    }

    private _handleMouseMove = (event: PointerEvent) => {
        if (!this._isOnScreenContent(event) && this._isMouseOnRenderer) {
            this._raiseCaptureEvent(CtrlCaptureEvent.MouseLeaving);
            this._isMouseOnRenderer = false;
            this._logger.info('Mouse leaving render region');
            return;
        } else if (this._isOnScreenContent(event) && !this._isMouseOnRenderer) {
            this._raiseCaptureEvent(CtrlCaptureEvent.MouseEntering);
            this._isMouseOnRenderer = true;
            this._logger.info('Mouse entering render region');
        }

        if (this._captureMode !== CtrlCaptureMode.Pointer && this._captureMode !== CtrlCaptureMode.Control) { return; }

        this._mouseMoveCount++;
        if (!this._isOnScreenContent(event) || this._mouseMoveCount % 2 === 0) { return; }

        const data: CtrlMouseData = {
            type: CtrlMouseEventType.Move
        };

        this._normalizeMousePosition(event, data);
        this._raiseMouseEvent(data);
    }

    private _handleMouseDown = (event: PointerEvent) => {
        if (this._captureMode !== CtrlCaptureMode.Pointer && this._captureMode !== CtrlCaptureMode.Control) { return; }
        if (!this._isOnScreenContent(event)) { return; }

        const data: CtrlMouseData = {
            type: CtrlMouseEventType.Button,
            buttonType: convertButton(event.button),
            buttonDown: true
        };

        this._normalizeMousePosition(event, data);
        this._raiseMouseEvent(data);
    }

    private _handleMouseUp = (event: PointerEvent) => {
        if (this._captureMode !== CtrlCaptureMode.Pointer && this._captureMode !== CtrlCaptureMode.Control) { return; }
        if (!this._isOnScreenContent(event)) { return; }

        const data: CtrlMouseData = {
            type: CtrlMouseEventType.Button,
            buttonType: convertButton(event.button),
            buttonDown: false
        };

        this._normalizeMousePosition(event, data);

        this._raiseMouseEvent(data);
    }

    private _handleKeyDown = (event: KeyboardEvent) => {
        this._raiseKeyboardEvent({
            codeType: CtrlKbdCodeType.VirtualKey,
            code: event.keyCode,
            repeat: event.repeat,
            keyUp: false
        });

        event.stopPropagation();
        event.preventDefault();
    }

    private _handleKeyUp = (event: KeyboardEvent) => {
        this._raiseKeyboardEvent({
            codeType: CtrlKbdCodeType.VirtualKey,
            code: event.keyCode,
            repeat: event.repeat,
            keyUp: true
        });

        event.stopPropagation();
        event.preventDefault();
    }

    private _handleWheel = (event: WheelEvent) => {
        this._raiseMouseEvent({
            type: CtrlMouseEventType.Wheel,
            wheelRotation: event.deltaY > 0 ? -120 : 120
        });
    }

    private _raiseMouseEvent(data: CtrlMouseData) {
        this.event('mouseControlEvent').raise(data);
    }

    private _raiseKeyboardEvent(data: CtrlKbdData) {
        this.event('keyboardControlEvent').raise(data);
    }

    private _handleContextMenu = (event: MouseEvent) => {
        event.preventDefault();
    }

    private _raiseCaptureEvent(event: CtrlCaptureEvent) {
        this.event('ctrlCaptureEvent').raise(event);
    }

    private _normalizeMousePosition(event: PointerEvent, data: CtrlMouseData) {
        data.xPos = (event.offsetX - this._captureRegion.left) / this._captureRegion.width * CTRL_MOUSE_MAXPOS;
        data.yPos = (event.offsetY - this._captureRegion.top) / this._captureRegion.height * CTRL_MOUSE_MAXPOS;
    }

    private _isOnScreenContent(event: MouseEvent) {
        const minX = this._captureRegion.left;
        const maxX = this._captureRegion.left + this._captureRegion.width;
        const minY = this._captureRegion.top;
        const maxY = this._captureRegion.top + this._captureRegion.height;

        return (minX <= event.offsetX && event.offsetX < maxX && minY <= event.offsetY && event.offsetY < maxY);
    }

    // Modifier key is pressed while the input capture window is losing focus, and it is released afterwards
    private _syncKeyStates(captureWindowLoseFocus: boolean) {
        this._raiseKeyboardEvent({
            codeType: CtrlKbdCodeType.VirtualKey,
            code: CTRL_VK_CONTROL,
            repeat: false,
            keyUp: captureWindowLoseFocus
        });

        this._raiseKeyboardEvent({
            codeType: CtrlKbdCodeType.VirtualKey,
            code: CTRL_VK_SHIFT,
            repeat: false,
            keyUp: captureWindowLoseFocus
        });

        this._raiseKeyboardEvent({
            codeType: CtrlKbdCodeType.VirtualKey,
            code: CTRL_VK_MENU,
            repeat: false,
            keyUp: captureWindowLoseFocus
        });

        this._raiseKeyboardEvent({
            codeType: CtrlKbdCodeType.VirtualKey,
            code: CTLR_VK_WINCMD,
            repeat: false,
            keyUp: captureWindowLoseFocus
        });

        this._logger.info('Synced Key states.');
    }

    public static formatMouseEvent(data: CtrlMouseData): Uint8Array {
        if (!data) { return new Uint8Array(0); }
        if (!data.buttonType) { data.buttonType = 0; }
        if (!data.xPos) { data.xPos = 0; }
        if (!data.yPos) { data.yPos = 0; }
        if (!data.wheelRotation) { data.wheelRotation = 0; }

        const buffer = new ArrayBuffer(MOUSE_EVENT_SIZE);
        const dataView = new DataView(buffer);

        const type = (data.type & 0b11) << 0;
        const buttonType = (data.buttonType & 0b111) << 2;
        const fButtonDown = (data.buttonDown ? 1 : 0) << 5;
        const fWheelButtonDown = (data.wheelButtonDown ? 1 : 0) << 6;
        const reserved = 0;

        dataView.setUint8(0, CtrlEventType.Mouse);
        dataView.setUint8(1, type | buttonType | fButtonDown | fWheelButtonDown | reserved);
        dataView.setUint16(2, data.xPos, true);
        dataView.setUint16(4, data.yPos, true);
        dataView.setUint8(6, data.wheelRotation);

        return new Uint8Array(buffer);
    }

    public static formatKeyboardEvent(data: CtrlKbdData): Uint8Array {
        const buffer = new ArrayBuffer(KEYBOARD_EVENT_SIZE);
        const dataView = new DataView(buffer);

        const codeType = (data.codeType & 0b11) << 0;
        const reserved = 0;
        const fKeyUp = (data.keyUp ? 1 : 0) << 4;
        const fRepeat = (data.repeat ? 1 : 0) << 5;
        const fExtended = 0;
        const fExtended1 = 0;

        dataView.setUint8(0, CtrlEventType.Keyboard);
        dataView.setUint8(1, codeType | reserved | fKeyUp | fRepeat | fExtended | fExtended1);
        dataView.setUint8(2, data.code);

        return new Uint8Array(buffer);
    }
}

function convertButton(button: number) {
    switch (button) {
        case 0: // left button
            return CtrlMouseButtonType.Button1;
        case 2: // right button
            return CtrlMouseButtonType.Button2;
        case 1: // middle button
            return CtrlMouseButtonType.Button3;
        default:
            return undefined;
    }
}



