
import {
    DataChannelProtocolMessage, DataChannelProtocolType, ICallParticipant, IMediaDataChannel, IMediaDataChannelEvents, MediaDataChannelState, MediaType
} from 'skype-calling-interface';

import * as async from '../../common/ts/async';
import AsyncOperationHandler from '../../common/ts/asyncOperationHandler';
import { CALL_OPERATIONS } from '../../common/ts/callConstants';
import * as SlimCoreCommon from './SlimCoreCommon';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronCall from './SlimCoreElectronCall';
import SlimCoreElectronCallParticipant from './SlimCoreElectronCallParticipant';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { IInternalLogger } from '../../common/ts/internalLogger';

// SourceID used for P2P
export const DATA_SOURCE_ID_ANY = 0xFFFFFFFE;

// Interface implemented by users of the SlimCore.DataChannel
// that share the Protocol source for sending protocol messages
export interface IMediaDataChannelUser {
    setDataChannel(channel: SlimCoreElectronDataChannel): Promise<void>;
    onDataChannelStatusChanged(dataChannelStatus: number): Promise<void>;
    setProtocolDataSource(source: SlimCore.DataSource): Promise<void>;
    processProtocolMessage(message: string, senderParticipant: ICallParticipant, senderParticipantId: string, senderSourceId: number): Promise<void>;
}

export default class SlimCoreElectronDataChannel extends SlimCoreElectronBase<IMediaDataChannelEvents> implements IMediaDataChannel {

    private _dataChannelActive: boolean = false;
    private _protocolDataSource: SlimCore.DataSource = null;
    private _protocolDataSink: SlimCore.DataSink = null;
    private _dataChannelAdapterSetInternalSourceSink: Function = null;
    public _lastDataChannelState: Enums.DataChannelStatus = Enums.DataChannelStatus.Unknown;
    private _asyncOperationHandler: AsyncOperationHandler;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        private _call: SlimCoreElectronCall,
        private _slimcoreCallHandler: SlimCoreCommon.CallHandler,
        private _dataChannel: SlimCore.DataChannel,
        private _dataChannelUsers: IMediaDataChannelUser[],
        public id: number
    ) {

        super(logger.createChild(() => `SlimCoreElectronDataChannel`), settings, engine);
        this._asyncOperationHandler = new AsyncOperationHandler(this._logger);

        // Initialize the array of data channel users - for now there's only screen sharing control
        this._logger.info('Setting DataChannel on all users');
        for (let i = 0; i < this._dataChannelUsers.length; i++) {
            if (this._dataChannelUsers[i]) {
                try {
                    this._dataChannelUsers[i].setDataChannel(this);
                } catch (error) {
                    this._logger.error(`Exception calling setDataChannel for i=${i} error=${error}`);
                }
            }
        }

        // Listen for data channel status changes
        this._hookSlimCorePropertyChangedEvents();
    }

    // Callback for adapter to mark protocol source sink since this file owns it
    // Maybe add to dataChannelUser internal interface (force tscalling to expose source/sinks to client)
    public setAdapterSourceSinkCallback(setSourceSink: Function) {
        this._dataChannelAdapterSetInternalSourceSink = setSourceSink;
    }

    private _hookSlimCorePropertyChangedEvents() {
        this._registerDisposable(this._slimcoreInstance.handle(
            'object-property-changed',
            { objectId: this.id, propKey: Enums.Property.DataChannelStatus },
            data => this._onDataChannelStatusChanged(data.value as number)));

        const dataChannelStatus = this._getIntProperty(this._slimcoreCallHandler, this.id, Enums.Property.DataChannelStatus);
        this._onDataChannelStatusChanged(dataChannelStatus);
    }

    public start(negotiationTag?: string): Promise<void> {
        if (this._dataChannelActive) {
            this._logger.info('start() called when channel already active');
            return Promise.resolve<void>();
        }
        if (this._asyncOperationHandler.hasPendingOperation(CALL_OPERATIONS.START_DATA_CHANNEL)) {
            this._logger.info('start promise already exists, returning previous promise');
        } else {
            this._asyncOperationHandler.createPendingOperation(CALL_OPERATIONS.START_DATA_CHANNEL);
            async.asap(() => this._startDataChannel(negotiationTag));
        }
        return this._asyncOperationHandler.waitForOperation(CALL_OPERATIONS.START_DATA_CHANNEL);
    }

    public stop(negotiationTag?: string): Promise<void> {
        if (!this._dataChannelActive) {
            this._logger.info('stop() called when channel not active');
            return Promise.resolve<void>();
        }
        if (this._asyncOperationHandler.hasPendingOperation(CALL_OPERATIONS.STOP_DATA_CHANNEL)) {
            this._logger.info('stop promise already exists, returning previous promise');
        } else {
            this._asyncOperationHandler.createPendingOperation(CALL_OPERATIONS.STOP_DATA_CHANNEL);
            async.asap(() => this._stopDataChannel(negotiationTag));
        }
        return this._asyncOperationHandler.waitForOperation(CALL_OPERATIONS.STOP_DATA_CHANNEL);
    }

    public registerDataSource(dataSource: SlimCore.DataSource): Promise<void> {
        return async.asap(() => this._dataChannel.registerDataSource(dataSource));
    }

    public unregisterDataSource(dataSource: SlimCore.DataSource): Promise<void> {
        return async.asap(() => this._dataChannel.unregisterDataSource(dataSource));
    }

    public registerDataSink(dataSink: SlimCore.DataSink): Promise<void> {
        return async.asap(() => this._dataChannel.registerDataSink(dataSink));
    }

    public unregisterDataSink(dataSink: SlimCore.DataSink): Promise<void> {
        return async.asap(() => this._dataChannel.unregisterDataSink(dataSink));
    }

    public sendUserEvents(events: string, participantIds?: string[]): Promise<void> {
        return async.asap(() => this._dataChannel.sendUserEvents(events, participantIds));
    }

    private _onDataChannelStatusChanged(dataChannelStatus: number) {
        this._logger.info(`Data channel status = ${dataChannelStatus}`);
        if (dataChannelStatus === Enums.DataChannelStatus.Active) {
            this._asyncOperationHandler.maybeResolveOperation(CALL_OPERATIONS.START_DATA_CHANNEL);

            try {
                this._dataChannel.setDataDevices();
            } catch (error) {
                this._logger.error(`setDataDevices threw exception: ${error}`);
            }

            if (this._dataChannel && !this._dataChannelActive) {
                this._dataChannelActive = true;

                // Create and attach protocol devices
                this._createAndAttachProtocolDevices();

                if (this._dataChannelAdapterSetInternalSourceSink) {
                    this._dataChannelAdapterSetInternalSourceSink(Enums.DataDeviceId.Protocol, this._protocolDataSource, this._protocolDataSink);
                }
                this._logger.info('Data channel ready');
            }
        } else if (dataChannelStatus === Enums.DataChannelStatus.Available) {
            this._asyncOperationHandler.maybeResolveOperation(CALL_OPERATIONS.STOP_DATA_CHANNEL);
            if (this._dataChannel && this._dataChannelActive) {
                // reset some state
                this._dataChannelActive = false;
                // detach and delete protocol devices
                this._detachAndDeleteProtocolDevices();
                this._logger.info('Data channel disposed');
            }
        }

        if (this._lastDataChannelState === Enums.DataChannelStatus.Starting && dataChannelStatus !== Enums.DataChannelStatus.Active) {
            this._asyncOperationHandler.maybeRejectOperation(CALL_OPERATIONS.START_DATA_CHANNEL);
        } else if (this._lastDataChannelState === Enums.DataChannelStatus.Stopping && dataChannelStatus !== Enums.DataChannelStatus.Available) {
            this._asyncOperationHandler.maybeRejectOperation(CALL_OPERATIONS.STOP_DATA_CHANNEL);
        }

        for (let i = 0; i < this._dataChannelUsers.length; i++) {
            try {
                this._dataChannelUsers[i].onDataChannelStatusChanged(dataChannelStatus);
            } catch (error) {
                this._logger.error(`Exception calling onDataChannelStatusChanged for i=${i} error=${error}`);
            }
        }

        if (this._lastDataChannelState !== dataChannelStatus) {
            this.event('stateChange').raise(SlimCoreElectronDataChannel._mapDataChannelStatus(dataChannelStatus));
            this._lastDataChannelState = dataChannelStatus;
        }
    }

    private _updateProtocolDataSource(dataSource: SlimCore.DataSource) {
        // Loop through all the data channel users, and update their
        // protocol data source accordingly:
        this._logger.info('Setting Protocol DataSource on all users');
        for (let i = 0; i < this._dataChannelUsers.length; i++) {
            if (this._dataChannelUsers[i]) {
                try {
                    this._dataChannelUsers[i].setProtocolDataSource(dataSource);
                } catch (error) {
                    this._logger.error(`Exception calling setProtocolDataSource for i=${i} error=${error}`);
                }
            }
        }
    }

    private _createAndAttachProtocolDevices(): void {
        this._logger.info('_createAndAttachProtocolDevices()');
        if (!SlimCoreCommon.hasCreateDataSource(this._slimcoreInstance) || !SlimCoreCommon.hasCreateDataSink(this._slimcoreInstance)) {
            this._logger.warn('createDataSource or createDataSink not implemented');
            return;
        }

        if (this._dataChannel) {

            // Create/register source/sink
            if (!this._protocolDataSource) {
                this._protocolDataSource = SlimCoreCommon.createDataSource(this._slimcoreInstance, Enums.DataDeviceId.Protocol);
                try {
                    this._dataChannel.registerDataSource(this._protocolDataSource);
                    this._logger.info('created Protocol data source');
                } catch (error) {
                    this._logger.error(`registerDataSource threw exception: ${error}`);
                }
            }

            if (!this._protocolDataSink) {
                this._protocolDataSink = SlimCoreCommon.createDataSink(this._slimcoreInstance, Enums.DataDeviceId.Protocol);
                try {
                    this._dataChannel.registerDataSink(this._protocolDataSink);
                    this._logger.info('created Protocol data sink');
                } catch (error) {
                    this._logger.error(`registerDataSink threw exception: ${error}`);
                }
            }

            // Attach protocol callback
            this._protocolDataSink.on('data', ((args: SlimCore.DataSink.Events.DataArgs) => {
                try {
                    const protocolMessage: DataChannelProtocolMessage = JSON.parse(SlimCoreCommon.bufferToString(args.data));
                    if (protocolMessage.type !== DataChannelProtocolType.Adapter) { // Adapter can register own listener for protocol adapter messages, ignore (adapter does not implement processProtocol)
                        if (protocolMessage.type >= this._dataChannelUsers.length) {
                            this._logger.error(`Error calling processProtocolMessage i=${protocolMessage.type} error=Index out of bounds`);
                        }
                        try {
                            // This message is meant for this data channel user, so invoke the process method on it
                            const senderParticipant: ICallParticipant = this._call.mapDataChannelSourceIdToParticipant(args.sourceID);
                            let senderParticipantId: string;

                            if (senderParticipant) {
                                const p: SlimCoreElectronCallParticipant = senderParticipant as SlimCoreElectronCallParticipant;
                                senderParticipantId = p.getParticipantIdForSourceId(MediaType.Data, args.sourceID);
                            }

                            this._dataChannelUsers[protocolMessage.type].processProtocolMessage(protocolMessage.message, senderParticipant, senderParticipantId, args.sourceID);
                        } catch (error) {
                            this._logger.error(`Error calling processProtocolMessage i=${protocolMessage.type} error=${error}`);
                        }
                    }
                } catch (error) {
                    this._logger.error(`Error handling protocol message error=${error}`);
                }
            }));

            // Set the procotol data source on all data channel users
            this._updateProtocolDataSource(this._protocolDataSource);

        } else {
            this._logger.warn('_createAndAttachProtocolDevices() null dataChannel');
        }
    }

    private _detachAndDeleteProtocolDevices(): void {
        this._logger.info('_detachAndDeleteProtocolDevices()');

        // Remove data callback
        if (this._protocolDataSink) {
            this._protocolDataSink.removeAllListeners('data');
        }

        if (this._dataChannel) {
            // Unregister the source/sink
            if (this._protocolDataSource) {
                try {
                    this._dataChannel.unregisterDataSource(this._protocolDataSource);
                } catch (error) {
                    this._logger.warn(`unregisterDataSource threw exception for protocol, call probably ended: ${error}`);
                }
                this._protocolDataSource = null;

                // Set the procotol data source on all data channel users
                this._updateProtocolDataSource(null);
                this._logger.info('deleted Protocol data source');
            }

            if (this._protocolDataSink) {
                try {
                    this._dataChannel.unregisterDataSink(this._protocolDataSink);
                } catch (error) {
                    this._logger.warn(`unregisterDataSink threw exception for protocol, call probably ended: ${error}`);
                }
                this._protocolDataSink = null;
                this._logger.info('deleted Protocol data sink');
            }
        } else {
            this._logger.warn('_detachAndDeleteProtocolDevices() null dataChannel');
        }

    }

    private _startDataChannel(negotiationTag?: string) {
        this._logger.info('_startDataChannel()');

        if (this._dataChannel) {
            try {
                this._dataChannel.start(negotiationTag);
            } catch (error) {
                this._logger.error(`_dataChannel.start threw exception: ${error}`);
            }
            this._logger.info('Data channel started');
        }
    }

    private _stopDataChannel(negotiationTag?: string) {
        this._logger.info('_stopDataChannel()');

        if (this._dataChannel) {
            try {
                this._dataChannel.stop(negotiationTag);
            } catch (error) {
                this._logger.error(`_dataChannel.stop threw exception: ${error}`);
            }
            this._logger.info('Data channel stopping');
        }
    }

    public static _mapDataChannelStatus(dataChannelStatus: number): MediaDataChannelState {
        switch (dataChannelStatus) {
            case Enums.DataChannelStatus.Available:
                return MediaDataChannelState.Available;
            case Enums.DataChannelStatus.Starting:
                return MediaDataChannelState.Starting;
            case Enums.DataChannelStatus.Active:
                return MediaDataChannelState.Active;
            case Enums.DataChannelStatus.Stopping:
                return MediaDataChannelState.Stopping;
            case Enums.DataChannelStatus.NotStarted:
                return MediaDataChannelState.NotStarted;
            case Enums.DataChannelStatus.Unknown:
            default:
                return MediaDataChannelState.Unknown;
        }
    }

    public dispose() {
        this._logger.info('SlimCoreElectronDataChannel.dispose()');

        this._detachAndDeleteProtocolDevices();

        this._call = null;
        if (this._dataChannel) {
            if (this._dataChannel.dispose) {
                this._dataChannel.dispose();
            }
            this._dataChannel = null;
        }

        super.dispose();
    }
}



