import * as _ from 'lodash';

import {
    CallEndDiagnosticsInfo, CallParticipantBalanceUpdate, CallParticipantBalanceUpdateMap,
    CallParticipantEndpoints, CallParticipantStreamMap, Capabilities, CauseId, EndpointMediaStreamInfo, ICallParticipant, MediaType,
    MeetingRole, ParticipantContentSharingRole, ParticipantRosterRole, ParticipantState, ParticipantStateReason, StreamType
} from 'skype-calling-interface';
import SlimCoreElectronBase from './SlimCoreElectronBase';
import SlimCoreElectronCall from './SlimCoreElectronCall';
import { DATA_SOURCE_ID_ANY } from './SlimCoreElectronDataChannel';
import { SlimCoreElectronRemoteStreamContext } from './SlimCoreElectronRemoteStream';
import SlimCoreElectronRemoteStreamManager from './SlimCoreElectronRemoteStreamManager';

import * as async from '../../common/ts/async';
import * as piiUtils from '../../common/ts/piiUtils';
import * as SlimCoreCommon from './SlimCoreCommon';
import * as EnumConverter from './SlimCoreElectronEnumConverter';

// From electron/typings/slimcore-enums.d.ts:
import Enums = SlimCore.Enums;
import { PARTICIPANT_OPERATIONS_INTERNAL } from '../../common/ts/callConstants';
import { getEndpointInformationForTelemetry, ICallTelemetry } from '../../common/ts/callTelemetry';
import { generateCauseId } from '../../common/ts/causeId';
import { IInternalLogger } from '../../common/ts/internalLogger';
import { stripMriAliases } from '../../common/ts/mriUtils';
import { getStreamInformationFromEndpoints } from './telemetry/helper';

const validStateTransitions: { [key: number]: ParticipantState[] } = {
    // ParticipatnState.None to Participant.Hold is possible for lobby scenario.  A lobby user admitted to the conference would first creates
    // the participants in the conference. Then it notices some participant in the conference is onhold when it tries to attachSlimcoreObjectId,
    // and setState to be hold.
    [ParticipantState.None]: [ParticipantState.Connecting, ParticipantState.Ringing, ParticipantState.Connected, ParticipantState.Disconnected, ParticipantState.EarlyMedia, ParticipantState.InLobby, ParticipantState.OnHold],
    [ParticipantState.Connecting]: [ParticipantState.Ringing, ParticipantState.Connected, ParticipantState.Disconnected, ParticipantState.EarlyMedia, ParticipantState.InLobby, ParticipantState.None],
    [ParticipantState.Ringing]: [ParticipantState.None, ParticipantState.Connecting, ParticipantState.Connected, ParticipantState.Disconnected, ParticipantState.EarlyMedia, ParticipantState.InLobby],
    [ParticipantState.Connected]: [ParticipantState.Ringing, ParticipantState.Connecting, ParticipantState.Disconnected, ParticipantState.OnHold, ParticipantState.None, ParticipantState.InLobby],
    [ParticipantState.Disconnected]: [ParticipantState.None],
    [ParticipantState.OnHold]: [ParticipantState.Connected, ParticipantState.Disconnected, ParticipantState.None],
    [ParticipantState.EarlyMedia]: [ParticipantState.Connected, ParticipantState.Disconnected, ParticipantState.None],
    [ParticipantState.InLobby]: [ParticipantState.Connected, ParticipantState.Disconnected, ParticipantState.None]
};

export const participantStrProps = {
    displayName: { propKey: Enums.Property.ParticipantDisplayName },
    role: { propKey: Enums.Property.ParticipantRole },
    tenantId: { propKey: Enums.Property.ParticipantTenantId },
    acceptedBy: { propKey: Enums.Property.ParticipantAcceptedBy },
    endpointDetails: { propKey: Enums.Property.ParticipantEndpointDetails },
    meetingRole: { propKey: Enums.Property.ParticipantMeetingRole }
};
export const participantIntProps = {
    isServerMuted: { propKey: Enums.Property.ParticipantIsServerMuted },
    capabilities: { propKey: Enums.Property.ParticipantCapabilities, fallback: SlimCore.Enums.Capability.All },
    contentRole: { propKey: Enums.Property.ParticipantContentRole, fallback: Enums.ContentSharingRole.None },
    status: { propKey: Enums.Property.ParticipantStatus },
    failure: { propKey: Enums.Property.ParticipantFailureReason }
};

export interface CallParticipantData {
    objectId: number;
    mri: string;
    props?: SlimCore.PropertyResults<typeof participantStrProps, typeof participantIntProps>;
}

export default class SlimCoreElectronCallParticipant extends SlimCoreElectronBase<{}> implements ICallParticipant {
    public streams: CallParticipantStreamMap = {
        [StreamType.Video]: [],
        [StreamType.ScreenSharing]: []
    };
    public slimcoreObjectId: number;

    public voiceLevel = 0;
    public isServerMuted = false;
    public dominantSpeakerRank = 0;
    public state = ParticipantState.None;
    public stateReason: ParticipantStateReason;
    public endpoints: CallParticipantEndpoints;
    public id: string = null;
    public role: ParticipantRosterRole = null;
    public tenantId: string = null;
    public displayName: string;
    public contentSharingRole: ParticipantContentSharingRole;
    public participantCapabilities: Capabilities;
    public acceptedBy: string;
    public balanceUpdates: CallParticipantBalanceUpdateMap = {};
    public callEndDiagnosticsInfo?: CallEndDiagnosticsInfo;
    public meetingRole?: MeetingRole;
    public previousId?: string;

    public enableParticipantLegToSourceIdMapping = true;

    private _admitParticipantDeffered: async.Deferred<void>;
    private _removeParticipantDeffered: async.Deferred<void>;
    private _rawEndpoints: string;
    private _rawCallEndDiagnosticsInfo: string;
    private _slimCoreElectronRemoteStreamManager: SlimCoreElectronRemoteStreamManager;
    private _raiseChangedDefer: number;

    public constructor(
        logger: IInternalLogger,
        settings: SlimCoreCommon.Settings,
        engine: SlimCoreCommon.Engine,
        mri: string,
        private _callHandler: SlimCoreCommon.CallHandler,
        private _streamContextMap: { [type: number]: SlimCoreElectronRemoteStreamContext },
        attachParticipantId: number = 0,
        private _callTelemetry: ICallTelemetry,
        isObserving: boolean = false,
        data?: CallParticipantData
    ) {
        super(logger.createChild(() => `ICallParticipant[${piiUtils.scrubMriOrOmit(this.id)}][${this.state}][${this.slimcoreObjectId}]`), settings, engine);
        this._logger.info('created');

        this.id = stripMriAliases(mri);

        this.participantCapabilities = SlimCoreElectronCallParticipant.convertCapabilities(SlimCore.Enums.Capability.All);

        if (attachParticipantId && attachParticipantId !== 0) {
            this.attachSlimcoreObjectId(attachParticipantId, 'ctor', isObserving, data);
        }

        if (this._slimcoreInstance.ecsGetSettingAsBool) {
            this.enableParticipantLegToSourceIdMapping = this._slimcoreInstance.ecsGetSettingAsBool('SkypeCalling', 'enableParticipantLegToSourceIdMapping', true);
        }
        this._logger.info(`enableParticipantLegToSourceIdMapping = ${this.enableParticipantLegToSourceIdMapping}`);
    }

    public getDiagnosticData(): string {
        return JSON.stringify({
            id: this.id, // auf.pii.Mri(this.id)
            slimcoreObjectId: this.slimcoreObjectId,
            state: this.state,
            isServerMuted: this.isServerMuted
        });
    }

    public attachSlimcoreObjectId(attachParticipantId: number, causeId: CauseId, isObserving: boolean = false, data?: CallParticipantData) {
        if (this.slimcoreObjectId && this.slimcoreObjectId !== 0) {
            this._callTelemetry.recordEvent(PARTICIPANT_OPERATIONS_INTERNAL.ELECTRON_ATTACH_SLIMCORE_PARTICIPANT_FAILED, { attachParticipantId, reason: 'Participant already attached to a SlimCore object' }, causeId);
            this._logger.debug('Attach called on existing participant');
            return;
        }

        this.slimcoreObjectId = attachParticipantId;

        const props = (data && data.props) || this._getProperties(this._callHandler, this.slimcoreObjectId, participantStrProps, participantIntProps);

        this.displayName = props.displayName;
        this.role = props.role as ParticipantRosterRole;
        this.tenantId = props.tenantId;
        this.participantCapabilities = SlimCoreElectronCallParticipant.convertCapabilities(props.capabilities);
        this.acceptedBy = props.acceptedBy;
        this.isServerMuted = !!props.isServerMuted;
        this.meetingRole = props.meetingRole as MeetingRole;

        this._slimCoreElectronRemoteStreamManager = new SlimCoreElectronRemoteStreamManager(
            this._logger,
            this._settings,
            this._slimcoreInstance,
            this._callHandler,
            this.slimcoreObjectId,
            this.streams,
            this._streamContextMap,
            isObserving);
        this._slimCoreElectronRemoteStreamManager.changed(() => this.raiseChanged());

        this._updateEndpointDetails(props.endpointDetails);

        this._onParticipantPropertyChanged(Enums.Property.ParticipantStatus, data => this._onStatusChanged(data.value as number));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantIsServerMuted, data => this._onIsServerMuted(!!data.value));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantFailureReason, data => this._onFailureReasonChange(data.value as number));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantAdmitFailureReason, data => this._onAdmitFailureReasonChange(data.value as number));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantRemoveFailureReason, data => this._onRemoveFailureReasonChange(data.value as number));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantIsActiveSpeaker, data => this.setVoiceLevel(data.value as number));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantDominantSpeakerRank, data => this._onDominantSpeakerRankChanged(data.value as number));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantEndpointDetails, data => this._updateEndpointDetails(data.value as string));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantCapabilities, data => this._onCapabilitiesChanged(data.value as number));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantAcceptedBy, data => this._onAcceptedByChanged(data.value as string));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantRole, data => this._onRoleChanged(data.value as string));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantTenantId, data => this._onTenantIdChanged(data.value as string));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantContentRole, data => this._onParticipantContentSharingRoleChanged(data.value as number));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantBalanceUpdate, data => this._onBalanceUpdateChanged(data.value as string));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantDiagnosticsCode, data => this._onParticipantDiagnosticsCodeChanged(data.value as string));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantMeetingRole, data => this._onMeetingRoleChanged(data.value as string));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantMriIdentity, data => this._onMriChanged(data.value as string));
        this._onParticipantPropertyChanged(Enums.Property.ParticipantDisplayName, data => this._onDisplayNameChanged(data.value as string));

        this._onStatusChanged(props.status, false, props.failure);
        this._onParticipantContentSharingRoleChanged(props.contentRole);
    }

    public getStrProperty(property: Enums.Property, fallback?: string): string {
        return this._getStrProperty(this._callHandler, this.slimcoreObjectId, property, fallback);
    }

    public getIntProperty(property: Enums.Property, fallback?: number): number {
        return this._getIntProperty(this._callHandler, this.slimcoreObjectId, property, fallback);
    }

    private _onParticipantPropertyChanged(propKey: Enums.Property, listener: SlimCore.EventHandler<SlimCore.Engine.Events.ObjectPropertyChangedArgs>) {
        this._onObjectPropertyChanged(this.slimcoreObjectId, propKey, listener);
    }

    private _onCapabilitiesChanged(value: Enums.Capability) {
        const caps = SlimCoreElectronCallParticipant.convertCapabilities(value);
        if (caps !== this.participantCapabilities) {
            this._logger.info(`Computed capabilities: ${JSON.stringify(caps)}`);
            this.participantCapabilities = caps;
            this.raiseChangedDeferred();
        }
    }

    private _onAcceptedByChanged(acceptedEndpoint: string) {
        if (acceptedEndpoint !== this.acceptedBy) {
            this._logger.info(`Accepted by is set to ${piiUtils.scrubMriOrOmit(acceptedEndpoint)}`);
            this.acceptedBy = acceptedEndpoint;
            this.raiseChanged();
        }
    }

    private _onRoleChanged(role: string) {
        if (role !== this.role) {
            this._logger.info(`Role is set to ${role}`);
            this.role = role as ParticipantRosterRole;
            this.raiseChanged();
        }
    }

    private _onTenantIdChanged(tenantId: string) {
        if (tenantId !== this.tenantId) {
            this._logger.info(`TenantId is set to ${tenantId}`);
            this.tenantId = tenantId;
            this.raiseChanged();
        }
    }

    private _onMeetingRoleChanged(meetingRole: string) {
        if (meetingRole !== this.meetingRole) {
            this._logger.info(`_onMeetingRoleChanged meetingRole: ${meetingRole}`);
            this.meetingRole = meetingRole as MeetingRole;
            this.raiseChanged();
        }
    }

    private _onMriChanged(newMri: string) {
        if (newMri !== this.id) {
            // currently should only be called for RNL scenarios
            this._logger.info(`_onMriChanged from old mri ${piiUtils.scrubMriOrOmit(this.id)} to new Mri: ${piiUtils.scrubMriOrOmit(newMri)}`);
            this.previousId = this.id;
            this.id = newMri;
            this.raiseChanged();
        }
    }

    private _onDisplayNameChanged(displayName: string) {
        if (displayName !== this.displayName) {
            // currently should only be called for RNL scenarios
            this._logger.info(`_onDisplayNameChanged new displayName: ${piiUtils.scrubMriOrOmit(displayName)}`);
            this.displayName = displayName;
            this.raiseChanged();
        }
    }

    private raiseChangedDeferred(): void {
        if (!this._raiseChangedDefer) {
            this._raiseChangedDefer = _.defer(() => {
                delete this._raiseChangedDefer;
                this.raiseChanged();
            });
        }
    }

    private _getParticipantIdForSourceId(mediaType: MediaType, sourceId: number): string {
        try {
            if (this.endpoints != null) {
                for (const endpointDetail of this.endpoints.endpointDetails) {
                    if (endpointDetail != null && endpointDetail.mediaStreams != null) {
                        for (const stream of endpointDetail.mediaStreams) {
                            if (sourceId === stream.sourceId && SlimCoreCommon.mapMediaTypeStringToMediaType(stream.type) === mediaType) {
                                return endpointDetail.participantId != null ? endpointDetail.participantId : null;  // being paranoid
                            }
                        }
                    }
                }
            }
        } catch (error) {
            this._logger.error(`getSourceIdForMediaType caught exception error=${error}`);
        }

        return undefined;  // not found. null would indicate an entry for the sourceId with an invalid participantId
    }

    public getParticipantIdForSourceId(mediaType: MediaType, sourceId: number): string {

        const participantId = this._getParticipantIdForSourceId(mediaType, sourceId);

        if (!participantId) {
            this._logger.warn(`participantId is ${participantId} for mediaType: ${mediaType}, sourceId: ${sourceId}`);
        }

        return participantId;
    }

    public hasSourceId(mediaType: MediaType, sourceId: number): boolean {

        if (!this.enableParticipantLegToSourceIdMapping) {
            return this.getSourceIdForMediaType(mediaType) === sourceId;
        }

        return this._getParticipantIdForSourceId(mediaType, sourceId) !== undefined;
    }

    // returns the first found source id for any of the users endpoints or DATA_SOURCE_ID_ANY if no endpoint defines mediaStreams
    // Returns -1 if error
    public getSourceIdForMediaType(mediaType: MediaType): number {
        let sourceId: number = -1;

        try {
            if (this.endpoints) {
                outerloop:
                for (const endpointDetail of this.endpoints.endpointDetails) {
                    if (endpointDetail !== null && endpointDetail.mediaStreams !== undefined) {
                        for (const stream of endpointDetail.mediaStreams) {
                            if (SlimCoreCommon.mapMediaTypeStringToMediaType(stream.type) === mediaType) {
                                sourceId = stream.sourceId;
                                break outerloop;
                            }
                        }
                    } else {
                        // If there's no endpointDetailWithStreams, then it's a 1:1 call
                        sourceId = DATA_SOURCE_ID_ANY;
                    }
                }
            }
        } catch (error) {
            this._logger.error(`getSourceIdForMediaType caught exception error=${error}`);
        }
        return sourceId;
    }

    // returns -1 if modality isn't available and in case of errors
    public getSourceId(participantId: string, mediaType: MediaType): number {

        if (!this.enableParticipantLegToSourceIdMapping) {
            return this.getSourceIdForMediaType(mediaType);
        }

        if (!this.endpoints) {
            this._logger.warn('endpoints is invalid');
            return -1;
        }

        try {
            const participantLeg = _.find(this.endpoints.endpointDetails, endpointDetail => endpointDetail != null && endpointDetail.participantId === participantId);

            if (!participantLeg) {
                this._logger.warn(`participantId not found: ${participantId}`);
                // TODO(robedum) we should probably not send any messages in this case.
                // for now we fallback to old behavior to return the first found channel or DATA_SOURCE_ID_ANY if no endpoint defines mediaStreams
                // they might go to the wrong endpoint.
                return this.getSourceIdForMediaType(mediaType);
            }

            if (!participantLeg.mediaStreams) {
                this._logger.warn(`participantLeg ${participantId} does not have valid mediaStreams`);
                // TODO(robedum) we should find a more resilient way to determine if this is a 1-1 call and return DATA_SOURCE_ID_ANY.
                // for now we fallback to old behavior to return the first found channel or DATA_SOURCE_ID_ANY if no endpoint defines mediaStreams
                // they might go to the wrong endpoint.
                return this.getSourceIdForMediaType(mediaType);
            }

            const stream = _.find(participantLeg.mediaStreams, stream => SlimCoreCommon.mapMediaTypeStringToMediaType(stream.type) === mediaType);

            if (!stream) {
                this._logger.warn(`participantLeg ${participantId} has no channel of type ${mediaType}`);
                return -1;
            }

            return stream.sourceId;

        } catch (error) {
            this._logger.error(`getSourceId caught exception error=${error}`);
        }

        return -1;
    }

    private _isScreenSharerStream(stream: EndpointMediaStreamInfo): boolean {
        return stream.direction === 'sendonly' && stream.label === 'applicationsharing-video';
    }

    // this method assumes for 1-1 calls that a participant is actually sharing
    public getSharingParticipantLeg(): string {

        if (!this.endpoints || !this.endpoints.endpointDetails) {
            this._logger.warn('endpoints are not defined');
            return undefined;
        }

        if (this.endpoints.endpointDetails.length === 1) {
            // we might not have stream information in a 1-1 call
            return this.endpoints.endpointDetails[0].participantId;
        }

        const endpoint = _.find(this.endpoints.endpointDetails,
            e => e != null && e.mediaStreams != null && _.some(e.mediaStreams,
                s => this._isScreenSharerStream(s)));

        if (!endpoint) {
            this._logger.warn('could not find sharer');
            return undefined;
        }

        if (!endpoint.participantId) {
            this._logger.warn('invalid participantId');
            return undefined;
        }

        return endpoint.participantId;
    }

    private static getEndpointDetailsLogString(endpoints: CallParticipantEndpoints): string {

        if (!endpoints || !endpoints.endpointDetails) {
            return '';
        }

        const result: any = [];

        endpoints.endpointDetails.forEach(e => {
            result.push({
                participantId: e.participantId,
                endpointId: e.endpointId,
                clientVersion: e.clientVersion,
                mediaStreams: e.mediaStreams
            });
        });

        return JSON.stringify(result);
    }

    private _updateEndpointDetails(rawEndpointDetails: string) {
        const causeId = generateCauseId();
        if (this._rawEndpoints === rawEndpointDetails || !rawEndpointDetails) {
            return;
        }

        try {
            this.endpoints = JSON.parse(rawEndpointDetails) as CallParticipantEndpoints;
            this._rawEndpoints = rawEndpointDetails;
            this._logger.info(`Updating endpointDetails: ${SlimCoreElectronCallParticipant.getEndpointDetailsLogString(this.endpoints)}`);
            this._callTelemetry.recordEvent(PARTICIPANT_OPERATIONS_INTERNAL.UPDATE_PARTICIPANT_STREAM, getStreamInformationFromEndpoints(this.endpoints), causeId);

        } catch (e) {
            this._logger.warn(`Failed to parse call member endpointDetails - ${e} -${rawEndpointDetails}`);
            return;
        }

        this.raiseChanged();
    }

    private _onStatusChanged(value: Enums.CallStatus, updateEndpointDetails = true, failure?: number) {
        const causeId = generateCauseId();
        this._logger.info(`[${causeId}]Status changed ${value} (${Enums.CallStatus[value]})`);
        const failureReason = failure !== undefined ? failure : this.getIntProperty(Enums.Property.ParticipantFailureReason);
        const participantState = SlimCoreElectronCallParticipant.convertParticipantStatus(value);

        if (participantState === undefined) {
            this._logger.warn(`Unrecognized ${participantState}`);
            return;
        }

        this.setState(
            participantState, causeId,
            EnumConverter.convertStatusAndFailureReasonToParticipantReason(value, failureReason), updateEndpointDetails);

        if (this._admitParticipantDeffered && SlimCoreElectronCallParticipant.isParticipantActive(this.state)) {
            this._admitParticipantDeffered.resolve();
            this._admitParticipantDeffered = null;
        }

        if (this._removeParticipantDeffered && !SlimCoreElectronCallParticipant.isParticipantActive(this.state)) {
            this._removeParticipantDeffered.resolve();
            this._removeParticipantDeffered = null;
        }
    }

    private _onParticipantContentSharingRoleChanged(value: Enums.ContentSharingRole) {
        this._logger.info(`Content sharing role changed ${value} (${Enums.ContentSharingRole[value]})`);
        this.setContentSharingRole(SlimCoreElectronCallParticipant.convertContentSharingRole(value));
    }

    private _onFailureReasonChange(value: Enums.ParticipantFailureReason) {
        const causeId = generateCauseId();
        this._logger.info(`[${causeId}]Failure reason changed ${value} (${Enums.ParticipantFailureReason[value]})`);
        const state = this.getIntProperty(Enums.Property.ParticipantStatus);
        this.setState(SlimCoreElectronCallParticipant.convertParticipantStatus(state), causeId,
            EnumConverter.convertFailureReasonToParticipantReason(value));
    }

    private _onAdmitFailureReasonChange(value: Enums.ParticipantFailureReason) {
        this._logger.info(`Admit failure reason changed ${value} (${Enums.ParticipantFailureReason[value]})`);

        const reason = EnumConverter.convertFailureReasonToParticipantReason(value);
        this.rejectAdmitParticipantDeferred(reason);
    }

    private _onRemoveFailureReasonChange(value: Enums.ParticipantFailureReason) {
        this._logger.info(`Remove failure reason changed ${value} (${Enums.ParticipantFailureReason[value]})`);

        const reason = EnumConverter.convertFailureReasonToParticipantReason(value);
        this.rejectRemoveParticipantDeferred(reason);
    }

    public setVoiceLevel(value: number) {
        if (this.voiceLevel === value) {
            return;
        }

        this.voiceLevel = value;
        this.raiseChanged();
    }

    private _onIsServerMuted(isServerMuted: boolean) {
        if (isServerMuted !== this.isServerMuted) {
            this.isServerMuted = isServerMuted;
            this.raiseChanged();
        }
    }

    private _onDominantSpeakerRankChanged(value: number) {
        if (this.dominantSpeakerRank === value) {
            return;
        }

        this._logger.info(`DominantSpeakerRank changing from ${this.dominantSpeakerRank} to ${value}`);

        this.dominantSpeakerRank = value;
        this.raiseChanged();
    }

    private _onBalanceUpdateChanged(rawBalanceUpdate: string) {
        // Balance updates can occur at a frequency of up to one per minute per
        // remote endpoint.
        if (_.isEmpty(rawBalanceUpdate)) {
            if (!_.isEmpty(this.balanceUpdates)) {
                this.balanceUpdates = {};
                this.raiseChanged();
            }
            return;
        }

        try {
            const balanceUpdate: CallParticipantBalanceUpdate = JSON.parse(rawBalanceUpdate);

            if (_.isEqual(this.balanceUpdates[balanceUpdate.sender.endpointId], balanceUpdate)) {
                return;
            }

            this.balanceUpdates[balanceUpdate.sender.endpointId] = balanceUpdate;
        } catch (e) {
            this._logger.warn(`Failed to parse call member balanceUpdate - ${e} -${rawBalanceUpdate}`);
            return;
        }

        this.raiseChanged();
    }

    private _onParticipantDiagnosticsCodeChanged(value: string) {
        this._logger.info(`_onParticipantDiagnosticsCodeChanged diagnosticsCode: ${value}`);
        this._updateDiagnosticsCode(value);
    }

    private _updateDiagnosticsCode(diagnosticsCode: string) {
        if (this._rawCallEndDiagnosticsInfo === diagnosticsCode || !diagnosticsCode) {
            return;
        }

        try {
            this.callEndDiagnosticsInfo = JSON.parse(diagnosticsCode) as CallEndDiagnosticsInfo;
            this._rawCallEndDiagnosticsInfo = diagnosticsCode;
            this.raiseChanged();
        } catch (err) {
            this._logger.info(`Failed to parse raw diagnostis details. Error: ${err}, diagnosticsCode: ${diagnosticsCode}`);
        }
    }

    public setContentSharingRole(role: ParticipantContentSharingRole): void {
        if (this.contentSharingRole === role) {
            return;
        }

        this.contentSharingRole = role;
        this.raiseChanged();
    }

    public setState(state: ParticipantState, causeId: CauseId, reason?: ParticipantStateReason, updateEndpointDetails = true): void {
        if (this.state === state) {
            return;
        }
        const fnLogger = this._logger.createFnLogger('setState', causeId);
        const validNextStates = validStateTransitions[this.state];
        const isValidNextState = validNextStates.indexOf(state) >= 0;

        const telemetryData = { state: this.state, reason: this.stateReason, endpoints: [] as any[] };
        if (this.endpoints && this.endpoints.endpointDetails && this.endpoints.endpointDetails.length) {
            telemetryData.endpoints = getEndpointInformationForTelemetry(this.endpoints.endpointDetails);
        }

        if (!isValidNextState) {
            fnLogger.logFailure(`Invalid state transition ${this.state}->${state} attempted`);
            this._callTelemetry.recordEvent(PARTICIPANT_OPERATIONS_INTERNAL.SET_PARTICIPANT_STATE_INVALID, telemetryData, causeId);
            return;
        }
        fnLogger.info(`state=${this.state}->${state} reason=${this.stateReason}->${reason}`);
        this._callTelemetry.recordEvent(PARTICIPANT_OPERATIONS_INTERNAL.SET_PARTICIPANT_STATE, telemetryData, causeId);
        this.state = state;
        this.stateReason = reason;

        if (this.state === ParticipantState.Connected && updateEndpointDetails) {
            const endpointDetails = this.getStrProperty(Enums.Property.ParticipantEndpointDetails);
            this._updateEndpointDetails(endpointDetails);
        } else if (this.state === ParticipantState.Disconnected) {
            const diagnostics = this.getStrProperty(Enums.Property.ParticipantDiagnosticsCode);
            this._updateDiagnosticsCode(diagnostics);
        }

        this.raiseChangedDeferred();
    }

    private static convertParticipantStatus(status: Enums.CallStatus) {
        if (SlimCoreElectronCall.isCallRinging(status)) {
            return ParticipantState.Ringing;
        } else if (SlimCoreElectronCall.isCallTerminated(status)) {
            return ParticipantState.Disconnected;
        } else if (SlimCoreElectronCall.isCallConnected(status)) {
            return ParticipantState.Connected;
        } else if (SlimCoreElectronCall.isCallConnecting(status)) {
            return ParticipantState.Connecting;
        } else if (SlimCoreElectronCall.isCallEarlyMedia(status)) {
            return ParticipantState.EarlyMedia;
        } else if (SlimCoreElectronCall.isCallInLobby(status)) {
            return ParticipantState.InLobby;
        } else if (status === Enums.CallStatus.LocalHold || status === Enums.CallStatus.RemoteHold) {
            return ParticipantState.OnHold;
        }

        return undefined;
    }

    public static isParticipantActive(state: ParticipantState) {
        switch (state) {
            case ParticipantState.Connected:
            case ParticipantState.EarlyMedia:
            case ParticipantState.OnHold:
                return true;
            default:
                return false;
        }
    }

    private static convertContentSharingRole(role: Enums.ContentSharingRole) {
        switch (role) {
            case Enums.ContentSharingRole.Attendee:
                return ParticipantContentSharingRole.Attendee;
            case Enums.ContentSharingRole.Presenter:
                return ParticipantContentSharingRole.Presenter;
            default:
                return ParticipantContentSharingRole.None;
        }
    }

    private static convertCapabilities(caps: Enums.Capability): Capabilities {
        return {
            canConference: !!(caps & Enums.Capability.Conference),
            canShareScreen: !!(caps & Enums.Capability.ShareScreen),
            canMerge: !!(caps & Enums.Capability.Merge)
        };
    }

    public addAdmitParticipantDeferred(admitParticipantDeffered: async.Deferred<void>): void {
        this._admitParticipantDeffered = admitParticipantDeffered;
    }

    public rejectAdmitParticipantDeferred(terminatedReason: ParticipantStateReason): void {
        if (this._admitParticipantDeffered) {
            this._admitParticipantDeffered.reject(terminatedReason);
            this._admitParticipantDeffered = null;
        }
    }

    public addRemoveParticipantDeferred(removeParticipantDeffered: async.Deferred<void>): void {
        this._removeParticipantDeffered = removeParticipantDeffered;
    }

    public rejectRemoveParticipantDeferred(terminatedReason: ParticipantStateReason): void {
        if (this._removeParticipantDeffered) {
            this._removeParticipantDeffered.reject(terminatedReason);
            this._removeParticipantDeffered = null;
        }
    }

    public stopVideos(): Promise<void> {
        return this._slimCoreElectronRemoteStreamManager.stopVideos();
    }

    public dispose(): void {
        if (this._slimCoreElectronRemoteStreamManager) {
            this._slimCoreElectronRemoteStreamManager.dispose();
        }
        this.streams[StreamType.Video] = [];
        this.streams[StreamType.ScreenSharing] = [];

        if (this._admitParticipantDeffered) {
            this._admitParticipantDeffered.resolve();
            this._admitParticipantDeffered = null;
        }

        if (this._removeParticipantDeffered) {
            this._removeParticipantDeffered.resolve();
            this._removeParticipantDeffered = null;
        }

        this.slimcoreObjectId = 0;
        super.dispose();
    }
}



