import { CallingLogger } from 'skype-calling-interface';

interface BindingRecord<T> {
    createPromise: Promise<T>;
    owners: any[];
    chained: boolean;
    releasePromise?: Promise<void>;
}

interface BindingMap<T> {
    [key: number]: BindingRecord<T>;
}

export class SlimCoreElectronBindingManager<T> {
    private _bindingMap: BindingMap<T> = {};

    public constructor(
        private _create: (id: number) => Promise<T>,
        private _release: (id: number, binding: T) => Promise<void>,
        private _logger: CallingLogger) {
        this._logger.info('BindingManager created');
    }

    public acquire(owner: any, videoObjectId: number): Promise<T> {
        if (!this._bindingMap[videoObjectId] || this._bindingMap[videoObjectId].releasePromise) {
            let chained = false;
            let prequisitePromise: Promise<void>;
            if (this._bindingMap[videoObjectId]) {
                prequisitePromise = this._bindingMap[videoObjectId].releasePromise.catch(e => this._logger.info(`Release binding error: ${e}`));
                chained = true;
            } else {
                prequisitePromise = Promise.resolve<void>();
            }
            const createPromise = prequisitePromise.then(() => this._create(videoObjectId)).then(binding => {
                return binding;
            }).catch(e => {
                delete this._bindingMap[videoObjectId];
                throw e;
            });
            this._bindingMap[videoObjectId] = { createPromise, owners: [], chained };
        }

        this._bindingMap[videoObjectId].owners.push(owner);
        return this._bindingMap[videoObjectId].createPromise;
    }

    public async release(owner: any, videoObjectId: number): Promise<void> {
        if (this._bindingMap[videoObjectId]) {
            const binding = await this._bindingMap[videoObjectId].createPromise;
            const index = this._bindingMap[videoObjectId].owners.indexOf(owner);
            if (index > -1) {
                this._bindingMap[videoObjectId].owners.splice(index, 1);
            }
            if (this._bindingMap[videoObjectId].owners.length === 0) {
                this._bindingMap[videoObjectId].releasePromise = this._release(videoObjectId, binding)
                    .then(() => {
                        if (!this._bindingMap[videoObjectId].chained) {
                            // delete only if not chained to create again
                            delete this._bindingMap[videoObjectId];
                        }
                    });
                return this._bindingMap[videoObjectId].releasePromise;
            }
        }
    }
}



