/**
 * ChainedPromise.ts
 * Author: Richard Speyer
 * Copyright: Microsoft 2016
 *
 * SyncTasks.Defer equivalent for ES6 Promises
 *
 * This class allows serialization of a set of promises without leaking the
 * result of one promise to subsequent ones
 */

import { CallingLogger } from 'skype-calling-interface';
import { generateCauseId } from './causeId';

// The objective of promise chaining is to serialize asynchronous operations, but
// not to cascade errors from one promise to the next. To handle this, we insert
// a catch() that swallows any previous errors before invoking the next promise
//
// This is safe because the error has already been propogated out as a result of
// its originating Promise, and hopefully handled accordingly
export default class ChainedPromise {
    private _promise = Promise.resolve<any>(undefined);
    private _actionQueue: string[] = [];

    constructor(private _logger: CallingLogger) { }

    public chainPromise<T>(promise: () => Promise<T>,
            action: string | (() => string),
            causeId = generateCauseId(),
            reset = false): Promise<T> {
        const startTime = Date.now();
        const actionMsg = ((typeof action === 'function' ? action() : action) || '').toString();
        this._actionQueue.push(actionMsg);
        this._logger.info(`[${causeId}][${actionMsg}]enqueueing, queue=[${this._actionQueue}], reset=${reset}`);

        const removeFromQueue = () => {
            this._actionQueue = this._actionQueue.filter(a => a !== actionMsg);
        };
        if (reset) {
            this.reset(causeId);
        }

        this._promise = this._promise
        .catch(() => {
            return undefined;
        })
        .then(() => {
            const delayMs = Date.now() - startTime;
            this._logger.info(`[${causeId}][${actionMsg}]begin, delayMs=${delayMs}`);
            return promise();
        });

        // Intentionally separate so we pass up a promise that still rejects
        this._promise.then(() => {
            removeFromQueue();
            const durationMs = Date.now() - startTime;
            this._logger.info(`[${causeId}][${actionMsg}]success, durationMs=${durationMs}`);
        }, error => {
            removeFromQueue();
            const durationMs = Date.now() - startTime;
            this._logger.warn(`[${causeId}][${actionMsg}]failed=${error}, drationMs=${durationMs}`);
        });

        return this._promise;
    }

    public reset(causeId?: string): void {
        this._logger.info(`[${causeId}] resetting`);
        this._promise = Promise.resolve<any>(undefined);
    }
}



