
import { CallState } from 'skype-calling-interface';

export function callStateIsAnyOf(currentState: CallState, states: CallState[]): boolean {
    return states.indexOf(currentState) !== -1;
}

export const validStateTransitions: { [state: number]: CallState[] } = {
    [CallState.None]: [CallState.Notified, CallState.Connecting, CallState.Observing, CallState.Disconnecting, CallState.InLobby, CallState.Preheating],
    [CallState.Notified]: [CallState.Connecting, CallState.Disconnecting, CallState.Disconnected, CallState.InLobby],
    [CallState.Connecting]: [CallState.Notified, CallState.Connected, CallState.Disconnecting, CallState.Disconnected, CallState.EarlyMedia, CallState.InLobby],
    [CallState.Connected]: [CallState.Disconnecting, CallState.Disconnected, CallState.LocalHold, CallState.RemoteHold, CallState.InLobby],
    [CallState.Observing]: [CallState.Notified, CallState.Preheating, CallState.InLobby, CallState.Connecting, CallState.Disconnecting, CallState.Disconnected],
    [CallState.LocalHold]: [CallState.Disconnecting, CallState.Disconnected, CallState.Connected, CallState.RemoteHold, CallState.InLobby],
    [CallState.RemoteHold]: [CallState.Disconnecting, CallState.Disconnected, CallState.Connected, CallState.LocalHold, CallState.InLobby],
    [CallState.EarlyMedia]: [CallState.Connected, CallState.Disconnecting, CallState.Disconnected, CallState.InLobby],
    [CallState.InLobby]: [CallState.Connected, CallState.Disconnecting, CallState.Disconnected, CallState.EarlyMedia, CallState.LocalHold, CallState.RemoteHold],
    [CallState.Preheating]: [CallState.Preheated, CallState.Notified, CallState.Connecting, CallState.Disconnecting, CallState.Disconnected],
    [CallState.Preheated]: [CallState.Connecting, CallState.Disconnecting, CallState.Disconnected],
    [CallState.Disconnecting]: [CallState.Disconnected],
    [CallState.Disconnected]: []
};



